define([
  'core/js/adapt',
  'core/js/views/questionView',

  // \/ These libraries are included with the "adapt-jquery-ui" extension.
  'libraries/jquery-ui.interactions',
  'libraries/jquery.ui.touch-punch' // MUST BE INCLUDED! Fixes several compatibility issues that jquery-ui has with touch devices.
], function(Adapt, QuestionView) {

	/**
	 * FIXME:
	 * - data-adapt-index gives away the correct order the draggables are meant to go in.
	 * 
	 * TODO:
	 * - Add keyboard support (maybe, user selects items with arrow keys, then presses Enter to move them down, and Shift+Enter to move them up?).
	 */

	// Makes jquery-ui.draggable() work with touch devices.
	// Depends on jquery-ui, and must be loaded AFTER it.
	/*require.config({
        shim: {
            "libraries/jquery-ui.touch-punch": {
                deps: ["libraries/jquery-ui.reorder"] // Standalone jquery-ui library which only includes the functionality this component needs.
            }
        }
    });*/

	const JITTER_THRESHOLD = 1;
	const VIEWPORT_SCROLL_THRESHOLD = 0.175; // If the event.pageY is within this ratio of the edges of the viewport, then have the page scroll.
	const VIEWPORT_SCROLL_SPEED = 10;

	class ReorderView extends QuestionView {

		events() {
			return {
				"mousedown .js-reorder-item" : "onItemMousedown",
				"touchstart .js-reorder-item" : "onItemMousedown",
				"mouseup .js-reorder-item" : "onItemMouseup",
				"touchend .js-reorder-item" : "onItemMouseup"

				/*'focus .js-item-input'  : 'onItemFocus',
				'blur .js-item-input'   : 'onItemBlur',
				'change .js-item-input' : 'onItemSelect',
				'keyup .js-item-input'  : 'onKeyPress'*/
			}
		}

		//isCorrectAnswerShown: false,

		initialize() {

			this.itemCount = this.model.getChildren().length;
			this.itemElementTopCache = new Array(this.itemCount);

			QuestionView.prototype.initialize.apply(this, arguments);
			this.update = _.debounce(this.update.bind(this), 1);
			
			this.listenTo(this.model, {
				"change:_isEnabled change:_isComplete change:_isSubmitted": this.update
			});
		}

		resetQuestionOnRevisit() {
			this.resetQuestion();
		}

		setupQuestion() {

			this.model.setupRandomisation();
		}

		postRender() {
			QuestionView.prototype.postRender.apply(this, arguments);

			// Makes jquery-ui.draggable() work with touch devices.
			// Must be loaded AFTER jQuery-ui.
			// FIXME: When the content is uploaded to VitalSource Bookshelf and viewed online, this can sometimes TIME OUT!
			//require(['libraries/jquery-ui.touch-punch.min'], () => {

				this.renderItems();
				this.updateMarking(); // Custom: initial call to set up aria labels for the inputs.
									// FIXME: Applying the initial aria could be done better...

				this.setUpEventListeners();

				this.setReadyStatus();
			//});
		}

		setUpEventListeners() {
			this.listenTo(this.model.getChildren(), {
				"change:_isGrabbed": this.onItemGrabbedChange,
				"change:_userIndex": this.onItemUserIndexChange,
				"change:_isVisited": this.onItemVisitedChange,
				"change:_isCorrect": this.onItemCorrectChange,
				"change:_isLocked": this.onItemLockedChange,
				"change:_positionTop": 	this.onItemPositionTopChange,
			});
		}

		/**
		 * The item should apply its grabbed class as soon as it's clicked.
		 * @param {*} event 
		 */
		onItemMousedown(event) {

			let itemModel = this.getItemModel($(event.currentTarget));
			if (itemModel.get("_isLocked")) return;
			$(event.currentTarget).addClass("is-grabbed");
			//itemModel.set("_isGrabbed", true);
		}

		onItemMouseup(event) {
			let itemModel = this.getItemModel($(event.currentTarget));
			if (itemModel.get("_isLocked") || itemModel.get("_isGrabbed")) return;
			$(event.currentTarget).removeClass("is-grabbed");
			//itemModel.set("_isGrabbed", false);
		}

		getItemModel($itemElement) {
			let index = $itemElement.attr("data-adapt-index");
			return this.model.getChildren().find((itemModel) => {
				return itemModel.get("_index") == index;
			});
		}

		getItemElement(itemModel) {
			return this.$(".js-reorder-item").filter('[data-adapt-index=' + itemModel.get("_index") + ']');
		}

		onItemGrabbedChange(itemModel, isGrabbed) {
			let $item = this.getItemElement(itemModel);
			$item.toggleClass("is-grabbed", isGrabbed);
		}

		onItemVisitedChange(itemModel, isVisited) {
			let $item = this.getItemElement(itemModel);
			$item.toggleClass("is-visited", isVisited);
		}

		onItemLockedChange(itemModel, isLocked) {
			let $item = this.getItemElement(itemModel);
			$item.toggleClass("is-disabled", isLocked);

			// Locked items can no longer be dragged.
			this.getItemElement(itemModel).draggable("option", "disabled", isLocked);
		}

		/**
		 * Note: isCorrect can === undefined, in which case the state is neither correct nor incorrect.
		 * @param {*} itemModel 
		 * @param {*} isCorrect 
		 */
		onItemCorrectChange(itemModel, isCorrect) {
			let $item = this.getItemElement(itemModel);
			$item
				.toggleClass("is-correct", isCorrect === true)
				.toggleClass("is-incorrect", isCorrect === false);
		}

		onItemUserIndexChange(itemModel, userIndex) {
			let $item = this.getItemElement(itemModel);
			$item.attr("data-adapt-user-index", userIndex);
		}

		onItemPositionTopChange(itemModel, positionTop) {
			
			if (itemModel.get("_isGrabbed")) return;

			let $item = this.getItemElement(itemModel);
			
			_.debounce(() => {
				let translate = positionTop - $item.position().top;
				// Prevent slight jitters from small values.
				translate = Math.abs(translate) < JITTER_THRESHOLD ? 0 : translate;
				
				if (translate) {
					$item.find(".js-reorder-item-transform")
					.css("transform", "translateY(" + translate + "px)");
				}
				else {
					$item.find(".js-reorder-item-transform")
					.css("transform", "");
				}
			},0)();
		}

		// Too costly. Do not use.
		/*getElementBB($el) {
			let bb = $el[0].getBoundingClientRect();
			return bb;
		}*/

		renderItems() {
			
			this.model.getChildren().each((itemModel) => {
				this.setUpItem(itemModel);
				this.getItemElement(itemModel).draggable("option", "disabled", itemModel.get("_isLocked"));
			});
		}

		refreshItemElementTopCache() {
			
			let top = 0;
			this.model.getChildren().each((itemModel, index) => {
				this.itemElementTopCache[index] = top;
				top += this.getItemElement(itemModel).outerHeight();
			});
		}

		// Calculates the top offset of the specified item's element based on _userIndex.
		getItemElementTop(itemModel) {
			return this.itemElementTopCache[itemModel.get("_userIndex")];
		}

		_getFirstEmptyIdx(arr) {

			let idx = 0;
			while(arr[idx]) {
				idx++;
				if (idx >= arr.length) throw "Unexpected error has occured: idx out of range of arr (there are no falsy elements)";
			}

			return idx;
		}

		setUpItem(itemModel) {

			let $item = this.getItemElement(itemModel);
			let $items;

			let curTop;  // The top value of the dragged item (current frame).
			let lastTop; // The top value of the dragged item (last frame).

			let children;
			let offsetY = 0;

			$item.draggable(
			{
				containment: this.$(".js-reorder-containment"),
				scroll: false, // Prevent the page scrolling while dragging (default jQuery implementation is bugged).
				//scrollSpeed: 5,

				start: (event, ui) => {
					itemModel.set("_isGrabbed", true);
					$items = this.$(".js-reorder-item");

					offsetY = 0;
					
					// Used to detect the direction along the Y axis the draggable is moving.
					let top = $item.position().top;
					curTop = top;
					lastTop = top;

					// Sort the items by their _userIndex.
					children = this.model.getChildren();
					children.sort();
				},

				// TODO: define these callback functions in the View itself and reference them here?
				// Called each tick the draggable is moved.
				drag: _.throttle((event, ui) => { // Requires a lot of processing. Throttled to improve performance.

					this.refreshItemElementTopCache();
					itemModel.set("_isGrabbed", true);

					let bb = $item[0].getBoundingClientRect();
					offsetY += this.scrollPage($item, dir, bb.top, bb.bottom);
					ui.position.top += offsetY;
					$item.css("top", ui.position.top);

					curTop = $item.position().top;
					let movement = curTop - lastTop;
					let dir = movement == 0 ? 0 : (movement < 0 ? -1 : 1); // The direction the draggable is travelling.
					lastTop = curTop;

					if (dir == 0) return;

					//let data = $item.data('ui-draggable');
					//console.log(data);

					

					//console.log(ui.position.top);
					//$item.css("top", ui.position.top);

					// Temporary array made which will contain the new order of the items.
					let newItemOrder = new Array(this.itemCount);
					let itemsAbove = [];
					let itemsBelow = [];
					
					// Fill areas of the newItemOrder that will always be consistent.
					// [ ][L][g][ ][L]
					children.each((item) => {
						if (!item.get("_isLocked")) return;

						// _index used instead of _userIndex as a fallback
						// (_isLocked items should ALWAYS have _index == _userIndex.).
						newItemOrder[item.get("_index")] = item; 
					});

					let curMiddle = curTop + $item.outerHeight()/2;

					// Travelling down.
					if (dir > 0) {

						// Find all items with a greater _userIndex that are above this item.
						children.each((eModel) => {

							// Ignore this $item when calculating.
							if (eModel == itemModel) return;
							if (eModel.get("_isLocked")) return; // Locked items can never move.

							// FIXME: This is likely what's causing the stutter.
							// Need to make item tops cache themselves until window resize / _userIndex change.
							let eTop = this.getItemElementTop(eModel);
							
							// Determine which items are above this one's middle point, and add them.
							if (eTop < curMiddle) itemsAbove.push(eModel);
							else itemsBelow.push(eModel);
						});
					}
					// Travelling up.
					else {

						// Find all items with a greater _userIndex that are above this item.
						children.each((eModel) => {

							// Ignore this $item when calculating.
							if (eModel == itemModel) return;
							if (eModel.get("_isLocked")) return; // Locked items can never move.

							let eBottom = this.getItemElementTop(eModel) + this.getItemElement(eModel).outerHeight();

							// Determine which items are below this one's middle point, and add them.
							if (eBottom > curMiddle) itemsBelow.push(eModel);
							else itemsAbove.push(eModel);
						});
					}

					// Create the final item array.
					_.each(itemsAbove, (item) => {
						// Find the first empty element in the newItemOrder and add this itemAbove to it.
						newItemOrder[this._getFirstEmptyIdx(newItemOrder)] = item;
					});
					// Find the first empty element and add the currently dragging item to it.
					newItemOrder[this._getFirstEmptyIdx(newItemOrder)] = itemModel;
					_.each(itemsBelow, (item) => {
						// Find the first empty element in the newItemOrder and add this itemBelow to it.
						newItemOrder[this._getFirstEmptyIdx(newItemOrder)] = item;
					});

					// Change the _userIndex values for all items based on the newItemOrder.
					_.each(newItemOrder, (item, index) => {
						item.set("_userIndex", index);
					});
					this.model.getChildren().sort();

					// Update the items _positionTop.
					_.each(newItemOrder, (item, index) => {
						item.set("_positionTop", this.getItemElementTop(item));
					});
				}, 1),
				stop: () => { 

					let $container = this.$(".reorder__items-container");

					let itemGrabbedTop = $item.offset().top;

					// Sort all items in order and remove their temporary offsets.
					this.model.getChildren().each((item) => {
						let $item = this.getItemElement(item);
						$item.find(".js-reorder-item-transform").css("transform", "");
						$item.css("top", "");
						$container.append($item);
					});

					// Smoothly transition the item that was just grabbed.
					let itemGrabbedOffset = itemGrabbedTop - $item.offset().top;
					$item
						.addClass("is-grabbed")
						.find(".js-reorder-item-transform")
						.css("top", itemGrabbedOffset + "px")
						.hide(0).show(0);
					_.debounce(() => {
						$item.find(".js-reorder-item-transform")
							.css("top", "0");
							itemModel.set("_isGrabbed", false);
					}, 0)();
				},
				axis: 'y'//,
				//
			});
		}

		scrollPage($item, dragDir, draggableTop, draggableBottom) {

			let bb = this.$(".js-reorder-containment")[0].getBoundingClientRect();

			if (draggableTop < bb.top) {
				//console.log(bb.top - draggableTop);
				return bb.top - draggableTop;
				//$item.css("top", "+=" + (bb.top - draggableTop) + "px");
				return 0;
			}
			if (draggableBottom > bb.bottom) {
				//console.log(bb.bottom - draggableBottom);
				return bb.bottom - draggableBottom;
				$item.css("top", "-=" + (draggableBottom - bb.bottom) + "px");
				return 0;
			}
			
			let pageY = draggableTop + (draggableBottom - draggableTop)/2;
			let screenHeight = Adapt.device.screenHeight;
			let dir = 0;
			if (pageY < screenHeight * VIEWPORT_SCROLL_THRESHOLD) dir = -1;
			else if (pageY > screenHeight - screenHeight * VIEWPORT_SCROLL_THRESHOLD) dir = 1;
			
			//console.log("MIDDLE");

			// If the pageY is within the center of the viewport, no need to scroll.
			if (dir == 0 /*|| dir != dragDir*/) return 0;
			
			$.scrollTo(dir > 0 ? "+=" + VIEWPORT_SCROLL_SPEED + "px" : "-=" + VIEWPORT_SCROLL_SPEED + "px", {});

			//console.log("GO");
			return dir * VIEWPORT_SCROLL_SPEED;
		}

		onQuestionRendered() {
			this.update();
		}

		// Blank method to add functionality for when the user cannot submit
		// Could be used for a popup or explanation dialog/hint
		onCannotSubmit() {}

		// This is important and should give the user feedback on how they answered the question
		// Normally done through ticks and crosses by adding classes
		showMarking() {
			this.update();
			this.updateMarking();
		}

		resetQuestion() {

			// Not implemented (DD doesn't use this at this time).
			return;

			this.model.set('_isAtLeastOneCorrectSelection', false);
			var resetAll = this.model.get('_shouldResetAllAnswers');

			this.model.getChildren().each((item, index) => {

				if (item.get("_isCorrect") && resetAll === false) return;
				
			}, this);
		}

		showCorrectAnswer() {
			this.isCorrectAnswerShown = true;
			this.update();
		}

		hideCorrectAnswer() {
			this.isCorrectAnswerShown = false;
			this.update();
		}

		update(model) {
			//this.updateSelection();
			// \/ questionPlus will call this.updateMarking() when necessary. It shouldn't be invoked here while it's in use.
			//this.updateMarking();
		}

		updateSelection() {

		}

		updateMarking() {

			// No need to implement. All marking happens when the itemModel properties change.

			//this.updateSelection();
		}

	};

	return ReorderView;

});
