define([
    'core/js/adapt'
], function(Adapt) {

    // View for each individual category's content.
    // Gets passed a collection by SideMenuView.

    class SideMenuCategoryResourcesView extends Backbone.View {

        className() {
            return "smcategory smcategory-type-" + this.model.get("_type");
        }

        init(){}

        initialize(){
            
            this.init();
            this.render();

            //this.setupTopicData();
        }

        render(){

            var template = Handlebars.templates[this.constructor.template];

            // Always provide a link to a PDF version of the content.
            // FIXME: Need to throw an error (in Grunt) if the PDF doesn't exist!
            let pdfDownloadName = Adapt.course.get("title") + " - " + this.model.get("_parent").get("title") + ".pdf";

            // FIXME: Hardcoded.
            let resources = this.model.get("_parent").get("_pageModel").get("_menuData")._resources;

            var data = {
                _type:              this.model.get("_type"),
                _pdfDownloadName:   pdfDownloadName,
                _isPDFEnabled:      resources._isPDFEnabled, // TEMP.
                _items:             resources._items || []   // FIXME: Resources not implemented yet.
            }

            this.$el.html(template(data));
        }
        
    };

    SideMenuCategoryResourcesView.template = 'sideMenuCategoryResources';
    return SideMenuCategoryResourcesView;
});
