define([
  'core/js/adapt',
  'core/js/views/componentView',
  'core/js/models/componentModel'
], function(Adapt, ComponentView, ComponentModel) {

  /**
   * Modified to support the adapt-dd-img extension (if the src has the *--r.svg suffix).
   */

  var GraphicView = ComponentView.extend({

    preRender: function() {

      // DD: Modified:
      // -------------------------------------------------------------
      // Replace the image with an adapt-dd-img helper if the src suffix is *--r.svg.
      // FIXME: Maybe all images should just use the img helper for consistency.
      if (this.model.get("_graphic")?.large.match(/--r\.svg$/)) {
        this.model.set("_isDDImg", true);
      }
      // Allow for image sizing to be used on the image itself.
      /*let sizing = this.model.get("_graphic")?._sizing;
      if (sizing) {
        sizing._classes = Adapt.sizing.getSizingClass(sizing);
        this.model.get("_graphic")._sizing = sizing;
      }*/
      // -------------------------------------------------------------

      this.listenTo(Adapt, 'device:changed', this.resizeImage);

      this.checkIfResetOnRevisit();
    },

    postRender: function() {
      this.resizeImage(Adapt.device.screenSize, true);
    },

    checkIfResetOnRevisit: function() {
      var isResetOnRevisit = this.model.get('_isResetOnRevisit');

      if (isResetOnRevisit) {
        this.model.reset(isResetOnRevisit);
      }
    },

    resizeImage: function(width, setupInView) {
      var imageWidth = width === 'medium' ? 'small' : width;
      var imageSrc = (this.model.get('_graphic')) ? this.model.get('_graphic')[imageWidth] : '';
      this.$('.js-graphic-set-image-src, .js-graphic-set-image-src img').attr('src', imageSrc); // Modified

      this.$('.graphic__widget').imageready(function() {
        this.setReadyStatus();

        if (setupInView) {
          // DD: Modified (if there's no graphic; track completion on the header instead)
          this.setupInviewCompletion(this.model.get("_graphic") ? '.graphic__widget' : '.graphic__header');
        }
      }.bind(this));
    }
  });

  return Adapt.register('graphic', {
    model: ComponentModel.extend({}),// create a new class in the inheritance chain so it can be extended per component type if necessary later
    view: GraphicView
  });

});
