define([
    'handlebars',
    'core/js/adapt'
], function(Handlebars, Adapt) {

    class MarkChainView extends Backbone.View {

        className() {
            let classes = [
                "markchain",
                this.model.get("theme") ? `theme-${this.model.get("theme")}` : "",
                this.model.get("class") || ""
            ];
            return classes.join(" ");
        }

        attributes() {

            let attr = {
                "name": this.model.get("name"),
                "style": this.model.get("style"),
                "data-markchain-idx": this.model.get("_idx") || -1  // Optional _idx property.
            }

            // adapt-stickyElement compatibility.
            // EDIT: Nevermind; this doesn't work.
            /*if (this.model.get("se_hide")) {
                attr["data-stickyelement-mark-idx"] = this.model.get("se_hide");
            }*/

            return attr;
        }

        tagName() {
            return "span";
        }

        events() {
            return {
                
            }
        }

        async initialize() {
            
            this.render();
            this.setUpEventListeners();
        }
        
        setUpEventListeners() {

            // Slightly delay the callback (there may be other, higher priority resize callbacks that need to be called first before markChain's size is recalculated).
            this.onDeviceResized = _.debounce(this.onDeviceResized.bind(this), 50);

            this.listenTo(this.model, "change:_next", this.onNextChanged);
            this.listenTo(Adapt, "device:resize", this.onDeviceResized);
        }

        render() {

            let data = this.model.toJSON();
            let template = Handlebars.templates["markChain"];
            this.$el.html(new Handlebars.SafeString(template(data)).string);
        }

        // If this View starts off hidden after it's rendered, it will need to be resized once it becomes visible again.
        setUpHiddenObserver() {

            this.$el.onUnhidden(() => {

                // This delay is important. If it's not added, then the _hiddenObserver will detect the updateArrow()
                // mutation and reset the debounce timer.
                _.delay(() => {
                    this.updateArrow();
                }, 0);
            });
        }

        // Update the arrows whenever the device is resized (as the MarkChainViews will likely have moved).
        onDeviceResized() {
            this.updateArrow();
        }

        /**
         * Once the next MarkChainModel in the chain becomes known, update the .js-markchain-arrow belonging to this view to point to it.
         * @param {*} model 
         * @param {*} nextModel 
         */
        onNextChanged(model, nextModel) {
            this.updateArrow();

            // If this markChain has another markChain connected to it, it will need to recalculate itself differently if
            // it starts off ":hidden".
            if (!nextModel) return;
            this.setUpHiddenObserver();
        }

        _getVectorIntersection(p0, v0, p1, v1) {

            let c = -v1.x * v0.y + v0.x * v1.y;
            let s = (-v0.y * (p0.x - p1.x) + v0.x * (p0.y - p1.y)) / c;
            let t = ( v1.x * (p0.y - p1.y) - v1.y * (p0.x - p1.x)) / c;

            if (s >= 0 && s <= 1 && t >= 0 && t <= 1)
            {
                return {
                    x: p0.x + (t * v0.x),
                    y: p0.y + (t * v0.y)
                }
            }

            // No collision was detected (shouldn't happen).
            return null;
        }

        /**
         * Returns where the provided vector intersects the provided boundingClientRect.
         * If no intersection is found, the middle of the boundingClientRect is returned as a fallback.
         * @param {*} p0 
         * @param {*} v0 
         * @param {*} bb 
         */
        _getVectorBoxIntersection(p0, v0, bb) {

            // Top edge
            let intersection = this._getVectorIntersection(p0, v0, 
                {x: bb.left, y: bb.top},
                {x: bb.width, y: 0}
            );
            // Bottom edge
            if (!intersection) intersection = this._getVectorIntersection(p0, v0, 
                {x: bb.left + bb.width, y: bb.top + bb.height}, 
                {x: -bb.width, y: 0}
            );
            // Right edge
            if (!intersection) intersection = this._getVectorIntersection(p0, v0, 
                {x: bb.left + bb.width, y: bb.top}, 
                {x: 0, y: bb.height}
            );
            // Left edge
            if (!intersection) intersection = this._getVectorIntersection(p0, v0, 
                {x: bb.left, y: bb.top + bb.height}, 
                {x: 0, y: -bb.height}
            );
            // No intersection found; default to the center.
            if (!intersection) intersection = {
                x: bb.left + bb.width/2,
                y: bb.top + bb.height/2
            };

            return intersection;
        }

        _getVectorLength(v) {
            return Math.sqrt(Math.pow(v.x, 2) + Math.pow(v.y, 2));;
        }

        _subtractVector(v0, v1) {
            return {
                x: v0.x - v1.x,
                y: v0.y - v1.y
            }
        }

        updateArrow() {

            let nextModel = this.model.get("_next");
            let $arrow = this.$(".js-markchain-arrow");

            // If there is no nextModel in this chain (meaning this is the last node in the chain), don't draw the arrow.
            if (!nextModel) {
                $arrow.hide();
                return;
            }

            // If this markChain is inside of a JavaScript transformed element, adjust values accordingly.
            // (This ajusts markChains that are inside of scaled figureItem lightboxes).
            let $scaled = this.$el.closest("[style*='scale('");
            let scale = 1;
            if ($scaled.length) {
                let transform = $scaled.css("transform");
                var matrix = transform.replace("matrix(", "").split(",");
                scale = parseFloat(matrix[0]); // scaleX (assume uniform scaling).
            }

            // Transform the arrow to point to the next View in the chain.
            $arrow.show();

            let fromBB = _.extend({}, this.$(".js-markchain-sizing")[0].getBoundingClientRect());
            let toBB = _.extend({}, nextModel.get("_view").$(".js-markchain-sizing")[0].getBoundingClientRect());

            let fromMid = {
                x: fromBB.left + fromBB.width/2,
                y: fromBB.top + fromBB.height/2
            }
            let toMid = {
                y: toBB.top + toBB.height/2,
                x: toBB.left + toBB.width/2
            }

            // Create a direction vector between the two points.
            let v = {
                x: toMid.x - fromMid.x,
                y: toMid.y - fromMid.y
            }
            // Find the closest edge of the Next View that the vector will intersect.
            // (Edges calculated going clockwise from the top-left corner of the toBB).
            // There should only be one intersection, so stop searching after finding it.
            
            let fromIn = this._getVectorBoxIntersection(fromMid, v, fromBB);
            let toIn = this._getVectorBoxIntersection(fromMid, v, toBB);

            // Create a vector going between the two intersections.
            let final = {
                x: toIn.x - fromIn.x,
                y: toIn.y - fromIn.y
            }

            let angle = Math.atan2(final.y, final.x) * 180 / Math.PI;
            let length = this._getVectorLength(final);
            let offset = this._getVectorLength(this._subtractVector(fromMid, fromIn));

            $arrow.css({
                "width": length/scale,
                "transform": `rotate(${angle}deg) translateX(${offset/scale}px)`
            });

            // Rotate the label (if it exists).
            let $label = this.$(".js-markchain-label");
            $label.css({
                "transform": `translateY(-50%) rotate(${-angle}deg)`
            });
        }
    };

    return MarkChainView;
});
