define([
    'core/js/adapt'
], function(Adapt) {

    /**
     * Component Models which contain other nested components get assigned this model.
     * 
     * This model will prevent its _parentView from being ready before all of its nestComponent _children are.
     */

    class NestComponentParentModel extends Backbone.Model {

        defaults() {
            return {
                _parentView: undefined,                     // Reference to the componentView that this NestComponentParentModel corresponds to.
                _isReady: false,                            // Only set to true when all children's _isReady values are set to true.
                _children: new Backbone.Collection()        // Collection of all stickyElementItemModels with this _id.
            }
        }

        initialize(){
            this.extendParentView();
            this.listenTo(this.get("_children"), "change:_isReady", this.onChildReadyChange);
        }

        registerChild(componentModel) {
            this.get("_children").add(componentModel);
        }

        onChildReadyChange(childModel, isReady) {

            // Set _isReady: true only if ALL children are ready.
            this.set("_isReady", !Boolean(this.get("_children").find((child) => {return !child.get("_isReady");})));
            this.get("_parentView").setReadyStatus();
        }

        extendParentView() {

            let parentView = this.get("_parentView");

            let _setReadyStatus = parentView.setReadyStatus;
            if (!_setReadyStatus.prototype._nestComponentOverride) { 
                parentView.setReadyStatus = () => {

                    // Only set ready once all children have been set ready.
                    if (this.get("_isReady")) {
                        return _setReadyStatus.apply(parentView, arguments);
                    }
                }
                parentView.setReadyStatus.prototype._nestComponentOverride = true; // Override only once.
            }
        }
    };

    return NestComponentParentModel;
});
