define([
  'core/js/adapt',
  'core/js/views/questionView',
  './glabelPopupView'
], function(Adapt, QuestionView, GLabelPopupView) {

  class GLabelView extends QuestionView {

    events() {
      return {
        "click .js-glabel-item"   : "onItemClick"
        /*'focus .js-item-input'  : 'onItemFocus',
        'blur .js-item-input'   : 'onItemBlur',
        'change .js-item-input' : 'onItemSelect',
        'keyup .js-item-input'  : 'onKeyPress'*/
      }
    }

    initialize() {

      this.setUpModelData();

      QuestionView.prototype.initialize.apply(this, arguments);
      
      this.setUpEventListeners();
    }

    setUpEventListeners() {

      this.update = _.debounce(this.update.bind(this), 1);

      this.listenTo(this.model, {
        "change:_isEnabled change:_isComplete change:_isSubmitted": this.update
      });

      this.listenTo(this.model.getChildren(), {
        "change:_isActive": this.onItemActiveChange,
        "change:_isFocused": this.onItemFocusedChange,
        "change:_isSelected": this.onItemSelectedChange,
        "change:_isCorrect": this.onItemCorrectChange,
        "change:_isIncorrect": this.onItemIncorrectChange, // FIXME: No other item in Adapt uses an "_isIncorrect" property.
        "change:tooltip": this.onItemTooltipChange
      });
    }

    setUpModelData() {

      // adapt-sizing for the background-graphic.
      let graphic = this.model.get("_graphic");
      if (graphic && graphic._sizing) {
        graphic._sizing._classes = Adapt.sizing.getSizingClass(graphic._sizing);
      }
    }

    /**
     * Update the tooltip text whenever the user's selection changes.
     * @param {*} itemModel 
     * @param {*} tooltip 
     */
    onItemTooltipChange(itemModel, tooltip) {
      let $item = this.$(".js-glabel-item").filter("[data-index=" + itemModel.get("_index") + "]");
      $item.find(".js-glabel-item-tooltip").html(tooltip);
    }

    onItemSelectedChange(itemModel, isSelected) {
      let $item = this.$(".js-glabel-item").filter("[data-index=" + itemModel.get("_index") + "]");
      $item.find(".js-glabel-tooltip").toggleClass("is-selected", isSelected);
    }

    onItemCorrectChange(itemModel, isCorrect){
      let $item = this.$(".js-glabel-item").filter("[data-index=" + itemModel.get("_index") + "]");
      $item.toggleClass("is-correct", isCorrect === true);
      //$item.toggleClass("is-incorrect", isCorrect === false);
    }

    onItemIncorrectChange(itemModel, isIncorrect){
      let $item = this.$(".js-glabel-item").filter("[data-index=" + itemModel.get("_index") + "]");
      $item.toggleClass("is-incorrect", isIncorrect === true);
      //$item.toggleClass("is-incorrect", isCorrect === false);
    }
    
    setupQuestion() {
      
    }

    /**
     * Whenever an item outline / item btn is clicked, open the popup with the list of options.
     */
    onItemClick(event) {

      if (this._isPopupOpen) return; // Don't allow the user to click on items while another one is set to open.

      // Set the item to active.
      let index = $(event.currentTarget).data("index");
      let itemModel = this.model.getItem(index);

      itemModel.set("_isActive", true); // Only one item can be active. An item is active for as long as its popup is open.
      itemModel.set("_isVisited", true);
    }

    // An item is focused until its notify is fully closed (slightly after is becomes inactive).
    onItemFocusedChange(itemModel, isFocused) {

      let $item = this.$(".js-glabel-item").filter("[data-index=" + itemModel.get("_index") + "]");
      $item.toggleClass("is-focused", isFocused);
    }

    onItemActiveChange(itemModel, isActive) {
      
      let $item = this.$(".js-glabel-item").filter("[data-index=" + itemModel.get("_index") + "]");
      $item.toggleClass("is-active", isActive);

      this.$(".js-glabel-item-container").toggleClass("has-active-item", isActive);

      // Inactive
      // ----------------------------------------------------
      if (!isActive) {
        return;
      } 

      // Active
      // ----------------------------------------------------
      this.openItemPopup(itemModel);

      // Set all other items to be inactive.
      this.model.getChildren().each((e) => {

        if (itemModel == e) return; // Don't deactivate self.
        e.set("_isActive", false);
        e.set("_isFocused", false);
      });
    }

    // Called when all items have _isActive = false (after the popup is closed).
    onAllItemsInactive() {
      this.$(".js-glabel-item-container").removeClass("has-active-item");
    }

    closeItemPopup() {

    }

    openItemPopup(itemModel) {

      this._isPopupOpen = true;

      itemModel.set("_isActive", true);

      //itemModel.set("_parent", this.model);
      this.itemPopupView = new GLabelPopupView({
        model: itemModel
      });

      Adapt.trigger("notify:popup", {
        _view: this.itemPopupView,
        _isCancellable: true,
        _classes: 'glabel ' + this.model.get('_classes') + " " + this.model.get("_id")
      });

      // When the user clicks to close the notify.
      this.listenToOnce(this.itemPopupView.model, "notify:close", this.onItemPopupClosed);
    }

    onItemPopupClosed() {

      let activeItem = this.model.getActiveItem();
      activeItem.set("_isActive", false);
      this._isPopupOpen = false;
      this.itemPopupView = null;
    }

    postRender() {
      QuestionView.prototype.postRender.apply(this, arguments);
      
      // Allow for easy positioning of the items.
      if (Adapt.debug?.setUpInteract) {
        Adapt.debug.setUpInteract(this.$(".js-glabel-item"), this.$(".js-glabel-item-container"), false);
      }

      //this.onDeviceChange();
      this.updateMarking(); // Custom: initial call to set up aria labels for the inputs.
                            // FIXME: Applying the initial aria could be done better...
    }

    onQuestionRendered() {
      this.setReadyStatus();
      this.update();
    }

    // This is important and should give the user feedback on how they answered the question
    // Normally done through ticks and crosses by adding classes
    showMarking() {
      this.update();
      this.updateMarking();
    }

    update(model) {
      this.updateSelection();
      // \/ questionPlus will call this.updateMarking() when necessary. It shouldn't be invoked here while it's in use.
      //this.updateMarking();
    }

    updateSelection() {

      //var isEnabled = this.model.get("_isEnabled");

      _.each(this.model.get("_items"), (item) => {

        let itemIdx = item._index;
        let isEnabled = !Boolean(item._isCorrect); // Disable items only when the item has been completed.

        _.each(item._optionsSelected, (option, optionIdx) => {

          var isSelected = this.isCorrectAnswerShown ?
            _.contains(item._optionsCorrect, optionIdx) :
            option

          let filter = '[data-adapt-index="' + itemIdx + '"][data-adapt-option-index="' + optionIdx + '"]';
          
          this.$('.js-item-label').filter(filter)
            .toggleClass('is-selected', isSelected)
            .toggleClass('is-disabled', !isEnabled)
            // Custom \/
            .find(".mmcq__item-answer-icon")
            .toggleClass('is-disabled', !isEnabled);

          this.$('.js-item-input').filter(filter)
              .prop('checked', isSelected)
              .prop('disabled', !isEnabled);
        });
      });
    }

    resetQuestionOnRevisit() {
      //this.resetQuestion();
    }

    updateMarking() {

      //var isInteractive = this.model.isInteractive();
      let canShowMarking = this.model.get('_canShowMarking');
      let ariaLabels = Adapt.course.get('_globals')._accessibility._ariaLabels;

      _.each(this.model.get("_items"), (item) => {

        let index = item._index;

        let $item = this.$('.js-mmcq-item').filter('[data-adapt-index="' + index + '"]');
        $item
          .toggleClass('is-correct', item._isCorrect)
          .toggleClass('is-incorrect', !item._isCorrect);

        if (!canShowMarking) {
          $item
          .removeClass('is-correct')
          .removeClass('is-incorrect');
        }

        /**
         * questionPlus: only apply marking to selected items.
         */
        _.each(item._optionsSelected, (option, optionIdx) => {

          let filter = '[data-adapt-index="' + index + '"][data-adapt-option-index="' + optionIdx + '"]';
          let $itemInput = this.$(".js-item-input").filter(filter);
          let $itemLabel = this.$(".js-item-label").filter(filter);

          let shouldBeSelected = Boolean(_.contains(item._optionsCorrect, optionIdx));
          let isCorrect = option == shouldBeSelected;
          let isSelected = option;

          // Set aria-labels
          let inputText = (this.model.get("_options") || item._options)[optionIdx];

          // Pre-checked state (inputs which haven't been submitted while selected yet).
          if (!item._optionsVisited[optionIdx]) {
            $itemInput.attr('aria-label', $.a11y_normalize(inputText));
          }
          // Checked inputs (correct or incorrect).
          // Only apply "Incorrect" / "Correct" aria labels after they've been submitted while selected.
          else {
            $itemInput.attr('aria-label', [
              (shouldBeSelected ? ariaLabels.correct : ariaLabels.incorrect),
              ", ",
              (isSelected ? ariaLabels.selectedAnswer : ariaLabels.unselectedAnswer),
              ". ",
              $.a11y_normalize(inputText)
            ].join(""));
          }

          if (!canShowMarking) {
            // Remove input marking
            $itemInput.removeClass('is-correct is-incorrect');
            $itemLabel.removeClass('is-correct is-incorrect');
            return;
          }

          // Apply styles to ONLY the selected inputs.
          // (DD questionPlus design choice).
          if (!isSelected) return;

          $itemInput.toggleClass("is-correct", isCorrect)
                    .toggleClass("is-incorrect", !isCorrect);

          $itemLabel
            .toggleClass("is-correct", isCorrect)
            .toggleClass("is-incorrect", !isCorrect);
        });

      });

      this.updateSelection();
    }
  }

  return GLabelView;

});
