define([
  'core/js/adapt',
  'core/js/models/componentModel'
], function(Adapt, ComponentModel) {

  class FreeThinkingModel extends ComponentModel {
	  
    initialize() {

      // Load defaults from course.json.
      this.set("_freeThinking", Adapt.course.get("_freeThinking"));

      // Start off with the answer hidden.
      this.set("_isShowingAnswer", false);
      

      // Add a FreeThinking logo using adapt-dd-componentLogo
      // ----------------------------------------------------
      this.set("_componentLogo", {
        src: "assets/freethinking-logo.svg"
      });

      // Add a class to the parent block to indicate that it contains a FreeThinking (allows for custom styling).
      let parentClasses = this.getParent().get("_classes");
      this.getParent().set("_classes", parentClasses + " is-freethinking");

      super.initialize();
    }
  }

  return FreeThinkingModel;

});
