define([
    'core/js/adapt',
    'libraries/lottie.min.js',
    './lottieControlsView'
], function(Adapt, Lottie, LottieControlsView) {

    /**
     * Guide: https://airbnb.io/lottie/#/web
     * 
     * FIXME:
     * - If possible, set up NPM to automatically keep the lottie.min.js file in the libraries up to date.
     * - If in EPUB mode, then the mutationObserver MUST ignore this element.
     */

    const INTERSECTION_THRESHOLD = 0.99; // Setting to 1 breaks on small devices, so this is used instead.

    Lottie.setQuality('high');

    class LottieView extends Backbone.View {

        className() {

            let classNames = [
                "lottie",
                this.model.get("theme") ? "theme-" + this.model.get("theme") : "",
                this.model.get("_controlsType") ? "controls-type-" + this.model.get("_controlsType") : "",
                this.model.get("isDecorative") ? "is-decorative" : ""
            ]

            return classNames.join(' ');
        }

        attributes() {
            return {
                name: this.model.get("name"),
                "aria-busy": true
            }
        }

        initialize() {
            this.setUpModelData();
            this.setUpEventListeners();
            this.render();

            // Reset autoplaying Lotties whenever they're scrolled into view.
            if (this.model.get("autoplay")) {
                this.setUpIntersectionObserver();
            }
        }

        setUpModelData() {
            
            // Always start off paused.
            // If "autoplay": true, then the Lottie will start playing once it's scrolled into view.
            this.model.set("_isPlaying", false);
        }

        setUpEventListeners() {
            this.listenTo(this.model, "change:_isPlaying", this.onIsPlayingChange);
            this.listenTo(this.model, "change:_isComplete", this.onIsCompleteChange);
            this.listenTo(this.model, "change:_isReady", this.onIsReadyChange);

            // adapt-stickyBlock compatibility
            // -------------------------------
            // If this animation is set to be decorative, and is inside a graphic component belonging to a stickyBlock.
            // have it reset each time it's scrolled into view.
            this.listenTo(this.model.get("_parent"), "stickyBlock:inview", this.onParentStickyBlockInview);
            this.listenTo(this.model.get("_parent"), "stickyBlock:outview", this.onParentStickyBlockOutview);


            // adapt-annotation compatibility
            // -------------------------------
            // Start playing if activated by an annotation.
            this.$el.on("annotation:active", () => {
                this.model.reset();
            });
            // Reset and pause if deactivated.
            this.$el.on("annotation:inactive", () => {
                this.model.reset();
                this.model.pause(0);
            });
        }

        /**
         * Observe when the Lottie is inview and reset its animation if applicable.
         */
        setUpIntersectionObserver(){

            // Observe when the Lottie is inview.
            const observer = new IntersectionObserver((entries) => {
                entries.forEach(this.onAutoplayIntersection.bind(this));
            }, {
                root: null, // Browser viewport.
                rootMargin: '0px',
                // Only start autoplaying once the entire View element is visible.
                // Only pause once the entire View element is hidden.
                threshold: [INTERSECTION_THRESHOLD,0]
            });
            observer.observe(this.$(".js-lottie-container")[0]);
        }

        onAutoplayIntersection(entry) {

            if (entry.isIntersecting && entry.intersectionRatio >= INTERSECTION_THRESHOLD) this.onAutoplayInview();
            else if (!entry.isIntersecting && entry.intersectionRatio == 0) this.onAutoplayOutview();
        }

        // When scrolled out of view, stop playing.
        onAutoplayOutview() {

            if (this.model.checkResetOnOutview()) {
                this.model.reset();
                this.model.pause(0);
            }
        }

        onAutoplayInview() {

            // Only reset if not already playing.
            if (!this.model.get("_isPlaying")) {
                if (this.model.checkResetOnOutview()) {
                    this.model.reset();
                }
            }
        }

        // Reset and play when the stickyBlock (if there is one) shows the component this LottieView is in.
        onParentStickyBlockInview() {

            // BUGFIX: StickyBlock triggers an inview on every item when rendered in mobile view.
            // Ignore that trigger. FIXME: Poor encapsulation. 
            if (Adapt.device.screenSize !== 'large') return;

            // In certain states; we DON'T want the Lottie to reset when scrolled out of view.
            if (this.model.checkResetOnOutview()) {
                this.model.reset();
            }
        }

        // Reset and pause if the stickyBlock is fully out of view.
        onParentStickyBlockOutview() {

            // BUGFIX: StickyBlock triggers an inview on every item when rendered in mobile view.
            // Ignore that trigger. FIXME: Poor encapsulation.
            if (Adapt.device.screenSize !== 'large') return;

            // In certain states; we DON'T want the Lottie to reset when scrolled out of view.
            if (this.model.checkResetOnOutview()) {
                this.model.reset();
                this.model.pause(0);
            }
        }

        onIsReadyChange(model, isReady) {

            this.$el.toggleClass("is-ready", isReady);
        }

        onIsPlayingChange(model, isPlaying) {
            this.$el.toggleClass("is-playing", isPlaying);
            this.$el.toggleClass("is-paused", !isPlaying);
        }

        onIsCompleteChange(model, isComplete) {
            this.$el.toggleClass("is-complete", isComplete);
        }

        render() {
            this.$el.html(new Handlebars.SafeString(Handlebars.templates['lottie'](this.model.toJSON())).string);
            this.renderLottie();
        }

        renderLottie() {

            let startLoadTime = new Date();
            let anim;

            try {
                anim = Lottie.loadAnimation({
                    container: this.$(".js-lottie-container")[0],
                    path: this.model.get("src"),
                    renderer: 'svg',
                    
                    // Optional
                    loop: true,
                    autoplay: false, //this.model.get("autoplay"),
                    name: this.model.get("src"),
                });
            }
            catch (err) {

            }

            // Wait for the animation to fully load, then callback.
            anim.addEventListener('DOMLoaded', () => {

                let callback = () => {

                    this.$el.attr("aria-busy", false);

                    // Initialize LottieControlsView.
                    this.model.set("anim", anim);
                    this.model.set("_isReady", true);
                    
                    this.$(".lottie__controls-container").html(new LottieControlsView({model: this.model}).$el);
                };
                
                callback();
            }, () => {
                
            });
        }

    };

    return LottieView;
});
