define([
    'core/js/adapt',

    './inputFieldView',
    './inputFieldModel',
    "./outputFieldView"
], function(
    Adapt,

    InputFieldView,
    InputFieldModel,
    
    OutputFieldView) {

    /**
     * This extension creates a jQuery plugin that can be used to set up <input type="text"> elements to consistant
     * DD standards.
     * 
     * This extension is used currently by adapt-textinputplus, but may be used again in future for other components / extensions.
     * 
     * FIXME: Uses weird blend of custom jQuery plugin and Backbone. The jQuery plugin aspect should be removed in future.
     * TODO: Remove the legacy versions.
     */

    //const pluginName = "inputField";

    class InputFieldController extends Backbone.Controller {
        
        initialize() {
            //this.setUpJQueryPlugin();
            Adapt.on('router:page', this.setUpHelpers.bind(this));

            // Dictionary of all inputFieldViews made so far.
            // FIXME: Doesn't remove the references to them if they're deleted, but that happens very rarely.
            // WARNING: Will not store references to duplicated inputField Views (like those that get cloned for notify:popups).
            this._inputFieldViews = {};
        }

        // Legacy
        // ------
        // Used by adapt-textInputPlus.
        /*setUpJQueryPlugin() {

            // Register as a jQuery plugin.
            $.fn[pluginName] = function(options) {
                var args = Array.prototype.slice.call(arguments, 1);

                return this.each(function(i) {
                    var $this = $(this),
                        data  = $this.data('plugin_' + pluginName);

                    // Create a new instance.
                    if (!data) {

                        let model = new InputFieldLegacyModel(options);

                        // Render the Handlebars template after the input.
                        // Move the input element into the template,
                        // then set the whole template to be the actual view.
                        // This is done to preserve any other events that may have been placed on the original input prior to this plugin
                        // rendering.
                        var data = model.toJSON();
                        var template = Handlebars.templates['inputFieldLegacy'];
                        let $template = $(template(data));
                        $this.after($template);

                        $template.find(".inputfield__input-container").append($this);
                        $this.addClass("inputfield__input js-inputfield-input");

                        let view = new InputFieldLegacyView({
                            model: model,
                            el: $template
                        });
                        data = model;
                        $this.data('plugin_' + pluginName, data);
                    }

                    // Make it possible to access methods from public.
                    // e.g `$element.rangeslider('method');`
                    if (typeof options === 'string') {
                        data[options].apply(data, args);
                    }
                });
            };
        }*/

        findViewByModelId(id) {
            return this._inputFieldViews[id];
        }

        findViewByEl(el) {
            return this._inputFieldViews[$(el).attr("data-inputfield-id")];
        }

        setUpHelpers() {

            Adapt.stickyBlock?.addResetOnReRender("_inputField");
            let t = this;

            // InputField
            // ---------------------------------------------
            Handlebars.registerHelperEl("inputField", function() {

                let context = arguments[arguments.length-1];
                let data = Handlebars.getContextHash.apply(this, arguments);

                // Find the componentModel that this Helper was placed in.
                let componentModel = Handlebars.getComponentModelFromContext(context);
                let name = data.name;
                let id = _.uniqueId("inputfield"); // Generate a uniqueId (used to get a reference to the model data).

                // data.correct is a legacy value.
                data.answers = data.answers || data.correct;

                if (data.decimals && isNaN(data.decimals))
                    throw `{{#inputField}}: "decimals" property must be a number (value given: ${decimals}).`;

                let modelJSON = {

                    _id: id,

                    _parent: componentModel,
                    _name: name,
                    _group: data.group, // inputFields in the same group will only be correct if each of them has a UNIQUE answer entered into them.

                    // Allows for custom HTML to be used for the placeholder.
                    placeholder: data.content || data.placeholder || "?",

                    // TODO: Have the data.type be determined automatically from the values provided if not specified.
                    _type: data.type || "number", // The datatype that this input field will allow.

                    // Provide an array of answers (even if only one answer is given).
                    _answers: Array.isArray(data.answers) ? data.answers : [data.answers],

                    // Allow for manually setting the width of the inputField.
                    _width: data.width,

                    // Defaults are for math.
                    _allowsAnyCase:     data.allowsAnyCase || false,
                    _allowsPunctuation: data.allowsPunctuation || false,
                    _decimals:          data.decimals || undefined, // Force the answer to be to this many decimals.

                    // Auto-check answers as soon as they're typed.
                    _requireSubmit: false,

                    // The hint icon should be enabled from the get-go.
                    _isHintEnabled: true
                }

                let model = new InputFieldModel(modelJSON);

                // Store this model in a collection, then assign/add to the collection in the parent ComponentModel.
                // (if a {{#textOutput}} helper is present in same component, it will read from there).

                //let blockModel = componentModel.getParent();
                if (!componentModel.get("_inputField")) {
                    componentModel.set("_inputField", {
                        _items: new Backbone.Collection(null, {model: InputFieldModel})
                    });
                }
                let inputFieldCollection = componentModel.get("_inputField")._items;
                inputFieldCollection.push(model);

                // Check for duplicate names being used (only if a name is explicitly set).
                if (name !== undefined) {
                    if (inputFieldCollection.filter(inputField => {return inputField.get("_name") == name;}).length > 1) {
                        throw `{{#inputField}}: Duplicate names are not allowed ("${name}" name used more than once in block ${componentModel.getParent().get("_id")}.`;
                    }
                }

                let view = new InputFieldView({model: model});
                if (t._inputFieldViews[id] === undefined) { // Only add if undefined (prevents cloned views being added...?)
                    t._inputFieldViews[id] = view;
                }
                else {
                    // I don't think this code is ever reached.
                    Adapt.log.warning(`adapt-dd-inputField: duplicate {{#inputField}} detected: ${id}`);
                }

                return view.$el;
            });

            /**
             * OutputField
             * -----------
             * 
             * Hash properties:
             * @param {String} fn The function which will be executed to calculate the result. Example: "(1 / ${i1}) * ${i2}".
             * @param {Number=2} decimals How many decimal places to round the result to.
             */
            Handlebars.registerHelperEl("outputField", function() {

                let context = arguments[arguments.length-1];
                let data = Handlebars.getContextHash.apply(this, arguments);
                
                // TextOutput MUST have a defined fn.
                if (!data.fn) throw "{{#outputField}}: \"fn\" property must be defined!";

                // Find the componentModel that this Helper was placed in.
                let componentModel = Handlebars.getComponentModelFromContext(context);
                
                data = _.extend({
                    // Default values.
                    decimals: 2 // Default to two decimal places.
                },
                data,
                {_parent: componentModel});
                
                data._parent = componentModel;
                

                let model = new Backbone.Model(data);

                /**
                 * Important explanation:
                 * ----------------------
                 * - OutputField listens for changes to the parent BlockModel's _inputField._items collection.
                 * - Both inputFields and outputFields are set up at the same time, so it's possible that at the time of the OutputField
                 *   model's creation, not all inputFields are ready.
                 * - This isn't an issue however, as the OutputField only starts listening for inputFields when its VIEW is created (we assume all InputFieldModels for the parent component are ready once the Views are being created). 
                 */

                return new OutputFieldView({model: model}).$el;
            });
        }
    }

    Adapt.inputField = new InputFieldController();
});
