define([
  'core/js/adapt',
  './activatorTargetModel'
], function(Adapt, ActivatorTargetModel) {

  /**
   * Called "target" instead of "item", as the activator targets other components
   * (which are not nested inside of it like items would be).
   * 
   * Note:
   * I may be setting this up in a weird way.
   * Basically, to make a component model compatible as an activator target,
   * it must define setupActivatorTarget and toggleActivatorTarget.
   */
  class ActivatorComponentTargetModel extends ActivatorTargetModel {

    defaults() {
        return _.extend(super.defaults(), {
          _parent: undefined   // Reference to the target component that will be activated.
                               // FIXME: Bad name used for this; but might break legacy content if renamed.
        });
    }

    initialize() {

      let component = this.get("_parent");

      this.listenTo(component, "change:_isReady", (m, isReady) => {
        this.setReadyStatus();
      });
        
      if (!component.setupActivatorTarget){
          //Adapt.log.error("function setupActivatorTarget() has not been implemented for component type: " + component.get("_component"));
      }
      else {
          component.setupActivatorTarget(this.get("_isActive"));
      }
    }

    onToggle(isActive) {
      
      let component = this.get("_parent");
      if (!component.toggleActivatorTarget){
          //Adapt.log.error("function toggleActivatorTarget() has not been implemented for component type: " + component.get("_component"));
      }
      else {
          component.toggleActivatorTarget(isActive);
      }
    }

    $getTargets() {
      return Adapt.findViewByModelId(this.get("_parent").get("_id")).$el;
    }
  }

  return ActivatorComponentTargetModel;

});
