define([
    'core/js/adapt'
], function(Adapt) {

    class ImgPopupView extends Backbone.View {

        className() {

            let classes = [
                "ddimg-popup",
                this.model.get("_classes") || ""
            ];

            return classes.join(" ");
        }

        events() {
            return {
                
            }
        }

        initialize(){

            this.setUpImgClasses();
            this.setUpEventListeners();
            this.render();
        }

        setUpImgClasses() {

            let lightbox = this.model.get("lightbox");

            let classes = [
                "ddimg-popup__img",
                "ddimg-popup__size-" + lightbox._width,
                lightbox._backgroundEnabled ? "has-bg" : ""
            ]

            this.model.set("_imgClasses", classes.join(" "));
        }

        setUpEventListeners() {
            this.listenToOnce(Adapt, "notify:opened", this.onOpened);
            this.listenToOnce(Adapt, "notify:closed", this.onClosed);
        }

        onOpened() {
            Adapt.scrollfix.disableA11yScroll();
        }

        onClosed() {
            this.$el.addClass("is-closed");
            Adapt.scrollfix.enableA11yScroll();
        }

        remove() {
            this.model.trigger("remove");
            super.remove();
        }

        render() {
            var data = this.model.toJSON();
            data.view = this;
            var template = Handlebars.templates['ddImgPopup'];
            this.$el.html(template(data));
        }

        // Allow for manually closing the popup.
        closePopup() {
            Adapt.trigger('notify:close');
        }
    };

    return ImgPopupView;
});
