define([
    'core/js/adapt',
    './SideNavCollection',
    './SideNavWidgetView'
], function(Adapt, SideNavCollection, SideNavWidgetView) {

    /**
     * (Code based on implementation of adapt-contrib-pageLevelProgress extension).
     */

    class SlideNav extends Backbone.Controller{

        initialize() {

            Adapt.on({
                'app:dataReady': this.onDataReady.bind(this)
            });

            
        }

        onDataReady() {

            Adapt.scrolling.isLegacyScrolling = false;

            // Do not proceed if pageLevelProgress is not enabled in course.json
            /*var coursePLPConfig = this.getCourseConfig();
            if (!coursePLPConfig || !coursePLPConfig._isEnabled) {
                return;
            }*/
            this.setUpBlocks();
            this.setUpEventListeners();
        }

        // Assign sequential block numbers to all blocks in the course.
        // These will be how the student / tutor will identify them.
        setUpBlocks() {
            Adapt.blocks.each((block, index) => {
                
                // Block numbers.
                block.set("_blockNum", index + 1);

                // Block completion percent (based on the number of completed child components).
                block.set("_completionPercent", 0);
            });
        }
      
        setUpEventListeners() {

            /*var headerIndicatorTypes = [
              'menu',
              'menuItem',
              'page',
              'article',
              'block',
              'component'
            ];
      
            var headerIndicatorEventNames = headerIndicatorTypes
              .concat(['']).join('View:render ');
      
            this.listenTo(Adapt, headerIndicatorEventNames, this.renderHeaderIndicatorView);*/
      
            this.listenTo(Adapt, {
              //'menuItemView:postRender': this.renderMenuItemIndicatorView,
              'router:page': this.renderNavigationView
            });

            // Keep track of each block's completion percentage.
            
            //this.listenTo(Adapt.course, 'bubble:change:_isComplete', this.onCompletionChange);
            this.listenTo(Adapt.components, "change:_isComplete", this.onComponentCompletionChange);
        }

        /*onCompletionChange() {

            if (!Adapt.location._currentId) return;
            console.log(Adapt.location);

            var currentModel = Adapt.findById(Adapt.location._currentId);
            var completionState = {
                currentLocation: CompletionCalculations.calculatePercentageComplete(currentModel),
                course: CompletionCalculations.calculatePercentageComplete(Adapt.course)
            };

            console.log(currentModel.get("_id") + " " + completionState.currentLocation);
        }*/

        onComponentCompletionChange(componentModel, isComplete) {
            
            // Update the percentage complete on the parent block based on the completion percentage of its child components.
            let block = componentModel.getParent();
            let blockPercentCompletion = 0;

            block.getChildren().each((componentModel) => {

                blockPercentCompletion += (componentModel.get("_isComplete") /*|| componentModel.get("_isOptional")*/) ? 1 : 0;
            });

            blockPercentCompletion /= block.getChildren().length;
            block.set("_completionPercent", blockPercentCompletion*100);
        }

        renderNavigationView(pageModel){

            var collection = new SideNavCollection(null, {
                pageModel: pageModel
            });

            this.collection = collection;

            $(".nav[role='navigation']").addClass("sidenav"); 
            
            this.renderSideNavWidget();

            /*.append(new SideNavView({
                model: pageModel,
                collection: collection
            }).$el);

            $('.slidenav__progress').after(new SideNavBlocknumView({
                //model: pageModel,
                //collection: collection
            }).$el);*/
        }

        renderSideNavWidget() {
            $('.nav__inner').append(new SideNavWidgetView({
                model: new Backbone.Model()
                //collection: collection
            }).$el);
        }
    };

    Adapt.sideNav = new SlideNav();
    
    //var slideNav = new SlideNav();

    /*Adapt.on('pageView:ready', function(view) {
        $(".nav").addClass("sidenav");
    });*/
});
