define([
    'core/js/adapt'
], function(Adapt) {

    // Shortcut checks for the adapt-pdf and adapt-epub extensions (if they exist in the build).
    Adapt.isPDFMode = function() {return Boolean(Adapt.pdf?._isEnabled);}
    Adapt.isEpubMode = function() {return Boolean(Adapt.epub?._isEnabled);}
    Adapt.isDebugEnabled = function() {return Boolean(Adapt.debug?._isEnabled);}


    //#region :hidden change detection
    //-----------------------------------------------
    /**
     * Certain Views/elements need a way to detect if they're ":hidden" (jQuery selector), and when they stop being
     * ":hidden".
     * 
     * This funcationality is common enough to be made global.
     */
     $.fn.onUnhidden = function(callback) {
        const HIDDEN_OBSERVER_DEBOUNCE = 10; //500; 
        let $el = $(this);

        // Find the closest component or notify element to the selection(s).
        let closestEl = $el.closest(".component")[0];
        if (!closestEl) closestEl = $el.closest(".notify")[0];
        if (!closestEl) {
            Adapt.log.error("$.fn.onUnhidden: Could not set up MutationObserver (could not find closest parent).", $el);
            return this;
        }

        // Whenever this mutation is triggered, check if this view is hidden, and update the "_isHidden" value.
        let observerCallback = _.debounce(() => {
            $el.each((i, e) => {
                let $e = $(e);
                let isHidden = $e.is(":hidden");
                let isUnhidden = $e.data("_isUnhidden");

                // Don't do anything if no state was changed.
                if (isHidden === !isUnhidden) return;
                
                $e.data("_isUnhidden", !isHidden);

                // Call the callback only when unhidden.
                if (isHidden) return;
                callback.apply($e);
            });
        }, HIDDEN_OBSERVER_DEBOUNCE);

        let unhiddenObserver = new MutationObserver(observerCallback);
        unhiddenObserver.observe(closestEl, {
            attributes: true,
            attributeFilter: ["style", "hidden", "aria-expanded", "class"],
            subtree: true
        });

        return this;
    }

    //#endregion





    //#region Rrror handling
    //-----------------------------------------------

    /**
     * If require() calls are made from within components, they can sometimes (rarely) time out while trying to load script(s).
     * If such a thing occurs, communicate to the user that they need to refresh by using adapt-dd-loading (if it exists).
     * 
     * To test whether this error will occur:
     * --------------------------------------
     * Go to Chrome Dev tools and enable Network throttling. If the page takes long enough to load, there should be a module timeout. 
     * 
     * HOW TO AVOID:
     * -------------
     * Avoid using require() calls within component/extension views.
     * 
     * Previous causes:
     * ----------------
     * - adapt-reorder needed both jQuery-ui (an AMD module) and jquery.touch-punch (a non-AMD module) to be loaded in a specific
     *   order that was prone to timing out.
     *   
     *   This issue has since been resolved (however, the third-party touch-punch plugin has to be modified (bad practice).).
     *   If anyone knows how to resolve this in a different way, please let me know. 
     * 
     * @param {*} err 
     */
    requirejs.onError = function (err) {

        // Module timeout. Ask the user to refresh.
        if (err.requireType === 'timeout') {
            if (Adapt.ddLoading) {
                Adapt.ddLoading.showError("Load timeout.");
            }
            throw err;
        }
        // Any other kind of error. Throw an error as normal. 
        else {
            throw err;
        }   
    };
    //#endregion
});
