define([
  'core/js/adapt',
  'core/js/models/itemsQuestionModel'
], function(Adapt, ItemsQuestionModel) {

  /**
   * FIXME: Not compatible with screen readers + colour blindness at the moment.
   */

  class FinderModel extends ItemsQuestionModel{

    /**
     * Correct:         ALL and ONLY the _shouldBeSelected items have been selected.
     * PartlyCorrect:   ONLY _shouldBeSelected items have been selected, but not all of them.
     * Incorrect:       ANY !_shouldBeSelected items have been selected.
     */
    isCorrect() {

      const props = {
        _numberOfIncorrectAnswers: 0, // Number of items selected that shouldn't have been.
        _numberOfCorrectAnswers: 0,   // Number of items selected that should have been.
        _numberOfRequiredAnswers: 0,  // Number of items which need to have been selected.
        _isAtLeastOneIncorrectSelection: false, // _numberOfIncorrectAnswers != 0
      };

      this.getChildren().each(itemModel => {
        
        let shouldBeSelected = Boolean(itemModel.get('_shouldBeSelected'));
        let isActive = Boolean(itemModel.get("_isActive"));
        
        itemModel.set('_isCorrect', shouldBeSelected == isActive);

        if (shouldBeSelected) {
          props._numberOfRequiredAnswers++;
        }

        if (!isActive) return; // Only mark selected anwswers.

        // This item was selected and it shouldn't have been.
        if (!shouldBeSelected) {
          props._numberOfIncorrectAnswers++;
          props._isAtLeastOneIncorrectSelection = true;
          return;
        }

        // This item was selected and it should have been.
        props._numberOfCorrectAnswers++;
      });

      this.set(props);

      const hasRightNumberOfCorrectAnswers = (props._numberOfCorrectAnswers === props._numberOfRequiredAnswers);
      const hasNoIncorrectAnswers = !props._isAtLeastOneIncorrectSelection;

      return hasRightNumberOfCorrectAnswers && hasNoIncorrectAnswers;
    }
    
    isPartlyCorrect() {
      return !this.get('_isAtLeastOneIncorrectSelection');
    }

    setupFeedback() {
      
      if (!this.has('_feedback')) return;

      if (this.get('_isCorrect')) {
        this.setupCorrectFeedback();
        return;
      }

      if (this.isPartlyCorrect()) {
        this.setupPartlyCorrectFeedback();
        return;
      }

      this.setupIncorrectFeedback();
    }
  }


  return FinderModel;

});
