define([
    'handlebars',
    'core/js/adapt',
    './imgView',
    './respImgView'
], function(Handlebars, Adapt, ImgView, RespImgView) {

    /**
     * adapt-dd-img
     * --------------------------------------
     * Register a Handlebars helper for creating <img> elements.
     * Will also automatically detect and implement any *--r.svg files as "responsive images". 
     * 
     * TODO: Allow the user to click and drag the img in the popup if it's scrolling horizontally.
     */

    // Helpers
    // ---------------------

    Adapt.on('router:page', () =>{

        Handlebars.registerHelperEl("img", function() {

            let data = Handlebars.getContextHash.apply(this, arguments);

            // FIXME: Likely a temp name.

            //data.content = data.context

            if (!data.src) {
                console.log(data);
                throw "{{#img}}: \"src\" parameter must be defined.";
            }

            data.src = data.src.trim();

            // Niche feature: allow removing max-width restrictions on the image.
            // If the image's "contain" is set to false, then remove its max-width style.
            // (Default value is "contain": true).
            data._contain = data.contain === undefined ? true : Boolean(data.contain);

            data._widthAuto = Boolean(data.src.match(/\.svg$/i)) ? false : true; // If the image is not a .svg, give it width: auto (prevent it scaling above its original size);

            data.sizing = data.sizing ? Adapt.sizing.getSizingClass(data.sizing) : "";

            // Determine if this is a responsive image or not.
            // At the moment, this is done by matching the filename to *--r.svg
            // ----------------------------------------------------------------
            // This is a responsive image.
            if (data.src.match(/--r\.svg$/)){
                let modelJSON = _.extend({}, data, {
                    _id: "rimg-" + _.uniqueId()
                });
                return new RespImgView({
                    model: new Backbone.Model(modelJSON)
                }).$el;
            }

            // This is a normal image.
            // ------------------------------------------------------
            let modelJSON = _.extend({}, data, {
                
            });
            return new ImgView({
                model: new Backbone.Model(modelJSON)
            }).$el;
            
            // DEFUNCT
            // This is a normal image. Render it immediately.
            let html = Handlebars.templates["ddImg"](data);
            return new Handlebars.SafeString(html).string;
        });

    });
});
