define([
    'core/js/adapt',
    './extraLearningPopupView'
], function(Adapt, ExtraLearningPopupView) {

    /**
     * TODO: https://developer.mozilla.org/en-US/docs/Web/Accessibility/ARIA/Roles/button_role (scrolldown and see Note: section).
     */

    class ExtraLearningView extends Backbone.View {

        className() {
            return "extralearning extralearning-btn";
        }

        tagName() {
            return "div"
        }

        events() {
            return {
                "click .js-extralearning-btn" : "onClick"
            }
        }

        initialize(){

            //this.setUpViewData();
            this.render();
        }

        setUpViewData() {
            this.popupView = null;
            this._isPopupOpen = false;
        }

        render(){
            let template = Handlebars.templates['extraLearning'];
            this.$el.html(new Handlebars.SafeString(template(this.model.toJSON())).string);
        }

        onClick(){
            this.openPopup();
        }

        /**
         * Open a popup using the default Adapt notify functionality.
         */
        openPopup() {

            if (this._isPopupOpen) return;
            this._isPopupOpen = true;
    
            let popupModel = new Backbone.Model(_.extend(
                {},
                this.model.get("_popupData"),
                {
                    // VERY IMPORTANT! Used by Handlebars.getComponentModelFromContext()
                    _parent: this.model.get("_componentParent"), 

                    _parentView: this,
                    _type: this.model.get("_type")
                }) // The popup uses a reference to this View to set up its icon animation.
            );

            this.popupView = new ExtraLearningPopupView({
                model: popupModel
            });
    
            this.notifyView = Adapt.notify.popup({
                _view: this.popupView,
                _isCancellable: true,
                _showCloseButton: true,
                _classes: 'extralearning'// + (this.model.get('_classes') || "")
            });
            //this.listenTo(notifyView.model, "change:_isReady", (model, isReady) => {console.log(isReady);});

            this.listenTo(Adapt, {
                // Important to use notify:closed instead of popup:closed (the former has a better callback).
                'notify:closed': this.onPopupClosed
            });

            // Wait for the popup to be fully removed before swapping the graphic visibilities.
            this.listenToOnce(popupModel, "remove", this.onPopupRemoved);
        }

        onPopupClosed(notifyView) {

            // If the popup that was just closed was NOT this one, do nothing.
            if (notifyView.model.get("_view") !== this.popupView) return;

            // Popup is closed; stop listening for it.
            this.stopListening(Adapt, "notify:closed", this.onPopupClosed);

            this.$el.addClass("is-visited");
            this._isPopupOpen = false;
        }

        onPopupRemoved() {
            this.$(".js-extralearning-icon").removeClass("u-visibility-hidden");
        }
    };

    return ExtraLearningView;
});
