define([
  'core/js/adapt',
  'components/adapt-contrib-mcq/js/adapt-contrib-mcq'
], function(Adapt, MCQ) {

  /**
   * Extends the Finder.view.
   */

  class MCQCodeView extends MCQ.view {
    
    events() {
      return {
        'focus .js-item-input': 'onItemFocus',
        'blur .js-item-input': 'onItemBlur',
        'change .js-item-input': 'onItemSelect',
        'keyup .js-item-input': 'onKeyPress'
      }
    }

    initialize() {
      super.initialize();

      this.setUpEventListeners();
    }

    render() {
      super.render();

      // Hide the .mcqcode__codebox-correct elements until the correct answer is shown.
      //this.$(".mcqcode__codebox-correct").hide();

      //console.log(this.$(".mcqcode__codebox-correct"));
    }

    setUpEventListeners() {
      if (super.setUpEventListeners){
        super.setUpEventListeners();
      }

      this.listenTo(this.model.getChildren(), "change:_isCorrect", this.onItemCorrectChange);
    }

    // FIXME: Very hardcoded.
    onItemCorrectChange(itemModel, isCorrect) {
      if (!isCorrect) return;

      let $item = this.getItemElement(itemModel);

      // Wait for the feedback to be closed.
      this.listenToOnce(Adapt, "notify:closed", () => {

        _.debounce(() => {
          // Render the inline feedback after the item.
          var data = itemModel.toJSON();
          data.view = this;
          var template = Handlebars.templates['mcqCodeFeedback'];

          let feedbackHTML = Handlebars.compile(template(data));

          // Mark that codebox as incorrect.
          $item.find(".js-item-codebox").addClass("mcqcode__codebox-incorrect");

          // Append the annotation
          // (very important to pass the data object so that Handlebars.getComponentModelFromContext can get the context from it).
          let annotationHTML = Handlebars.compile('{{#annotation tooltip="' + data._feedback.body + '" hasIcon="true"}}{{/annotation}}')(data);

          $item.find(".js-item-content-inner")
            .append(annotationHTML);

          // Append the correct answer.
          $item.find(".js-item-content-feedback")
            .html(feedbackHTML)
            .hide(0)
            .slideDown();
        }, 500)();
      });
    }

    getItemElement(model) {
      var index = model.get('_index');
      return this.$('.js-mcqcode-item').filter('[data-adapt-index="' + index + '"]');
    }

    onItemsActiveChange(model, _isActive) {

      this.getItemElement(model).toggleClass('is-active', _isActive).find(".js-item-label").toggleClass("is-selected", _isActive);
      this.updateContainer();
    }
  }

  MCQCodeView.template = "mcqCode";
  return MCQCodeView;
});
