const Handlebars = require("handlebars");

define([
    'core/js/adapt'
], function(Adapt) {

    class FigurePopupView extends Backbone.View {

        className() {

            let classes = [
                "figure-popup",
                this.model.get("_classes") || ""
            ];

            return classes.join(" ");
        }

        events() {
            return {
                
            }
        }

        initialize(){
            
            this.setUpEventListeners();
            this.render();
        }

        setUpEventListeners() {
            this.listenToOnce(Adapt, "notify:opened", this.onOpened);
            this.listenToOnce(Adapt, "notify:closed", this.onClosed);
        }

        onOpened() {
            Adapt.scrollfix.disableA11yScroll();
        }

        onClosed() {
            this.$el.addClass("is-closed");
            Adapt.scrollfix.enableA11yScroll();
        }

        remove() {
            this.model.trigger("remove");
            super.remove();
        }

        render() {
            var data = this.model.toJSON();
            data.view = this;

            var template = Handlebars.templates['ddFigurePopup'];
            this.$el.html(template(data));

            // FIXME: Delay needed to be used (if this was called too early, it would detect the figure helperEl placeholder instead).
            _.delay(() => {Handlebars.fixPlaceholders(this.$el);}, 50);
        }

        // Allow for manually closing the popup.
        closePopup() {
            Adapt.trigger('notify:close');
        }
    };

    return FigurePopupView;
});
