define([
    'core/js/adapt'
], function(Adapt) {

    /**
     * FIXME:
     * Known bugs:
     * - Multiple and/or nested elements with StickyElementAttributes will likely cause errors in the animation.
     * 
     * TODO:
     * - Have all stickyElementItems in a group synchronise their scrollbars.
     */

    /**
     * FIXME: These are copies of the attributes created by the Handlebars helpers in adapt-stickyElement.js
     */
    const StickyElementAttributeClasses = {
        "data-stickyelement-active-idx": "is-se-active",
        "data-stickyelement-mark-idx": "is-se-mark",
        "data-stickyelement-hide-idx": "is-se-hide",
        "data-stickyelement-fold-idx": "is-se-fold",
        "data-stickyelement-slideleft-idx": "is-se-slideleft",
        "data-stickyelement-slidedown-idx": "is-se-slidedown" // Requires JS to fully animate.
    }

    class StickyElementItemView extends Backbone.View {

        className() {
            return "stickyelement u-visibility-hidden";
        }

        attributes() {
            return {
                "data-stickyelement-id" : this.model.get("_id"),
                "data-item-index" : this.model.get("_componentItem").get("_index")
            }
        }

        initialize(){

            // Keeps track of how many times toggleAttributes has been called.
            // FIXME: Done for the sake of slidedown for now.
            this._toggleCount = 0;

            //this.toggleAttributes = _.debounce(this.toggleAttributes.bind(this), 0);
            this.setUpEventListeners();

            // Ensure the initial state of the stickyElement is correct.
            this.render();

            // Trigger intiial state for the {{#se_*}} elements within.
            if (this.model.get("_isActive")) {
                this.onActiveChange(
                    this.model,
                    this.model.get("_isActive")
                );
            }
            else {
                // This call sets up the states of hidden {{#se_*}} elements.
                // Is necessary to properly adjust the sizing of {{#se_fold}} helpers inside of an adapt-narrative component.
                _.delay(() => {
                    this.toggleAttributes(
                        this.$el,
                        this.model.get("_componentItemIndex"),
                        this._stickyBlockPrevIdx
                    );
                }, 0);
            }
        }

        render() {
            let template = Handlebars.templates['stickyElement'];
            this.$el.html(new Handlebars.SafeString(template(this.model.toJSON())).string);

            this.postRender();
        }

        postRender() {

            // FIXME:
            // Delay used to delay this in time for any inner HandlebarsEls. There should be a more elegant way of doing this.
            _.delay(this.setUpScrolling.bind(this), 0);
        }

        setUpEventListeners(){

            if (this.model.get("_parentHasTransitions")) {
                this.onActiveChange = _.debounce(this.onActiveChange.bind(this), 0); // Makes the transition slightly smoother.
            }
            
            this.listenTo(this.model, "change:_isActive", this.onActiveChange);

            // adapt-stickyBlock compatibility.
            this.listenTo(this.model, "change:_componentItemIndex", this.onComponentItemIndexChange);

            // Synchronize scrolling.
            this.listenTo(this.getParentModel(), "change:_itemScrollTop", this.synchronizeScrollTop.bind(this));
            this.listenTo(this.getParentModel(), "change:_itemScrollLeft", this.synchronizeScrollLeft.bind(this));

            // Adjust {{#se_fold}} elements.
            /*this.listenTo(Adapt, {
                'device:resize': this.onDeviceResize
            });*/
        }

        /*onDeviceResize() {
            
        }*/

        _isPropertyScrollable(propertyVal) {
            if (propertyVal == "auto" || propertyVal == "scroll") return true;
            return false;
        }

        synchronizeScrollLeft(model, scrollLeft) {
            if (!this.$scroll?.length) return;
            // Synchronize the element to the global scroll values.
            this.$scroll[0].scrollLeft = scrollLeft;
        }

        synchronizeScrollTop(model, scrollTop) {
            if (!this.$scroll?.length) return;
            // Synchronize the element to the global scroll values.
            this.$scroll[0].scrollTop = scrollTop;
        }

        setUpScrolling() {

            // Find the first scrolling element in this view and record it.
            let found = false;
            this.$scroll = this.$("*").filter((i, e) => {
                if (found) return;
                if (this._isPropertyScrollable($(e).css("overflow"))) {
                    found = true;
                    return true;
                }
                if (this._isPropertyScrollable($(e).css("overflow-x"))) {
                    found = true;
                    return true;
                }
                if (this._isPropertyScrollable($(e).css("overflow-y"))) {
                    found = true;
                    return true;
                }
                return false;
            }).first();

            // If no scrolling element, don't set up a listener.
            // FIXME: If the element somehow gains a scrollbar after loading, then this listener won't be set up (only results in minor graphical bugs).
            if (!this.$scroll.length) return;

            this.$scroll.addClass("js-stickyelement-scroll"); // Used by $clones.

            // Listen for scroll events.
            this.$scroll[0].addEventListener("scroll",
            _.debounce((event) => {
                this.getParentModel().set("_itemScrollTop", event.target.scrollTop);
                this.getParentModel().set("_itemScrollLeft", event.target.scrollLeft);
            }, 100), {
                passive: true
            });
        }

        onComponentItemIndexChange(model, itemIdx) {

            this._stickyBlockPrevIdx = model.previous("_componentItemIndex");
            this.onActiveChange(this.model, true);
        }

        onActiveChange(model, isActive, _force) {
            
            // Always make sure the active element is visible.
            if (isActive) {
                this.$el.toggleClass("u-visibility-hidden", false);
            }
            else {
                if (this.$clone) this.$clone.remove();
            }
            this.$el.toggleClass("is-stickyelement-active", isActive);

            // Ignore if not currently active.
            if (!isActive) return;

            let curCIndex = this.model.get("_componentItemIndex");
            this.$el.attr("data-stickyelement-curidx", curCIndex);

            // If this is the first component item activated, do nothing.
            let itemPrevModel = this.model.getItemPrev();
            if (
                !itemPrevModel //&&
                //!this.model.get("_isStickyBlock") // StickyBlock compatibility.
            ) {
                this.toggleAttributes(this.$el, curCIndex, this._stickyBlockPrevIdx); // Set the initial attribute states immediately.
                return;
            }

            // Get the index of the previous item's componentItem.
            let prevCIndex = itemPrevModel.get("_componentItemIndex");

            // Align this view to the previous itemModel's view, them immidiately revert it.
            // This can be made smooth by applying a CSS transition on the "transform" property.
            if (curCIndex == prevCIndex) return; // Don't do anything if it's the same item.

            //this.$el.imageready(() => {
                this.transitionFromEl(itemPrevModel.get("_view").$el, curCIndex, prevCIndex);
            //});
        }

        // TODO: Tidy up + add more ways to select indexes.
        getActiveIdxs(attrString) {
            
            let itemsCount = this.model.getItemsCount();

            // If the first character is a '!', invert the selection.
            let inverted = false;
            if (attrString[0] == '!') {
                inverted = true;
                attrString = attrString.substring(1);
            }

            let _idxs = attrString.split(",");
            let idxs = [];
            _.each(_idxs, (idx) => {
                
                // x+
                if (idx.includes("+")) {
                    let from = idx.split("+")[0];
                    for (let i = parseInt(from); i < itemsCount; i++){
                        idxs.push(i + "");
                    }
                }
                // x-y
                else if (idx.includes("-")) {
                    let from = idx.split("-")[0];
                    let to = idx.split("-")[1];
                    for (let i = parseInt(from); i <= parseInt(to); i++){
                        idxs.push(i + "");
                    }
                }
                // x
                else {
                    idxs.push(idx + "");
                }
            });

            if (!inverted) return idxs;

            // Invert selection.
            let invertedIdxs = [];
            for (let i = 0; i < itemsCount; i++) invertedIdxs.push(i + "");
            invertedIdxs = _.filter(invertedIdxs, e => {return !_.contains(idxs, e);});
            return invertedIdxs;
        }

        toggleAttributes($targetEl, curCIndex, prevCIndex) {
            
            let isStickyBlock = this.model.get("_isStickyBlock");

            _.each(StickyElementAttributeClasses, (className, attrName) => {

                // Find all elements with this attribute.
                let $els = $targetEl.find("[" + attrName + "]");

                $els.each((i, e) => {

                    // Get the indexes that this attribute applies its classes for.
                    let activeIdxs = this.getActiveIdxs($(e).attr(attrName));

                    // Determine the state this element was in on the previous and current views.
                    let curActive = _.includes(activeIdxs, "" + curCIndex);

                    // If this is the first active StickyElementItemView, then immediately set the classes with no transitions. 
                    if (
                            prevCIndex === undefined
                        ) {
                        $(e)
                            .toggleClass(className + "-transition", false)
                            .toggleClass(className, curActive);

                        // FIXME: Hardcoded functionality for slidedown.
                        // FIXME: Is incorrectly triggering on pageload.
                        if (className == "is-se-slidedown") {
                            if (this._toggleCount === 0) {
                                $(e).toggle(curActive);
                            }
                            else {
                                if (curActive) {
                                    $(e).hide(0).slideDown(400);
                                }
                                else {
                                    $(e).show(0).slideUp(400);
                                }
                            }
                        }

                        return;
                    }

                    // Determine the state this element was in on the previous and current views.
                    let prevActive = _.includes(activeIdxs, "" + prevCIndex);

                    // If the last view's state matches this one, no transition.
                    let hasTransition = prevActive != curActive;
                    if (isStickyBlock) hasTransition = true; // If in StickyBlock mode, always use transitions.

                    // If there is a transition, ensure that the classes are set correctly to trigger it.
                    if (hasTransition){

                        if (!isStickyBlock){
                            $(e)
                                .toggleClass(className + "-transition", false) 
                                .toggleClass(className, !curActive);
                        }

                        // Trigger the transition.
                        _.debounce(() => {
                            $(e)
                                .toggleClass(className + "-transition", hasTransition)
                                .toggleClass(className, curActive);
                        }, 0)();

                        // FIXME: Hardcoded functionality for slidedown.
                        if (className == "is-se-slidedown") {
                            if (curActive) {
                                $(e).hide(0).slideDown(400);
                            }
                            else {
                                $(e).show(0).slideUp(400);
                            }
                        }
                    }
                    else {

                        $(e)
                            .toggleClass(className + "-transition", hasTransition)
                            .toggleClass(className, curActive);

                        // FIXME: Hardcoded functionality for slidedown.
                        if (className == "is-se-slidedown") {
                            $(e).toggle(curActive);
                        }
                    }
                });
            });

            this._toggleCount++;
        }

        getParentModel() {
            return this.model.get("_parent");
        }

        transitionFromEl($otherEl, curCIndex, prevCIndex) {

            // Remove any previous clones created for this group of stickyElements.
            if (this.getParentModel().get("_$clone")?.length) {
                this.getParentModel().get("_$clone").remove();
            }

            // Create a clone of this $el (that the user will see).
            let $clone = this.$el.clone();
            this.$clone = $clone;
            $clone.width(this.$el.width());

            this.$el.closest(".component__widget").append($clone.addClass("is-clone"));
            this.getParentModel().set("_$clone", this.$clone);

            // Synchronize the scroll on the clone.
            let $cloneScroll = $clone.find(".js-stickyelement-scroll");
            if ($cloneScroll.length) {
                $cloneScroll[0].scrollTop = this.getParentModel().get("_itemScrollTop");
                $cloneScroll[0].scrollLeft = this.getParentModel().get("_itemScrollLeft");
            }

            let removeClone = () => {
                $clone.remove();
                this.$el.toggleClass("u-visibility-hidden", false).hide(0).show(0);
            }

            /**
             * Fallback:
             * Sometimes the transitionend trigger can be missed (if the user clicks too fast).
             * If this happens, then the stickyElement may get rendered off-screen (very bad).
             * This fallback has been added to at least snap it into place after a delay if the 'transitionend' trigger doesn't come
             * after a while.
             */
            if (this.getParentModel().get("_fallbackTimeout")) {
                clearTimeout(this.getParentModel().get("_fallbackTimeout"));
            }
            let fallbackTimeout = setTimeout(removeClone, 1500);
            this.getParentModel().set("_fallbackTimeout", fallbackTimeout);

            // Remove the $clone after the transition.
            $clone.on('transitionend', (event) => {
                if (event.target != event.currentTarget) return;

                // Wait for the transition to come to a COMPLETE stop before swapping the $clone for the original.
                // This debounce ensures this (otherwise, there's sometimes a millisecond of jitter on transition).
                _.debounce(removeClone, 50)();

                clearTimeout(this.getParentModel().get("_fallbackTimeout"));
                this.getParentModel().set("_fallbackTimeout", undefined);
            });

            // Hide this and the other element.
            // Delay slightly (helps prevent flickering on item change).
            _.delay(() => {
                this.$el.toggleClass("u-visibility-hidden", true);
                $otherEl.toggleClass("u-visibility-hidden", true);
            }, 0);

            // Clear any transform CSS on this View's element.
            //this.$el.css({"transform": ""/*, "position": "fixed"*/}).hide(0).show(0);

            // FIXME: Throws errors if element missing.
            let thisBB = this.$el[0].getBoundingClientRect();
            let cloneBB = $clone[0].getBoundingClientRect();
            let otherBB = $otherEl[0].getBoundingClientRect();

            // Transform this clone's element to place it over the $otherEl.
            let cloneOffsetBefore = {
                top: Math.round(otherBB.top - cloneBB.top),
                left: Math.round(otherBB.left - cloneBB.left)
            }
            let cloneOffsetAfter = {
                top: Math.round(thisBB.top - cloneBB.top),
                left: Math.round(thisBB.left - cloneBB.left)
            }

            // Uniform scaling only.
            //let scale = otherBB.width / thisBB.width;
            let scale = 1;

            // translateZ added to ensure a transition ALWAYS occurs.
            $clone.css({
                "transform": "translate(" + cloneOffsetBefore.left + "px, " + cloneOffsetBefore.top + "px) translateZ(1px) scale(" + scale + ")"
            });

            // Specific template offset (fixes alignment issues on "narrativeplus" component).
            cloneOffsetAfter.left += otherBB.left - thisBB.left;

            // Apply the CSS immediately (skip transitions), then revert it (if there are any transitions, they will play here).
            $clone.hide(0).show(0);
            
            $clone.css("transform", "translate(" + (cloneOffsetAfter.left) + "px, " + cloneOffsetAfter.top + "px)  translateZ(0px) scale(" + scale + ")");

            this.toggleAttributes($clone, curCIndex, prevCIndex);
            this.toggleAttributes(this.$el, curCIndex, undefined);
        }
    };

    return StickyElementItemView;
});
