define([
  'core/js/adapt',
  'core/js/views/componentView'
], function(Adapt, ComponentView) {

  class ActivatorView extends ComponentView {

    events() {
      return {
        'click .js-activator-click': 'onBtnClicked'
      }
    }

    initialize() {
      ComponentView.prototype.initialize.call(this);
      this.setUpEventListeners();
    }

    setUpEventListeners() {
      this.listenTo(this.model, 'change:_isActivatorActive', this.onActivatorActiveChange);

      // Start off activated if in PDF mode (once ready).
      if (Adapt.isPDFMode()) {
        this.listenToOnce(this.model, 'change:_isReady', () => {
          this.model.set("_isActivatorActive", true);
          Adapt.log.info("Activator: PDF mode detected; activating automatically.");
        });
      }

      /*this.listenTo(this.model.get('_children'), {
        'change:_isActive': this.onItemsActiveChange,
        'change:_isVisited': this.onItemsVisitedChange,
        'change:_isVisible': this.onItemsVisibleChange
      });*/
    }

    checkIfResetOnRevisit() {
      var isResetOnRevisit = this.model.get('_isResetOnRevisit');

      // If reset is enabled set defaults
      if (isResetOnRevisit) {
        this.model.reset(isResetOnRevisit);
      }
    }

    onBtnClicked(event) {

      this.model.set("_isActivatorActive", !this.model.get("_isActivatorActive")); // This will trigger a change event inside of activatorModel.
      this.setCompletionStatus(); // Mark the component as complete once it's been interacted with.

      // Single activation implementation.
      if (this.model.get("_toggleEnabled") === false) {
        // FIXME: Not sure if I should be removing the click event entirely (would make resetting the view difficult).
        Adapt.log.warn("activatorView._toggleEnabled === false functionality needs re-implementation.");
        this.$el.off("click", event.currentTarget);
        $(event.currentTarget).attr("disabled", true);
      }
    }

    onActivatorActiveChange(model, isActive) {
      this.$(".js-activator-click")
        .toggleClass("is-active", isActive)
        .toggleClass("is-disabled", isActive);
        //.prop("disabled", isActive);


      if (!isActive) return;

      // Automatically attempt to scroll all the activated targets into view.
      // Slight delay added to give the targets a chance to modify their dimensions from the activation.
      _.debounce(this.scrollToTargets.bind(this), 0)();
    }

    scrollToTargets() {
      let $targets = $();
      this.model.get("_targets").each((target) => {
        $targets = $targets.add(target.$getTargets());
      });

      // adapt-scrollfix required for this to work.
      try {
        Adapt.navigateToElement($targets, {
          duration: 400,
          align: "inview",
          offset: {
            top: -100,
            left: 0
          },
          priority: -1 // Allow for the targets to override this navigation.
        });
      }
      catch (err) {
        Adapt.log.error("An unexpected error prevented adapt-activator from scrolling its targets into view (is \"adapt-scrollfix\" installed?).", err);
      }
    }

    preRender() {
      
    }

    postRender() {

      // Set ready status immediately (even if the other targets aren't ready at this point; the page won't render until all components are ready anyway).
      this.setReadyStatus();

      return;

      // Ensure ALL targets are ready before setting own ready status.
      if (this.model.get("_targetsReady")) {
        this.setReadyStatus();
      }
      else {
          this.listenToOnce(this.model, "change:_targetsReady", () => {
          this.setReadyStatus();
        });
      }
    }

  }
  
  ActivatorView.template = "activator";
  return ActivatorView;
});
