define([
    'handlebars',
    'core/js/adapt',
    './lottieModel',
    './lottieView'
], function(Handlebars, Adapt, LottieModel, LottieView) {

    /**
     * adapt-lottie
     * --------------------------------------
     * Embeds a AirBNB Lottie (https://airbnb.io/lottie/) animation using a Handlebars helper.
     * 
     * TBA: Allow for more customization.
     */

    /**
     * context.hash properties:
     * ------------------------
     * @param {string}  src                 Location of the Lottie animation .json.
     * @param {string}  controls            Which style of controls to use. Possible values: "" | "none" (default) | "basic" | "full" 
     * @param {boolean} [autoplay=false]    If false, the user will need to click to start the animation.
     */
    Handlebars.registerHelperEl("lottie", function() {
        let context = arguments[arguments.length-1];

        // Check for mandatory properties.
        if (!context.hash.src) throw "{{{lottie}}} helper: the \"src\" property must be defined.";

        let componentModel = Handlebars.getComponentModelFromContext(context);

        // Pass all properties to the model.
        let modelJSON = _.extend({
            _parent: componentModel,
            _controlsType: context.hash.controls || "none",
            autoplay: false,
            loops: context.hash.loops !== undefined ? context.hash.loops : 1,
            theme: context.hash.theme || "" // Set the theme for the Lottie animation.
        }, context.hash);

        return new LottieView({
            model: new LottieModel(modelJSON)
        }).$el
    });
});
