define([
    'handlebars',
    'core/js/adapt',
    './stickyElementModel'
], function(Handlebars, Adapt, StickyElementModel) {

    /**
     * adapt-stickyElement
     * --------------------------------------
     * This extension is experimental at this time.
     * Please read the provided README.txt for more information.
     * 
     * FIXME:
     * - Known bug: will not work if the item gets re-rendered. Should work if the component gets re-rendered though.
     * - Major bug: dd-Helpers using createHelperCallback used inside of an _item body break it!
     */

    /**
     * stickyElement helper
     * --------------------
     * These helpers will populate with the body of corresponding component._stickyElement._items
     */
    Adapt.on('router:page', () => {
        Handlebars.registerHelperEl("stickyElement", function() {
            
            let context = arguments[arguments.length-1];
            let hash = context.hash;

            // Check that all properties have been defined.
            let id = hash.id;
            if (id === undefined) throw "{{#stickyElement}} helper must have the \"id\" parameter defined.";

            // New: added adapt-stickyBlock compatibility.
            let itemIdx = hash.itemIdx;
            let isStickyBlock = itemIdx == "stickyBlock";

            if (!isStickyBlock) {
                itemIdx = parseInt(itemIdx);
                if (hash.itemIdx === undefined) throw "{{#stickyElement}} helper must have the \"itemIdx\" parameter defined.";
            }

            // Find the componentModel that this Helper was placed in.
            let componentModel = Handlebars.getComponentModelFromContext(context);

            // Find the component._stickyElement._items that contains the HTML for this helper.
            let se = componentModel.get("_stickyElement");
            if (!se) {
                throw "Could not render {{#stickyElement}} helper in component " + componentModel.get("_id") + ": component has no \"_stickyElement\" property defined.";
            }
            // The parent component's _stickyElements have been disabled. Don't render this helper.
            if (!se._isEnabled) return $();

            // Find the ID in the _stickyElement._items.
            let item = _.find(se._items, (item) => {
                return item._id == id;
            });
            // Error check.
            if (item === undefined) {
                throw componentModel.get("_id") + ": component._stickyElement._items does not have an item with \"_id\": \"" + id + "\"";
            }

            // -------------------------------------
            
            // Create the StickyElementModel for this id if none exists yet.
            if (!item._model) {
                item._model = new StickyElementModel({
                    _id: id,
                    _parent: componentModel,
                    body: item.body
                });
            }

            // Register the ComponentItemModel that this stickyElement resides in.
            if (componentModel.getChildren === undefined) throw "The {{#stickyElement}} helper cannot be used in a \"" + componentModel.get("_component") + "\" component.";

            // If inside a stickyBlock, use the component as the itemModel instead.
            if (isStickyBlock) {
                return item._model.registerComponentItemModel(componentModel, {
                    _isStickyBlock: true
                }).$el;
            }
            
            return item._model.registerComponentItemModel(componentModel.getChildren().models[itemIdx]).$el;
        });
    });

    // StickyElement transition helpers
    // ---------------------------------------------------------
    // TODO: Allow for {{{se_mark "1"}}} instead of {{{se_mark idx="1"}}} (saves on characters).
    var helpers = {

        /**
         * Needs to be added as an ATTRIBUTE to a HTML element.
         * For example:
         * <div {{{se_active idx="0,2"}}}>Activate me</div>
         * 
         * Will toggle the "is-se-active" class on the element on and off as the itemIdx changes. 
         */
        se_active: function(idx) {
            let context = arguments[arguments.length-1];
            let _idx = context.hash.idx !== undefined ? context.hash.idx : idx;
            return 'data-stickyelement-active-idx="' + _idx + '"';
        },

        /**
         * Needs to be added as an ATTRIBUTE to a HTML element.
         * 
         * Mark selected element when itemIdx == provided value(s)
         * 
         * @param {string} idx The index(es) that the element this attribute is placed on should be active for.
         * @param {string} hash.theme (Optional) theme name for the mark (allows for using different styles of marks).
         *                            Had to add this one as an attribute instead of a class (else I would run the risk of having two
         *                            class attributes on an element at once).
         */
        se_mark: function(idx) {
            let context = arguments[arguments.length-1];
            let _idx = context.hash.idx !== undefined ? context.hash.idx : idx;
            
            return `data-stickyelement-mark-idx="${_idx}"${context.hash.theme ? `data-theme="${context.hash.theme}"` : ""}`;
        },

        /**
         * Needs to be added as an ATTRIBUTE to a HTML element.
         * 
         * Show selected element only when itemIdx != provided value(s)
         */
        se_hide: function(idx) {
            let context = arguments[arguments.length-1];
            let _idx = context.hash.idx !== undefined ? context.hash.idx : idx;
            return 'data-stickyelement-hide-idx="' + _idx + '"';
        },

        /**
         * Needs to be added as an ATTRIBUTE to a HTML element.
         * 
         * Show selected element only when itemIdx != provided value(s).
         * Uses a jQuery fold animation to do so.
         */
        se_fold: function(idx) {
            let context = arguments[arguments.length-1];
            let _idx = context.hash.idx !== undefined ? context.hash.idx : idx;
            return 'data-stickyelement-fold-idx="' + _idx + '"';
        },

        /**
         * Needs to be added as an ATTRIBUTE to a HTML element.
         * 
         * Slides selected element in left only when itemIdx != provided value(s).
         */
        se_slideleft: function(idx) {
            let context = arguments[arguments.length-1];
            let _idx = context.hash.idx !== undefined ? context.hash.idx : idx;
            return 'data-stickyelement-slideleft-idx="' + _idx + '"';
        },

        /**
         * Needs to be added as an ATTRIBUTE to a HTML element.
         * 
         * Slides selected element in down only when itemIdx != provided value(s).
         */
         se_slidedown: function(idx) {
            let context = arguments[arguments.length-1];
            let _idx = context.hash.idx !== undefined ? context.hash.idx : idx;
            return 'data-stickyelement-slidedown-idx="' + _idx + '"';
        }
    }

    for (var name in helpers) {
        if (!helpers.hasOwnProperty(name)) continue;
        Handlebars.registerHelper(name, helpers[name]);
    }
});
