define([
    'core/js/adapt',
    './questionPlusMultiModel'
], function(Adapt, QuestionPlusMultiModel) {

    /**
     * adapt-dd-questionPlus
     * --------------------------------------
     * Note:
     * Due to the change in the core Question functionality, each Question's reset function + incorrect states will
     * need to be modified to function correctly with this extension.
     * 
     * Explanation:
     * - In Adapt core, it waits for Submit to be clicked, then applies .is-incorrect and .is-correct to ALL items, then locks
     *   the Question until Reset is clicked. Once Reset has been clicked, all .is-incorrect and .is-correct classes are removed
     *   from the items, and the user tries again.
     * - The way we have it set up is as follows. The user clicks on Submit, then .is-incorrect and .is-correct is applied ONLY
     *   to the items which have been interacted with by the user. If the answer isn't correct, the user can then change their answer,
     *   while keeping track of all the items which they've already tried. In essence, "narrowing down" their options until the
     *   correct answer is reached.
     * - As such, individual Question components will need slight modifications to how they mark their items; to prevent them marking
     *   them all at once.
     * 
     * Important:
     * - All Question components will need to REMOVE updateMarking() calls from inside their update() functions.
     *   updateMarking() will be set by questionPlus to be called automatically ONLY when the user clicks Submit.
     * - Furthermore, updateMarking() should be modified to only mark the items which the user has submitted, and not all items.
     *   Example: for an item with 3 options for the answer, apply .is-correct / .is-incorrect only to the answer the user submitted, 
     *            leaving the other two unknown.
     * - The _feedback property has been modified to be formatted as follows (core Adapt formatting doesn't suit our implementation):
     * "_feedback":{
			"_correct": {
				"title": "",
				"body": <<<"
					CORRECT
				">>>
			},
			"_incorrect": {
				"title": "",
				"body": <<<"
					INCORRECT
				">>>
			},
			"_partlyCorrect": {
				"title": "",
				"body": <<<"
					PARTLY CORRECT
				">>>
			}
     * 
     */

    /**
     * If true, all question components will reset their answers on pageload
     * regardless of whether the user answered the question correctly/incorrectly in a previous session.
     * This type of functionality is used throughout DD content, as our Questions are for self testing,
     * and do not need strict "lock the user out of attempting again" rules.
     */
    const RESET_ON_RELOAD = true;

    function _fixString(val) {
        if (!val) return "";
        if (typeof val === "string") return val.trim();
        return val;
    }

    class QuestionPlus extends Backbone.Controller{

        initialize() {
            
            Adapt.on({
                'app:dataReady': this.onDataReady.bind(this)
            });
        }

        onDataReady() {
            this._onDataReady();

            Adapt.on("blockView:preRender", this.onBlockViewPrerender.bind(this));
            Adapt.on("blockView:postRender", this.onBlockViewPostRender.bind(this));
            //Adapt.on("componentView:preRender", this.onComponentViewPreRender.bind(this));
            Adapt.on("componentView:postRender", this.onComponentViewPostRender.bind(this));
            Adapt.on("questionView:showFeedback", this.onQuestionViewShowFeedback.bind(this));
        }

        /**
         * Set a property on any block containing a Question component.
         */
        onBlockViewPrerender(blockView) {

            // Count how many Question components are in this block.
            let questionRowCount = 0;
            let questionRows = [];
            _.each(blockView.model.get("_componentRow").get("_children").models, (componentRow) => {
                // FIXME: Currently assumes ALL children in the block are question components.
                if (componentRow.get("_isQuestionType")) {
                    questionRowCount++;
                    questionRows.push(componentRow);
                }
                /*else {
                    throw "adapt-dd-questionPlus does not yet support a mixture of Question and non-Question type components in the same block: " + blockView.model.get("_id");
                }*/
            });

            if (questionRowCount == 0) return;

            // This block has Question components in it. Consider it a "Question" block.
            blockView.model.set("_isQuestionType", true);

            // Add the questionPlus logo to the first component (using the adapt-dd-componentLogo extension).
            blockView.model.getChildren().first().set("_componentLogo", {
                src: "assets/questionplus-logo.svg"
            });

            // If this block has more than one Question component, then it will be modified to use the QuestionPlusMulti functionality.
            if (questionRowCount > 1){
                blockView.model.set("_questionPlusMulti", new QuestionPlusMultiModel({
                    _parent: blockView.model,
                    _children: new Backbone.Collection(questionRows)
                }));
            }

            // Add a class to question blocks.
            blockView.model.set("_classes", blockView.model.get("_classes") + " is-question");
            blockView.$el.addClass("is-question"); // FIXME: Why can I do this in preRender? Weird implementation on Adapt's part.
        }

    
        // Ensure the correct settings are used for the question components
        // ----------------------------------------------------------------
        _onDataReady() {

            Adapt.components.each(componentModel => {

                if (!componentModel.get("_isQuestionType")) return;
                
                // This causes Adapt to show the 100% correct answer to the user once they've used their all of their attempts.
                // We don't use this feature (we instead narrow down the user's options to the correct answer with each user attempt).
                componentModel.set("_canShowModelAnswer", false);

                // Fallback: this extension already has scripts to give infinite attempts. This is just in case.
                componentModel.set("_attempts", 999);

                if (RESET_ON_RELOAD) {
                    // FIXME: Arbitrary use of debounce to delay this call until questionModel's trackable triggers.
                    _.debounce(() => {
                        componentModel.reset(true,true);
                    })();
                }

                // ----------------------------------------------------------------

                let block = componentModel.getParent();

                // This block has multiple Question components in it. It needs to be set up to use the QuestionPlusMulti behaviour.
                //if (block.get("_isQuestionType")) return;

                // This block contains Question components.
                block.set("_isQuestionType", true);
                
                //Adapt.findViewByModelId(block.get("_id")).$el.addClass("is-question");
            });
        }

        // Add a class to question blocks.
        onBlockViewPostRender(blockView) {
            blockView.$el.toggleClass("is-question", Boolean(blockView.model.get("_isQuestionType")));
        }

        /**
         * Custom DD functionality: always have the question components reset on pageload
         * (even if the user has already completed them in the LMS, don't carry over their answers).
         * @param {*} componentView 
         */
        onComponentViewPreRender(componentView) {
            
            if (!RESET_ON_RELOAD) return;

            let componentModel = componentView.model;
            if (!componentModel.get("_isQuestionType")) return;
            componentModel.reset(true,true);
        }

        /**
         * Override default submit behaviour.
         */
        onComponentViewPostRender(componentView) {

            let componentModel = componentView.model;

            // Only extend Question components
            if (!componentModel.get("_isQuestionType")) return;

            // Only extend once.
            if (componentModel.get("_isQuestionPlus")) return;
            componentModel.set("_isQuestionPlus", true);

            // Add a class (for styling)
            componentView.$el.addClass("is-questionplus");
            
            // To prevent the Question locking itself on Submit, and to prevent the buttonsView from changing to its RESET state,
            // the following modifications must be made.
            componentModel.setQuestionAsSubmitted = function(){

                // If the question isn't complete yet, don't disable the component.
                let isCorrect = componentModel.isCorrect();

                // Changed:
                componentModel.set({
                    _isEnabled: !isCorrect,
                    _isSubmitted: isCorrect
                });
            }

            // Give the user infinite attempts by preventing the model from decrementing the count.
            componentModel.updateAttempts = function(){
                // Infinite attempts.
                this.set('_attemptsLeft', 999);
            }

            this.setUpFeedback(componentModel);

            // Add the .show-user-answer class after the Submit has been clicked once.
            let _onSubmitClicked = componentView.onSubmitClicked;
            if (!_onSubmitClicked.prototype._questionPlusOverride) { 
                componentView.onSubmitClicked = function(){
                    _onSubmitClicked.apply(componentView, arguments);
                    this.$('.component__widget')
                        .addClass('show-user-answer');
                }
                componentView.onSubmitClicked.prototype._questionPlusOverride = true; // Override only once.
            }

            // ButtonsView behaviour change
            // ----------------------------
            // The buttonView.onFeedbackMessageChanged will disable the feedback button if no _feedback.correct text is provided.
            // Since DD sometimes doesn't provide a value for _feedback.correct, this feature is removed here.
            let buttonsView = componentView.buttonsView;
            let _onFeedbackMessageChanged = buttonsView.onFeedbackMessageChanged;
            if (!_onFeedbackMessageChanged.prototype._questionPlusOverride) { 
                buttonsView.onFeedbackMessageChanged = () => {

                    let canShowFeedback = buttonsView.model.get('_canShowFeedback');

                    Adapt.a11y.toggleAccessibleEnabled(
                        buttonsView.$('.js-btn-feedback'),
                        canShowFeedback
                    );

                    buttonsView.$('.js-btn-feedback').prop("disabled", !canShowFeedback);
                }
                buttonsView.onFeedbackMessageChanged.prototype._questionPlusOverride = true; // Override only once.
            }
            // Re-apply the listener.
            buttonsView.stopListening(buttonsView.model, 'change:feedbackMessage');
            buttonsView.listenTo(buttonsView.model, 'change:feedbackMessage', buttonsView.onFeedbackMessageChanged);
        }

        // Modify the questionModel to get its feedback title and messages from our DD formatted feedback instead.
        setUpFeedback(questionModel){

            let _getFeedbackTitle = questionModel.getFeedbackTitle;

            let _default = Adapt.course.get("_feedback");
            if (!_default) throw "\"course._feedback\" must be defined for un-set component._feedback titles to fall back on.";

            if (!_getFeedbackTitle.prototype._questionPlusOverride) {
                questionModel.getFeedbackTitle = function() {

                    let _feedback = questionModel.get("_feedback");
                    
                    if (Boolean(questionModel.get("_isCorrect"))) {
                        if (!_feedback._correct) return _getFeedbackTitle.apply(questionModel, arguments);
                        return _feedback._correct.title || _default._correct.title || _getFeedbackTitle.apply(questionModel, arguments);
                    }

                    if (Boolean(questionModel.get("_isAtLeastOneCorrectSelection"))) {
                        if (!_feedback._partlyCorrect) return _getFeedbackTitle.apply(questionModel, arguments);
                        return _feedback._partlyCorrect.title || _default._partlyCorrect.title || _getFeedbackTitle.apply(questionModel, arguments);
                    }

                    if (!_feedback._incorrect) return _getFeedbackTitle.apply(questionModel, arguments);
                    return _feedback._incorrect.title || _default._incorrect.title || _getFeedbackTitle.apply(questionModel, arguments);
                }
                questionModel.getFeedbackTitle.prototype._questionPlusOverride = true; // Only once.
            }

            // Copy over the message text from the DD formater _feedback to the _feedback format core Adapt expects.
            let _feedback = questionModel.get("_feedback");
            let formatMessage = (body, instruction) => {
                return Handlebars.templates["questionPlusFeedbackBody"]({
                    body: body,
                    instruction: instruction
                });
            }

            _feedback = _.extend({}, _feedback, {
                correct: formatMessage((_fixString(_feedback._correct) ? _fixString(_feedback._correct.body) : "") || ""),
                _incorrect: _.extend({}, _feedback._incorrect, {
                    final: formatMessage(
                        _fixString(_feedback._incorrect.body) || "",
                        _fixString(_feedback._incorrect.instruction) || _fixString(_default._incorrect.instruction) || "")
                }),
                // Partly correct will automatically use the _incorrect body and instruction unless told otherwise.
                _partlyCorrect: _.extend({}, _feedback._partlyCorrect, {
                    final: formatMessage(
                        _fixString(_feedback._partlyCorrect.body) || _fixString(_feedback._incorrect.body) || "",
                        _fixString(_feedback._partlyCorrect.instruction) ||  _fixString(_feedback._incorrect.instruction) || _fixString(_default._partlyCorrect.instruction) || "")
                })
            });

            questionModel.set("_feedback", _feedback);
        }

        /**
         * Call the component's updateMarking() function whenever the user clicks submit.
         */
        onQuestionViewShowFeedback(questionView) {
            if (questionView.updateMarking) questionView.updateMarking();

            // If the question is incomplete, scroll the component intruction text into view once the feedback has been closed.
            if (questionView.model.get("_isComplete")) return;

            this.listenToOnce(Adapt, {
                'notify:closed': () => {

                    _.debounce(() => {
                        Adapt.navigateToElement(questionView.$(".component__instruction"), {
                            align: 'inview'
                        });
                    }, 500)();

                    return;

                    // Scroll the user back to the top of the component_widget on wrong answer.
                    _.debounce(() => {
                        this.model.getChildren().each((itemModel) => {
                
                          // Only deselect visited answers.
                          let isVisited = Boolean(itemModel.get("_isVisited"));
                          if (!isVisited) return;
                
                          let shouldBeSelected = Boolean(itemModel.get("_shouldBeSelected"));
                          let isActive = Boolean(itemModel.get("_isActive"));
                          let isCorrect = shouldBeSelected && isActive;
                
                          if (!isCorrect && isActive) itemModel.set("_isActive", false);
                        })
                    }, 500)();
                }
            });
        }
    }

    let questionPlus = new QuestionPlus();
    Adapt.questionPlus = questionPlus;
});
