define([
    'handlebars',
    'core/js/adapt',
    "./stickyBlockView",
    "./stickyBlockEpubView"
], function(Handlebars, Adapt, StickyBlockView, StickyBlockEpubView) {

    /**
     * adapt-stickyBlock
     * --------------------------------------
     * ! BE SURE TO INCLUDE ALL REQUIRED EXTENSIONS (see bower.json) FOR THIS EXTENSION !
     * 
     * Modifies specific blocks to display their content in a "sticky scroll" fashion.
     * 
     * How to use:
     * -----------
     * - All components inside the block will be affected.
     * - Components with _layout == _stickyBlock._layout will scroll (put text here).
     * - Components with _layout != _stickyBlock._layout will stick to the screen (put graphics here).
     * - Basically, set up components as normal "left" + "right" pairs. The block will make one side sticky, the other side scroll.
     * 
     * TODO:
     * -----
     * - If possible, have the first/last reveal of the sticky be "disguised" as a normal componentRow until the user starts scrolling.
     *   (basically, have the spacing between the sticky and the surrounding blocks expand while it's stuck).
     * - Have the stuck column offer scrolling if necessary (as a fallback).
     * 
     * FIXME:
     * ------
     * - Add support for stickyElement.
     */

    // adapt-componentRow compatibility
    // ------------------------------------------------------------
    // Don't let the components be wrapped with the componentRow extension if the block is sticky.
    Adapt.on("app:dataReady", () => {

        Adapt.blocks.each((model) => {
            let _stickyBlock = model.get("_stickyBlock");
            if (!_stickyBlock || _stickyBlock._isEnabled === false) return;
            
            let _componentRow = model.get("_componentRow");
            model.set("_componentRow", _.extend(_componentRow || {}, {_isEnabled: false}));
        });
    });

    Adapt.on("blockView:postRender", (blockView) => {

        let blockModel = blockView.model;
        let _stickyBlock = blockModel.get("_stickyBlock");
        if (!_stickyBlock || _stickyBlock._isEnabled === false) return;

        blockView.$el.addClass("is-stickyblock");
        
        let view = StickyBlockView;
        if (Adapt.isEpubMode()) view = StickyBlockEpubView;

        // Create and append the view which will rearrange the block's components.
        blockView.$(".component__container").append(new view({
            model: blockModel
        }).$el);
    });

    /**
     * Allows any element to get the stickyBlock index of the component they're currently in (if applicable).
     */
    $.fn.getParentStickyBlockIndex = function() {

        let $component = $(this).closest(".component");
        if (!$component.length) Adapt.log.error("$.fn.getStickyBlockIndex(): could not find closest \".component\" for selector:", $(this));
        
        return $component.attr("data-stickyblock-index");
    }

    class StickyBlock extends Backbone.Controller {
        
        initialize() {

            this._resetOnReRender = [];

            // Globals
            this.inviewEvent = "stickyBlock:inview"
        }

        /**
         * CUSTOM:
         * The following properties will be cleared on componentModels whenever they are re-rendered by StickyBlock.
         * This needs to be explicitly called by other plugins.
         */
        addResetOnReRender(propertyName) {
            this._resetOnReRender.push(propertyName);
            this._resetOnReRender = _.uniq(this._resetOnReRender);
        }

        getResetOnReRender() {
            return this._resetOnReRender;
        }
    }

    Adapt.stickyBlock = new StickyBlock();
});
