define([
  'core/js/adapt',
  'core/js/models/questionModel'
], function(Adapt, QuestionModel) {

  /**
   * Notes:
   * - Each item has 2+ buttons.
   * - Items become activated as long as one or more of their buttons is clicked.
   * - Each item stores an array of options that have been selected for it.
   * - Each item will need one or more of its buttons selected to be correct.
   *   SCORM (or Adapt?) doesn't have a way to store multiple multiple "choice" interactions that in one go,
   *   so I've set this up to use "fill-in" as the submission type for now.
   */

  var MmcqModel = QuestionModel.extend({

    init: function() {
      QuestionModel.prototype.init.call(this);

      this.setupQuestionItemIndexes();
      this.checkCanSubmit();
    },

    getHasUniqueOptions: function() {
      return this.get("_combineOptions") === false || this.get("_hasInlineButtons"); // _hasInlineButtons = legacy.
    },

    // Set up indexes + collections for user answers.
    setupQuestionItemIndexes: function() {

      // Force inline buttons if display mode is set to "rows".
      let hasUniqueOptions = this.getHasUniqueOptions();
      this.set("_combineOptions", !hasUniqueOptions);

      let _options = this.get("_options");

      this.get('_items').forEach((item, index) => {
        if (item._index === undefined) {

          // If !hasUniqueOptions, then give each item the same options from the root of the component.
          let itemOptions = [];
          try {
            // If hasUniqueOptions, first try to use each individual item's _options, then fallback to global _options if need be.
            _.each(hasUniqueOptions ? (item._options || _options) : _options, (optionText, optionIndex) => {
              itemOptions.push({
                text: optionText,
                _index: optionIndex
              });
            });
          }
          catch (err) {
            if (hasUniqueOptions) {
              throw `"mmcq" component: if "_combineOptions": false, then each "_items" element must define its own "_options" array.`;
            }
            else {
              throw `"mmcq" component: if "_combineOptions": true, then the root of the component\'s JSON must define an "_options" array.`;
            }
          }
          let optionsCount = itemOptions.length;

          item._index = index;
          //item._options = (hasUniqueOptions && this.get("_isRandom")) ? _.shuffle(itemOptions) : itemOptions; // Randomize if inline.
          item._options = itemOptions;
          item._optionsSelected = new Array(optionsCount);
          item._optionsVisited = new Array(optionsCount);
          try {
            item._optionsSelected.fill(false);
            item._optionsVisited.fill(false);
          } catch (err) {}
        }
      });
    },

    setupRandomisation: function() {

      if (!this.get('_isRandom') || !this.get('_isEnabled')) return;

      // If the component has unique options for each item, then the options should be shuffled instead of the items.
      if (this.getHasUniqueOptions()) {
        this.get('_items').forEach((item, index) => {
          item._options = _.shuffle(item._options);
        });
        return;
      }

      this.set('_items', _.shuffle(this.get('_items')));
    },

    canSubmit: function() {
      // Can submit if every item has at least one input selected.
      // (At least one element in item._optionsSelected is == true).
      var canSubmit = _.every(this.get('_items'), function(item) {
        return _.find(item._optionsSelected, (e) => {return Boolean(e);}) !== undefined;
      });

      return canSubmit;
    },

    toggleOptionsSelected: function(itemIndex, optionIndex, _isSelected){

      if (optionIndex === undefined) throw "\"optionIndex\" cannot be undefined.";
      let isRadio = this.get("_isRadio");

      // Get the original index of the item (before the item order was shuffled).
      let item = _.find(this.get('_items'), (item) => {
        return item._index === itemIndex;
      });

      if (!isRadio){
        item._optionsSelected[optionIndex] = 
          (_isSelected === undefined) ? !Boolean(item._optionsSelected[optionIndex]) : Boolean(_isSelected);
      }
      else {
        item._optionsSelected = _.map(item._optionsSelected, (option, _optionIndex) => {
          if (optionIndex != _optionIndex) return false;
          else return (_isSelected === undefined) ? !Boolean(option) : Boolean(_isSelected);
        });
      }
      
      // Fixme: custom trigger used.
      this.trigger("_change:_optionsSelected");

      this.checkCanSubmit();
    },

    isCorrect: function() {
      var numberOfCorrectAnswers = 0;

      this.get('_items').forEach(function(item, index) {

        /**
         * An item is correct when only the _optionsSelected at indexes in the _optionsCorrect are true.
         * isCorrect will == true only if the _.find returns undefined (it couldn't find an incorrect match).
         */
        var isCorrect = true;
        _.find(item._optionsSelected, (option, optionIdx) => {
          let shouldBeSelected = Boolean(_.contains(item._optionsCorrect, optionIdx));
          if (option != shouldBeSelected) {
            isCorrect = false;
            return true; // Stop searching if there's any option in the wrong state.
          }
        });

        /**
         * All options which have been selected and checked should be marked as visited.
         */
        _.each(item._optionsSelected, (optionSelected, optionIdx) => {
          if (optionSelected) {
            item._optionsVisited[optionIdx] = true;
          }
        });
        
        if (!isCorrect) {
          item._isCorrect = false;
          return;
        }

        numberOfCorrectAnswers++;
        item._isCorrect = true;
        this.set({
          '_numberOfCorrectAnswers': numberOfCorrectAnswers,
          '_isAtLeastOneCorrectSelection': true
        });

      }, this);

      this.set('_numberOfCorrectAnswers', numberOfCorrectAnswers);

      if (numberOfCorrectAnswers === this.get('_items').length) {
        return true;
      }

      return false;
    },

    /**
     * Score currently calculated per-item
     * (meaning that if an item required 2 of its options to be selected, the same score will be given
     * if 0 or 1 of those 2 options was selected upon submission).
     */
    setScore: function() {
      var questionWeight = this.get('_questionWeight');

      if (this.get('_isCorrect')) {
        this.set('_score', questionWeight);
        return;
      }

      var numberOfCorrectAnswers = this.get('_numberOfCorrectAnswers');
      var itemLength = this.get('_items').length;

      var score = questionWeight * numberOfCorrectAnswers / itemLength;

      this.set('_score', score);
    },

    isPartlyCorrect: function() {
      return this.get('_isAtLeastOneCorrectSelection');
    },

    restoreUserAnswers: function() {

      if (!this.get('_isSubmitted')) return;

      var userAnswer = this.get('_userAnswer');
      var items = this.get("_items");

      _.each(userAnswer, (e, itemIdx) => {
        items[itemIdx]._optionsSelected = _.toArray(e)
      });

      // Fixme: custom trigger used.
      this.trigger("_change:_optionsSelected");

      this.setQuestionAsSubmitted();
      this.markQuestion();
      this.setScore();
      this.setupFeedback();
    },

    storeUserAnswer: function() {

      var userAnswer = new Array(this.get('_items').length);

      this.get('_items').forEach(function(item, index) {
        userAnswer[item._index] = _.toArray(item._optionsSelected);
      }, this);

      this.set({
        '_userAnswer': userAnswer
      });
    },

    resetUserAnswer: function() {

      this.set('_userAnswer', []);

      _.each(this.get("_items"), (item) => {
        item._isCorrect = false;
        item._optionsSelected.fill(false);
      });
    },

    /**
    * Used by tracking extensions to return an object containing the component's specific interactions.
    */
    /*getInteractionObject: function() {
      
      // textInputModel didn't implement this function either.
      
      return;
    },*/

    /**
     * used by adapt-contrib-spoor to get the user's answers in the format required by the cmi.interactions.n.student_response data field
     * returns the user's answers as a string in the format 'answer1[,]answer2[,]answer3'
     * the use of [,] as an answer delimiter is from the SCORM 2004 specification for the fill-in interaction type
     * 
     * --------------------------------------------------------------
     * 
     * Each item could have one or MORE of its buttons selected.
     * SCORM (or Adapt?) doesn't have a way to store multiple multiple "choice" interactions that in one go,
     * so I've set this up to use "fill-in" as the submission type for now.
     * 
     * FIXME: This is most likely going to look a bit messy if the answers are viewed in the LMS UI.
     * FIXME: Has not been tested yet!!!
     */
    getResponse: function() {

      let resp = [];
      _.each(this.get("_items"), (item) => {
        resp.push(item._optionsSelected.join('.'));
      });

      return resp.join('[,]');
    },

    /**
    * Used by adapt-contrib-spoor to get the type of this question in the format required by the cmi.interactions.n.type data field
    * @return {string}
    */
    getResponseType: function() {
      return "fill-in";
    }
  });

  return MmcqModel;

});
