define([
  'core/js/adapt',

  // \/ These libraries are included with the "adapt-jquery-ui" extension.
  'libraries/jquery-ui.interactions',
  'libraries/jquery.ui.touch-punch' // MUST BE INCLUDED! Fixes several compatibility issues that jquery-ui has with touch devices.
], function(Adapt) {

  const DRAGGING_BUFFER = 50; // Don't allow the outline to be removed after dragging until this many ms have passed.

  class ImgFinderOutlineView extends Backbone.View {
    
    className() {
      let classes = [
        "imgfinder-outline",
        this.model.get("_isCorrect") ? "is-correct" : "",
        this.model.get("_isIncorrect") ? "is-incorrect" : "",
        this.model.get("_isLocked") ? "is-locked" : ""
      ];

      return classes.join(" ");
    }

    attributes() {
      return {
        "style": `top: ${this.model.get("_top")}%; left: ${this.model.get("_left")}%; width: ${this.model.get("_width")}%; height: ${this.model.get("_height")}%;`
      }
    }

    events() {
      return {
        // FIXME: The outlines can sometimes get stuck in their mousedown state.
        "mousedown" : "onMouseDown",
        "touchstart" : "onMouseDown",
        "click" : "onClick"
      };
    }

    initialize() {
      this.render();
      this.setupEventListeners();
    }

    setupEventListeners() {
      this.listenTo(this.model, {
        "change:_left":             this.onPositionChange,
        "change:_top":              this.onPositionChange,
        "change:_isDragging":       this.onDraggingChange,
        "change:_isMouseDown":      this.onMouseDownChange,
        "change:_isCorrect":        this.onCorrectChange,
        "change:_isIncorrect":       this.onIsIncorrectChange,
        "change:_isLocked":         this.onLockedChange,
        "change:_isRemoved":        this.onIsRemovedChange
      });
    }

    onCorrectChange(model, isCorrect) {
      this.$el
        .toggleClass("is-correct", isCorrect);
    }

    onIsIncorrectChange(model, isIncorrect) {
      this.$el
        .toggleClass("is-incorrect", isIncorrect);
    }

    // TODO:? Allow for un-locking.
    onLockedChange(model, isLocked) {
      if (!isLocked) return;

      // Once locked, disable interaction.
      this.$el.draggable("option", "disabled", true);
      this.$el
        .off("mousedown")
        .off("touchstart")
        .off("click");
    }

    // Once the user clicks down on the outline, it should visually react.
    onMouseDown() {
      this.model.set("_isMouseDown", true);
    }

    onMouseDownChange(model, isMouseDown) {
      this.$el.toggleClass("is-mousedown", isMouseDown);
    }

    onBlur() {
      this.model.set("_isMouseDown", false);
    }

    onDraggingChange(model, isDragging) {
      this.$el.toggleClass("is-dragging", isDragging);
    }

    onPositionChange(model) {
      this.$el.css("left", this.model.get("_left") + "%");
      this.$el.css("top", this.model.get("_top") + "%");
    }

    render() {
      var data = this.model.toJSON();
      data.view = this;
      var template = Handlebars.templates['imgFinderOutline'];
      this.$el.html(template(data));

      this.setupDraggable();
    }

    /**
     * The user can click and drags the outline to reposition it after creating it.
     */
    setupDraggable() {
      this.$el.draggable(
        {
          containment: "parent",
          scroll: false, // Prevent the page scrolling while dragging (default jQuery implementation is bugged).
  
          start: (event, ui) => {
            this.model.set("_isDragging", true);
            this._isDragging = true;
          },
  
          // TODO: define these callback functions in the View itself and reference them here?
          // Called each tick the draggable is moved.
          /*drag: _.throttle((event, ui) => { // Requires a lot of processing. Throttled to improve performance.
            console.log("DRAG");

          }, 1),*/
          stop: () => { 

            // jQuery-ui draggable sets the "top" and "left" using px.
            // This will convert those values back to %.
            this.recalcPosition();
            this.model.set("_isDragging", false);
            this.model.set("_isMouseDown", false);

            // The user cannot remove the outline while this._isDragging == true.
            _.delay(() => {
              this._isDragging = false;
            }, DRAGGING_BUFFER);
          }
        });
    }

    /**
     * Clicking an outline (without dragging it) should remove it.
     */
    onClick(event) {
      event.preventDefault();
      event.stopPropagation(); // Prevent the $area element behind the outline detecting user clicks. 
      this.model.set("_isMouseDown", false);

      // Don't remove the outline if it was clicked too soon after being dragged.
      if (this._isDragging) return;

      this.model.remove();
    }

    $getArea() {
      return this.$el.closest(".js-imgfinder-outline-area").first();
    }

    /**
     * Sets the values of "_left" and "_top" based on the CSS position of this view.
     * Needs to be called after draggable() completes.
     */
    recalcPosition() {
      let pos = this.$el.position();

      let $area = this.$getArea();
      let bb = $area[0].getBoundingClientRect();

      // Set the "_position" (the CENTER of the outline).
      // The model will then update the style values accordingly.
      this.model.set("_position", {
        _left: (pos.left / bb.width) * 100 + this.model.get("_width")/2,
        _top: (pos.top / bb.height) * 100 + this.model.get("_height")/2
      });
    }

    onIsRemovedChange(model, isRemoved) {
      if (!isRemoved) return;
      this.remove();
    }

    remove() {
      this.$el.addClass("is-removed");
      this.model.remove();

      // Once the CSS transition is complete, remove this view as well.
      // FIXME: Timing hardcoded.
      _.delay(() => {
        super.remove();
      }, 500);
    }
  }

  return ImgFinderOutlineView;
});
