define([
  'core/js/adapt',
  './activatorView',
  './activatorModel',
  './activatorHelpers'
], function(Adapt, ActivatorView, ActivatorModel) {

  /**
   * Activator
   * ---------
   * 
   * - This component creates a button that triggers state changes on OTHER components that are compatible with it.
   * - The state changes it causes are usually things such as:
   *      - Revealing and playing a timeline (causing an animation to start playing).
   *      - Causing an element to change its appearance.
   *      - Causing text to update itself / solving a math equation.
   *      - Misc. (this component is still in development; features may change over time).
   */

  // Extensions
  // -----------------------------------------------------
  // Add default activator behaviour to components which haven't implemented any.
  // FIXME: Temp. location for implementation.
  Adapt.on("componentView:preRender", (componentView) => {

    if (componentView.onActivatorToggle) return; // This component has already implemented the functionality.
    let activatorTarget = componentView.model.get("_activatorTarget");
    if (!activatorTarget) return; // This component isn't being targetted by an activator.

    // Add a class to identify + stylize the target.
    componentView.$el
      .addClass("is-activator-target")
      .attr("data-activator-activation", activatorTarget.get("_activationType"));

    // FIXME: Is this the right way to set this?
    // Have each target component toggle its is-activator-active class based on the state of its activator target.
    componentView.listenTo(activatorTarget, "change:_isActive", (model, isActive) => {
      componentView.$el.toggleClass("is-activator-active", isActive);
    });

    // Add default activator functionality to any text being targetted.
    if (componentView.model.get("_component") == "text" && activatorTarget.get("_activationType") == "default"){
      setUpDefaultTargetComponent(componentView);
    }
  });

  function setUpDefaultTargetComponent(componentView) {

    let activatorTarget = componentView.model.get("_activatorTarget");

    // Compatibility with componentRow: remove the divider line.
    componentView.model.get("_componentRow").set("_hasDivider", false);

    // Start the componentView off hidden.
    componentView.$el.hide(0);

    // Since the componentView is hidden, it needs to manually calculate its bounding client rect.
    componentView.getBoundingClientRect = () => {

      componentView.$el.show(0).css("height", "");
      let bb = componentView.$el[0].getBoundingClientRect();

      // Hide the component again (if applicable).
      if (!activatorTarget.get("_isActive")) {
        componentView.$el.hide(0);
      }

      return bb;
    }

    // Have it slideUp/Down when toggled.
    componentView.listenTo(activatorTarget, "change:_isActive", (model, isActive) => {
      if (isActive) {
        componentView.$el.slideDown();
          
        // Scroll it and the activator button into view if it's the first child.
        /*if (activatorTarget.get("_index") === 0) {

          //let $activatorBtn = Adapt.findViewByModelId(activatorTarget.get("_activatorModel").get("_id")).$(".activator__btn");
          //let $selector = $activatorBtn.add($("." + componentView.model.get("_id")));
        }*/
      }
      else {
        componentView.$el.slideUp();
      }
    });
  }

  return Adapt.register('activator', {
    model: ActivatorModel,
    view: ActivatorView
  });

});
