define([
    'core/js/adapt'
], function(Adapt) {

    /**
     * FIXME:
     * Possible bug: it's possible that if an annotationView has targets in a component that's very slow to render,
     * that the initTargets function will trigger before all targets are ready.
     */

    class AnnotationView extends Backbone.View {

        className() {
            let classNames = [
                "annotation",
                "js-annotation-click",
                this.model.get("_hasIcon") ? "has-icon" : "",
                this.model.get("_tooltip") ? "has-tooltip" : ""
            ]
            return classNames.join(" ");
        }

        tagName() {
            return "span";
        }

        events() {
            return {
                "click" : "onClick"
            }
        }
        
        attributes() {

            let attributes = {
                "data-annotation-num": this.model.get("_number")
            };

            return attributes;
        }

        initialize(){

            this.setupEventListeners();
            _.debounce(this.initTargets.bind(this))(); // Debounce to allow for all mark elements to be rendered first.
            this.render();
        }

        setupEventListeners(){

            this.listenTo(this.model, "change:_isActive", this.onActiveChange);
            this.listenTo(this.model, "change:_isVisited", this.onVisitedChange);

            Adapt.on("annotation:toggle", this.onAdaptAnnotationToggle.bind(this));
        }

        initTargets(){
            let $targets = this.$getForTargets();
            
            $targets
            .addClass("is-annotation-target")
            .attr("data-annotation-num", this.model.get("_number"));

            // PDF mode
            if (Adapt.isPDFMode()) {
                $targets.addClass("is-active");
            }
        }

        render(){
            let template = Handlebars.templates['annotation'];
            this.$el.html(new Handlebars.SafeString(template(this.model.toJSON())).string);

            if (Adapt.isPDFMode()) this.renderTooltipFallback();
        }

        /**
         * Render the tooltip for this annotation in the nearest .tooltip-fallback element after this one.
         * FIXME: Will only work inside of a component.
         */
        async renderTooltipFallback() {
            let tooltip = this.model.get("_tooltip");
            if (!tooltip) return;

            // FIXME: Need a proper way to await for HelperEl's to render.
            // FIXME: The way that the tooltipFallback is being found is quite arbitrary at the moment.
            _.debounce(() => {
                let componentView = Adapt.findViewByModelId(this.model.get("_parent").get("_id"));
                let $tooltipFallback = componentView.$(".tooltip-fallback");
                if (!$tooltipFallback.length) { 
                    throw "AnnotationView: Could not find a {{{tooltipFallback}}} element in component \"" + this.model.get("_parent").get("_id") + "\" (required for PDF mode).";
                }
                
                $tooltipFallback.append("<div class='tooltip-fallback__item' data-annotation-num='" + this.model.get("_number") + "'>" + tooltip + "</div>");
            }, 0)();
        }

        onClick(){
            this.toggle();

            // FIXME: Tooltip activation should be the in the active event, not on the click event.
            if (!this.model.get("_isActive")) return;
            
            // Allow the annotation to also create adapt-tooltip extension popups over itself when clicked.
            let tooltip = this.model.get("_tooltip");
            if (tooltip) {
                let tooltipPopupView = Adapt.createTooltip(this.$el, {
                    content: tooltip,
                    _type: "annotation"
                });
                this.tooltipPopupView = tooltipPopupView;

                this.listenToOnce(tooltipPopupView.model, "change:_isActive", () => {this.model.set("_isActive", false);});
            }
        }

        toggle(){

            // Change the model value and...
            this.model.set("_isActive", !this.model.get("_isActive"));

            // ...deactivate ALL the other annotations with _toggleEnabled == true.
            Adapt.trigger("annotation:toggle", this.model);

            /*_.each(models, function(annotationModel){
                
                if (annotationModel == this.model) return; // Don't toggle yourself though.
                if (!annotationModel.get("_toggleEnabled")) return;
                annotationModel.deactivate();
            }.bind(this));*/
        }

        /**
         * Whenever another AnnotationModel becomes active, deactivate this one.
         * @param {*} model 
         */
        onAdaptAnnotationToggle(model) {
            
            if (model == this.model) return; // Ignore self triggers.
            this.model.set("_isActive", false);
        }

        onVisitedChange(model, isVisited){
            if (!isVisited) return;
            this.$el.addClass("is-visited");
        }

        /**
         * Find and return a jQuery object selecting all _for elements.
         * Will search for all elements in the entire parent .block / .notify of the annotation.
         */
        $getForTargets() {
            var _for = this.model.get("_for");
            var $for = $();

            if (_for) {
                let $container = this.$el.closest(".block, .notify");
                if (!$container.length) {
                    Adapt.log.error("adapt-annotation: could not find closest .block / .notify element.", "Content: " + this.model.get("content"));
                    return $for;
                }
                
                _.each(_for, (forName) => {

                    let $f = $for.add($container.find("[name='" + forName + "']"));

                    // Check if the element being toggled exists.
                    if (!$f.length) {
                        Adapt.log.error("adapt-annotation: Could not find element with name=\"" + forName + "\" inside its componentView (" + this.model.get("_parent").get("_id") + ").");
                    }

                    $for = $for.add($f);
                });
            }

            return $for;
        }

        onActiveChange(model, isActive){

            // Toggle the active + visited classes on the button.
            this.$el.toggleClass("is-active", isActive);
            if (isActive) {this.model.set("_isVisited", true);}

            // Toggle all _for elements.
            let $forTargets = this.$getForTargets();
            if (!$forTargets.length) return;

            Adapt.annotation.toggleEl($forTargets, isActive);
        
            /**
             * Ensure the user sees the annotations by scrolling them into view.
             */
            if (!isActive) {
                if (this.tooltipPopupView) this.tooltipPopupView.remove(); // Remove the tooltip (if it exists).
                $(document).off("click touch", this.onClickAnywhereEvent); // Remove the global event this made.
                return;
            }

            // Listen for a click anywhere to close it.
            _.debounce(() => {
                // The user clicking anywhere is enough to close the tooltip popup.
                this.onClickAnywhereEvent = this.onClickAnywhere.bind(this);
                $(document).one("click touch", this.onClickAnywhereEvent);
            })();
        }

        onClickAnywhere(event) {

            //let innerEl = this.$(".annotation__inner")[0];
            //if (event.target == innerEl || $.contains(innerEl, event.target)) return; // Clicks on or inside annotation.
            
            // Check if the click was on one of the targets that this annotation activates.
            let $target = $(event.target);
            if ($target.closest(".is-annotation-target.is-active").length) return;

            this.model.set("_isActive", false);
        }
    };

    return AnnotationView;
});
