define([
    'core/js/adapt'
], function(Adapt) {

    class BlockBackgroundModel extends Backbone.Model {

        defaults() {
            return {
                _activeItemIdx: null
            };
        }

        initialize() {

            if (!this.get("_items")) {throw "BlockBackground: Must have an '_items' array property defined.";}
            
            this.setUpId();
        }

        // FIXME: Groups need to be set up on postRender.
        // FIXME: Bad encapsulation; this needs to be called by adapt-blockBackground.
        onParentPostRender() {
            this.setUpGroups();
        }

        // Give the blockBackground the same ID as its parent, only b-X -> b-X-bbg.
        setUpId() {
            this.set("_id", this.get("_parent").get("_id") + "-bbg");
        }

        setUpGroups() {
            // Get all componentRow children that will be rendered inside of this block.
            let componentRows = this.get("_parent").get("_componentRow").get("_children");
            let items = this.get("_items");

            const childIds = items.map(e => {return e._child;});

            // Group the componentRows.
            // -----------------------------------------------
            // If groups are automatic, they will be created by grouping:
            // ComponentRowWithTitle + AllComponentRowsWithoutTitlesImmediatelyAfterIt
            let groups = [];
            let curGroup = [];
            componentRows.each((componentRow) => {
                
                const title = componentRow.get("title");

                // If the ComponentRow has a title, or one of its children's _ids appear in the items, Start a new group.
                if (
                    title ||
                    componentRow.getChildren().find(e => {return childIds.includes(e.get("_id"));})
                ) {
                    if (curGroup.length) {groups.push(new Backbone.Collection(curGroup));}
                    curGroup = [];
                }
                curGroup.push(componentRow);
            });
            groups.push(new Backbone.Collection(curGroup));

            // Assign the groups to the items.
            _.each(items, (item, i) => {
                item._componentRows = i < groups.length ? groups[i] : null;
            });

            // If there are more groups than items, clone additional items from the last real item added.
            if (items.length < groups.length) {
                Adapt.log.warn("BlockBackground: More component groups found than _items in " + this.get("_parent").get("_id") + " (groups: " + groups.length + ", _items: " + items.length + ") \n\n(There aren't enough _blockBackground._items elements).");
                for (let i = items.length; i < groups.length; i++) {
                    
                    let cloneItem = _.extend({}, items[items.length-1]);
                    cloneItem._componentRows = groups[i];
                    items.push(cloneItem);
                }
            }
            // If there are more items than groups, remove all items that didn't get a group.
            else if (items.length > groups.length) {
                items = _.filter(items, (item, i) => {
                    return Boolean(item._componentRows);
                });

                Adapt.log.error("BlockBackground: Could not find componentRows for all _items in " + this.get("_parent").get("_id") + " (groups: " + groups.length + ", _items: " + items.length + ") \n\n(There are too many _blockBackground._items for the amount of components in the block).");
            }

            // Set up IDs.
            _.each(items, (item, i) => {
                item._id = this.get("_id") + "-" + i;
                
                // Set the values on the components.
                item._componentRows.each((cr) => {
                    cr.set("_blockBackgroundItemId", item._id);
                });
            });

            this.set("_items", items);
        }
    }

    return BlockBackgroundModel;
});
