define([
    'core/js/adapt'
], function(Adapt) {

    /**
     * Regular expression for selecting the inputField names inside of the "fn" string.
     * 
     * Example: "(1 / ${i1}) * ${i2}"
     * $1: ["${i1}", "${i2}"]
     * $2: ["i1", "i2"]
     */ 
    const FN_REGEX = new RegExp(/(\${([\w-]*?)})/g);

    class OutputFieldView extends Backbone.View {

        className() {
            let classes = [
                "outputfield",
                this.model.get("_hasError") ? "has-error" : ""
            ];

            return classes.join(" ");
        }

        events() {
            return {

            }
        }

        initialize() {
            this.setUpModelData();
            
            this.render();
            this.setUpEventListeners();

            // Set the initial value + state.
            this.updateResult();
            this.updateValidationState();
        }

        /**
         * It's important to set up the collection of InputFields that this view listens to HERE, and not in the OutputFieldModel.
         * 
         * Reason: If this data was set up during the creation of the OutputFieldModel instead, there's a chance that not all
         * InputFieldModels would be ready yet.
         */
        setUpModelData() {

            // Get the function that this outputField will use for its calculations.
            let fn = this.model.get("fn"); // Example: "(1 / ${i1}) * ${i2}";
            let allItems = this.model.get("_parent").get("_inputField")._items; // Reference to ALL inputFields in this block.

            // Find the names of all inputFields this outputField will need to read.
            let names = [];
            fn.replace(FN_REGEX, (match, $1, $2) => { // No need to replace anything; we just need to iterate through all matched groups.
                names.push($2);
                return match;
            });
            
            // Find all input fields of matching names.
            let items = allItems.filter(item => {return names.indexOf(item.get("_name")) >= 0; });
            
            // Create a new collection using only the InputFieldModels that this OutputField uses for its calculation.
            this.model.set("_inputFields", new Backbone.Collection(items));
        }

        setUpEventListeners() {

            // Listen for changes to the parent BlockModel's "_inputField._items" collection Models.
            this.listenTo(this.model.get("_inputFields"), {
                "change:_val" : this.onInputFieldValChange,
                "change:_isCorrect" : this.onInputFieldStateChange
            });

            this.listenTo(this.model, "change:result", this.onResultChange);
            this.listenTo(this.model, "change:_hasError", this.onHasErrorChange);
            this.listenTo(this.model, "change:_isVisited", this.onIsVisitedChange);
        }

        onInputFieldValChange(inputFieldModel, val) {
            this.updateResult();

            // Consider this outputField "visited" once any of the inputFields that it monitors has been interacted with.
            this.model.set("_isVisited", true);
        }

        onIsVisitedChange(model, isVisited) {
            this.$el.toggleClass("is-visited", isVisited);
        }

        /**
         * Whenever any inputField changes its state to correct/incorrect, check ALL inputFields this outputField references.
         * Set the state of this outputField based on these values.
         */
        onInputFieldStateChange(inputFieldModel) {
            this.updateValidationState();
        }

        /**
         * If even a single InputField that this OutputField references is incorrect, set this OutputField to be incorrect too.
         */
        updateValidationState() {
            
            let isAnyIncorrect = Boolean(this.model.get("_inputFields").find(inputField => {return !inputField.get("_isCorrect");}));
           
            this.model.set("_isCorrect", !isAnyIncorrect);
            this.model.set("_isIncorrect", isAnyIncorrect);

            this.$el
                .toggleClass("is-incorrect", isAnyIncorrect)
                .toggleClass("is-correct", !isAnyIncorrect);
        }

        /**
         * Recalculate the "fn" and update the .js-outputfield-result with the result.
         */
        updateResult() {

            let fn = this.model.get("fn");
            let eqn = fn.replace(FN_REGEX, (match, $1, $2) => {

                // Swap each $1 group with the value from its corresponding inputFieldModel.
                // $1: "${name}"
                // $2: "name"

                let inputField = this.model.get("_inputFields").find(inputField => {return inputField.get("_name") == $2;});
                if (!inputField) throw `OutputFieldView: could not find inputField with name: "${$2}"!\n\nfn: ${fn}`;

                let val = inputField.getNumber();

                return val;
            });

            // FIXME: eval() used (should be safe).
            let result = eval(eqn);

            // It's possible for the result to be "Infinity", or isNan(). If either, show "N/A" instead.
            if (isNaN(result) || !isFinite(result)) {
                this.model.set("result", "N/A");
                this.model.set("_hasError", true);
                return;
            }

            this.model.set("result", result);
            this.model.set("_hasError", false);
        }

        onResultChange(model, result) {

            let r = result;

            // Round the result to X decimal places.
            if (!isNaN(r)) r = parseFloat(r.toFixed(this.model.get("decimals") || 0));

            // If the result was rounded, indicate that it was by appending ... to it.
            // TODO: Enable this? I don't think it's necessary.
            //if (r != result) r += "...";

            this.$(".js-outputfield-result").html(r);

            // Update the "title" attribute of the container.
            this.$(".js-outputfield-content").attr(
                "title",
                `${this.model.get("prefix") || ""}${result}${this.model.get("suffix") || ""}`
            );
        }

        onHasErrorChange(model, hasError) {
            this.$el.toggleClass("has-error", hasError);
        }

        render() {
            var data = this.model.toJSON();
            
            var template = Handlebars.templates['outputField'];
            this.$el.html(template(data));
        }
    }

    return OutputFieldView;
});
