define([
  'core/js/adapt',
  'core/js/views/componentView',
  './modeEnum'
], function(Adapt, ComponentView, MODE) {

  class NarrativePlusView extends ComponentView {

    events() {
      return {
        'click .js-narrativeplus-controls-click': 'onNavigationClicked',
        'click .js-narrativeplus-progress-click': 'onProgressClicked',
        'swiperight .narrativeplus__widget':  "onSwipeRight",
        'swipeleft .narrativeplus__widget':  "onSwipeLeft"
      }
    }

    setupEventListeners() {
      if (this.model.get('_setCompletionOn') === 'inview') {
        this.setupInviewCompletion('.component__widget');
      }

      // Listen for swipe events.
      // TODO: Implement this.
      this.$(".component__widget")
      
    }

    preRender() {

      //this.moveSliderToIndex = _.debounce(this.moveSliderToIndex.bind(this),0);

      this._isUninteracted = true; // Set to false only once the user interacts with the buttons.
      this.setUpModelData();
      
      this.listenTo(Adapt, {
        'device:changed device:resize': this.reRender,
        'notify:closed': this.closeNotify
      });
      this.renderMode();

      this.listenTo(this.model.getChildren(), {
        'change:_isActive': this.onItemsActiveChange,
        'change:_isVisited': this.onItemsVisitedChange
      });

      this.checkIfResetOnRevisit();
      this.calculateWidths();
    }

    setUpModelData() {

      if (!Adapt.sizing) return;

      // adapt-sizing extension compatibility. Allow for individual item sizing.
      let globalSizing = this.model.get("_itemSizing");
      _.each(this.model.get("_items"), item => {

        let graphic = item._graphic;
        if (!graphic) return; // If no graphic, no need for sizing.

        // Override the global sizing with the individual item sizing (if any).
        let itemSizing = graphic._sizing || {};
        itemSizing = _.extend(
          {},
          globalSizing,
          itemSizing
        );

        // Set the item's sizing classes.
        item._graphic._sizing = itemSizing;
        item._graphic._sizing._classes = Adapt.sizing.getSizingClass(item._graphic._sizing);
        item._graphic._sizing._parClasses = Adapt.sizing.getParentClasses(item._graphic._sizing);
      });
    }

    renderMode() {
      this.calculateMode();
      if (this.isLargeMode()) {
        this.$el.addClass('mode-large').removeClass('mode-small');
      } else {
        this.$el.addClass('mode-small').removeClass('mode-large');
      }
    }

    calculateMode() {
      var mode = Adapt.device.screenSize === 'large' ?
          MODE.LARGE :
          MODE.LARGE; //MODE.SMALL;
      this.model.set('_mode', mode);
    }

    isLargeMode() {
      return this.model.get('_mode') === MODE.LARGE;
    }

    onItemsActiveChange(item, _isActive) {
      if (_isActive === true) {
        this.setStage(item);
      }
    }

    onItemsVisitedChange(item, isVisited) {
      if (!isVisited) return;
      this.$('[data-index="' + item.get('_index') + '"]').addClass('is-visited');
    }

    setupNarrative() {

      this.renderMode();
      var items = this.model.getChildren();
      if (!items || !items.length) return;

      // Ensure there's always an active item.
      var activeItem = this.model.getActiveItem();
      if (!activeItem) {
        activeItem = this.model.getItem(0);
        activeItem.toggleActive(true);
      } else {
        // Manually trigger change as it is not fired on reentry
        items.trigger('change:_isActive', activeItem, true);
      }

      this.calculateWidths();

      /*if (!this.isLargeMode() && !this.model.get('_wasHotgraphic')) {
        this.replaceInstructions();
      }*/
      this.setupEventListeners();
    }

    setStage(item) {
      var index = item.get('_index');
      if (this.isLargeMode()) {
        // Set the visited attribute for large screen devices
        item.toggleVisited(true);
      }

      // Set the selected classes for the progress dots
      this.$('.narrativeplus__progress:visible')
        .removeClass('is-selected')
        .filter('[data-index="'+index+'"]')
        .addClass('is-selected');

      /*var $slideGraphics = this.$('.narrativeplus__slider-image-container');
      $slideGraphics.children('.controls').a11y_cntrl_enabled(false);
      $slideGraphics.filter('[data-index="'+index+'"]').children('.controls').a11y_cntrl_enabled(true);*/

      // Toggle aria visibility on the items (only the currently active one should be visible).
      let $items = this.$('.narrativeplus__slider-item');
      Adapt.a11y.toggleAccessible($items/*.addClass('u-visibility-hidden u-display-none')*/, false);
      Adapt.a11y.toggleAccessible($items.filter('[data-index="'+index+'"]')/*.removeClass('u-visibility-hidden u-display-none')*/, true);

      this.evaluateNavigation();
      this.evaluateCompletion();
      this.moveSliderToIndex(index);
    }

    /**
     * Change the state of the left and right buttons based on the active item index.
     */
    evaluateNavigation() {
      var active = this.model.getActiveItem();
      if (!active) return;

      var currentStage = active.get('_index');
      var itemCount = this.model.getChildren().length;

      var isAtStart = currentStage === 0;
      var isAtEnd = currentStage === itemCount - 1;

      this.$('.narrativeplus__controls-left').toggleClass('u-visibility-hidden', isAtStart);
      this.$('.narrativeplus__controls-right').toggleClass('u-visibility-hidden', isAtEnd);
    }

    evaluateCompletion() {
      if (this.model.areAllItemsCompleted()) {
        this.trigger('allItems');
      }
    }

    moveSliderToIndex(itemIndex) {

      var $slider = this.$('.narrativeplus__slider');

      // Finish any prior transitions (only really noticeable if the user mashes the < or > buttons.
      // looks a bit jarring, but prevents stickyElement bugs).
      $slider.hide(0).show(0);

      var offset = this.model.get('_itemWidth') * itemIndex;
      if (Adapt.config.get('_defaultDirection') === 'ltr') {
        offset *= -1;
      }
      var cssValue = 'translateX('+offset+'%)';

      // Debounce used to improve visual movement of stickyElementItems.
      if (this._debounceTransitionEnd) {
        this._debounceTransitionEnd.cancel(); // End the previous debounce callback if it hasn't triggered yet.
        this.onTransitionEnd();
      }

      this._debounceTransitionEnd = _.debounce(() => {
        $slider.css('transform', cssValue);

        if (Adapt.config.get('_disableAnimation') || this._isUninteracted) {
          this.onTransitionEnd();
        } else {
          $slider.one('transitionend', this.onTransitionEnd.bind(this));
        }

        this._debounceTransitionEnd = null;
      }, 0);
      this._debounceTransitionEnd();
    }

    onTransitionEnd() {
      if (this._isUninteracted) return;

      var index = this.model.getActiveItem().get('_index');
      if (this.isLargeMode()) {

        // THIS IS WHAT WAS CAUSING THE CONTENT TO JUMP TO A "RANDOM" LOCATION ONLOAD.
        Adapt.a11y.focusFirst(this.$('.narrativeplus__slider-item[data-index="'+index+'"]'), {defer: true});
      }
    }

    calculateWidths() {
      var itemCount = this.model.getChildren().length;
      this.model.set({
        '_totalWidth': 100 * itemCount,
        '_itemWidth': 100 / itemCount
      });
    }

    postRender() {
      
      this.renderMode();
      this.setupNarrative();
      
      this.$('.narrativeplus__slider').imageready(this.setReadyStatus.bind(this));

      // FIXME: Not implemented.
      if (Adapt.config.get('_disableAnimation')) {
        this.$el.addClass('disable-animation');
      }
    }

    checkIfResetOnRevisit() {
      var isResetOnRevisit = this.model.get('_isResetOnRevisit');
      // If reset is enabled set defaults
      if (isResetOnRevisit) {
        this.model.reset(isResetOnRevisit);
      }
    }

    /**
     * Custom:
     * Whenever the user clicks on a navigation button, ensure that the top of the content is inview.
     */
    scrollIntoView(){

      _.debounce(() => {
        Adapt.navigateToElement(this.$(".narrativeplus__widget"), {
          duration: 400,
          align: "inview"
        });
      }, 0)();
    }

    onNavigationClicked(event) {

      this._isUninteracted = false;
      
      var stage = this.model.getActiveItem().get('_index');

      if ($(event.currentTarget).hasClass('narrativeplus__controls-right')) {
        this.model.setActiveItem(++stage);
      } else if ($(event.currentTarget).hasClass('narrativeplus__controls-left')) {
        this.model.setActiveItem(--stage);
      }

      this.scrollIntoView();
    }

    onProgressClicked(event) {

      this._isUninteracted = false;

      event && event.preventDefault();
      var clickedIndex = $(event.target).data('index');

      if (clickedIndex == this.model.getActiveItem().get("_index")) return; // Do nothing if this item is already active.

      this.model.setActiveItem(clickedIndex);

      this.scrollIntoView();
    }

    onSwipeRight(event) {
      console.log("right");
    }

    onSwipeLeft(event) {
      console.log("left");
    }
  }
  
  NarrativePlusView.template = "narrativePlus";
  return NarrativePlusView;
});
