define([
  'core/js/adapt',
  'core/js/models/questionModel'
], function(Adapt, QuestionModel) {

  var TextInputPlusModel = QuestionModel.extend({

    init: function() {
      QuestionModel.prototype.init.call(this);
      this.checkCanSubmit();
      this.setupEventListeners();
    },

    setupEventListeners() {
      this.listenTo(this, "change:_userInput", this.checkCanSubmit);
    },

    restoreUserAnswers: function() {
      if (!this.get('_isSubmitted')) return;

      var userAnswer = this.get('_userAnswer');
      var genericAnswers = this.get('_answers');
      this.get('_items').forEach(function(item) {
        var answerIndex = userAnswer[item._index];
        if (answerIndex >= TextInputPlusModel.genericAnswerIndexOffset) {
          item.userAnswer = genericAnswers[answerIndex - TextInputPlusModel.genericAnswerIndexOffset];
          item._answerIndex = answerIndex;
        } else if (answerIndex > -1) {
          item.userAnswer = item._answers[answerIndex];
          item._answerIndex = answerIndex;
        } else {
          if (item.userAnswer === undefined) item.userAnswer = '******';
          item._answerIndex = -1;
        }
        if (item.userAnswer instanceof Array) item.userAnswer = item.userAnswer[0];
      });

      this.setQuestionAsSubmitted();
      this.markQuestion();
      this.setScore();
      this.setupFeedback();
    },

    setupFeedback() {

      let customFeedback = "";

      this.get("_incorrectItems").forEach((item) => {
        let incorrectText = item?._feedback?._incorrect?.body;
        if (incorrectText) {
          customFeedback += `<p>${incorrectText}</p>`;
        }
      });
      this.get("_correctItems").forEach((item) => {
        let correText = item?._feedback?._correct?.body;
        if (correText) {
          customFeedback += `<p>${correText}</p>`;
        }
      });

      this.get("_feedback")._incorrect = _.extend({}, this.get("_feedback")._incorrect, {
        body: customFeedback
      });

      // Re-set up the feedback for this component.
      Adapt.questionPlus.setUpFeedback(this);

      QuestionModel.prototype.setupFeedback.call(this);
    },

    setupRandomisation() {
      
    },

    // Can submit if the user has typed enough characters into the input field.
    canSubmit: function() {

      let userInputLength = this.get('_userInput')?.length;

      return userInputLength > this.get("_answerLength")._min && 
             userInputLength < this.get("_answerLength")._max;
    },

    setItemUserAnswer:function(itemIndex, userAnswer) {
      var item = this.get('_items')[itemIndex];
      item.userAnswer = userAnswer;
      this.checkCanSubmit();
    },

    //This preserve the state of the users answers for returning or showing the users answer
    storeUserAnswer: function() {
      var items = this.get('_items');

      this.isCorrect();

      var userAnswer = new Array( items.length );
      items.forEach(function(item, index) {
        userAnswer[ item._index ] = item._answerIndex;
      });
      this.set('_userAnswer', userAnswer);
    },

    isCorrect: function() {

      let userInput = this.get("_userInput");
      let items = this.get("_items");

      let correctItems = [];
      let incorrectItems = [];

      // For each item...
      items.forEach(item => {

        // See if any of its terms appear in the userInput.
        let found = Boolean(item._terms.find(term => {
          
          let termFound = userInput.match(new RegExp("\\b" + term + "\\b", "i"));

          return Boolean(termFound);
        }));

        if (found) correctItems.push(item);
        else       incorrectItems.push(item);
      });

      this.set({
        _correctItems: correctItems,
        _incorrectItems: incorrectItems
      })

      return incorrectItems.length === 0;
    },

    isPartlyCorrect: function() {
      return this.get('_isAtLeastOneCorrectSelection');
    },

    checkAnswerIsCorrect: function(possibleAnswers, userAnswer) {
      var uAnswer = this.cleanupUserAnswer(userAnswer);
      var matched = possibleAnswers.filter(function(cAnswer) {
        return this.cleanupUserAnswer(cAnswer) == uAnswer;
      }, this);

      var answerIsCorrect = matched && matched.length > 0;
      if (answerIsCorrect) this.set('_hasAtLeastOneCorrectSelection', true);
      return answerIsCorrect;
    },

    cleanupUserAnswer: function(userAnswer) {
      if (this.get('_allowsAnyCase')) {
        userAnswer = userAnswer.toLowerCase();
      }
      if (this.get('_allowsPunctuation')) {
        userAnswer = userAnswer.replace(/[\.,-\/#!$£%\^&\*;:{}=\-_`~()]/g, '');
        //remove any orphan double spaces and replace with single space (B & Q)->(B  Q)->(B Q)
        userAnswer = userAnswer.replace(/(  +)+/g, ' ');
      }
      // removes whitespace from beginning/end (leave any in the middle)
      return $.trim(userAnswer);
    },

    // Used to set the score based upon the _questionWeight
    setScore: function() {
      var numberOfCorrectAnswers = this.get('_numberOfCorrectAnswers');
      var questionWeight = this.get('_questionWeight');
      var itemLength = this.get('_items').length;

      var score = questionWeight * numberOfCorrectAnswers / itemLength;

      this.set('_score', score);
    },

    resetUserAnswer: function() {
      this.get('_items').forEach(function(item) {
        item._isCorrect = false;
        item.userAnswer = '';
      });
    },

    /**
    * used by adapt-contrib-spoor to get the user's answers in the format required by the cmi.interactions.n.student_response data field
    * returns the user's answers as a string in the format 'answer1[,]answer2[,]answer3'
    * the use of [,] as an answer delimiter is from the SCORM 2004 specification for the fill-in interaction type
    */
    getResponse: function() {
      return _.pluck(this.get('_items'), 'userAnswer').join('[,]');
    },

    /**
    * used by adapt-contrib-spoor to get the type of this question in the format required by the cmi.interactions.n.type data field
    */
    getResponseType: function() {
      return 'fill-in';
    }
  }, {
    genericAnswerIndexOffset: 65536
  });

  return TextInputPlusModel;

});
