define([
    'handlebars',
    'core/js/adapt',
    'core/js/models/componentModel',
], function(Handlebars, Adapt, ComponentModel) {

    /**
     * adapt-dd-helpers
     * --------------------------------------
     * Defines Handlebars helpers for inline Adapt content commonly used in DD learning material.
     * 
     * ! All function header parameters are optional unless otherwise specified !
     * 
     * WARNING:
     * --------
     * - If any HTML inside of a helper using createHelperCallback has custom scripts / events applied to it before the helper's
     *   debounce re-renders it, then they WILL BE REMOVED (due to the HTML context being re-rendered).
     * 
     * TODO:
     * - 
     * 
     * FIXME:
     * - 
     */

    /**
     * This object will contain the data for all elements created with registerHelperEl.
     * This is used to allow for a way to process duplicate ".registerhelperel__placeholder" elements,
     * which sometimes occur when using the helperEl function inside of notify:popups.
     */
    const helperElAttr = "data-registerhelperel-id";
    let helperElData = {};

    /**
     * Used by the helper functions to translate expression parameters into an easy to use object.
     * Returns an extended context.hash object.
     * Be SURE to only call with getContextHash.apply(this, arguments);
     * @param {*} args The arguments object for the specific helper function.
     */
    function getContextHash(args) {
        
        // In Handlebars, the context is always the last parameter.
        let context = arguments[arguments.length-1];

        // Parse certain string values in the expression parameters.
        context.hash = _.mapObject(context.hash, (val, key) => {
            
            // Boolean
            if (val == "true") return true;
            if (val == "false") return false;

            // Fallback
            if (val === undefined) return false;

            // Relaxed JSON object (EXPERIMENTAL).
            // Allow for strings like this to be used:
            // xxx="{prop1:'A', prop2:true}"
            // xxx="{'prop1':'A', 'prop2':true}"
            //
            // FIXME: Will not work if the variables provided have a '-' in their key.
            if (val[0] == "{" && val[val.length-1] == "}") {

                // UPDATE: Using negative lookbehind (?<!) in a regex will NOT work in Safari.
                // To fix this, the following workaround is used:
                // - The string is scanned for all escaped quotes (/', ///", etc.), and those quotes are replaced with a placeholder.
                // - The main regex is used to find and wrap all property keys with ''.
                // - The placeholdered quotes are replaced with normal quotes again.
                const SQUOT_PLACEHOLDER = "@@!_S_!@@";
                const DQUOT_PLACEHOLDER = "@@!_D_!@@";
                val = val.replace(/([^\\]\\(?:\\{2})*)(['"])/g, (match, $1, $2) => {
                    return $1 + ($2 === "'" ? SQUOT_PLACEHOLDER : DQUOT_PLACEHOLDER);
                });

                let regex = new RegExp(/(['"]?)([\w-]*)(\1)\s*?:\s*?((['"]).*?(\5)|(\w+))/g);
                
                // For each key + value, ensure the key is surrounded by quotes
                // (for the JSON parsing that takes place after this step).
                val = val.replace(regex, (match, $1, $2, $3, $4) => {
                    // $1: Left side quote (if any).
                    // $2: Key
                    // $3: Right side quote (if any).
                    // $4: Value
                    
                    // If the key isn't surrounded by quotes, surround it with quotes.
                    if (!$1) return `'${$2}': ${$4}`; 

                    // Replace all placeholders in the value with the real quotes that they were.
                    $4 = $4.replace(new RegExp(SQUOT_PLACEHOLDER, 'g'), "'");
                    $4 = $4.replace(new RegExp(DQUOT_PLACEHOLDER, 'g'), '"');

                    // Otherwise, leave it as is.
                    return match;
                });
            }

            // JSON object (EXPERIMENTAL).
            // Must be formatted like this:
            // xxx="{'prop1':'A','prop2':true}"
            // xxx="['A', 'B']"
            if (
                (val[0] == "{" && val[val.length-1] == "}") ||
                (val[0] == "[" && val[val.length-1] == "]")
            ){
                try {
                    val = val.replace(/[']/g, '"'); // Parsing JSON requires "" to be used.
                    return JSON.parse(val);
                }
                catch(err) {
                    throw "adapt-dd-helpers could not parse string as JSON: " + val;
                }
            }

            return val;
        });

        // Add the expression's content to the object too.
        context.hash.content = context.fn ? context.fn(this) : "";

        return context.hash;
    }

    // Helpers
    // ---------------------
    // Hardcoded for DD functionality.

    var helpers = {

        /**
         * Can be used as follows:
         * {{#otherhelper class=(concat "hotgraphicflip__image" ../_itemSizing._itemClasses)}}{{/otherhelper}}
         * 
         * Note: will NOT add " " between each joined string.
         * @returns 
         */
        concat: function() {
            var arg = Array.prototype.slice.call(arguments,0);
            arg.pop();
            return arg.join('');
        },

        // Partials
        // --------------------------
        // These can be used to create complex parameters for the expressions below.
        // Example: {{#figure sizing=(arr 4 true)}}{{/figure}}

        /**
         * (Partial) returns an array made from the parameters.
         */
        arr: function() {
            return Array.from(arguments);
        },

        // Main helpers
        // --------------------------

        /**
         * Creates a NON-SELETABLE Zero-Width-Space.
         * Used rarely when certain text is needed to wrap.
         * 
         * Note: you may need "word-break: break-word;" on the parent element for it to work.
         */
        zws: function() {

            let data = getContextHash.apply(this, arguments);
            return `<span class='u-no-select'>​</span>`;
        },

        /**
         * Creates a normal hyperlink.
         * 
         * @param {string} hash.href (Optional) specify the href. If not provided, will use the content instead.
         * @param {Boolean} hash.ref (Optional) If true, will wrap the hyperlink in &lt; and &gt; (reference tags).
         */
        hyperlink: function() {

            let data = getContextHash.apply(this, arguments);

            data.content = data.content.trim();

            let html = Handlebars.templates["ddHyperlink"](data);
            return new Handlebars.SafeString(html);
        },

        /**
         * Creates a download link to a local / external URL.
         * 
         * EPUB MODE:
         * ------------------------------------
         * If the content is in _isEpub mode, all local download links will be re-directed
         * to an external hosting server.
         * This is done due to some EPUB readers not supporting local links to files (inside the .EPUB zip itself).
         * 
         * FIXME:? will interpret "www.*.com" as local due to lack of http* prefix (href should never have values like
         * this provided regardless though).
         */

        /** Phone link */
        tel: function() {
            
            let data = getContextHash.apply(this, arguments);

            let html = Handlebars.templates["ddTel"](data);
            return new Handlebars.SafeString(html);
        },

        /**
         * Keyboard key
         * ------------
         * Makes the content resemble a keyboard key. Should be used when instructing users on what keyboard keys to press.
         */
        kbd: function() {
            let data = getContextHash.apply(this, arguments);
            let html = Handlebars.templates["ddKbd"](data);
            return new Handlebars.SafeString(html);
        },

        /** Clearfix */
        clearfix: function() {

            let data = getContextHash.apply(this, arguments);

            let html = Handlebars.templates["ddClearfix"](data);
            return new Handlebars.SafeString(html);
        },

        /**
         * Inline icons
         * ---------------------------------
         * Creates either a font icon (using Adapt's default icon font),
         * or an embedded inline image.
         * 
         * @param {string} type             Faster way of writing "type='type'".
         * @param {string} context.type     The name of the font icon to use.
         * @param {string} context.src     [OPTIONAL]: Use an inline <img> with the provided src instead of an icon. 
         */
        inlineIcon: function(type) {

            let context = arguments[arguments.length-1];

            // Get icon type:
            let iconType = typeof type === "string" ? type : context.hash.type;

            // Check that either iconType or src are defined.
            if (!iconType && !context.hash.src) throw "{{{#icon}}}: Must have a defined \"type\" or \"src\" property.";

            let data = _.extend(context.hash, {
                "iconClass": "icon-" + iconType
            });

            let html = Handlebars.templates["ddIcon"](data);
            return new Handlebars.SafeString(html);
        },

        /* Blockquote */
        blockquote: function() {

            let context = arguments[arguments.length-1];

            let html = Handlebars.templates["ddBlockquote"](_.extend(context.hash, {content: context.fn(this)}));
            return new Handlebars.SafeString(html);
        },

        /**
         * Paper block (decorative).
         * 
         * @param {string} context.class [OPTIONAL]: CSS classes.
         * @param {string} context.style [OPTIONAL]: Different visual styles: "" | "steps" 
         */
        paper: function() {
            let context = arguments[arguments.length-1];
            let html = Handlebars.templates["ddPaper"](_.extend(context.hash, {content: context.fn(this)}));
            return new Handlebars.SafeString(html);
        },

        /**
         * Used to create inline error icons (similar to how comments are placed in a Word document).
         * These expressions are debug only, and MUST be removed prior to publishing the content.
         * @param {*} options 
         */
        error: function() {

            let context = arguments[arguments.length-1];

            Adapt.log.error("Page has {{#error}} tag present.");

            let html = Handlebars.templates["ddError"](_.extend(context.hash, {content: context.fn(this)}));
            return new Handlebars.SafeString(html);
        }

    };

    for (var name in helpers) {
        if (helpers.hasOwnProperty(name)) {
            Handlebars.registerHelper(name, helpers[name]);
        }
    }

    /**
     * registerHelperEl
     * ------------------------
     * Modified version of Handlebars.registerHelper.
     * Helpers registered with this function will be able to return $el and el element objects instead of just HTML strings.
     */
    let registeredHelperEls = [];

    /**
     * Trigger a callback once a placeholder element has been rendered with Handlebars.
     */
    function observePlaceholderRender(placeholderAttrId, el, callback){

        // Store the data.
        helperElData[placeholderAttrId] = el;

        let mutationObserver;

        // Timeout
        // --------------------------------------------------
        // If the observer doesn't detect a mutation within this time, throw an error.
        // (If everything is running correctly, the observer should have detected the mutation almost immediately after starting).
        const OBSERVER_TIMEOUT = 1000;
        let observerTimeout = setTimeout(() => {

            let $placeholder = $("[" + helperElAttr + "='"+ placeholderAttrId + "']");
            if ($placeholder.length) {
                Adapt.log.error("registerHelperEl: Could not be rendered into its placeholder element: [" + helperElAttr + "='"+ placeholderAttrId + "']\nMutationObserver could not detect the placeholder being rendered.", $placeholder);
            }
            else {
                Adapt.log.warn("registerHelperEl: Could not be rendered into its placeholder element.\nThis was likely caused by the placeholder being incorrectly rendered as an attribute value.", );
            }

            mutationObserver.disconnect();

            // FIXME: Is there a good way of preventing these placeholders being added inside of attribute values?
        }, OBSERVER_TIMEOUT);

        // Main
        // --------------------------------------------------
        mutationObserver = new MutationObserver((mutations) => {

            //mutationsCount++;

            _.each(mutations, (mutation) => {
                if (mutation.type != "childList") return;
                
                _.find(mutation.addedNodes, (node) => {
                    if (!node) return;

                    // Placeholder element found. Stop the mutationObserver and call the callback.
                    let placeholderEl;
                    try {
                        placeholderEl = node.querySelector("[" + helperElAttr + "='"+ placeholderAttrId + "']");
                        // If the placeholder is still undefined, check if the node itself it the cause of the mutation.
                        if (!placeholderEl) {
                            if (node.getAttribute(helperElAttr) == placeholderAttrId) {
                                placeholderEl = node;
                            }
                        }
                    }
                    catch (err) {
                        placeholderEl = false;
                    }
                    if (placeholderEl) {
                        clearTimeout(observerTimeout);
                        mutationObserver.disconnect();

                        let $el = $(el);
                        $(placeholderEl).replaceWith($el);

                        if (callback) {
                            callback($el);
                        }

                        return true;
                    }
                });
            });
        });
        // FIXME: observing the entire body may cause slowdown.
        mutationObserver.observe($("body")[0], {
            childList: true,
            subtree: true
        });

        // Notify fallback:
        // ----------------
        // EDIT: Replaced with manual calls to Handlebars.updatePlaceholders.
        /*Adapt.on("notify:opened", (view) => {
            
            let $placeholder = view.$el.find("[" + helperElAttr + "='"+ placeholderAttrId + "']");
            if ($placeholder.length) {
                let $el = $(el).clone();
                $placeholder.replaceWith($el);
            }
        });*/
    }

    /**
     * Re-used helperEl HTML fix:
     * -------------------------------------------------------------------
     * (Needs to be called manually).
     * Looks through the $parentEl and replaces all placeholder elements generated with "registerHelperEl()" with
     * CLONES of the "el" that are meant to be rendered there.
     * 
     * Explanation:
     * ------------
     * - figurePopupView has a lightbox feature, which re-renders the same HTML from the main page inside of a notify:popup.
     * - This breaks the helperEl, as it causes there to be two copies of the same .registerhelperel__placeholder being rendered,
     *   and only the first one gets replaced by the "el", while the second remains a placeholder.
     * - figurePopupView calls this function to have it scan through any .registerhelperel__placeholder elements that haven't
     *   been replaced, and replace them with a clone of the original "el".
     */
    Handlebars.fixPlaceholders = ($parentEl) => {
        let $placeholders = $parentEl.find(".registerhelperel__placeholder");
        $placeholders.each((i, e) => {
            let id = $(e).data("registerhelperel-id");
            $(e).replaceWith(helperElData[id].clone());
        });
    }
    
    /**
     * TODO: Allow for a way to return a custom placeholder element, and then append to that placeholder with callback later.
     * 
     * @param {*} helperName 
     * @param {*} helperCallback        Called within the normal Handlebars.registerHelper. Must return an element.
     * @param {*} postRenderCallback    [Optional] Gets called and passed the $el after it has replaced its placeholder.
     */
    Handlebars.registerHelperEl = (helperName, helperCallback, postRenderCallback) => {

        // Only register each helper once.
        if (_.includes(registeredHelperEls, helperName)) return;
        if (!helperCallback) throw "helperCallback must be defined";

        // Initially, render a placeholder element in Handlebars that will be replaced by the $el as soon as the mutation is observed.
        Handlebars.registerHelper(helperName, function() {

            let el = helperCallback.apply(this, Array.prototype.slice.call(arguments));

            // If the "el" returned by the callback is a string, then behave like a regular Handlebars Helper
            // (just render the string assuming that it's HTML; don't create and wait for a placeholder).
            if (typeof el === "string") return el;

            // Same goes for if it's a Handlebars SafeString.
            if (el instanceof Handlebars.SafeString) return el;
            
            helperName = helperName.toLowerCase(); // Very important: ensure the attribute name is entirely lower case (for XHTML).

            let _uniqueId = _.uniqueId(helperName);
            let placeholderHTML = `<span class="registerhelperel__placeholder" ${helperElAttr}="${_uniqueId}">PLACEHOLDER(${helperName})</span>`;
            
            // Start a mutationObserver that will wait for the first childList mutation that includes the HTML which contains the placeholder element.
            observePlaceholderRender(_uniqueId, el, postRenderCallback);

            return placeholderHTML;
        });
    }

    /**
     * Finds the nearest view and returns it (if any).
     * @param {*} helperContext 
     */
    /*Handlebars.getViewFromContext = (helperContext) => {
        let data = helperContext.data.root;
        if (data.view) return data.view;

        Adapt.log.error(`Could not find a View for helperContext:`, data);
        throw "";
    }*/

    /**
     * Finds and returns the componentModel belonging to the component that the Handlebars helper is placed in. 
     * @param {*} helperContext 
     */
     Handlebars.getComponentModelFromContext = (helperContext) => {

        let componentData = helperContext.data.root;
        let model;
        
        // Check if the model object is an Adapt ComponentModel.
        let isComponentModel = (m) => {
            try {
                if (
                    m instanceof ComponentModel ||  // FIXME: Fails with ItemsComponentModels.
                    m.get("_type") === "component"  // Fallback if the above fails.
                ) {
                    //if (!(m instanceof ComponentModel)) console.log(m);
                    return true;
                }
            }
            catch (err) {
                return false;
            }
            return false;
        }

        // First, try to find the nearest model by going through the "view" property.
        // This works in most contexts (such as if the helper is in a component or a popup).
        try {
            model = componentData.view.model;
            if (isComponentModel(model)) return model;
        }
        catch (err) {
            // If the above fails, it's because the componentData has no view property.
            // Treat the componentData as a model itself and try to continue.
            model = componentData;
        }

        // Fallback:
        // ---------
        // If the context that the helper is in is NOT the componentModel, then attempt to traverse up the "_parent" ancestors until it
        // is found. 
        const errMsg = `Could not find ComponentModel from provided Handlebars Helper context.
See the array below for the ancestors ("_parent"s) that were traversed.`;
        let path = [];
        do {
            try {
                path.push(model);

                if (model instanceof Backbone.Model) {

                    // Check if this model is the ComponentModel.
                    if (isComponentModel(model)) break;

                    // And go to its parent if it's not.
                    model = model.getParent?.apply(model) || model.get("_parent");
                }
                // Fallback in case the context is in a non-Backbone Model object.
                // (This is not the component).
                else {
                    model = model._parent;
                }
            }
            catch {
                Adapt.log.error(errMsg, path);
                throw "";
            }

        } while (Boolean(model));

        if (!model) {
            Adapt.log.error(errMsg, path);
            throw "";
        }

        // Code should never reach this point.
        return model;
    }

    /**
     * Finds and returns the blockModel parent of the component that the Handlebars helper is placed in. 
     * @param {*} helperContext 
     */
    Handlebars.getBlockModelFromContext = (helperContext) => {
        let componentModel = Handlebars.getComponentModelFromContext(helperContext);
        return componentModel.get("_parent");
    }

    // Common
    // -----------------------

    /**
     * Returns the HTML to create temporary element, then overrides it with the provided view after a debounce.
     * See annotation extension implementation for an example.
     * FIXME: If multiple helpers using createHelperCallback are nested within each other,
     * then the debounces will stack (and cause postRender to trigger before all the content is ready)!
     * @param {*} context       The last parameter of the calling helper.
     * @param {string} name     The name of the calling helper
     * @param {*} callback      Callback with parameters ($el, blockText, componentView).
     */
    let createHelperCallback = function(context, name, callback){
        
        Adapt.log.error("createHelperCallback has been depractated (had asynchronous bug). Use Handlebars.registerHelperEl instead.");
        throw "";

        return placeholderHTML;
    }
    
    Handlebars.createHelperCallback = createHelperCallback;
    Handlebars.getComponentViewFromContext = (context) => {

        Adapt.log.error("getComponentViewFromContext has been depractated (had asynchronous bug). Use Handlebars.getComponentModelFromContext instead.");

        let componentView = context.data.root.view;
        if (componentView.model.get("_type") != "component") {
            componentView = Adapt.findViewByModelId(componentView.model.get("_parent").get("_id"));
        }
        return componentView || undefined;
    }
    Handlebars.getContextHash = getContextHash;
});
