define([
  'core/js/adapt'
], function(Adapt) {

  /**
   * Base class for ActivatorCompontentTargetModel and ActivatorElementTargetModel.
   */
  class ActivatorTargetModel extends Backbone.Model {

    defaults() {
        return {
          _activatorModel: null,        // Reference to the ActivatorModel that contains this ActivatorTargetModel's collection.
          _activationType: "default",   // Can be set to "custom" to prevent the default activation behaviour.

          _isReady: false,      // Needs to be manually set by the children once their target it ready.
          _isActive: false,     // Toggled whenever the Activator button is clicked.
          _duration: 800,       // When a single adapt-activator has many targets,
                                // the activation of each sequential target will wait for the duration of the previous target to elapse.
        }
    }

    initialize() {

    }

    setReadyStatus() {

      // FALLBACK:
      // Delay added in-case setReadyStatus() is called in initialize
      // (which would cause the activatorModel parent to miss the "change" event for "_isReady").
      _.delay(() => {
        this.set("_isReady", true);
      }, 0);
    }

    toggle() {
      
      return new Promise((resolve) => {
        
        this.set("_isActive", !this.get("_isActive"));
        this.onToggle(this.get("_isActive"));

        _.debounce(resolve, this.get("_duration") || 0)();
      });
    }

    onToggle(isActive) {
      throw `activatorTargetModel.js: abstract function 'onToggle()' not implemented.`;
    }

    /**
     * Needs to return the $el of the target(s) that this model will active.
     */
    $getTargets() {
      throw `activatorTargetModel.js: abstract function '$getTargets()' not implemented.`;
    }
  }

  return ActivatorTargetModel;

});
