define([
    'core/js/adapt'
], function(Adapt) {

    /**
     * This extension creates a jQuery plugin that can be used to set up <input type="text"> elements to consistant
     * DD standards.
     * 
     * This extension is used currently by adapt-textinputplus, but may be used again in future for other components / extensions.
     * 
     * InputField groups:
     * ------------------
     * Sometimes, you may need to have multiple inputFields which allow for interchangeable values between them; so long as there's
     * no double up.
     * 
     * Example: four inputFields, and any of these user inputs are considered correct:
     * 
     * "A", "B", "C", "D"
     * "D", "B", "C", "A",
     * "D", "A", "B", "C"
     * 
     * HOWEVER, the following would still have one inputField be incorrect:
     * 
     * "A", "A", "B", "C"
     * 
     * InputFields weren't originally set up with this functionality; but I've added some code below that has made it possible.
     * It may be a bit confusing, so here's an explanation on how it works.
     * 
     * InputField groups explanation:
     * ------------------------------
     * Whenever an inputField in a GROUP checks whether the answer within it is correct; it first checks if any inputFields that
     * came before it (and are in the same group) ALSO have that value.
     * 
     * For example:
     * 
     * "A", "A", "B", "C"
     * 
     * In the above, the SECOND inputField would be incorrect, while the FIRST would be correct, despite "A" being in both of their
     * "_answers" arrays.
     * 
     * The SECOND inputField can still become correct if the FIRST one's values changes:
     * 
     * "D", "A", "B", "C"
     * 
     * Meaning that grouped inputFields are able to change states when their SIBLINGS change states too.
     * (Or they would, but that feature is TBA).
     * 
     * In a similar way, when a grouped InputField needs to reveal its answer, it picks the first value in its "_answers" array that none
     * of its other siblings are currently using.
     * 
     * In addition to all of this, if the "_answers" array is ["A", "A", "B", "C"], then the following user input:
     * 
     * "A", "B", "A", "A"
     * 
     * Would have the first three inputFields be correct, and the last one be incorrect. A.k.a, duplicate answers are also allowed.
     * 
     * 
     * InputField groups FIXME:
     * ------------------------
     * - Currently, sibling inputFields don't listen for changes to their other siblings.
     *   (Enter a duplicate value, then remove the first instance of that value. Despite now being correct, the latter value will
     *   still be red until interacted with).
     *   (This is not an issue for inputFields which don't auto-submit; like those in Questions).
     * 
     * - When making inputField groups, the developer has to make sure that ALL of them have the exact same "_answers" array set.
     *   There could be unforseen bugs if the "_answers" array differs between the inputFields in a group.
     */

    class InputFieldModel extends Backbone.Model {

        defaults(){
            return {
                // Public settings (set when creating model)
                // ------------------------------------

                // Required
                _answers: [],           // Array of acceptable answers.

                // Optional
                _requireSubmit: true,   // If false, will check user answer immediately whenever the value changes.
                                        // If true, will only change states when the submit function is called.

                _canShowMarking: true,  // If false, will not apply .is-correct / .is-incorrect styles to the element.
                _isHintEnabled: false,  // If false, the hint icons will only appear after enableHint() is called.
                                        // If true, the hint icon will always be visible.

                _allowsAnyCase: true,
                _allowsPunctuation: true,
                _width: undefined,      // Explicitly set or automatically calculated width that this inputField will use.

                // Private properties
                // ------------------------------------
                _val: "",               // Value in the input element.
                _isCorrect: false,      
                _isLocked: false,       // Don't allow interaction if locked.
                
                _isSubmitted: false,    // Don't show marking unless in a submitted state (unless _requireSubmit == false).
                _incorrectUserAnswers: [],   // All incorrect user answers that have been explicitly submitted so far.

                _submitCount: 0        // Number of times the field has checked the answer inside of it.
            }
        }

        getAnswers() {
            return this.get("_answers");
        }

        initialize(){

            this.setUpWidth();
            this.checkGroupError();
            this.setUpEventListeners();
        }

        /**
         * The min-width of this inputfield will be based on the length of the longest allowed answer.
         * (Or the explicitly set "width" value if it was used).
         */
        setUpWidth() {
            let answerLength = 1;
            let explicitWidth = this.get("_width");

            if (explicitWidth) {
                answerLength = explicitWidth;
            }
            else {
                _.each(this.get("_answers"), a => {
                    let s = a.toString();
                    if (s.length > answerLength) answerLength = s.length;
                });
            }

            this.set("_width", answerLength);
        }

        /**
         * If an inputField belongs to a group of inputFields, ensure that its "_answers" array is at least as big as the group.
         * 
         * Note: This check is delayed to allow for all inputFields in the group to be made first.
         * Since it's only purpose it to error check; the delay shouldn't cause any problems for other scripts.
         */
        checkGroupError() {

            _.delay(() => {
                let groupItems = this.getGroupItems();
                if (!groupItems) return;

                if (groupItems.length > this.get("_answers").length) {
                    throw `{{#inputField}}: inputField in group "${this.get("_group")}" must have at least ${groupItems.length} possible answers (currently only has ${this.get("_answers").length}).`;
                }
            }, 0);
        }

        setUpEventListeners(){
            this.listenTo(this, "change:_val", this.onValChange);
        }

        onValChange(model, val){

            // If this inputField requires submission, only check for the same incorrect answer being entered multiple times.
            if (this.get("_requireSubmit")) {

                // This val has already been submitteed, and is incorrect.
                // Turn the inputField red without submission.
                if (this.checkAnswerInArray(this.get("_incorrectUserAnswers"), val)) {
                    this.set("_isCorrect", false);
                    this.set("_isSubmitted", true);
                }
                else {
                    this.set("_isCorrect", false);
                    this.set("_isSubmitted", false);
                }
            }
            // If this inputField automatically submits the answers, submit after each value change.
            else {
                this.submit();
            }
        }

        /**
         * Checks the current _val and updates properties accordingly.
         */
        submit() {
            
            let val = this.get("_val");

            this.set("_submitCount", this.get("_submitCount") + 1);
            let isCorrect = this.checkAnswerInArray(this.get("_answers"), val);

            // If the val === "" (and that isn't a correct answer), don't show a correct/incorrect state.
            this.set("_isSubmitted", val !== "" || isCorrect); 
            this.set("_isCorrect", isCorrect);

            /**
             * If this inputField requires manual submission, store the user's incorrect answer.
             * If they type it in again, turn the inputField red before they submit
             * (prevents them submitting the same wrong answer multiple times).
             */
            if (!isCorrect && this.get("_requireSubmit")) {
                let iua = this.get("_incorrectUserAnswers");
                iua.push(val);

                this.set("_incorrectUserAnswers", _.uniq(iua));
            }
        }

        reset() {
            this.set("_isSubmitted", false);
            this.set("_isCorrect", false);
            this.set("_val", "");
        }

        lock() {
            this.set("_isLocked", true);
        }

        unlock() {
            this.set("_isLocked", false);
        }

        enableHint() {
            this.set("_isHintEnabled", true);
        }

        disableHint() {
            this.set("_isHintEnabled", false);
        }

        enableRequireSubmit() {
            this.set("_requireSubmit", true);
        }

        getVal() {
            return this.get("_val");
        }

        isCorrect() {
            return this.get("_isCorrect");
        }

        isGrouped() {
            return this.get("_group") !== undefined;
        }

        /**
         * Used by textInputPlus.
         * 
         * If currently incorrect, always return -1.
         * If grouped, returns the index of this model in the group.
         * If un-grouped, returns the index of the first matched answer.
         * 
         * FIXME: Un-tested with SCORM. Should work; but should be checked at some point.
         */
        getAnswerIndex() {

            if (!this.isCorrect()) return -1;

            // This inputField belongs to a group.
            // Since the answers in the group can be entered in any order, instead return the index of this inputField in that group.
            //
            // Explanation: This is done so that, as far as the SCORM data is concerned, the user entered the values into the group
            // using the default order.
            let groupItems = this.getGroupItems();
            if (groupItems) {
                return groupItems.indexOf(this);
            }

            // This inputField is ungrouped. Find the first answer that it matches in the "_answers" array, and return it.
            let idx = -1;
            _.find(this.model.get("_answers"), (a, i) => {
                let isCorrect = this.compareVals(this.getVal(), a);
                if (isCorrect) idx = i;
                return isCorrect;
            });

            return idx;
        }

        /**
         * Converts the value to a number and then returns it.
         * If there's no value (""), the default is 0.
         * If the value isNaN(), an error is logged.
         */
        getNumber() {
            let val = this.get("_val");
            
            if (!val) return 0;
            if (!isNaN(val)) return parseFloat(val);

            Adapt.log.error(`inputFieldModel.getNumber(): value "${val}" is not a number. Fallback value of "0" used.`);
            return 0;
        }

        /**
         * These need to be gotten on the fly each time; as when this inputFieldModel is initially created; the others in its
         * group may not exist yet (due to its index).
         * @returns 
         */
        getGroupItems() {
            let group = this.get("_group");
            let groupItems = [this];

            if (group !== undefined) {
                groupItems = this.get("_parent").get("_inputField")._items.filter((itemModel) => {
                    // Important to include self in the array.
                    return itemModel.get("_group") === group
                });
            }

            return groupItems;
        }

        /**
         * Returns true if both values equal each other; given the settings applied to this specific inputField.
         * @param {*} val1 
         * @param {*} val2 
         */
        compareVals(val1, val2) {
            let type = this.get("_type");
            let decimals = this.get("_decimals");

            let v1 = this.cleanupUserAnswer(val1);
            let v2 = this.cleanupUserAnswer(val2);

            // If this is a number input, use number comparisson.
            if (type == "number") {

                // If the "_decimals" property was set, ensure that the user answer was to that many decimal points.
                // (This can be easily achieved by just comparing the two numbers as strings instead).
                if (decimals !== undefined) {
                    return v1.toString() == v2.toString();
                }

                let num1 = parseFloat(v1);
                let num2 = parseFloat(v2);

                return num1 == num2;
            }

            return v1 == v2;
        }

        // Below functions copied from adapt-contrib-textinput/js/textinputModel
        // ---------------------------------------------------------------------

        checkAnswerInArray(arr, val) {

            /**
             * If this inputField belongs to a group; it will only ever be correct if the value entered into it is unique among the
             * other values entered into the other inputFields in the group.
             * 
             * FIXME: If the user enters "A" into inputField 1, and "A" into inputField 2, the latter will validate wrong (intentional).
             * However, if they then remove "A" from inputField 1, inputField 2 will remain red; despite it now containing a unique value.
             * This is only an issue for auto-submiting inputFields.
             */
            let group = this.get("_group");
            let groupItems = this.getGroupItems();

            var uAnswer = this.cleanupUserAnswer(val);
            var matched = arr.filter(function(cAnswer, idx) {
                return this.compareVals(uAnswer, cAnswer);
            }, this);
        
            let answerIsCorrect = matched && matched.length > 0;

            /**
             * If this inputField is in a group, then the answer can still be wrong if an EARLIER item in the group has the
             * exact same answer.
             */
            if (answerIsCorrect && group !== undefined) {

                // Count how many times the value entered appears in the "_answers" array.
                let maxCorrect = matched.length;

                // Starting from the beginning of the group, iterate through the inputFields until a maxCorrect number of myVals are found.
                // If this model isn't found by then, then consider it wrong.
                _.find(groupItems, (itemModel) => {

                    // If itemModel is THIS model, and maxCorrect > 0, then consider it valid.
                    if (itemModel == this) return true; 

                    // Otherwise, detract from maxCorrect.
                    if (this.compareVals(itemModel.getVal(), this.getVal())) {
                        maxCorrect--;
                        
                        // If that was the last inputFieldModel that could have had the value, consider this one incorrect.
                        if (maxCorrect <= 0) {
                            answerIsCorrect = false;
                            return true;
                        }
                    }
                });
            }

            if (answerIsCorrect) this.set('_isCorrect', true);
            return answerIsCorrect;
        }

        /**
         * Returns the correct answer that this input needs (is _answers[0] by default; but if groups are used, this may change).
         */
        getCorrectAnswer() {

            let answers = this.get("_answers");
            let group = this.get("_group");

            if (group === undefined) {
                // Allow for empty _answers arrays to be used (for inputs which should be left intentionally blank).
                // FIXME: Blank inputs not fully implemented yet.
                return answers ? answers[0] : "";
            }

            // If in a group, find and return a value from the _answers array that none of the other inputFields in the group have. 
            let groupItems = this.getGroupItems();

            // Go through each element in the answer array and find a unique groupItem which has it.
            return _.find(answers, (a) => {

                let matchingItem = _.find(groupItems, (itemModel) => {
                    
                    // Don't match self.
                    if (itemModel === this) return false;

                    return this.compareVals(itemModel.getVal(), a);
                });
                
                // If no other inputFields in the group have this value. Return it.
                if (!matchingItem) return true;

                // Another inputField already has this value. Remove it from the groupItems being checked.
                groupItems.splice(groupItems.indexOf(matchingItem), 1);
            });
        }
        
        cleanupUserAnswer(userAnswer) {
            if (this.get('_allowsAnyCase')) {
                userAnswer = userAnswer.toLowerCase();
            }
            if (this.get('_allowsPunctuation')) {
                userAnswer = userAnswer.replace(/[\.,-\/#!$£%\^&\*;:{}=\-_`~()]/g, '');
                //remove any orphan double spaces and replace with single space (B & Q)->(B  Q)->(B Q)
                userAnswer = userAnswer.replace(/(  +)+/g, ' ');
            }
            // removes whitespace from beginning/end (leave any in the middle)
            return $.trim(userAnswer);
        }
    }

    return InputFieldModel;
});
