define([
    'core/js/adapt'
], function(Adapt) {

    /**
     * adapt-dd-componentLogo
     * --------------------------------------
     * Adds a componentLogo in the top-right corner of the component that the following partial is added to:
     * 
     * "_componentLogo": {
     *  "src": "assets/questionplus-logo.svg"
     * }
     * 
     * // TODO:
     * - Have the logo appear in the adapt-dd-sideMenu too.
     */

    class ComponentLogo extends Backbone.Controller {
        
        initialize() {
            Adapt.on("componentView:postRender", this.onComponentPostRender.bind(this));
        }

        onComponentPostRender(componentView) {
            let data = componentView.model.get("_componentLogo");
            if (!data) return;

            let mode = data._mode;
            this.render(mode, componentView, data);

            // StickyBlock + ComponentRow compatibility:
            // ----------------------------------------------------------------------------
            // Re-render whenever the componentRow is disabled + re-enabled.
            let componentRow = componentView.model.get("_componentRow");
            this.listenTo(componentRow, "change:_isEnabled", (componentRow, isEnabled) => {
                if (!isEnabled) return;
                
                // Delay is necessary (gives componentRow time to finish rendering).
                _.delay(() => {
                    this.render(mode, componentView, data);
                }, 0);
            });
        }

        render(mode, componentView, data) {
            switch (mode) {
                case "small":
                    this._renderLogoSmall(componentView, data);
                    break;

                default:
                    this._renderLogoNormal(componentView, data);
            }
        }

        /**
         * Render the logo inside of the .component__header element
         */
         _renderLogoNormal(componentView, data) {

            let template = Handlebars.templates['componentLogo'];
            let html = Handlebars.compile(template(data));

            componentView.$(".component__header")
                .addClass("has-componentlogo")
                .prepend(html)
                .after("<div class='u-clearfix'></div>"); // Prevent the floating logo affecting the .component__widget. 
                
            // adapt-componentRow compatibility.
            componentView.$el.closest(".componentrow").find(".componentrow__title").addClass("has-componentlogo");
        }

        /**
         * Render the logo inline with the component title.
         * (Legacy; only used in BU11).
         */
         _renderLogoSmall(componentView, data) {

            let template = Handlebars.templates['componentLogoSmall'];
            let html = Handlebars.compile(template(data));

            // Prepend the logo to the title and the componentRow replacement title (if it exists).
            let $parents = componentView.$(".component__title-inner");
            $parents = $parents.add(componentView.$el.closest(".componentrow").find(".componentrow__title-inner"));

            $parents.each((i, e) => {

                // Error handling: don't accidentally prepend multiple logos.
                if ($(e).hasClass("has-componentlogo")) return;
                
                $(e)
                    .addClass("has-componentlogo")
                    .prepend(html);
            });
        }
    }

    Adapt.componentLogo = new ComponentLogo();
});
