define([
    'core/js/adapt',
    './markChainModel',
    './markChainView'
], function(Adapt, MarkChainModel, MarkChainView) {

    class MarkChain extends Backbone.Controller {

        initialize() {
            Adapt.on('router:page', this.setUpHelpers.bind(this));
        }

        setUpHelpers() {

            // StickyBlock compatibility.
            Adapt.stickyBlock?.addResetOnReRender("_markChain");

            let t = this;

            // Very important to use function() instead of () => here, as we need the access to the context provided by Handlebars.
            Handlebars.registerHelperEl("markChain", function() {
        
                let context = arguments[arguments.length-1];
                let data = Handlebars.getContextHash.apply(this, arguments);

                if (!data.name) throw "{{#markChain}}: \"name\" property must be defined!";

                // Find the componentModel that this Helper was placed in.
                let componentModel = Handlebars.getComponentModelFromContext(context);

                /**
                 * Set up the _markChain property for the componentModel.
                 * 
                 * Here's a visualization:
                 * 
                 *  ComponentModel
                 *      _markChain:
                 *          _groups(Collection): [
                 *              {
                 *                  _name: data.name,
                 *                  _items(Collection): [
                 *                      markChainModel,
                 *                      markChainModel,
                 *                      markChainModel
                 *                  ]
                 *              },
                 *              {
                 *                  etc.
                 *              }
                 *          ]
                 */

                if (!componentModel.get("_markChain")) {
                    componentModel.set("_markChain", {});
                }

                // Add a property to the parent componentModel.
                let groupsCollection = componentModel.get("_markChain")._groups;
                if (!groupsCollection) {
                    componentModel.get("_markChain")._groups = new Backbone.Collection();
                    groupsCollection = componentModel.get("_markChain")._groups;
                }
                
                // All MarkChains with the same name need to know about their siblings.
                let group = groupsCollection.find((g) => {return g.get("_name") == data.name});
                
                // Compatibility with Component reRendering:
                // -----------------------------------------
                // If a component is reRendered, its markChain groups aren't cleared.
                // The following fixes this. If the first model in the chain is somehow already rendered while the others are still being added, assume a re-render has occured.
                /*if (group) {
                    if (group.get("_items").first()?.get("_isRendered")) {
                        groupsCollection.remove(group);
                        group = undefined;
                        console.log("REDO");
                    }
                }*/

                // No group found for this name, make a new one.
                if (!group) {

                    group = new Backbone.Model({
                        _name: data.name,
                        _items: new Backbone.Collection(null, {
                            model: MarkChainModel,
                            // Developers can explicitly specify "idx" values (to indicate the order that markChains connect).
                            comparator: '_idx'
                        })
                    });
                    groupsCollection.push(group);

                    // FIXME: Bad design. Assumes all models belonging to the group will be made after the delay
                    // (though this is usually the case).
                    // Wait for all MarkChain models belonging to this group to be added to it, then sort it.
                    _.delay(() => {

                        let items = group.get("_items");
                        items.sort();
                        items.each(markChainModel => {
                            markChainModel.set("_isReady", true);
                        });
                    }, 1);
                }

                // Add this model to this specific group's collection
                data._idx = data.idx;
                if (!data.content || !data.content.length) data.content = "&#8203;"; // Use a zero width space if the mark has no content.
                let model = new MarkChainModel(data);
                
                group.get("_items").push(model);

                let view = new MarkChainView({
                    model: model
                });

                // Let the model know of which view it controls (FIXME: I think this is bad design, but saves a lot of time on development).
                model.set("_view", view);

                return view.$el;
            });
        }
    }

    let markChain = new MarkChain();
    Adapt.markChain = markChain;
});
