define([
  'core/js/adapt',
  './narrativePlusView',
  'core/js/models/itemsComponentModel'
], function(Adapt, NarrativePlusView, ItemsComponentModel) {

  /**
   * NarrativePlus
   * ---------
   * This component is a heavily modified version of the adapt-contrib-narrative component.
   * The most major change was the removal of the strapline + the component changes to a different component in small mode.
   */

  return Adapt.register('narrativeplus', {
    model: ItemsComponentModel,
    view: NarrativePlusView
  });

});
