define([
    'core/js/adapt'
], function(Adapt) {

    class ComponentRowModel extends Backbone.Model{

        defaults() {
            return {
                _index: 0,              // Index of this componentRow in the block.
                _children: undefined,   // Collection of 1 or 2 componentModel children stored in this component row.
                _parent: undefined,     // BlockModel that this componentRow is inside of.
                _prevSibling: undefined,// Reference to the previous component row in the block (if there is one).

                _isEnabled: true,       // If set to false, will move the components outside the componentRow, then delete its view.
                _isVisible: true,       // If set to false, the view will slideUp the componentRow (and vice-versa).
                _isComplete: false,     // Will become true once all _children._isComplete is true.

                _isQuestionType: false, // If any of the children in this component are Question components, set this to true.
                _isReverse: false,      // Whether the componentRow should display its components in reverse order on large view.
                _isSingle: false,       // True if _children.length == 1.
                title: "",              // If any of the _children have a title, it will be assigned to this property.
                _hasDivider: false      // Whether this componentRow should render a divider line above it (determined automatically).
            }
        }
        
        initialize() {

            this.setUpTitle();
            this.setUpReverse();
            this.setUpDivider();
            this.setUpQuestionType();
            this.setUpIsComplete();
            this.set("_isSingle", this.get("_children").length <= 1);
            //this.setUpHasFloat();
            this.setUpSizing();

            this.listenTo(this.get("_parent").get("_componentRow"), "change:_isEnabled", this.onParentEnabledChange);
            
            // Let the child components know of the componentRow they belong to.
            this.get("_children").each((model) => {
                model.set("_componentRow", this);
            });
        }

        getChildren() {
            return this.get("_children");
        }

        /**
         * FIXME: QUICK-FIX
         * Returns true if the children in this componentRow are all !_isAvailable.
         */
        isAvailable() {

            let available = this.getChildren().find((model) => {return model.get("_isAvailable") !== false;});
            return Boolean(available);
        }

        /**
         * adapt-sizing compatibility
         * --------------------------
         * Set up adapt-sizing classes for the componentrow__inner element.
         */
        setUpSizing() {
            if (!Adapt.sizing) return;

            this.get("_children").each((childModel) => {
                let sizing = childModel.get("_sizing");
                if (sizing) {
                    this.set("_parSizingClasses", Adapt.sizing.getParentClasses(sizing));
                }
            });
            
        }

        onParentEnabledChange(ComponentRow, _isEnabled) {
            this.set("_isEnabled", _isEnabled);
        }

        // Fixme: these two functions could be 
        setUpIsComplete() {

            let children = this.get("_children");
            
            // Check if it's already complete.
            this.updateIsComplete();

            // Listen for changes.
            this.listenTo(children, "change:_isComplete", this.updateIsComplete.bind(this));
        }

        // Check each child and set _isComplete to true only if every one of them has _isComplete: true.
        updateIsComplete() {

            let isComplete = true;
            let children = this.get("_children");
            children.find((child) => {
                if (!child.get("_isComplete")) {
                    isComplete = false;
                    return true;
                }
            });

            this.set("_isComplete", isComplete);
            return isComplete;
        }

        setUpTitle() {

            let componentWithTitle = _.find(this.get("_children").models, (c) => {
                return c.get("title") || c.get("displayTitle");
            });
            if (!componentWithTitle) return;

            let title = componentWithTitle.get("title") || componentWithTitle.get("displayTitle");
            this.set("title", title);

            this.get("_parent").set("_blockTitle", title);
        }

        // If the components are in reverse order (["right", "left"]), then set it.
        setUpReverse() {

            let children = this.get("_children");
            if (children.length != 2) return;

            if (children.models[0].get("_layout") == "right") {
                this.set("_isReverse", true);
            }
        }

        // If the previous componentRow had 2 components, or if its component count != the cur component count,
        // then it needs a divider.
        // Or if one of the children has "_hasDivider": true.
        // FIXME: Currently hardcoded to DD's preferred pattern.
        setUpDivider(){
            
            let prevCR = this.get("_prevSibling");
            if (!prevCR) return;

            if (this.get("_children").find(m => {return m.get("_hasDivider");})) {
                this.set("_hasDivider", true);
                return;
            }

            // If one of the children has hardcoded "_hasDivider" === false, then disable it.
            if (this.get("_children").find(m => {return m.get("_hasDivider") === false;})) {
                this.set("_hasDivider", false);
                return;
            }

            if (prevCR.get("_children").length == 2 || 
                prevCR.get("_children").length != this.get("_children").length ||
                this.get("title")) {

                    // Don't give dividers to full component rows with no titles.
                    if (!this.get("title") && this.get("_children").length < 2) return;
                    
                    this.set("_hasDivider", true);
            }
        }

        setUpQuestionType(){
            this.set("_isQuestionType", Boolean(this.get("_children").find((child) => {
                return child.get("_isQuestionType");
            })));
        }
    }

    return ComponentRowModel;
});
