define([
    'handlebars',
    'core/js/adapt'
], function(Handlebars, Adapt) {

    /**
     * FIXME: This extension is a WIP.
     * TODO: port all styles from the theme that modify notify to our DD standards into this extension.
     */

    Adapt.on("notify:opened", (notifyView) => {

        /**
         * Bugfix: allow the user to click on the background element(s) to close it.
         * 
         * EDIT:
         * Added ".notify__popup" element too (if the notify is set to scroll, ".notify__popup-inner" won't cover the full area).
         * Relevant for popups like figure lightboxes.
         */
        notifyView.$(".js-notify-bg").on("click", (event) => {
            
            //event.stopPropagation();
            if (event.target != event.currentTarget) return;

            Adapt.trigger('notify:close');
        });
    });
});
