define([
    'core/js/adapt'
], function(Adapt) {

    // FIXME: Improve element naming.

    class LottieControlsView extends Backbone.View {

        className() {
            return "lottie__controls";
        }

        events() {
            return {
                "click .js-controls-playpause" : "onPlayPauseClick"
            }
        }

        initialize() {
            this.setUpEventListeners();
            this.render();
        }

        onPlayPauseClick() {

            // If all the loops have finished, reset the animation from the start.
            if (this.model.isLoopsComplete()) {
                this.model.reset();
                return;
            }

            // If the animation is decorative, don't allow any interaction until it's finished.
            if (this.model.isDecorative()) return;

            // Otherwise, pause the animation instead.
            this.model.toggle();
        }

        setUpEventListeners() {
            
        }

        render() {
            this.$el.html(new Handlebars.SafeString(Handlebars.templates['lottieControls'](this.model.toJSON())).string);
        }
    };

    return LottieControlsView;
});
