define([
    'core/js/adapt'
], function(Adapt) {

    class ComponentRowView extends Backbone.View {

        className() {
            let classNames = [
                "componentrow",
                (this.model.get("title") ? "componentrow-has-title" : ""),
                (this.model.get("_hasDivider") ? "componentrow-has-divider" : ""),
                (this.model.get("_isReverse") ? "componentrow-is-reverse" : "componentrow-is-not-reverse"),
                (this.model.get("_isSingle") ? "componentrow-is-single" : "componentrow-is-double")
            ];

            // Add a class specifying the types of the child components inside this component row.
            this.model.get("_children").each((childModel) => {
                classNames.push("componentrow-contains-" + childModel.get("_component"));
            });

            return classNames.join(' ');
        }

        attributes() {
            return {
                "data-componentrow-index" : this.model.get("_index")
            };
        }

        initialize() {

            
            this.validateChildren();

            this.setUpEventListeners();
            this.render();
        }

        /**
         * Error check. If _isSingle, then the child must have _layout: full.
         * This check was moved to the View in case external scripts modify the model's values after they have been set.
         */
        validateChildren(){
            if (this.model.get("_isSingle") && this.model.get("_children").first().get("_layout") !== "full") {
                Adapt.log.error("componentRow:\nComponent with block parent \"" + this.model.get("_parent").get("_id") + "\" must have its \"layout\" property be \"full\" (it is the only component in its row).");
            }
        }

        setUpEventListeners() {
            this.listenTo(this.model, "change:_isEnabled", this.onEnabledChange.bind(this));
            this.listenTo(this.model, "change:_isVisible", this.onVisibleChange.bind(this));

            // FIXME: Temp. This shouldn't be being toggled after the componentRowView has rendered.
            this.listenTo(this.model, "change:_hasDivider", this.onDividerChange.bind(this));
        }

        onDividerChange(model, hasDivider) {
            this.$el.toggleClass("componentrow-has-divider", hasDivider);
        }

        onVisibleChange(model, isVisible) {
            if (isVisible) {
                this.$el.slideDown();
            }
            else {
                this.$el.slideUp();
            }
        }

        onEnabledChange(model, isEnabled) {
            if (isEnabled) return;

            // Move the child components out of this view.
            let $container = this.$el.closest(".component__container");

            this.model.get("_children").each((child) => {
                Adapt.findViewByModelId(child.get("_id")).$el.appendTo($container);
            });

            // Remove this view entirely.
            this.remove();
        }

        render() {
            
            this.$el.html(Handlebars.templates["componentRow"](this.model.toJSON()));

            if (!this.model.get("_isVisible")) this.$el.hide();

            // Trigger an event (for use by extensions).
            // FIXME: Not sure why a delay is needed, but it adapt-blockBackground doesn't work without it.
            _.delay(() => {
                // Due to the way componentRow is set up, a block with multiple componentRows will have these events all trigger in the same order the componentRows are in every time.
                Adapt.trigger("componentRowView:postRender", this);
            });
        }
    }

    return ComponentRowView;
});
