define([
    'core/js/adapt'
], function(Adapt) {


    class MarkChainModel extends Backbone.Model {

        defaults() {
            return {
                "_name": "",        // Name assigned to all Models belonging to this group.
                "_isReady": false,  // Set externally by adapt-markChain once all models in the same collection as this one are ready.
                
                "_prev": null,      // Reference to the prev MarkChainModel in the chain (if any).
                "_next": null       // Reference to the next MarkChainModel in the chain (if any).
            }
        }

        initialize() {

            this.setUpLabel();
            this.setUpEventListeners();    
        }

        // Niche: if the label's length is between 1-3 characters, indicate it with a class.
        setUpLabel() {
            let label = this.get("label");
            if (!label) return;

            if (label.length >= 1 && label.length <= 3) {
                this.set("_isSmallLabel", true);
            }
        }

        setUpEventListeners() {
            this.listenToOnce(this, "change:_isReady", this.onReadyChange);
        }

        onReadyChange(model, isReady) {
            if (!isReady) throw "An unexpected error has occurred (\"_isReady\" should not be false).";

            let prev = this.collection.at(this.collection.indexOf(this) - 1);
            this.set("_prev", prev);

            let next = this.collection.at(this.collection.indexOf(this) + 1);
            this.set("_next", next);
        }
    };

    return MarkChainModel;
});
