define([
    'core/js/adapt',
    './stickyBlockView'
], function(Adapt, StickyBlockView) {

    /**
     * StickyBlockEpubView
     * -------------------
     * An extension of the normal StickyBlockView that explicitly sticks/unsticks the sticky column using JavaScript instead of 
     * the default CSS "position: sticky" method.
     * 
     * Explanation:
     * ------------
     * When Adapt is converted to EPUB for use with the Desktop and Online Bookshelf apps, the StickyBlockView "sticking" behaviour breaks.
     * In order to fix the issue, the following needs to be done:
     * 
     * - For the Desktop Bookshelf app, IntersectionObservers are used on a helper element to determine when to stick/unstick.
     *   jQuery's "inview" listener was used, but didn't seem to work for Desktop Bookshelf.
     * 
     * - For the Online Bookshelf app, the jQuery "inview" listener is used on the helper element instead, since the
     *   IntersectionObservers don't seem to be working correctly in Online Bookshelf.
     * 
     * IMPORTANT:
     * ----------
     * VitalSource Bookshelf's Desktop app does NOT support BoundingClientRect.x or y. Use top/left instead.
     */

    class StickyBlockEpubView extends StickyBlockView {

        className() {
            let classes = super.className();
            return classes + " is-epub-stickyblock";
        }

        initialize() {
            this.model.get("_stickyBlock")._isEpubMode = true;
            super.initialize();
        }

        setUpEventListeners() {
            super.setUpEventListeners();
            this.listenTo(this.model, "change:_epubHelperInview", this.onEpubHelperInviewChange);
        }

        /**
         * Append an "inview helper" element after rendering; which will be used by the IntersectionObserver and the jQuery listener
         * to determine when to stick/unstick the sticky column.
         */
        render() {
            super.render();

            let $inviewHelper = $("<div>", {class: "stickyblock__epub-inview-helper"});
            let $blockPar = Adapt.findViewByModelId(this.model.get("_id")).$el;                
            $blockPar.append($inviewHelper);

            this.$inviewHelper = $inviewHelper;

            // Start off unstuck and aligned to the top.
            this.setEpubHelperInview(false, true);
        }

        /**
         * Sets the values of the _epubHelperInview property.
         * FIXME:? Use of strings is weird.
         * 
         * @param {Boolean} isInview Should the sticky column be stuck (true) or unstuck (false)
         * @param {Boolean} isTop    Should the sticky column move to the start (true) or end (false) of the StickyBlockEpubView (only applicable if !isInview).
         */
        setEpubHelperInview(isInview, isTop) {
            
            if (isInview) {
                this.model.set("_epubHelperInview", "inview" + (isTop ? "Top" : "Bottom"));
            }
            else {
                this.model.set("_epubHelperInview", "outview" + (isTop ? "Top" : "Bottom"));
            }
        }

        /**
         * Add an IntersectionObserver to monitor the helper element. 
         * 
         * EDIT: This may not be necessary; I think I got the jQuery event working in Desktop Bookshelf too.
         */
        /*setUpIntersectionObserver() {
            super.setUpIntersectionObserver();

            // IMPORTANT: Do NOT use negative % values for the 'rootMargin' setting. For example: '0px 0px -100% 0px'.
            // Due to the way that Bookshelf nests its iframes, these values do not work accurately.
                
            const epubBlockObserver = new IntersectionObserver((entries) => {
                entries.forEach(this.onEpubIntersection.bind(this));
            }, {
                root: null, // Browser viewport.
                rootMargin: '0px', // Detect when the top of this view goes off the top of the viewport.
                threshold: [0]
            });
            epubBlockObserver.observe(this.$inviewHelper[0]);
        }*/

        onEpubIntersection(entry) {

            if (!this.isLargeMode()) return; // Completely ignore this callback if the sticky is in single column (static) view.

            // Bugfix: Ignore a "fake" intersection that sometimes triggers when slow loading.
            if (!entry.rootBounds) return;
            
            let isIntersecting = entry.isIntersecting;
            if (isIntersecting) {
                // If the top of the $inviewHelper is off the top of the viewport when coming into view, it means that the user 
                // is looking at the bottom of the stickyBlock.
                let isBottom = entry.boundingClientRect.top <= 0;
                this.setEpubHelperInview(true, !isBottom);
            }
            else {
                // When the bottom of the observer helper is off the top of the viewport, stick the column to the
                // bottom of the StickyBlockView.
                let isBottom = entry.boundingClientRect.bottom <= 0;
                this.setEpubHelperInview(false, !isBottom);
            }
        }

        _isInview() {
            return Boolean(this.model.get("_epubHelperInview").match(/^inview/g));
        }

        _isTop() {
            return Boolean(this.model.get("_epubHelperInview").match(/Top$/g));
        }
        
        /**
         * Epub mode exclusive inview observer.
         * Used as a workaround for "position: sticky;" not working when used with the root scrollbar.
         * 
         * TODO: If an alternative to this approach is found, swap to it (using the onscreen observer is more costly and has worse 
         * performance than position: sticky).
         * @returns 
         */
        initEpubHelperInview() {

            this._isEpubHelperInview = undefined;

            this.$inviewHelper.on('inview.stickyBlockHelper', (event, isInview) => {

                if (!this.isLargeMode()) return; // Completely ignore this callback if the sticky is in single column (static) view.

                //let isInview = m.onscreen;

                // Don't do anything if this is the same inview state from last time.
                if (this._isInview() === isInview) return;

                let helperBB = this.$inviewHelper[0].getBoundingClientRect();
                
                if (isInview) {

                    // If the top of the $inviewHelper is off the top of the viewport when coming into view, it means that the user 
                    // is looking at the bottom of the stickyBlock.
                    let isBottom = helperBB.top <= 0;
                    this.setEpubHelperInview(true, !isBottom);
                }
                else {
                    // When the bottom of the inview helper is off the top of the viewport, stick the column to the
                    // bottom of the StickyBlockView.
                    let isBottom = helperBB.bottom <= 0;
                    this.setEpubHelperInview(false, !isBottom);
                }
            });
        }

        removeEpubHelperInview() {
            this.$(".stickyblock__epub-inview-helper").off("inview.stickyBlockHelper");
        }

        renderModeSmall() {
            this.removeEpubHelperInview();
            super.renderModeSmall();

            // Fallback: Sometimes the child elements don't properly reset their visibility after a resize.
            /*this.model.getChildren().each((model) => {
                model.set("_isVisible", true);
            });*/
        }

        renderModeLarge() {
            super.renderModeLarge();
            this.initEpubHelperInview();
        }

        remove() {
            super.remove();
            this.removeEpubHelperInview();
        }

        onEpubHelperInviewChange(model, epubHelperInview) {

            Adapt.log.debug("StickyBlockEpubView: " + epubHelperInview);
            //console.log("StickyBlockEpubView: " + epubHelperInview);

            // Whenever the stickyBlock becomes stuck/unstuck using this callback, there's a high chance that it will sharply
            // "jump" between two y (top) values. This jump can't be prevented (without making the entire page run slower), but it can be smoothed.
            let staticChildren = this.getStaticChildren();

            let isInview = this._isInview();
            let isBottom = !this._isTop();

            let $componentContainer = this.$(".js-stickyblock-epub-component-container");
            let fromTop = $componentContainer[0].getBoundingClientRect().top;
            
            this.$el
                .toggleClass("is-epub-stuck", isInview)
                .toggleClass("is-epub-free", !isInview);

            // The "this.setUpIntersectionObserver()" observers don't work consistently in some EPUB contexts.
            // This is the fallback.
            if (isInview) {
                
                // The user just scrolled up to the stickyBlockView.
                if (isBottom) {

                    // Ensure the last sticky child is shown.
                    staticChildren.last().set("_stickyBlockStaticChildInview", true);
                    
                    // Trigger an inview event on the active sticky component.
                    this.triggerStickyChildInview(true, staticChildren.length-1);
                }
                // The user just scrolled down to the stickyBlockView.
                else {

                    // Ensure the first sticky child is shown.
                    staticChildren.first().set("_stickyBlockStaticChildInview", true);

                    // Trigger an inview event on the active sticky component.
                    this.triggerStickyChildInview(true, 0);
                }
            }

            /**
             * If the sticky column is to become "free" (non-'position: fixed;'), determine which end (top/bottom)
             * of the stickyBlockView to stick it to.
             * Only do this on the initial change from isInview -> !isInview.
             */
            if (!isInview) {
                this.$el
                    .toggleClass("is-top", !isBottom)
                    .toggleClass("is-bottom", isBottom);
            }

            // Smoothly transition the .js-stickyblock-epub-block to reduce the y (top) "snap".
            let toTop = $componentContainer[0].getBoundingClientRect().top;
            let topDiff = fromTop - toTop;

            let $epubBlock = this.$(".js-stickyblock-epub-block");
            
            // Go to this transform immediately (skip transitions).
            $epubBlock.css("transform", "translateY(" + topDiff + "px)").hide(0).show(0);
            
            // Then smoothly transition to the untransformed state.
            $epubBlock.css("transform", "translateY(0px)");
        }

    };

    return StickyBlockEpubView;
});
