define([
    'core/js/adapt',
    "./componentRowCollection",
    "./componentRowView"
], function(Adapt, ComponentRowCollection, ComponentRowView) {

    /**
     * adapt-componentRow
     * ==================
     * 
     * Description:
     * ------------
     * Automatically creates container elements around each row of components (like an inner container between block & container).
     * 
     * Components can have "left", "right", and "full" layouts.
     * Each "row" of component that are side-by-side on large views should be wrapped together for layout purposes.
     * 
     * The component row wrapper will also allow a "right" component to be added before a "left" one.
     * On large screens, they will still show up side-by-side as "left" "right", but when they're placed above each other,
     * the order will become "right" "left" (the same as in the JSON).
     * 
     * Also offers the option to interlace components on small devices by always placing the componentRow title at the top
     * of both stacked components; like so (or vice-versa):
     * [left component's title]
     * [right component]
     * [left component's body + widget]
     * 
     * Any component with _componentRow._single == true will be given its own wrapper.
     * 
     * TODO:
     * ----- 
     * 
     * FIXME:
     * ------
     * - (Maybe?) If the component is re-rendered, check if it leaves the componentRow element (as well as losing the correct order).
     * 
     * Notes:
     * ------
     * 
     */

    class ComponentRow extends Backbone.Controller{

        initialize() {

            Adapt.on("blockView:preRender", this.setupComponentRowData.bind(this));
            Adapt.on("blockView:postRender", this.renderComponentRowViews.bind(this));
            Adapt.on("componentView:postRender", this.onComponentPostRender.bind(this));
        }

        // Set up data only. Actual modification of the DOM takes place postRender.
        setupComponentRowData(blockView) {
            
            let blockModel = blockView.model;

            // Advanced: allow blocks to not use component rows.
            //let blockComponentRow = blockModel.get("_componentRow");
            //if (blockComponentRow && blockComponentRow._isEnabled === false) return;

            // Overload the block child sorting functionality (Adapt sorts block children by _layout. We don't want that).
            let _getChildren = blockModel.getChildren;
            if (!_getChildren.prototype._componentRowOverride) {
                blockModel.getChildren = () => {
                    
                    // Call the original (which sorts the children by _layout)
                    _getChildren.apply(blockModel, arguments);
                    
                    // Re-sort the children by cid (to undo the sorting that was done in adaptModel).
                    if (blockModel._childrenCollection.length === 2) {

                        blockModel._childrenCollection.comparator = (model) => {
                            // Parsed "c99" into int to prevent rare bug where "c99" and "c100" are sorted in the wrong order.
                            return parseInt(model.cid.substring(1));
                        };
                        blockModel._childrenCollection.sort();
                        blockModel.setChildren(blockModel._childrenCollection);
                    }
                    return blockModel._childrenCollection;
                }
                blockModel.getChildren.prototype._componentRowOverride = true; // Override only once.
            }

            blockModel.set("_componentRow", new Backbone.Model(_.extend(
                {_isEnabled: true},
                blockModel.get("_componentRow") || {}
            )));
            let componentRowCollection = new ComponentRowCollection(null, {
                blockModel: blockModel
            });
            blockModel.get("_componentRow").set("_children", componentRowCollection);

            // Advanced
            // --------
            // Allow componentRow to be toggled on and off.
            // FIXME: Weird to have the enabling functionality here, and the disabling functionality in the views.
            // FIXME: Enabling is listened to on a block level, while disabling is listened to on a componentRow level.
            this.listenTo(blockModel.get("_componentRow"), "change:_isEnabled", (componentRow, isEnabled) => {
                
                if (!isEnabled) return; // The individual componentRowViews handle disabling (and removing) themselves.

                // Re-create the componentRowViews.
                if (isEnabled) {
                    this.appendComponentRowViews(blockView);

                    // FIXME: Could this break if the component isn't currently rendered when this is called?
                    componentRow.get("_children").each((componentRowModel) => {
                        componentRowModel.get("_children").each(model => {
                            this.appendComponentView(Adapt.findViewByModelId(model.get("_id")));
                        });
                    });
                }
            });
        }

        renderComponentRowViews(blockView) {

            // Don't do anything if manually disabled.
            let blockModel = blockView.model;
            let blockComponentRow = blockModel.get("_componentRow");
            if (blockComponentRow && blockComponentRow.get("_isEnabled") === false) return;

            this.appendComponentRowViews(blockView);
        }

        /**
         * Whenever a component inside of a componentRow is rendered, move it to be inside of its assigned row.
         * @param {*} componentView 
         */
        onComponentPostRender(componentView){

            this.appendComponentView(componentView);
        }

        appendComponentRowViews(blockView){
            
            // Render the views.
            let $container = blockView.$(".component__container");
            _.each(blockView.model.get("_componentRow").get("_children").models, (model) => {

                if (!model.isAvailable()) return; // Don't render the componentRow if there's no available components in it.

                $container.append(new ComponentRowView({
                    model: model
                }).$el);
            });
        }

        appendComponentView(componentView) {

            // Ignore componentViews which are clones (temporary copies of components made by stickyBlock).
            if (componentView.isClone) return;

            let _componentRow = componentView.model.get("_componentRow");            
            if (!_componentRow) return;

            // Determine whether to prepend or append the componentView.
            let isFirst = _componentRow.get("_children").first().get("_id") == componentView.model.get("_id");

            // Append the components.
            let $container = componentView.$el.closest(".component__container");
            $container = $container.find("[data-componentrow-index='" + _componentRow.get("_index") + "'] .js-componentrow-items");

            if (isFirst)    $container.prepend(componentView.$el);
            else            $container.append(componentView.$el);
        }
    }

    new ComponentRow();
});
