define([
  'core/js/adapt',
  'core/js/models/itemsQuestionModel'
], function(Adapt, ItemsQuestionModel) {

  /**
   * DD Modified:
   * ------------
   * Set the value of "_selectable" to be = to the amount of items this model has.
   * This is done due to the value of "_isRadio" being automatically set in Adapts itemsQuestionModel.js initialize() based on its value.
   * (And we want the reverse; for the value of "_selectable" to be determined by the value of "_isRadio").
   */

  var McqModel = ItemsQuestionModel.extend({

    initialize: function() {

      this.set("_selectable", this.get("_isRadio") ? 1 : this.get("_items").length);

      if (this.isSingleSelect()){
        // DD modified:
        // ------------
        // Need to copy each item's "_feedback" property into "feedback".
        // This is done for compatibility with QuestionPlus.
        // (core/js/models/itemsQuestionModel.js checks both, and this is the best way to override it without modifying it).
        _.each(this.get("_items"), (item) => {
          
          if (item._feedback){
            if (!item._feedback.title)        delete item._feedback.title;
            if (!item._feedback.body)         delete item._feedback.body;
            if (!item._feedback.instruction)  delete item._feedback.instruction;
          }

          // Set up unique feedback defaults.
          if (item._shouldBeSelected) {item._feedback = _.extend({}, this.get("_feedback")._correct, item._feedback);}
          else {item._feedback = _.extend({}, this.get("_feedback")._incorrect, item._feedback);}

          item.feedback = item._feedback;
        });

        // DD modified:
        // -----------
        // Override component _feedback with item specific feedback if applicable (singleSelect only).
        let itemCorrectFeedback = _.find(this.get("_items"), (item) => {return item._shouldBeSelected;})._feedback;
        let globalCorrectFeedback = this.get("_feedback")._correct;
        
        this.get("_feedback")._correct = {
          title: itemCorrectFeedback?.title || globalCorrectFeedback?.title,
          body: itemCorrectFeedback?.body || globalCorrectFeedback?.body
        }
      }

      ItemsQuestionModel.prototype.initialize.apply(this, arguments);
    },

    // DD modified:
    // ------------
    // Override ItemsQuestionModel's function for QuestionPlus compatibility.
    setupIndividualFeedback(selectedItem) {

      if (this.get("_isCorrect")) return;
      if (!this.isSingleSelect()) return;

      // Set the global component _feedback._incorrect values (this is what QuestionPlus will read).
      let itemFeedback = selectedItem.get("_feedback");
      this.get("_feedback")._incorrect = _.extend({}, this.get("_feedback")._incorrect, itemFeedback);

      // Re-set up the feedback for this component.
      Adapt.questionPlus.setUpFeedback(this);

      this.set({
        feedbackTitle: selectedItem.get("_feedback")?.title || this.getFeedbackTitle(this.get('_feedback')),
        feedbackMessage: this.get("_feedback")._incorrect.final
      });
    }

  });

  return McqModel;

});
