define([
    'handlebars',
    'core/js/adapt',
    "./figurePopupView"
], function(Handlebars, Adapt, FigurePopupView) {

    /**
     * Applied on the EXISTING item HTML by the figureView.
     * (Important to apply to existing, as figureItems often contain elements with scripts which would break if the view re-renders).
     */

    // FIXME: This uses a lot of the same lightbox scripts ddImgPopup uses.
    // Need to make these scripts shared somehow.

    const HIDDEN_OBSERVER_DEBOUNCE = 500; 

    class FigureItemView extends Backbone.View {

        events() {
            
            let e = {

            };

            if (this.model.get("lightbox")) {
                e["click"] = "onLightboxClick";
            }

            return e;
        }

        initialize(){

            this.setUpModelData();
            this.setupEventListeners();

            _.delay(this.postRender.bind(this), 0);
        }

        // FIXME: Bad to do it this way (retrieving the model properties from the element itself).
        setUpModelData(){
            this.setUpLightboxData();
            this.model.set("content", this.$(".js-figure-item-inner").html());
        }

        setupEventListeners(){
            // Slight delay added for StickyBlock compatibility.
            // (This should always be calculated last anyway).
            this.onDeviceResize = _.debounce(this.onDeviceResize.bind(this), 10);
            this.listenTo(Adapt, 'device:resize', this.onDeviceResize);

            // Trigger a resize as soon as it's ready.
            // Delay added to allow for {{#img}} helpers (and other registerHelperEl() helpers) time to be rendered.
            _.delay(() => {
                this.$el.imageready(this.onDeviceResize.bind(this));
            }, 10);

            // FIXME: These events are only triggered if lightbox is enabled. Should they have a different name?
            this.listenTo(this.model, "change:_isTransformed", this.onTransformedChange);
            this.listenTo(this.model, "change:_isHidden", this.onHiddenChange);
        }

        onDeviceResize() {

            let scaleDown = this.model.get("scaleDown");

            if (scaleDown) {
                this.updateScaleDown();
                this.updateLightbox();
            }
            else {
                this.updateScrolling();
            }
        }

        /**
         * EXPERIMENTAL:
         * -------------
         * Only apply 'overflow: auto' if the .js-figure-item-inner element exceeds the .js-figure-item.
         * This is being used instead of a global 'overflow: auto' due to the need for 'position: absolute'
         * elements to be able to overflow the figureItem without being clipped.
         */
        updateScrolling() {

            // Don't do anything if explicitly set not to scroll.
            if (this.model.get("scrolling") === false) return;

            this.$el.toggleClass("has-overflow-scrolling", false);

            let $itemInner = this.$(".js-figure-item-inner");

            // 'display: inline-block' forces the inner element to be as wide as its non-absolute contents.
            $itemInner.css("display", "inline-block");

            let myWidth = this.$el[0].getBoundingClientRect().width;
            let innerWidth = $itemInner[0].getBoundingClientRect().width;

            $itemInner.css("display", "");

            this.$el.toggleClass("has-overflow-scrolling", myWidth < innerWidth);
        }

        /**
         * @returns {Boolean} True if this FigureItem has the lightbox setting enabled.
         */
        isLightboxEnabled() {
            return Boolean(this.model.get("lightbox")?._isEnabled);
        }

        /**
         * @returns {Boolean} True only if this view is being scaled down (and thus, warranting a lightbox).
         */
        isLightboxActive() {
            return Boolean(this.model.get("lightbox")?._isActive);
        }

        onTransformedChange(model, isTransformed) {
            // If using a lightbox, only show it when the figure is transformed.
            let lightbox = this.model.get("lightbox");
            if (!lightbox) return;

            lightbox._isActive = isTransformed;
            this.$el.toggleClass("lightbox-active", isTransformed);
        }

        setUpLightboxData() {
            // Possible lightbox values and what they map to:
            // true/false -----------> {_isEnabled: *, _backgroundEnabled: true}
            // Number ---------------> {_isEnabled: true, _width: *, _backgroundEnabled: true}
            // {_width:x, bg:true} --> {_isEnabled: true, _width: *, _backgroundEnabled: *}

            let lightbox = this.model.get("lightbox");
            if (!lightbox) return;
            
            if (lightbox === true) lightbox = {};

            this.model.set("lightbox", {
                _isEnabled: true,
                _width: !isNaN(lightbox) ? lightbox : (lightbox._width || "auto"),
                _backgroundEnabled: lightbox.bg === false ? false : true
            });
        }

        onLightboxClick() {
            // Check if the lightbox is active at this time (it can be toggled on/off by device resizing).
            if (!this.isLightboxActive()) return;
            this.openLightbox();
        }

        openLightbox() {

            if (this._isPopupOpen) return;
            this._isPopupOpen = true;
            this.popupView = new FigurePopupView({
                model: this.model
            });
    
            Adapt.trigger("notify:popup", {
                _view: this.popupView,
                _isCancellable: true,
                _showCloseButton: true,
                _classes: 'figure-lightbox dragscroll'// + (this.model.get('_classes') || "")
            });

            this.listenToOnce(Adapt, {
                'popup:closed': this.onLightboxClosed
            });
        }

        onLightboxClosed() {
            this._isPopupOpen = false;
        }

        /**
         * Trigger an event whenever this View becomes un-hidden.
         * @param {*} model 
         * @param {*} isHidden 
         */
        onHiddenChange(model, isHidden) {

            if (isHidden) return;

            let scaleDown = this.model.get("scaleDown");

            if (scaleDown) {
                this.updateScaleDown();
                this.updateLightbox();
            }
            else {
                this.updateScrolling();
            }
        }
        
        postRender() {

            // Only do this if using a lightbox.
            if (!this.isLightboxEnabled()) return;

            // First, check if this view is :hidden.
            // If so, only update it once it becomes visible again.
            this.model.set("_isHidden", this.$el.is(":hidden"));

            /**
             * Whenever this mutation is triggered, check if this view is hidden, and update the "_isHidden" value.
             */
            let callback = _.debounce(() => {
                this.model.set("_isHidden", this.$el.is(":hidden"));
            }, HIDDEN_OBSERVER_DEBOUNCE);
            
            this._hiddenObserver = new MutationObserver(callback);

            // Find the closes component or notify element.
            let closestEl = this.$el.closest(".component")[0];
            if (!closestEl) closestEl = this.$el.closest(".notify")[0];

            // Start observing.
            this._hiddenObserver.observe(closestEl, {
                attributes: true,
                subtree: true
            });
        }

        updateScaleDown() {

            if (!this.model.get("scaleDown")) return;
            if (this.model.get("_isHidden")) return; // Don't update if hidden (width can't be calculated accurately in this state).

            let $item = this.$el;
            let $itemInner = this.$(".js-figure-item-inner");

            // Reset inline CSS.
            $item.css("height", "");
            $itemInner.css("transform", "");

            // Scale the item-inner element to fit inside its item.
            let bb = $item[0].getBoundingClientRect();
            let bbInner = $itemInner[0].getBoundingClientRect();

            let xPadding = parseFloat($item.css("padding-left"), 10) + parseFloat($item.css("padding-right"), 10);

            let scale = (bb.width-xPadding) / bbInner.width;
            let isTransformed = scale < 1;

            this.model.set("_isTransformed", isTransformed);
            this.$el.toggleClass("is-transformed", isTransformed);

            // If the scale is >= 1, don't do anything (we don't need to upscale).
            if (!isTransformed) return;

            $itemInner.css("transform", `scale(${scale})`);

            // Resize the outer item's height to match the scaledown.
            let yPadding = parseFloat($item.css("padding-top"), 10) + parseFloat($item.css("padding-bottom"), 10);

            $item.css("height", bbInner.height * scale + yPadding);
        }

        updateLightbox() {

            
        }
    };

    return FigureItemView;
});
