define([
    'core/js/adapt',
    './modeEnum'
], function(Adapt, MODE) {

    // Must be >= 50. "85" seems to be a good number here. "70" resulted in some inviews skipping if the user scrolled too fast.
    // Note: the shorter the screen (vertically), the more likely some inview events will be skipped from fast scrolling.
    const componentInviewThreshold = 85; 

    class StickyBlockView extends Backbone.View {

        className() {
            let classes = [
                "stickyblock",
                "layout-" + (this.model.get("_stickyBlock")._layout || "left"),
                (this.model.get("_stickyBlock")._timeline?._isEnabled) ? "has-timeline" : "" // Optional timeline feature. 
            ];
            return classes.join(" ");
        }

        events() {
            
        }

        initialize(){
            
            this.setUpModelData();
            this.setUpEventListeners();
            this.render();
            this.setUpIntersectionObserver();
            this.setUpTimeline();
        }

        /**
         * Set up additional model data needed to add the _timeline feature.
         */
        setUpTimeline() {

            if (!this.model.get("_stickyBlock")._timeline?._isEnabled) return;

            // If the child model has a timeline title, customize its componentRow divider.
            this.getStaticChildren().each(childModel => {
                let timelineTitle = childModel.get("_stickyBlock")?._timeline?.title;
                if (timelineTitle) {
                    childModel.get("_componentRow").set("_divider", {
                        title: timelineTitle,
                        _classes: "is-stickyblock-timeline-divider"
                    });
                }
            });
        }

        /**
         * The IntersectionObserver will keep track of when the entire stickyBlockView.$el
         * enters / exits the viewport.
         * 
         * It's used to determine whether to show the first or last stickyBlockStickyChild when the
         * user scrolls the element into view.
         * 
         * TODO: IntersectionObservers are more efficient than scroll listeners. Implement them
         * for the component children too when time permits.
         */
        setUpIntersectionObserver(){

            // Observe when the sticky column is fully inview (stuck).
            const stickyColObserver = new IntersectionObserver((entries) => {
                entries.forEach(this.onStickyColIntersection.bind(this));
            }, {
                root: null, // Browser viewport.
                rootMargin: '-1px 0px 0px 0px',
                threshold: [0.9]
            });
            stickyColObserver.observe(this.$(".stickyblock__col.is-sticky")[0]);

            // Observe main stickyBlock.
            const stickyBlockObserver = new IntersectionObserver((entries) => {
                entries.forEach(this.onIntersection.bind(this));
            }, {
                root: null, // Browser viewport.
                rootMargin: '0px',
                threshold: [0]
            });
            stickyBlockObserver.observe(this.$el[0]);
        }

        onStickyColIntersection(entry) {

            // When true, it means that the "graphic" half of this template is fully visible.
            this.model.set("_isStickyColInview", entry.isIntersecting);
            
            if (!entry.isIntersecting) return;

            // Trigger an inview event on the active sticky component.
            this.triggerStickyChildInview(
                true, 
                entry.intersectionRect.top <= 2 ? this.model.get("_stickyBlock")._children.static.length-1 : 0
            );
        }

        onIntersection(entry) {

            let staticChildren = this.getStaticChildren();

            // The user has just scrolled >=1px of the StickyBlock into view.
            if (entry.isIntersecting) {

                this.model.get("_stickyBlock")._isInview = true;

                // The user was scrolling up.
                if (entry.boundingClientRect.top < 0) {

                    // Ensure the last sticky child is shown.
                    staticChildren.last().set("_stickyBlockStaticChildInview", true);
                }
                // The user was scrolling down.
                else {
                    // Ensure the first sticky child is shown.
                    staticChildren.first().set("_stickyBlockStaticChildInview", true);
                }
            }
            // The user has just scrolled the StickyBlock entirely out of view. 
            else {

                this.model.get("_stickyBlock")._isInview = false;

                this.getStickyChildren().each(childModel => {
                    childModel.trigger("stickyBlock:outview");
                });
            }
        }
        
        /**
         * Create two collections to store all the left and right aligned components in this block.
         */
        setUpModelData(){

            let _stickyBlock = this.model.get("_stickyBlock");

            // Determine which column will be static.
            /*let _layout = _stickyBlock._layout;
            if (_layout != "left" && _layout != "right") {
                throw "_stickyBlock._layout cannot = \"" + _layout + "\". MUST = either \"left\" | \"right\".";
            }*/
            let _layout = "left"; // If the "_layout" property is set to "right", then the CSS will change the layouts itself.

            let c = {};
            c["left"] = [];
            c["right"] = [];

            // Populate the two collections using the block's componentRow values to determine pairings.
            let blockComponentRow = this.model.get("_componentRow");

            blockComponentRow.get("_children").each(componentRow => {

                let _children = componentRow.get("_children");
                
                let child0;
                let child1;

                // Allow for the same stickyChild to be held between multiple reveals (shared stickyChild).
                if (_children.length !== 2) {

                    child0 = _children.models[0];

                    let layout = _children.models[0].get("_layout");
                    layout = layout == "left" ? "right" : "left";

                    child1 = c[layout][c[layout].length-1];
                }
                else {
                    child0 = _children.models[0];
                    child1 = _children.models[1];
                }

                let layout0 = child0.get("_layout");
                let layout1 = child1.get("_layout");

                if (layout0 != "left" && layout0 != "right") {
                    throw "_stickyBlock can't have a component with \"_layout\": \"" + _layout0 + "\"";
                }
                if (layout1 != "left" && layout1 != "right") {
                    throw "_stickyBlock can't have a component with \"_layout\": \"" + _layout0 + "\"";
                }

                // Create easy links between the paired components.
                if (!child0.get("_stickyBlockSiblings")) child0.set("_stickyBlockSiblings", new Backbone.Collection());
                if (!child1.get("_stickyBlockSiblings")) child1.set("_stickyBlockSiblings", new Backbone.Collection());
                child0.get("_stickyBlockSiblings").push(child1);
                child1.get("_stickyBlockSiblings").push(child0);

                // Only set if undefined (prevents shared stickyChildren being given the last index they use instead of the first).
                if (child0.get("_stickyBlockIndex") === undefined) child0.set("_stickyBlockIndex", componentRow.get("_index"));
                if (child1.get("_stickyBlockIndex") === undefined) child1.set("_stickyBlockIndex", componentRow.get("_index"));

                c[layout0].push(child0);
                c[layout1].push(child1);
            });

            c.left = new Backbone.Collection(c.left);
            c.right = new Backbone.Collection(c.right);
            c.static = c[_layout];
            c.sticky = c[_layout == "right" ? "left" : "right"];

            // adapt-sizing compatibility
            // --------------------------
            let _sizing = this.model.get("_sizing");
            _.each(c.sticky.models, (model) => {

                // Apply the block's sizing properties to the components by default.
                // Individual component's _sizing properties will extend them.
                let modelSizing = model.get("_sizing");
                modelSizing = _.extend(
                    {},         // Ensure a deep clone.
                    _sizing,    // Inherit the sizing values assigned to the block.
                    _sizing ? { // Apply the block's _maxWidth values.
                        _maxWidth:_sizing._maxWidth === true ? _sizing._width : _sizing._maxWidth
                    } : {},
                    modelSizing // Finally, override with the component's _sizing properties. 
                );

                model.set("_sizing", modelSizing);
            });

            // ComponentRow override:
            // No matter what, every single componentRow in the stickyBlock has "two children",
            // (Either normally, or faked in the case of shared stickyChildren).
            this.model.get("_componentRow").get("_children").each((componentRow) => {
                componentRow.set("_isSingle", false);
            });

            this.model.get("_stickyBlock")._children = c;
        }

        setUpEventListeners(){

            this.listenTo(this.model.getChildren(), {
                "change:_stickyBlockStaticChildInview": this.onStaticChildInviewChange.bind(this),
                "change:_isVisible": this.onChildVisibleChange.bind(this)
            });

            // Wait for all children to be ready, then decide whether to enable/disable the stickyBlockView.
            // adapt-events provided this event.
            this.listenToOnce(this.model, "blockView:childrenReady", this.onChildrenReady.bind(this));
        }

        onChildrenReady() {
            this.listenTo(Adapt, {'device:changed': this.onDeviceChanged});
            this.listenTo(this.model, "change:_mode", this.reRender);
            
            // Set up initial mode.
            this.calculateMode();
        }

        calculateMode() {
            let mode = Adapt.device.screenSize === 'large' ?
                MODE.LARGE :
                MODE.SMALL;
            this.model.set('_mode', mode);
        }

        onDeviceChanged(width) {
            this.calculateMode();
        }

        /**
         * Whenever a child in the sticky column changes its visibility, toggle a class on its closest .js-stickyblock-item.
         * 
         * FIXME: Will trigger on static children if an external script changes their "_isVisible" value.
         * 
         * FIXME?: May need to rework stickyBlock to not use "_isVisible" and to instead toggle a custom value
         * (in case another script changes the component's "_isVisible" value in future).
         * @param {*} model 
         * @param {*} isVisible 
         */
        onChildVisibleChange(model, isVisible) {
            
            let $child = Adapt.findViewByModelId(model.get("_id")).$el;
            $child.closest(".js-stickyblock-item")
                .toggleClass("is-visible", isVisible)
                .toggleClass("is-hidden", !isVisible);
        }

        reRender(model, mode) {

            // Small
            // ---------------------------------------------
            // Disable the stickyBlockView on small devices.
            if (!this.isLargeMode()) {
                this.renderModeSmall();
                //this.$el.removeClass("u-visibility-hidden");
                return;
            }
            
            // Large
            // ---------------------------------------------
            // Re-enable the stickyBlockView on large devices.
            this.renderModeLarge();

            // Trigger another resize (since the components have just been repositioned).
            Adapt.trigger('device:resize');
            Adapt.trigger('device:changed');
        }

        /**
         * Removes all clone views created for renderModeSmall().
         */
        removeClones() {

            let $block = this.$el.closest(".block"); // Store a reference to the parent blockView's element.
            $block.find(".js-stickyblock-clone").remove();
        }

        /**
         * Hide the stickyBlockView entirely on small devices (including its events).
         */
        renderModeSmall() {
            
            let $container = Adapt.findViewByModelId(this.model.get("_id")).$(".component__container");

            // Move all componentViews out of this view's containment, make them visible, and remove their events.
            this.model.getChildren().each((model) => {
                let view = Adapt.findViewByModelId(model.get("_id"));
                model.set("_isVisible", true); // In small view, all components are always visible (due to it being static).
                $container.append(view.$el);

                this.removeChildInview(view);

                // Set their an attribute on each of them (makes it easier to determine their index in debug).
                view.$el.attr("data-stickyblock-index", model.get("_stickyBlockIndex"));
            });

            this.$el.hide(0);
            this.removeClones();

            // Enable componentRow (it will automatically wrap itself around the components inside this block again).
            this.model.get("_componentRow").set("_isEnabled", true);
            

            // Shared stickyChild functionality:
            // -----------------------------------------------------------------------------------------------------------------
            // WARNING: Keep an eye on this. Has the potential to cause errors if used with complex components.
            // For each componentRow which has only one child, create a temporary view clone of the previous stickyChild's model.
            let $block = this.$el.closest(".block"); // Store a reference to the parent blockView's element.
            this._cloneModels = new Backbone.Collection();
            this.model.get("_componentRow").get("_children").each((componentRow) => {
                
                // If this componentRow has two children, then it's a normal static + sticky child pair.
                // No cloning needed.
                if (componentRow.getChildren().length != 1) return;

                let componentRowIndex = componentRow.get("_index");

                // If the componentRow has only one child, it will ALWAYS be the static child.
                let staticChild = componentRow.getChildren().first();

                // Get the shared stickyChild the staticChild is paired with.
                let stickyChild = staticChild.get("_stickyBlockSiblings").first();
                
                let view = Adapt.getViewClass(stickyChild.get("_component"));

                // CUSTOM:
                // Clear any "resetOnReRender" properties that may have been explicitly specified by other plugins.
                _.each(Adapt.stickyBlock.getResetOnReRender(), propertyName => {stickyChild.set(propertyName, null);});

                // Clone the model too (having all the cloneViews share the same model results in many issues with other helpers.
                let cloneModel = stickyChild.clone();
                this._cloneModels.push(cloneModel);
                
                let cloneView = new view({ model: cloneModel }); 
                let $componentRow = $block.find(".componentrow").filter("[data-componentrow-index=" + componentRowIndex + "]");

                // Note: if componentRow's componentView:postRender event is triggered on a componentView with .isClone == true,
                // it will stop the componentRow callback (intentional). 
                cloneView.isClone = true;
                let $container = $componentRow.find(".js-componentrow-items");

                cloneView.$el
                    .addClass("js-stickyblock-clone")
                    .attr("data-adapt-id", cloneModel.get("_id") + "--stickyblock-clone")
                    .attr("data-stickyblock-index", staticChild.get("_stickyBlockIndex")); // (Used by custom scripts).

                // Determine whether to append or prepend the clone to the componentRow based on the original componentModels index in the original componentRow.
                let cr = cloneModel.get("_componentRow");
                let originalIndex = cr.getChildren().indexOf(cloneModel);

                if (originalIndex == 0) { // The original componentModel was the first element in its componentRow. Prepend.
                    $container.prepend(cloneView.$el);
                }
                else { // The original componentModel was the second element in its componentRow. Append.
                    $container.append(cloneView.$el);
                }
            });

            // Log which properties were just reset,
            Adapt.log.info(`StickyBlock: Cleared component properties on reRender: [${Adapt.stickyBlock.getResetOnReRender().join(",")}]`);

            // Trigger an inview on ALL the stickyComponents and ALL clones (since they're ALL technically inview now).
            _.delay(() => {
                this.getStickyChildren().each((stickyChild) => {
                    stickyChild.trigger("stickyBlock:inview");
                    //stickyChild.trigger("stickyBlock:reRender");
                });
                this._cloneModels.each((model) => {
                    model.trigger("stickyBlock:inview");
                    //model.trigger("stickyBlock:reRender");
                });
            }, 0);
            
            return;
        }

        /**
         * Moves components inside the stickyBlockView containers and enables events.
         */
        renderModeLarge() {
            
            this.removeClones();
            this.$el.show(0);
            this.model.get("_componentRow").set("_isEnabled", false); // Will automatically move all components out of the componentRow elements before deleting them.
            
            let staticChildren = this.getStaticChildren();

            // Inview calculations.
            let viewportHeight = window.innerHeight;
            let scrollTop = window.scrollY;
            let onscreenView = undefined;

            this.model.getChildren().each((model, i) => {
                let _id = model.get("_id");
                let view = Adapt.findViewByModelId(_id);
                this.$(".js-stickyblock-item." + _id).find(".stickyblock__item-inner").append(view.$el);

                // Set an attribute on each of them (makes it easier to determine their index in debug).
                view.$el.attr("data-stickyblock-index", model.get("_stickyBlockIndex"));
                
                // This model is a static child.
                // Have the childView start listening for inview events on the scrolling sibling.
                if (staticChildren.includes(model)) {
                    this.initChildInview(view);
                    model.set("_stickyBlockStaticChildInview", false);

                    // Hide all sticky children, and then reveal the one closest to the center of the viewport.
                    // (This only needs to happen once each resize, after which point the scroll event will take over).
                    let bb = view.$el[0].getBoundingClientRect();
                    let offsetTop = bb.top;
                    let height = bb.height;

                    let inviewTop = scrollTop + viewportHeight - offsetTop;
                    let inviewPercent = inviewTop / ((viewportHeight + height) / 100);

                    // Record the value to the view.
                    view.inviewPercent = inviewPercent;

                    // Store a reference to the view that's closest to the center of the screen.
                    if (onscreenView == undefined || Math.abs(view.inviewPercent) < Math.abs(onscreenView.inviewPercent)) {
                        onscreenView = view;
                    }
                }
                // This model is a sticky child.
                else {
                    model.set("_isVisible", false); // Fallback to ensure all sticky children are hidden when _stickyBlockStaticChildInview:change turns on visiblity for one of them.
                }
            });

            // Show the sticky child component that's paired with the onscreenView.
            onscreenView.model.set("_stickyBlockStaticChildInview", true);
        }

        isLargeMode () {
            return Adapt.device.screenSize === "large";
        }

        getStickyBlockData(){
            return this.model.get("_stickyBlock");
        }

        getStaticChildren(){
            return this.getStickyBlockData()._children.static;
        }

        getStickyChildren(){
            return this.getStickyBlockData()._children.sticky;
        }

        /**
         * Listen to inview events on the components in the static column.
         * https://github.com/adaptlearning/jquery.onscreen
         */
        initChildInview(view) {

            view.$el.on('onscreen.stickyBlockView', (event, m) => {

                const threshold = componentInviewThreshold;
                
                // Only one child will be active at once; the one that's closest to the vertical center of the screen.
                if (m.percentFromTop < threshold && m.percentFromBottom < threshold) {
                    view.model.set("_stickyBlockStaticChildInview", true);
                }
            });
        }

        removeChildInview(view) {
            view.$el.off("onscreen.stickyBlockView");
        }

        /**
         * If this view is removed, be sure to remove the onscreen event listeners too.
         */
        remove() {
            this.getStaticChildren().each((model) => {
                this.removeChildInview(Adapt.findViewByModelId(model.get("_id")));
            });

            Backbone.View.remove.apply(this, arguments);
        }

        getActiveStaticChild(){
            return this.getStaticChildren().find(childModel => {
                return childModel.get("_stickyBlockStaticChildInview");
            });
        }

        /**
         * @param {int} staticChildIndex The index of the staticChild currently inview.
         * @returns 
         */
        triggerStickyChildInview(isInview, staticChildIndex){

            // Don't trigger if the stickyColumn isn't inview.
            if (!this.model.get("_isStickyColInview")) return;

            let activeStaticChild = this.getActiveStaticChild();
            if (!activeStaticChild) return;

            // Trigger an event on the stickyChild (allows for custom extensions).
            activeStaticChild.get("_stickyBlockSiblings").each((model) => {

                let view = Adapt.findViewByModelId(model.get("_id"));
                view.$el.attr("data-stickyblock-index", staticChildIndex); // Update the attribute on the view.

                model.trigger("stickyBlock:inview", staticChildIndex /* LEGACY! DO NOT REFERENCE THE staticChildIndex PARAMETER ANYMORE */);
            });
        }

        /**
         * Whenever a static child is inview, show the corresponding sticky child.
         * Also, make all other children not inview.
         * @param {*} model 
         * @param {*} isInview 
         */
        onStaticChildInviewChange(childModel, isInview) {

            childModel.get("_stickyBlockSiblings").each(model => {
                
                // Only hide the stickyChild sibling of this staticChild only if all of its siblings are hidden.
                // (This is done in case multiple staticChildred share the same stickyChild).
                let isVisible = false;
                model.get("_stickyBlockSiblings").find(staticChildModel => {
                    if (staticChildModel.get("_stickyBlockStaticChildInview")) {
                        isVisible = true;
                        return;
                    }
                });

                model.set("_isVisible", isVisible);
            });

            if (!isInview) return;
            
            // adapt-annotation compatibility: toggle off any active annotations, as this is an item change.
            Adapt.trigger("annotation:toggle");

            // Record which static child is active.
            this.model.get("_stickyBlock").activeItem = childModel;

            _.each(this.getStaticChildren().models, (model) => {
                if (childModel == model) return; // Don't affect self.
                model.set("_stickyBlockStaticChildInview", false);
            });

            // FIXME:? _.defer() was causing a single frame of flicker with Lotties.
            // I commented it out, but it's possible that it may cause other problems now.
            //_.defer(() => {
                // Inform the stuck component that it is active (that component can listen for changes to this value for custom functionality).
                this.triggerStickyChildInview(isInview, childModel.get("_stickyBlockIndex"));
            //});
        }

        render(){
            
            var template = Handlebars.templates['stickyBlock'];
            let data = this.model.toJSON();
            data._stickyBlock._children.left = data._stickyBlock._children.left.toJSON();
            data._stickyBlock._children.right = data._stickyBlock._children.right.toJSON();
            this.$el.html(template(data));
        }

    };

    return StickyBlockView;
});
