define([
  'core/js/adapt',
  'core/js/views/questionView'
], function(Adapt, QuestionView) {

  /**
   * Modified version of adapt-contrib-textInput/js/textInputView
   * 
   * DD SPECIFIC! WILL NOT WORK WITH VANILLA ADAPT!
   * 
   * FIXME:
   * ------
   * Due to being a copy paste; this script needs a good cleanup.
   */

  class TextInputPlusView extends QuestionView {

    events() {
      return {
        "input .js-essay-textarea" : "onTextAreaInput",
        "click .js-essay-answer-btn" : "onAnswerBtnClick"
      }
    }

    onTextAreaInput(event) {
      this.model.set("_userInput", $(event.currentTarget).val());
    }

    setupEventListeners() {

      this.listenTo(this.model, "change:_userInput", this.onUserInputChange)
      this.listenTo(this.model, "change:_isShowingAnswer", this.onShowingAnswerChange)
    }

    onUserInputChange(model, userInput) {

      this.$(".js-essay-length")
        .toggleClass("has-error", 
          userInput.length < this.model.get("_answerLength")._min || 
          userInput.length > this.model.get("_answerLength")._max);

      this.$(".js-essay-length-count").text(userInput.length);
    }

    // FIXME: _isShowingAnswer is a temp name for the variable.
    onAnswerBtnClick() {
      this.model.set("_isShowingAnswer", !Boolean(this.model.get("_isShowingAnswer")));
    }

    onShowingAnswerChange(model, isShowingAnswer) {
      this.$el.toggleClass("is-answer-shown", isShowingAnswer);
      this.$(".js-essay-answer-btn").text(
        isShowingAnswer ? "Hide example essay" : "Show example essay"
      )
    }

    postRender() {
      super.postRender();

      this.setupEventListeners();
    }

    resetQuestionOnRevisit() {
      this.setAllItemsEnabled(false);
      this.resetQuestion();
    }

    setupQuestion() {
      this.model.setupRandomisation();
    }

    disableQuestion() {
      this.setAllItemsEnabled(false);
    }

    enableQuestion() {
      this.setAllItemsEnabled(true);
    }

    setAllItemsEnabled(isEnabled) {
      this.model.get('_items').forEach(function(item, index) {
        var $itemInput = this.$('.js-textinputplus-textbox').eq(index);

        $itemInput.prop('disabled', !isEnabled);
      }, this);
    }

    onQuestionRendered() {
      this.setReadyStatus();
    }

    clearValidationError() {
      //this.$('.js-textinputplus-textbox-container').removeClass('is-incorrect');
    }

    // Blank method for question to fill out when the question cannot be submitted
    onCannotSubmit() {
      this.showValidationError();
    }

    showValidationError() {
      //this.$('.js-textinputplus-textbox-container').addClass('is-incorrect');
    }

    // This is important and should give the user feedback on how they answered the question
    // Normally done through ticks and crosses by adding classes
    showMarking() {

    }

    updateMarking() {
      
    }

    // Used by the question view to reset the look and feel of the component.
    resetQuestion() {
      this.$('.js-textinputplus-textbox')
        .prop('disabled', !this.model.get('_isEnabled'))
        .val('');

      /*this.$(".js-textinputplus-textbox-container")
        .removeClass('is-incorrect is-correct');*/

      this.attemptsBeforeHint = 3;

      this.model.set({
        _isAtLeastOneCorrectSelection: false,
        _isCorrect: undefined
      });
    }

    showCorrectAnswer() {

      if (this.model.get('_answers'))  {

        var correctAnswers = this.model.get('_answers');
        this.model.get('_items').forEach(function(item, index) {
          this.$('.js-textinputplus-textbox').eq(index).val(correctAnswers[index][0]);
        }, this);

      } else {
        this.model.get('_items').forEach(function(item, index) {
          this.$('.js-textinputplus-textbox').eq(index).val(item._answers[0]);
        }, this);
      }

    }

    hideCorrectAnswer() {
      this.model.get('_items').forEach(function(item, index) {
        this.$('.js-textinputplus-textbox').eq(index).val(item.userAnswer);
      }, this);
    }

    onInputChanged(e) {
      var $input = $(e.target);
      this.model.setItemUserAnswer($input.attr('data-adapt-index'), $input.val());
    }

    // Completely reset an item its initial state (visibly; its record of incorrect answers will remain).
    clearItemState(index) {
      
      let $input = this.$('.js-textinputplus-textbox');
      let $item = this.$('.js-textinputplus-item');

      // Allow for a specific index to be used if the parameter is provided.
      if (index !== undefined) {
        $item = $item.filter("[data-adapt-index=" + index + "]");
        $input = $input.filter("[data-adapt-index=" + index + "]");
      }

      $input
        .prop('disabled', !this.model.get('_isEnabled'))
        .val('') // Clear value
        .parent().removeClass('is-incorrect is-correct') // Remove error class.

      this.model.setItemUserAnswer(index, '');

      $item.removeClass('is-correct is-incorrect');
    }

    /**
     * Note: hidden feature.
     * If the user thinks the 'X' over the input is a button, they may try to click it.
     * Have the action simply clear the input field if they do (since the X is there, it means it's wrong anyway). 
     */
    onIncorrectIconClick(event) {
      let index = $(event.currentTarget).attr("data-adapt-index");
      this.clearItemState(index);
    }

  }

  return TextInputPlusView;

});
