define([
  'core/js/adapt',
  'core/js/models/componentModel',
  './ActivatorComponentTargetModel',
  './ActivatorElementTargetModel'
], function(Adapt, ComponentModel, ActivatorComponentTargetModel, ActivatorElementTargetModel) {

  class ActivatorModel extends ComponentModel {

    // Used to set model defaults
    defaults() {

      // Extend from the ComponentModel defaults
      return ComponentModel.resultExtend('defaults', {
        _isActivatorActive: false // IMPORTANT! This property used to be called "_isActive".
                                  // DO NOT USE THIS KEY! It is used by Adapt's code functionality and can cause errors to be thrown.
      });
    }

    initialize() {
      
      ComponentModel.prototype.initialize.call(this);
      this.setUpEventListeners();
    }

    setUpEventListeners() {
      this.listenTo(Adapt, "app:dataReady", this.onDataReady.bind(this));
      this.listenTo(this, "change:_isActivatorActive", this.onActivatorActiveChange.bind(this));
    }

    onDataReady() {
      this.initTargets();

      // Wait for all targets to be ready before setting this component to be ready.
      this.listenTo(this.get("_targets"), "change:_isReady", this.onTargetReadyChange);
    }

    onTargetReadyChange(targetModel, isReady) {

      // Wait for all targets to be ready.
      let notReadyModel = this.get("_targets").find(m => {return !m.get("_isReady");});
      if (notReadyModel) {
        return;
      }

      this.set("_targetsReady", true);
    }

    /**
     * Send out an event to all components targetted by the activator.
     * It is up to the INDIVIDUAL COMPONENT to implement how it will react.
     * 
     * If the no activator reaction is defined for the target component, then a default slideDown animation will be used.
     */
    initTargets(){

      var targets = this.get("_targets");
      var _targets = [];

      // Go the _targets dictionary and find all componentModels that this activator will activate.
      let index = 0;
      _.each(targets, (target, key) => {

          // Store a reference to the component's model.
          let componentId = target._id;

          // Depracated (in earlier builds, _targets was a dictionary instead of an array).
          if (componentId === undefined && typeof key === "string") {
            componentId = key;
            Adapt.log.deprecated("Set \"_target\" as an array with property \"_id\" in its elements instead of a dictionary.");
          }

          // Support targetting elements in the same block (by name).
          if (!componentId && target._name) {

            let activatorTarget = new ActivatorElementTargetModel({
              _activatorModel: this,
              _name: target._name,
              _activationType: target._activationType ? target._activationType : "default",
              _index: index
            });
            _targets.push(activatorTarget);
            index++;

            return;
          }

          let componentModel = Adapt.findById(componentId);
          if (!componentModel) throw `activatorModel.js: could not find "_target" component with "_id": "${componentId}".`

          // Tell that component that it has been marked as activateable.
          //target.component.trigger("activator:ready");

          // Store an ActivatorComponentTargetModel in it to keep track of its state.
          let activatorTarget = new ActivatorComponentTargetModel({
              _activatorModel: this,
              _parent: componentModel,
              _activationType: target._activationType ? target._activationType : "default",
              _index: index
          });

          componentModel.set("_activatorTarget", activatorTarget);
          _targets.push(activatorTarget);
          index++;
      });

      // Set the targets to be a collection of ActivatorTarget models.
      this.set("_targets", new Backbone.Collection(_targets));
    }

    onActivatorActiveChange(model, isActive) {
      this.toggleTargets(isActive);
    }

    async toggleTargets(isActive) {
      
      let targetModels = this.get("_targets").models;
      for (let i = 0; i < targetModels.length; i++) {

        // Stagger activation of targets when isActive is false.
        if (isActive) {
          await targetModels[i].toggle();
        }
        else {
          targetModels[i].toggle();
        }
      }
    }
  }

  return ActivatorModel;

});
