define([
  'core/js/adapt',
  'core/js/views/questionView'
], function(Adapt, QuestionView) {

  /**
   * Modified version of adapt-contrib-textInput/js/textInputView
   * 
   * DD SPECIFIC! WILL NOT WORK WITH VANILLA ADAPT!
   * 
   * FIXME:
   * ------
   * Due to being a copy paste; this script needs a good cleanup.
   */

  class TextInputPlusView extends QuestionView {

    events() {
      return {
        'focus .js-textinputplus-textbox': 'clearValidationError',
        'change .js-textinputplus-textbox': 'onInputChanged',
        'keyup .js-textinputplus-textbox': 'onInputChanged',
        'click .js-textinputplus-incorrect-icon': 'onIncorrectIconClick'
      }
    }

    postRender() {
      super.postRender();
      
      // The input field Models and Views are set up by the external {{#inputField}} helper plugin.
      this.setUpInputFields();

      // Once the user submits the component this many times, the adapt-dd-inputFields will enable their hint buttons.
      // FIXME: Rough implementation.
      this.attemptsBeforeHint = 3; 
    }

    /**
     * InputFields
     * -----------
     * For consistency, this component uses the {{#inputField}} helper to make its input fields have the same behaviour
     * and appearance.
     * 
     * WARNING:
     * --------
     * Changes the Model's "_item" property to accomplish this.
     */
    setUpInputFields() {

      //if (!this.model.get("custom")) return false;

      let items = [];
      let $inputFields = this.$(".inputfield");

      $inputFields.each((i, e) => {
        
        // Find the InputFieldView that created this element.
        let view = Adapt.inputField.findViewByEl($(e));
        let model = view.model;
        
        // Add the classes to the fields that would be on this (textInputPlus) view's native <input> elements.
        // (Do not remove this, as {{#inputField}} helpers defined in the "custom" property HTML need it).
        view
          .$(".js-inputfield-input")
          .addClass("js-textinputplus-textbox")
          .attr("data-adapt-index", i);

        // Add an item to the model for it.
        items.push({
          _answers: model.getAnswers(),
          _inputFieldModel: model
        });

        // Hide the hint buttons from the inputFields (they'll be re-enabled if the user submits the wrong answer 3 times).
        model.disableHint();
        model.enableRequireSubmit();
      });

      this.model.set("_items", items);
      this.model.setupQuestionItemIndexes();
      this.model.checkCanSubmit();

      return true;
    }

    resetQuestionOnRevisit() {
      this.setAllItemsEnabled(false);
      this.resetQuestion();
    }

    setupQuestion() {
      this.model.setupRandomisation();
    }

    disableQuestion() {
      this.setAllItemsEnabled(false);
    }

    enableQuestion() {
      this.setAllItemsEnabled(true);
    }

    setAllItemsEnabled(isEnabled) {
      this.model.get('_items').forEach(function(item, index) {
        var $itemInput = this.$('.js-textinputplus-textbox').eq(index);

        $itemInput.prop('disabled', !isEnabled);
      }, this);
    }

    onQuestionRendered() {
      this.setReadyStatus();
    }

    clearValidationError() {
      //this.$('.js-textinputplus-textbox-container').removeClass('is-incorrect');
    }

    // Blank method for question to fill out when the question cannot be submitted
    onCannotSubmit() {
      this.showValidationError();
    }

    showValidationError() {
      //this.$('.js-textinputplus-textbox-container').addClass('is-incorrect');
    }

    // This is important and should give the user feedback on how they answered the question
    // Normally done through ticks and crosses by adding classes
    showMarking() {
      
      /*if (!this.model.get('_canShowMarking')) return;

      console.log("SHOWING MARKING");

      this.model.get('_items').forEach(function(item, i) {
        
        var $item = this.$('.js-textinputplus-item').eq(i);
        
        //$item.removeClass('is-correct is-incorrect').addClass(item._isCorrect ? 'is-correct' : 'is-incorrect');

        $item.toggleClass('is-correct', item._isCorrect);
        $item.toggleClass('is-incorrect', !item._isCorrect);

      }, this);*/

      // Moved to updateMarking();
    }

    updateMarking() {

      if (!this.model.get('_canShowMarking')) return;

      this.model.get('_items').forEach(function(item, i) {
        
        var $item = this.$('.js-textinputplus-item').eq(i);
        
        //$item.removeClass('is-correct is-incorrect').addClass(item._isCorrect ? 'is-correct' : 'is-incorrect');

        $item.toggleClass('is-correct', item._isCorrect);
        $item.toggleClass('is-incorrect', !item._isCorrect);

        /*$item.find(".js-textinputplus-textbox")
        .prop('disabled', item._isCorrect) // Prevent correct answers from being modified after submission.
        .inputField("submit");*/

        // CUSTOM MODE
        // -----------
        // Lock correct inputFields.
        if (item._inputFieldModel?.isCorrect()) {
          item._inputFieldModel.lock();
        }

        // Add a class to allow custom styles for the input box if it contains an incorrect input (that has been checked).
        /*$item.find('.js-textinputplus-textbox-container')
          .toggleClass('is-correct', item._isCorrect)
          .toggleClass('is-incorrect', !item._isCorrect);*/

      }, this);

      
      // Show hint icons after the feedback is closed if the user hasn't reached the correct answer after X submissions.
      this.attemptsBeforeHint--;
      if (this.attemptsBeforeHint == 0) {
        this.listenToOnce(Adapt, "notify:closed", () => {
            _.debounce(() => {

              _.each(this.model.get("_items"), (item) => {

                // Custom mode (using current inputField implementation).
                if (item._inputFieldModel) {
                  item._inputFieldModel.enableHint();
                }
                /*else {
                  // FIXME: Legacy support (using legacy inputField implementation).
                  //this.$(".js-textinputplus-textbox").filter(`[data-adapt-index="${item._index}"]`).inputField("enableHint");
                }*/
              });
            }, 600)();
        });
      }
    }

    // Used by the question view to reset the look and feel of the component.
    resetQuestion() {
      this.$('.js-textinputplus-textbox')
        .prop('disabled', !this.model.get('_isEnabled'))
        .val('');

      /*this.$(".js-textinputplus-textbox-container")
        .removeClass('is-incorrect is-correct');*/

      this.attemptsBeforeHint = 3;

      this.model.set({
        _isAtLeastOneCorrectSelection: false,
        _isCorrect: undefined
      });
    }

    showCorrectAnswer() {

      if (this.model.get('_answers'))  {

        var correctAnswers = this.model.get('_answers');
        this.model.get('_items').forEach(function(item, index) {
          this.$('.js-textinputplus-textbox').eq(index).val(correctAnswers[index][0]);
        }, this);

      } else {
        this.model.get('_items').forEach(function(item, index) {
          this.$('.js-textinputplus-textbox').eq(index).val(item._answers[0]);
        }, this);
      }

    }

    hideCorrectAnswer() {
      this.model.get('_items').forEach(function(item, index) {
        this.$('.js-textinputplus-textbox').eq(index).val(item.userAnswer);
      }, this);
    }

    onInputChanged(e) {
      var $input = $(e.target);
      this.model.setItemUserAnswer($input.attr('data-adapt-index'), $input.val());
    }

    // Completely reset an item its initial state (visibly; its record of incorrect answers will remain).
    clearItemState(index) {
      
      let $input = this.$('.js-textinputplus-textbox');
      let $item = this.$('.js-textinputplus-item');

      // Allow for a specific index to be used if the parameter is provided.
      if (index !== undefined) {
        $item = $item.filter("[data-adapt-index=" + index + "]");
        $input = $input.filter("[data-adapt-index=" + index + "]");
      }

      $input
        .prop('disabled', !this.model.get('_isEnabled'))
        .val('') // Clear value
        .parent().removeClass('is-incorrect is-correct') // Remove error class.

      this.model.setItemUserAnswer(index, '');

      $item.removeClass('is-correct is-incorrect');
    }

    /**
     * Note: hidden feature.
     * If the user thinks the 'X' over the input is a button, they may try to click it.
     * Have the action simply clear the input field if they do (since the X is there, it means it's wrong anyway). 
     */
    onIncorrectIconClick(event) {
      let index = $(event.currentTarget).attr("data-adapt-index");
      this.clearItemState(index);
    }

  }

  return TextInputPlusView;

});
