define([
    'handlebars',
    'core/js/adapt'
], function(Handlebars, Adapt) {

    class DownloadView extends Backbone.View {

        tagName() {
            return "span";
        }

        className() {
            let classes = [
                "download"
            ];
            return classes.join(" ");
        }

        events() {
            return {
                "click .js-download-tooltip" : "onTooltipClick"
            }
        }

        initialize() {
            this.render();
        }

        render() {

            let template = Handlebars.templates["ddDownload"];
            this.$el.html(new Handlebars.SafeString(template(this.model.toJSON())).string);
        }

        onTooltipClick() {
            Adapt.createTooltip(this.$(".js-download-tooltip"), this.model.get("tooltip"));
        }
    };

    return DownloadView;
});
