define([
    'handlebars',
    'core/js/adapt',
    './resourcePopupBtnView'
], function(Handlebars, Adapt, ResourcePopupBtnView) {

    /**
     * adapt-resourcePopup
     * --------------------------------------
     * This extension sets up resource buttons in the content to show fullscreen popup resources.
     * 
     * TODO:
     *  - May be changed to being a normal Component; depends on how frequently it's seen inside of other Components.
     * 
     * FIXME:
     * - Won't work if the resource is inside of another fullscreen reveal (like a HotgraphicPlus).
     * - A bit tedious to use (needs a partial to work).
     */

    // Helpers
    // ---------------------
    
    /**
     * Use these to create the inline tags for the resource button.
     * 
     * Note:
     * -----
     * Using Handlebars fixes the issue previous extensions developed for DD had, that being that
     * they couldn't be used inside of dynamically generated HTML (like inside of a popup for example).
     * Now, whenever the Handlebars is compiled, it knows to create the appropriate View for the thing.
     * Plus, it keeps the components.json tidy and consistant to use Handlebars expressions for such things.
     * (And no need for custom HTML tags).
     * 
     * FIXME:
     * ------
     * Had to use a weird workaround to get this one working. Monitor for possible bugs.
     */

    var helpers = {

        /**
         * Creates a resource button.
         * 
         * Note:
         * "this" returns the context of the JSON. If the {{}} is inside of an _item element, it returns the ITEM, not its component.
         * 
         * @param {string} resourceId       The ID of the resourcePopup item.
         */
        resourcePopup: function(resourceId, context) {

            // Find the closest component view.
            // FIXME: Only works for one level of nesting, and only in items.
            let componentView = this.view;
            if (this.view.model.get("_type") != "component")
                componentView = Adapt.findViewByModelId(this.view.model.get("_parent").get("_id"));

            // If the component has no _resourcePopup partial, show an error.
            let resourcePopup = componentView.model.get("_resourcePopup");
            if (!resourcePopup) {
                throw componentView.model.get("_id") + " component uses a _resourcePopup, but has not defined it as a partial.";
            }

            let _uniqueId = _.uniqueId("resourcepopup");
            let btnHTML = "<div class='resourcepopup-btn-container' data-resourcepopup-id='" + _uniqueId + "'></div>";

            // Wait for the HTML to render, then apply a view + model to the button.
            _.debounce(() => {

                // Find the resource item in _resourcePopup which matches the resourceId.
                let modelJSON = _.find(resourcePopup._items, (item) => {return item._id == resourceId});
                if (!modelJSON) {
                    throw componentView.model.get("_id") + " component could not find _resourcePopup item with \"_id\": \"" + resourceId + "\".";
                }

                // Extend the model properties.
                modelJSON = _.extend(modelJSON, {
                    buttonText: context.fn(),
                    _id: _uniqueId,
                    _parentView: componentView
                })

                let $btnContainer = componentView.$("[data-resourcepopup-id='" + _uniqueId + "']");
                $btnContainer.html(new ResourcePopupBtnView({
                    model: new Backbone.Model(modelJSON)
                }).$el);
            })();

            return new Handlebars.SafeString(btnHTML);
        }

    };

    for (var name in helpers) {
        if (helpers.hasOwnProperty(name)) {
            Handlebars.registerHelper(name, helpers[name]);
        }
    }
});
