define([
  'core/js/adapt',
  'handlebars'
], function(Adapt, Handlebars) {

  // StickyElement transition helpers
  // ---------------------------------------------------------
  var helpers = {

    /**
     * Needs to be added as an ATTRIBUTE to a HTML element.
     * For example:
     * <div {{{se_active idx="0,2"}}}>Activate me</div>
     * 
     * Mark selected element when itemIdx == provided value(s).
     */
    ac_mark: function() {
        return 'data-activator-transition="mark"';
    },

    ac_show: function() {
        return 'data-activator-transition="show"';
    }
  }

  for (var name in helpers) {
      if (!helpers.hasOwnProperty(name)) continue;
      Handlebars.registerHelper(name, helpers[name]);
  }
});
