define([
  'core/js/adapt',
  'core/js/views/questionView'
], function(Adapt, QuestionView) {

  /**
   * Notes:
   * - Each item has 2+ buttons.
   * - Items become activated as long as one or more of their buttons is clicked.
   * - Each item stores an array of buttons that have been clicked in it.
   * - Each item will need one or more of its buttons selected to be correct.
   *   SCORM (or Adapt?) doesn't have a way to store multiple multiple "choice" interactions that in one go,
   *   so I've set this up to use "fill-in" as the submission type for now.
   */

  var MmcqView = QuestionView.extend({

    events: {
      'focus .js-item-input'  : 'onItemFocus',
      'blur .js-item-input'   : 'onItemBlur',
      'change .js-item-input' : 'onItemSelect',
      'keyup .js-item-input'  : 'onKeyPress'
    },

    isCorrectAnswerShown: false,

    initialize: function() {
      QuestionView.prototype.initialize.apply(this, arguments);
      this.update = _.debounce(this.update.bind(this), 1);
      
      this.listenTo(this.model, {
        "change:_isEnabled change:_isComplete change:_isSubmitted": this.update
      });

      // FIXME: Custom trigger comes from mmcqModel.
      this.listenTo(this.model, {
        "_change:_optionsSelected": this.update
      });

      this.listenTo(Adapt, "device:changed", this.onDeviceChange);
    },

    resetQuestionOnRevisit: function() {
      this.resetQuestion();
    },

    setupQuestion: function() {

      this.model.setupRandomisation();
    },

    postRender: function() {
      QuestionView.prototype.postRender.apply(this, arguments);
      this.onDeviceChange();
      this.updateMarking(); // Custom: initial call to set up aria labels for the inputs.
                            // FIXME: Applying the initial aria could be done better...
    },

    onDeviceChange: function() {
      this.resizeHeaderLabels();
    },

    /**
     * Have all the .mmcq__items-header-label elements have the same widths.
     * (Only if "_combineOptions": true).
     */
    resizeHeaderLabels: function() {

      if (!this.model.get("_combineOptions")) return;

      let $labelsInner = this.$(".mmcq__items-header-label-inner");
      let minWidth = this.$(".mmcq__item-icon").first().outerWidth() + 1; // Don't let the width go below the width of the clickable icons.
      let maxWidth = 0;

      $labelsInner.each((i, e) => {
        maxWidth = Math.max($(e).width(), maxWidth);
      });
      if (maxWidth < minWidth) maxWidth = minWidth;
      this.$(".mmcq__items-header-label").width(maxWidth);
    },

    onQuestionRendered: function() {
      this.setReadyStatus();
      this.update();
    },

    onKeyPress: function(event) {
      if (event.which !== 13) return;
      //<ENTER> keypress
      this.onItemSelect(event);
    },

    onItemFocus: function(event) {
      if (!this.model.isInteractive()) return;

      $('.js-item-label[for='+$(event.currentTarget).attr('id')+']').addClass('is-highlighted');
    },

    onItemBlur: function(event) {
      $('.js-item-label[for='+$(event.currentTarget).attr('id')+']').removeClass('is-highlighted');
    },

    onItemSelect: function(event) {
      if (!this.model.isInteractive()) return;

      var index = $(event.currentTarget).data('adapt-index');             // Adapt item index
      var inputIndex = $(event.currentTarget).data('adapt-option-index');
      
      this.model.toggleOptionsSelected(index, inputIndex);

      return;
    },

    // Blank method to add functionality for when the user cannot submit
    // Could be used for a popup or explanation dialog/hint
    onCannotSubmit: function() {},

    // This is important and should give the user feedback on how they answered the question
    // Normally done through ticks and crosses by adding classes
    showMarking: function() {
      this.update();
      this.updateMarking();
    },

    resetQuestion: function() {

      return;
      this.$('.matching__item').removeClass('is-correct is-incorrect');
      this.model.set('_isAtLeastOneCorrectSelection', false);
      var resetAll = this.model.get('_shouldResetAllAnswers');

      this.model.get('_items').forEach(function(item, index) {
        if (item._isCorrect && resetAll === false) return;
        this.selectValue(index, null);
        item._options.forEach(function(option, index) {
          option._isSelected = false;
        });
        item._selected = null;
      }, this);
    },
    
    showCorrectAnswer: function() {
      this.isCorrectAnswerShown = true;
      this.update();
    },

    hideCorrectAnswer: function() {
      this.isCorrectAnswerShown = false;
      this.update();
    },

    update: function(model) {
      this.updateSelection();
      // \/ questionPlus will call this.updateMarking() when necessary. It shouldn't be invoked here while it's in use.
      //this.updateMarking();
    },

    updateSelection: function() {

      //var isEnabled = this.model.get("_isEnabled");

      _.each(this.model.get("_items"), (item) => {

        let itemIdx = item._index;
        let isEnabled = !Boolean(item._isCorrect); // Disable items only when the item has been completed.

        _.each(item._optionsSelected, (option, optionIdx) => {

          var isSelected = this.isCorrectAnswerShown ?
            _.contains(item._optionsCorrect, optionIdx) :
            option

          let filter = '[data-adapt-index="' + itemIdx + '"][data-adapt-option-index="' + optionIdx + '"]';
          
          this.$('.js-item-label').filter(filter)
            .toggleClass('is-selected', isSelected)
            .toggleClass('is-disabled', !isEnabled)
            // Custom \/
            .find(".mmcq__item-answer-icon")
            .toggleClass('is-disabled', !isEnabled);

          this.$('.js-item-input').filter(filter)
              .prop('checked', isSelected)
              .prop('disabled', !isEnabled);
        });
      });
    },

    updateMarking: function() {

      //var isInteractive = this.model.isInteractive();
      let canShowMarking = this.model.get('_canShowMarking');
      let ariaLabels = Adapt.course.get('_globals')._accessibility._ariaLabels;

      _.each(this.model.get("_items"), (item) => {

        let index = item._index;

        let $item = this.$('.js-mmcq-item').filter('[data-adapt-index="' + index + '"]');
        $item
          .toggleClass('is-correct', item._isCorrect)
          .toggleClass('is-incorrect', !item._isCorrect);

        if (!canShowMarking) {
          $item
          .removeClass('is-correct')
          .removeClass('is-incorrect');
        }

        /**
         * questionPlus: only apply marking to selected items.
         */
        _.each(item._optionsSelected, (option, optionIdx) => {

          let filter = '[data-adapt-index="' + index + '"][data-adapt-option-index="' + optionIdx + '"]';
          let $itemInput = this.$(".js-item-input").filter(filter);
          let $itemLabel = this.$(".js-item-label").filter(filter);

          let shouldBeSelected = Boolean(_.contains(item._optionsCorrect, optionIdx));
          let isCorrect = option == shouldBeSelected;
          let isSelected = option;

          // Set aria-labels
          let inputText = (this.model.get("_options") || item._options)[optionIdx];

          // Pre-checked state (inputs which haven't been submitted while selected yet).
          if (!item._optionsVisited[optionIdx]) {
            $itemInput.attr('aria-label', $.a11y_normalize(inputText));
          }
          // Checked inputs (correct or incorrect).
          // Only apply "Incorrect" / "Correct" aria labels after they've been submitted while selected.
          else {
            $itemInput.attr('aria-label', [
              (shouldBeSelected ? ariaLabels.correct : ariaLabels.incorrect),
              ", ",
              (isSelected ? ariaLabels.selectedAnswer : ariaLabels.unselectedAnswer),
              ". ",
              $.a11y_normalize(inputText)
            ].join(""));
          }

          if (!canShowMarking) {
            // Remove input marking
            $itemInput.removeClass('is-correct is-incorrect');
            $itemLabel.removeClass('is-correct is-incorrect');
            return;
          }

          // Apply styles to ONLY the selected inputs.
          // (DD questionPlus design choice).
          if (!isSelected) return;

          $itemInput.toggleClass("is-correct", isCorrect)
                    .toggleClass("is-incorrect", !isCorrect);

          $itemLabel
            .toggleClass("is-correct", isCorrect)
            .toggleClass("is-incorrect", !isCorrect);
        });

      });

      this.updateSelection();
    }

  });

  return MmcqView;

});
