define([
    'core/js/adapt',
    'handlebars',
    './codeBoxView',
    './codeBoxIframeView'
], function(Adapt, Handlebars, CodeBoxView, CodeBoxIframeView) {

    /**
     * WARNING:
     * --------
     * This extention requires the JSONSYNT {{#code}} grunt build task to be executed for the best results.
     * See grunt/config/jsonsynt-build.js for more information.
     */

    Adapt.on('router:page', () => {

        // Code
        Handlebars.registerHelperEl("code", function() {

            let context = arguments[arguments.length-1];
            let hash = Handlebars.getContextHash.apply(this, arguments);

            // Set the code language.
            let _default = Adapt.course.get("_codeBox");
            let _lang = hash.lang ? hash.lang : (_default ? _default._lang : "");

            let modelJSON = {
                content: context.fn(this),
                _classes: hash.class,
                _style: hash.style,
                _filename: hash.filename,
                // adapt-sizing compatibility removed after rework of design.
                //_sizing: hash.sizing ? Adapt.sizing.getSizingClass(hash.sizing) : "",
                _type: "code",
                _lang: _lang,
                _wrappingEnabled: Boolean(hash.wrapping)
            };

            return new CodeBoxView({
                model: new Backbone.Model(modelJSON)
            }).$el;
        });

        // Console
        Handlebars.registerHelperEl("console", function() {

            let context = arguments[arguments.length-1];
            let hash = context.hash;

            // Set the console language (only matters if the language is "html"; changes its appearance).
            // TODO: Have it inherit its language from the paired codebox if possible.
            let _default = Adapt.course.get("_codeBox");
            let _lang = hash.lang ? hash.lang : (_default ? _default._lang : "");

            let modelJSON = {
                content: context.fn(this),
                _classes: hash.class,
                _style: hash.style,
                _type: "console",
                _lang: _lang,
                _wrappingEnabled: Boolean(hash.wrapping)
            };

            // Use a different view for HTML consoleboxes.
            let view = _lang == "html" ? CodeBoxIframeView : CodeBoxView;

            return new view({
                model: new Backbone.Model(modelJSON)
            }).$el;
        });
    });

    // Other helpers
    // ---------------------------------------------------------
    var helpers = {

        pseudocode: function() {
            let context = arguments[arguments.length-1];
            return "<span class='codebox__pseudocode'>" + context.fn(this) + "</span>";
        }
    }

    for (var name in helpers) {
        if (!helpers.hasOwnProperty(name)) continue;
        Handlebars.registerHelper(name, helpers[name]);
    }

    Adapt.codeBox = {
        /**
         * This regex will be used to find the "<br class="br" />" tags generated by the jsonsynt-build Grunt task.
         * 
         * adapt-epub compatibility
         * --------------------------------------------------
         * Sometimes the XHTML renders the <br /> as <br xmlns="http://www.w3.org/1999/xhtml" />
         */
        BR_REGEX:   new RegExp(/<br\s+[^>]*?class="br"\s*.*?\/?>/, "g"),
        TAB_REGEX:  new RegExp(/<span[^>]*?class="tab"\s*.*?>.*?<\/span>/, "g"),
        
        /**
         * Converts a code string to a regular string (replaces BR_REGEX and TAB_REGEX).
         * Used mainly by tryHtml (for now).
         * 
         * @param {string} codeString 
         */
        toString: function(codeString) {

            if (typeof(codeString) !== "string") throw "adapt-codeBox: codeString must be a valid string.";

            let s = codeString;
            s = s.replace(this.BR_REGEX, "\n");
            s = s.replace(this.TAB_REGEX, "\t");

            return s;
        },

        getLines: function(codeString) {
            if (typeof(codeString) !== "string") throw "adapt-codeBox: codeString must be a valid string.";

            let lines = codeString.split(this.BR_REGEX);
            return lines;
        }
    }
});
