define([
    'core/js/adapt'
], function(Adapt) {

    class ExtraLearningPopupView extends Backbone.View {

        className() {
            return "extralearning-popup " + (this.model.get("_classes") || "");
        }

        events() {
            return {
                "click .js-extralearning-icon" : "closePopup"
            }
        }

        initialize(){
            
            this.setUpEventListeners();
            this.render();
        }

        setUpEventListeners() {
            this.listenToOnce(Adapt, "notify:opened", this.onOpened);
            this.listenTo(Adapt, "notify:closed", this.onNotifyClosed);
        }

        closePopup() {
            Adapt.trigger('notify:close');
        }

        onOpened() {
            
            // Hide the icon in the popup until it has been fully transformed.
            this.$(".js-extralearning-icon").addClass("u-visibility-hidden");

            // Set up the large popup icon to be in the same location and the same size as the parent button's icon.
            this.$el.imageready(_.debounce(this.setUpIconTransition.bind(this), 0));
        }

        onNotifyClosed(notifyView) {

            // Check that the notify that was just closed was the one that contained this view.
            // (Prevents bugs caused by stacking multiple notifys).
            if (notifyView.model.get("_view") !== this) return;

            this.$el.addClass("is-closed");
            this.alignIconToParent(); // Re-align the icon just in case.
            this.$(".js-extralearning-icon").removeClass("is-active"); // Have the icon animate back to the parentView.
        }

        remove() {
            this.model.trigger("remove");
            super.remove();
        }

        render() {
            var data = this.model.toJSON();
            data.view = this;
            var template = Handlebars.templates['extraLearningPopup'];
            this.$el.html(template(data));
        }

        setUpIconTransition() {
            this.alignIconToParent();
            this.$(".js-extralearning-icon")
                .hide(0)
                .removeClass("u-visibility-hidden")
                .show(0) // Skip the initial CSS transition for the icon.
                .addClass("is-active");
        }

        /**
         * Notify revealing animation:
         * ---------------------------
         * Have the .extralearning__icon element in the popup look like the one from the parentView's button
         * initially, then grow to the final reveal size (making it look like one and the same).  
         */
        alignIconToParent() {

            let parentView = this.model.get("_parentView"); // This value is set by ExtraLearningView.

            let $icon = this.$(".js-extralearning-icon");
            let $parentIcon = parentView.$(".js-extralearning-icon");

            $icon.css({
                "transform": ""
            })

            // FIXME: Throws errors if element missing.
            let iconBB = $icon[0].getBoundingClientRect();
            let parentIconBB = $parentIcon[0].getBoundingClientRect();

            // Transform the $icon to place it over the $parentIcon (and make it look like it's the same thing).
            let offset = {
                top: parentIconBB.top - iconBB.top,
                left: parentIconBB.left - iconBB.left
            }

            // Uniform scaling only.
            let scale = parentIconBB.width / iconBB.width;

            $icon.css({
                "transform": "translate(" + offset.left + "px, " + offset.top + "px) scale(" + scale + ")"
            });

            // Make the parentIcon invisible until this notify is closed.
            $parentIcon.addClass("u-visibility-hidden");

            return $icon;
        }
    };

    return ExtraLearningPopupView;
});
