define([
  'core/js/adapt',
  'core/js/views/componentView',
  './hotgraphicPlusPopupView'
], function(Adapt, ComponentView, HotgraphicPlusPopupView) {
  'use strict';

  var HotGraphicPlusView = ComponentView.extend({

    events: {
      'click .js-hotgraphicplus-item-click': 'onPinClicked',
      'click .js-hotgraphicplus-counter': 'onFinderCounterClick'
    },

    className: function() {
      var classes = ComponentView.prototype.className.call(this);
      if (this.model.get("_isSingleColumn")) classes += " is-single";
      return classes;
    },

    initialize: function() {
      this.setUpModelData(); // FIXME: Should probably move this into pre-render?
      ComponentView.prototype.initialize.call(this);
      this.setUpViewData();
      this.setUpEventListeners();
      this.checkIfResetOnRevisit();
    },

    setUpViewData: function() {
      this.popupView = null;
      this._isPopupOpen = false;
    },

    setUpModelData: function() {

      // adapt-sizing extension compatibility
      // ------------------------------------------

      // adapt-sizing for optional background-graphic.
      let graphic = this.model.get("_graphic");
      if (graphic && graphic._sizing) {
        graphic._sizing._classes = Adapt.sizing.getSizingClass(graphic._sizing);
      }
      // If no background graphic, use the adapt-sizing for items instead.
      else {
        let itemSizing = this.model.get("_itemSizing");
        if (itemSizing) {
          //itemSizing._classes = Adapt.sizing.getSizingClass(itemSizing);
          // adapt-sizing grid
          itemSizing._classes = Adapt.sizing.getSizingGridClasses(itemSizing);
        }
      }

      // Allow for the sizing for all popups to be set in one property.
      let itemPopupSizing = this.model.get("_itemPopupSizing");
      if (itemPopupSizing) {
        this.model.getChildren().each((item) => {

          if (!item.get("_graphic")) item.set("_graphic", {});

          let itemSizing = item.get("_sizing") /* Legacy */ || item.get("_graphic")._sizing || {};

          // Important to specify {} as the first parameter (making _.extend create a copy);
          // otherwise the original itemPopupSizing variable gets extended (causing issues for subsequent items).
          item.get("_graphic")._sizing = _.extend({}, itemPopupSizing, itemSizing);
        });
      }

      // Add sizing to the counter (if in _finder mode).
      let finderData = this.model.get("_finder");
      if (finderData && finderData._isEnabled !== false) {
        let counterSizing = finderData._counter?._sizing;
        if (!counterSizing) throw "adapt-hotgraphicplus: \"_finder._counter._sizing\" must be defined if using \"_finder\" mode.";
        
        finderData._counter._sizing._classes = Adapt.sizing.getSizingClass(counterSizing);
      }

      /*if (this.model.get('_canCycleThroughPagination') === undefined) {
        this.model.set('_canCycleThroughPagination', false);
      }*/
    },

    setUpSizingListeners() {
      
      let itemSizing = this.model.get("_itemSizing");
      if (itemSizing) {
        Adapt.sizing.setUpGridListeners(this.$(".sizing-grid"));
      }
    },

    /**
     * Update the number (and suffix) in the counter based on the amount of non-visited items remaining.
     * @returns 
     */
    updateFinderCounter: function() {

      let finderData = this.model.get("_finder");
      if (!finderData || finderData._isEnabled === false) return;

      let items = this.model.getChildren();

      let itemsRemaining = 0;
      items.each(item => {
        if (!item.get("_isVisited")) itemsRemaining++;
      });

      this.$(".js-hotgraphicplus-counter-num").text(itemsRemaining);
      this.$(".js-hotgraphicplus-counter-suffix").text(itemsRemaining == 1 ? "Remains" : "Remain");

      // When complete
      if (itemsRemaining === 0) {
        this.$(".js-hotgraphicplus-counter").addClass("is-complete");
        this.$(".js-hotgraphicplus-counter-hint").text("Well done!");
      }
    },

    /**
     * If the user clicks the counter, have the first non-visited item flicker.
     */
    onFinderCounterClick: function() {

      let items = this.model.getChildren();
      let nonVisitedItem = items.find(item => {if (!item.get("_isVisited")) return item;});

      if (!nonVisitedItem) return; // There are no non-visited items (the component is already complete).

      this.clearFinderHints();

      // (Delay in order to reset CSS animation).
      _.delay(() => {

        // Apply the hint class to this item.
        var $item = this.getItemElement(nonVisitedItem);
        $item.addClass("is-finder-hint");

        // Scroll it into view just in case.
        Adapt.navigateToElement($item, {
          duration: 400,
          align: "inview"
        });
      });
    },

    clearFinderHints: function() {
      // Remove the hint class from all items (done to reset animations).
      this.$(".is-finder-hint").removeClass("is-finder-hint");
    },

    setUpEventListeners: function() {
      //this.listenTo(Adapt, 'device:changed', this.reRender);

      this.listenTo(this.model.getChildren(), {
        'change:_isActive': this.onItemsActiveChange,
        'change:_isVisited': this.onItemsVisitedChange,
        'change:_isVisible': this.onItemsVisibleChange
      });

      if (this.isTabsMode()) {
        this.listenTo(Adapt, 'device:resize', this.onDeviceResizeTabs);
        this._isTabsOpen = false;
      }
    },

    isTabsMode: function() {
      return this.model.get("_tabs")?._isEnabled;
    },

    reRender: function() {

      

      /*if (Adapt.device.screenSize !== 'large') {
        this.replaceWithNarrative();
      }*/
    },

    /*replaceWithNarrative: function() {
      var NarrativeView = Adapt.getViewClass('narrative');

      var model = this.prepareNarrativeModel();
      var newNarrative = new NarrativeView({ model: model });
      var $container = $(".component__container", $("." + this.model.get("_parentId")));

      newNarrative.reRender();
      newNarrative.setupNarrative();
      $container.append(newNarrative.$el);
      Adapt.trigger('device:resize');
      _.defer(this.remove.bind(this));
    },*/

    /*prepareNarrativeModel: function() {
      var model = this.model;
      model.set({
        '_component': 'narrative',
        '_wasHotgraphic': true,
        'originalBody': model.get('body'),
        'originalInstruction': model.get('instruction')
      });

      // Check if active item exists, default to 0
      var activeItem = model.getActiveItem();
      if (!activeItem) {
        model.getItem(0).toggleActive(true);
      }

      // Swap mobile body and instructions for desktop variants.
      if (model.get('mobileBody')) {
        model.set('body', model.get('mobileBody'));
      }
      if (model.get('mobileInstruction')) {
        model.set('instruction', model.get('mobileInstruction'));
      }

      return model;
    },*/

    onItemsActiveChange: function(model, _isActive) {
      this.getItemElement(model).toggleClass('is-active', _isActive);
    },

    // Toggle pin visibility. Done to create a smooth transition between the page and the notify popup.
    onItemsVisibleChange: function(model, _isVisible) {
      this.getItemElement(model).toggleClass("is-hidden", !_isVisible);
    },

    getItemElement: function(model) {
      var index = model.get('_index');
      return this.$('.js-hotgraphicplus-item-click').filter('[data-index="' + index + '"]');
    },

    onItemsVisitedChange: function(model, _isVisited) {
      if (!_isVisited) return;
      var $pin = this.getItemElement(model);

      // Append the word 'visited.' to the pin's aria-label
      var visitedLabel = this.model.get('_globals')._accessibility._ariaLabels.visited + ".";
      $pin.find('.aria-label').each(function(index, el) {
        el.innerHTML += " " + visitedLabel;
      });

      $pin.addClass('is-visited');

      _.delay(() => {
        $pin.addClass('is-finder-visited');
      }, 500);

      this.updateFinderCounter();
      this.clearFinderHints();
    },

    checkIfResetOnRevisit: function() {
      var isResetOnRevisit = this.model.get('_isResetOnRevisit');

      // If reset is enabled set defaults
      if (isResetOnRevisit) {
        this.model.reset(isResetOnRevisit);
      }
    },

    preRender: function() {

      




      //if (Adapt.device.screenSize === 'large') {
      //  this.render();
      //} else {
      //  this.reRender();
      //}
    },

    postRender: function() {
      // Only start showing the component once all images are ready.
      this.$('.hotgraphicplus__widget').imageready(this.setReadyStatus.bind(this));

      // Not being used.
      if (this.model.get('_setCompletionOn') === 'inview') {
        this.setupInviewCompletion('.component__widget');
      }

      this.setUpDebug();

      this.setUpSizingListeners();
    },

    /**
     * Allow for easy positioning of pin items using the adapt-dd-debug plugin.
     */
    setUpDebug: function() {

      if (!Adapt.debug?._isEnabled) return;

      let $pinItems = this.$(".hotgraphicplus__pin-item");
      
      if (Adapt.debug?.setUpInteract) {
        Adapt.debug.setUpInteract($pinItems, this.$(".hotgraphicplus__pin-item-container"));
      }
    },

    // --------------------------------------------------------------------------------------
    // Customized DD behaviour begins here.
    // --------------------------------------------------------------------------------------

    /**
     * (For now) we do not want to allow for the user to navigate between each item from within its popup.
     * @param {*} event 
     */
    
    onPinClicked: function (event) {
      if (event) event.preventDefault();

      var item = this.model.getItem($(event.currentTarget).data('index'));

      // Set this item to be active, and all others to not be active.
      this.model.getChildren().each(e => {
        e.toggleActive(e == item);
      });
      item.toggleVisited(true);

      // If in tabs mode, don't open a popup, and instead toggle the corresponding tab.
      if (this.isTabsMode() && Adapt.device.screenSize !== "small") {
        if (!this._isTabsOpen) {
          this.showTab(item);
          this.$(".js-hotgraphicplus-tabs").slideDown();
          this._isTabsOpen = true;
        }
        else {
          this.showTab(item);
          this.$(".js-hotgraphicplus-tabs").show(0); // Fallback.
        }
      }
      else {
        this.openPopup(item);
      }
    },

    // FIXME: This doesn't seem to be doing anything.
    onDeviceResizeTabs: function() {
      _.delay(this.updateTabTail.bind(this), 0);
    },

    // Move tail to be under the clicked item.
    // TODO: Room for improvement. + the tail is a bit hard to see.
    updateTabTail: function() {

      let item = this.model.getActiveItem();
      if (!item) return; // Don't do anything if no tab has been activated yet.

      let $item = this.getItemElement(this.model.getActiveItem());

      let itemBB = $item[0].getBoundingClientRect();
      let widgetBB = this.$(".hotgraphicplus__widget")[0].getBoundingClientRect();

      let left = itemBB.left - widgetBB.left;
      let width = itemBB.width;
      let $tail = this.$(".js-hotgraphicplus-tabs-tail");
      
      let tailLeft = left + width/2;
      tailLeft = tailLeft / this.$(".component__widget").width();

      // Set the tail's left position.
      $tail.css({
        "left": (tailLeft * 100) + "%"
      });

      // If this is the first activation, transition the tail to that position immediately, then activate it.
      if (!$tail.hasClass("is-active")) {
        _.delay(() => {
          $tail.hide(0).show(0);
          $tail.addClass("is-active");
        }, 500); // FIXME: Delay added to counteract the slideDown animation.
      }
    },

    showTab: function(itemModel) {

      this.updateTabTail();

      // Show the corresponding tab content item.
      this.$(".js-hotgraphicplus-tab-item")
        .removeClass("is-active")
        .filter("[data-index=" + itemModel.get("_index") + "]")
        .addClass("is-active");

      // Ensure the user sees the reveal.
      let $tabs = this.$(".js-hotgraphicplus-tabs");
      
      if (!this._isTabsOpen) $tabs.show(0);
      Adapt.navigateToElement($tabs, {
        duration: 400,
        align: "inview"
      });
      if (!this._isTabsOpen) $tabs.hide(0);
    },

    /**
     * For now, set up to re-render the popup each time.
     * @param {*} itemModel 
     */
    openPopup: function(itemModel) {
      if (this._isPopupOpen) return;

      this._isPopupOpen = true;

      itemModel.set("_parent", this.model);
      itemModel.set("_isSingleColumn", this.model.get("_isSingleColumn"));
      
      try {
        this.popupView = new HotgraphicPlusPopupView({
          model: itemModel //this.model
        });
      }
      catch (err) {
        Adapt.log.error("hotgraphicPlusView: could not render popup (view has errors; see below):");
        this._isPopupOpen = false;
        throw err;
      }

      this.listenToOnce(this.popupView.model, "hotgraphicPlusPopupView:ready", () => {
        itemModel.set("_isVisible", false);
      });

      Adapt.trigger("notify:popup", {
        _view: this.popupView,
        _isCancellable: true,
        //_showCloseButton: false,
        _classes: 'hotgraphicplus ' + this.model.get('_classes') + " " + this.model.get("_id")
      });

      // Wait for the popup to be fully removed before swapping the graphic visibilities.
      this.listenToOnce(this.popupView.model, "remove", this.onPopupRemoved);
    },

    onPopupRemoved: function() {
      this.model.getActiveItem().set("_isVisible", true);
      this.model.getActiveItem().toggleActive();
      this._isPopupOpen = false;
    }

  },
  {
    template: "hotgraphicPlus"
  });

  return HotGraphicPlusView;

});
