const { bind } = require("underscore");

define([
    'handlebars',
    'core/js/adapt',
    './imgView'
], function(Handlebars, Adapt, ImgView) {

    const MAX_RESIZE_ATTEMPTS = 5;
    const RESIZE_BUFFER = 2; // Can go this many pixels outside the edges of the parent item (used to accomodate float rounding errors).

    const HIDDEN_OBSERVER_DEBOUNCE = 500; 

    /**
     * Renders an <img> with "responsive" textboxes overlayed over it.
     */
    class RespImgView extends ImgView {

        className() {
            let classes = super.className();
            return classes + " rimg";
        }

        initialize(){

            this.model.set("_isHidden", false);
            this._fontSizeMulti = 1;
            super.initialize();
        }

        _areItemsOverflowing() {

            let overflowing = false;

            let $items = this.$(".rimg__item");
            $items.each((i, e) => {

                let $e = $(e);

                // Deep clone to allow editing the properties.
                let bb = _.extend({}, $e[0].getBoundingClientRect());

                // If padding was added to the .rimg__item, factor it into the calculations.
                let pTop = parseFloat($e.css("padding-top"), 10);
                let pLeft = parseFloat($e.css("padding-left"), 10);
                let pRight = parseFloat($e.css("padding-right"), 10);
                let pBottom = parseFloat($e.css("padding-bottom"), 10);

                bb.top += pTop;
                bb.left += pLeft;
                bb.width -= pLeft + pRight;
                bb.height -= pTop + pBottom;

                let bbInner = $e.find(".rimg__item-inner")[0].getBoundingClientRect();

                if (
                    (bb.top - bbInner.top) > RESIZE_BUFFER ||
                    (bb.left - bbInner.left) > RESIZE_BUFFER ||
                    (bbInner.bottom - bb.bottom) > RESIZE_BUFFER ||
                    (bbInner.right - bb.right) > RESIZE_BUFFER
                ) {
                    // This item is overflowing its container.
                    overflowing = $e;
                    return false;
                }
            });

            return overflowing;
        }

        // These listeners are specific to respImg, and need to be called at a different time to the parent ImgView's "setUpEventListeners()" function.
        setUpRespImgEventListeners(){

            $(".rimg__img").imageready(() => {
                this.$el.addClass("is-ready");
            });

            this.listenTo(Adapt, {
                // The delay fixes a rare issues where the respImg may not re-size its text after a resize if within certain component items (HotgraphicMini).
                'device:resize': _.debounce(this.onDeviceResize, 0).bind(this)
            });

            this.listenTo(this.model, "change:_isHidden", this.onHiddenChange);

            // Detect if the language/html inside of the items changes.
            let observer = new MutationObserver((event) => {
                
                this._fontSizeMulti = 1;    // Set the fontSizeMulti back to 1.
                this.onDeviceResize();      // Trigger a recalculation of the main element's em value.

                // Attempt to scale down the font X times to get it to fit.
                let attempts = MAX_RESIZE_ATTEMPTS;
                let $overflowingItem = null;
                do {
                    $overflowingItem = this._areItemsOverflowing();
                    if ($overflowingItem) {
                        this._fontSizeMulti -= 0.05;
                        this.onDeviceResize();
                    }
                    else {
                        Adapt.log.info("RespImg: text resized after " + (MAX_RESIZE_ATTEMPTS - attempts) + " attempts.", this.$el);
                        return;
                    }
                    attempts--;
                } while (attempts > 0);

                Adapt.log.warn("RespImg: text resize was unsuccessful after " + MAX_RESIZE_ATTEMPTS + " attempts.", this.$el, $overflowingItem);
            });
            observer.observe(this.$(".rimg__inner")[0], {
                subtree: true,
                childList: true // When Google Translate activates, it adds <font> elements around the items.
            });
        }

        /**
         * Ensure that at all times, the "font-size" property for the view is equal to:
         * its current width / the svgs width (the size it was saved as).
         */
        onDeviceResize() {
            this.recalculate();
        }

        recalculate() {
            let width = this.$el.width();
            let svgWidth = this.model.get("_svgWidth") || 1;

            let em = (width/svgWidth) * this._fontSizeMulti;
            this.$el.css("font-size", em + "px");
        }

        render(){
            let template = Handlebars.templates['ddRespImg'];
            this.$el.html(new Handlebars.SafeString(template(this.model.toJSON())).string);

            this.renderItems();
        }

        // Whenever the view becomes un-hidden, re-calculate its content.
        onHiddenChange(model, isHidden) {
            if (isHidden) return;
            this.onDeviceResize();
            Adapt.log.info("respImgView: :visible event detected.");
        }

        postRender() {
            
            // If the RespImgView starts off hidden after it's rendered, it will need to be resized once it becomes visible again.
            this.model.set("_isHidden", this.$el.is(":hidden"));

            _.delay(() => {
                this.recalculate();
            }, 0);

            /**
             * Whenever this mutation is triggered, check if this view is hidden, and update the "_isHidden" value.
             */
            let callback = _.debounce(() => {
                this.model.set("_isHidden", this.$el.is(":hidden"));
            }, HIDDEN_OBSERVER_DEBOUNCE);
            
            this._hiddenObserver = new MutationObserver(callback);

            // Find the closes component or notify element.
            let closestEl = this.$el.closest(".component")[0];
            if (!closestEl) closestEl = this.$el.closest(".notify")[0];

            this._hiddenObserver.observe(closestEl, {
                attributes: true,
                subtree: true
            });
        }

        renderItems() {
            
            let jsonPath = this.model.get("src").replace(/\.svg$/, ".json");

            $.ajax({
				dataType: "json",
				url: jsonPath,
				cache: true,
				success: (data) => {

                    this.model.set("_svgWidth", data._width);
                    this.model.set("_svgHeight", data._height);

                    // Render styles.
                    let css = document.createElement('style');
                    css.type = "text/css";
                    let styles = "";
                    _.each(data._styles, (props, className) => {
                        styles += "#" + this.model.get("_id") + " " + className + "{";
                        _.each(props, (e, i) => {
                            styles += i + ":" + e + ";";
                        });
                        styles += "} ";
                    });
                    css.appendChild(document.createTextNode(styles));
                    this.$el[0].appendChild(css);

                    // Render items.
                    _.each(data._items, (item) => {
                        
                        let $item = $("<div>");
                        $item.addClass("rimg__item");
                        $item.addClass("align-" + (item.align || "center"));
                        $item.css({
                            top: item.top + "%",
                            left: item.left + "%",
                            width: item.width + "%",
                            height: item.height + "%",
                            transform: item.transform
                        });
                        let $itemInner = $("<div>");
                        $itemInner
                            .attr("class", item.class) // Apply any textbox classes to the INNER element
                                                       //(so as to not affect the em sizing of the .rimg__item parent).
                            .addClass("rimg__item-inner")
                            .html(item.html);
                            
                        this.$(".rimg__inner").append($item);
                        $item.append($itemInner);
                    });

                    // Only start listening now.
                    this.setUpRespImgEventListeners();

                    // Initial calculation. Don't need to wait for SVG to load, as its width is dictated by external elements anyway.
                    this.postRender();
				},
				fail: () => {
					throw "respImgView.js: Could not load \"" + jsonPath + "\".";
				}
			});
        }
    };

    return RespImgView;
});
