define([
    'core/js/adapt'
], function(Adapt) {

    /**
     * Keeps track of the Lottie animation object and updates model values accordingly.
     */

    class LottieModel extends Backbone.Model{

        defaults() {
            return {
                "anim": null,               // Lottie animation object. LottieView assigns it once it has been loaded.
                
                // Settings passed through Handlebars helper
                // -----------------------------------------
                "loops": 1,                 // How many times the animation should play before stopping (supports decimal values).
                "_controlsType": "none",    // What style of controls the Lottie should have.
                "isDecorative": true,       // TBA: Non-decorative animations to have proper video controls.
                
                // Calculated
                // -----------------------------------------
                "_isPlaying": true,
                "_duration": 0,             // Animation duration in seconds.
                "_loopsNum": 0,             // How manu full loops the animation has completed.
                "_isComplete": false,       // == true when the animation pauses after completing all of its loops.
                
                // FIXME: Weird implementation...
                "_resetOnOutview": true,    // If true, the Lottie will reset itself whenever it's scrolled out of view
                "_resetOnOutviewEnabled": true,  // Will be toggled on and off by other scripts.
            }
        }
        
        initialize() {
            this.setUpEventListeners();
        }

        setUpEventListeners() {
            
            // Wait for the animation to load before proceeding.
            this.listenToOnce(this, "change:anim", this.onAnimLoad);
            
            // Playback events.
            this.listenTo(this, "change:_isPlaying", this.onIsPlayingChange);
            this.listenTo(this, "change:_isComplete", this.onIsCompleteChange);
        }

        onIsPlayingChange(model, isPlaying) {
            let anim = this.get("anim");
            if (!anim) return; // Wait for the animation to be loaded before affecting it.

            if (!isPlaying) {anim.pause();}
            else {anim.play();}
        }

        onIsCompleteChange(model, isComplete) {
            
            /**
             * If the animation completes, and has a reset button, don't reset it when it's scrolled out of view anymore.
             * FIXME: This rule could be implemented more clearly.
             */
            if (isComplete) {

                if (this.get("_controlsType") == "basic") {
                    this.set("_resetOnOutviewEnabled", false);
                }
            }
            else {

                if (this.get("_controlsType") == "basic") {
                    this.set("_resetOnOutviewEnabled", true);
                }
            }
        }

        checkResetOnOutview() {
            return this.get("_resetOnOutview") && this.get("_resetOnOutviewEnabled");
        }

        setUpAnimEventListeners() {

            let anim = this.get("anim");

            anim.addEventListener('loopComplete', this.onAnimLoopComplete.bind(this));
            anim.addEventListener('enterFrame', this.onAnimEnterFrame.bind(this));
        }

        onAnimLoopComplete() {
            // Increment the loop count
            this.set("_loopsNum", this.get("_loopsNum") + 1);

            // Pause if at the end of the loop
            // (only works if this.get("loops") is a whole number, otherwise onAnimEnterFrame handles it).
            if (this.get("_loopsNum") >= this.get("loops")) {
                this.pause(this.get("_duration") - 1);
                this.set("_isComplete", true);
            }
        }

        // Returns the location in the animation from 0-1.
        getProgressPercent() {
            let anim = this.get("anim");
            if (!anim) return 0;
            
            return anim.currentFrame / anim.totalFrames;
        }

        onAnimEnterFrame(event) {

            // Only respond to frame changes while playing.
            if (!this.get("_isPlaying")) return;

            // Note: event.currentTime = the current FRAME the animation's on.

            let currentProgressPercent = event.currentTime / event.totalTime;
            this.set("_currentProgressPercent", currentProgressPercent);

            // Pause the animation when the final loop is reached (only if "loops" isn't a whole number).
            // (If "loops" is = to 1.5, the animation will play 1.5 times).
            if (this.get("loops") % 1 !== 0 && this.get("_loopsNum") + currentProgressPercent > this.get("loops")) {

                let duration = this.get("_duration");
                let pauseTime = duration * (this.get("loops") % 1) - 1;
                if (pauseTime < 0) pauseTime = duration - 1; // If the time is < 0, then pause at the end of the duration.

                this.pause(pauseTime);
                this.set("_isComplete", true);
            }
        }

        onAnimLoad(model, anim) {
            
            if (!anim) throw "lottieModel: an unexpected error has occured (Lottie animation object is falsy).";

            this.set("_duration", anim.getDuration() * 1000); // Returns duration in seconds; needs to be converted to milliseconds.

            // If an external script has changed the value of _isPlaying, then play the animation as soon as it's ready.
            this.onIsPlayingChange(this, this.get("_isPlaying"));

            this.setUpAnimEventListeners();
        }

        pause(time) {
            this.set("_isPlaying", false);
            if (time !== undefined) {
                this.get("anim")?.goToAndStop(time, false); // Stop the animation at the specific time (in milliseconds).
            
            }
        }

        play(time) {
            this.set("_isPlaying", true);
            if (time !== undefined) {
                this.get("anim")?.goToAndPlay(time, false); // Play the animation at the specific time (in milliseconds).
            }
        }

        toggle() {
            this.set("_isPlaying", !this.get("_isPlaying"));
        }

        /** Legacy */
        isLoopsComplete(){
            return Boolean(this.get("_isComplete"));
        }

        isComplete() {
            return Boolean(this.get("_isComplete"));
        }

        isDecorative(){
            return Boolean(this.get("isDecorative"));
        }

        reset() {
            this.set("_loopsNum", 0);           // Reset loop counter.
            this.set("_isComplete", false);
            this.play(0);
        }
    }

    return LottieModel;
});
