define([
  'core/js/adapt',
  'components/adapt-contrib-media/js/adapt-contrib-media'
], function(Adapt, Media) {

  /**
   * This extension was made to keep core Adapt's default media player while adding new features.
   * 
   * WARNING: Extends adapt-contrib-media.
   * Any changes to the original will affect this.
   */

  function _stringToSeconds(str) {
    let a = str.split(":");
    let l = a.length;

    if (l == 0 || a > 3) throw "adapt-mediaPlus: Invalid time string value: " + str;

    let seconds = parseInt(a[l-1], 10);
    if (l > 1) seconds += parseInt(a[l-2], 10) * 60;
    if (l > 2) seconds += parseInt(a[l-3], 10) * 60 * 60;

    return seconds;
  }

  class MediaPlusModel extends Media.model {  

    initialize(...args) {
      this.setUpModelData();
      super.initialize(...args);
    }

    setUpModelData() {
      this.setUpDefaultData();

      // Determine the seconds value of each _chapters._items[*].time
      // Note: will not check if the time is outside of the bounds of the video.
      let chapters = this.get("_chapters");
      if (chapters?._isEnabled) {
        _.each(chapters._items, (_items) => {
          _items._time = _stringToSeconds(_items.time);
        });
      }
    }

    /**
     * Set specific values automatically (hardcoded to DD specifications).
     */
    setUpDefaultData() {
      let defaults = {};

      // Extend root properties.
      defaults = {
        _allowFullScreen: 	  true,
        _setCompletionOn: 	  "play",
        _showVolumeControl:   true,
        _pauseWhenOffScreen:  false, // Changed.
      };

      // Extend transcript properties.
      defaults._transcript = _.extend({
        _inlineTranscript: 			      true,
        _externalTranscript: 		      false,
        inlineTranscriptButton:       "Transcript",
        inlineTranscriptCloseButton:  "Close Transcript",
        transcriptLinkButton: 		    "Transcript",
        transcriptLink: 				      ""
      }, this.get("_transcript"));

      // Extend player options.
      defaults._playerOptions = _.extend({
        alwaysShowControls:               true,
        toggleCaptionsButtonWhenOnlyOne:  true,
        iPadUseNativeControls:            true,
        iPhoneUseNativeControls:          true,
        AndroidUseNativeControls:         true,
        enableProgressTooltip:            false // Not implemented in the version of MediaElement being used.
      }, this.get("_playerOptions"));

      this.set(defaults);
    }
  };

  class MediaPlusView extends Media.view {

    events() {
      return _.extend({}, super.events, {
        "click .js-mediaplus-chapter-item-time" : "onChapterItemClick"
      });
    }

    initialize(...args) {
      super.initialize(...args);
    }

    onPlayerReady(...args) {
      super.onPlayerReady(...args);

      // Hide the time float tooltip
      // (MediaElement has a bug where it appears with 0:00 on the left side if the user mouses over before video metadata has been loaded).
      this.$(".mejs-time-float").addClass("u-display-none");
      this.renderChaptersTimeFloat();
      this.renderChaptersMenu();
      // Un-hide the time float element once the video's duration is known.
      this.mediaElement.addEventListener('loadedmetadata', () => {
        this.$(".mejs-time-float").removeClass("u-display-none");
        this.renderChaptersDividers();
      });
    }

    setupEventListeners(...args){
      super.setupEventListeners(...args);

      // If no player (adapt-contrib-media doesn't initialize the player on mobile devices), then don't track the time float element.
      if (!this.mediaElement.player) return;
      
      // If no chapters, then no need to monitor the time float element.
      if (!this.model.get("_chapters")?._isEnabled) return;

      this.listenTo(this.model, "change:_timeFloatTime", this.onTimeFloatTimeChange);
      this.setUpTimeFloat();
    }

    onTimeFloatTimeChange(model, time) {
      
      // Determine the chapter title.
      let title = "";
      _.find(this.model.get("_chapters")._items, (item) => {
        if (item._time <= time) {
          title = item.title;
        }
        else return true;
      });

      this.$(".mejs-time-float-title").text(title);
    }

    // Fallback: In case a new update changes the capitalization of the base function.
    setUpEventListeners(...args) {
      throw "adapt-mediaPlus: Recent Adapt update has changed \"setupEventListeners\" to \"setUpEventListeners\". Please update the script to use the right capitalization.";
    }

    /**
     * Show the title of the current chapter in the time float element that appears when the user scrubs the video.
     */
    setUpTimeFloat() {

      let $timeFloat = this.$(".mejs-time-float");
      let $timeFloatCur = $timeFloat.find(".mejs-time-float-current");

      let observer = new MutationObserver((event) => {
        this.model.set("_timeFloatTime", _stringToSeconds($timeFloatCur.text()));
      });

      // Whenever the time in the floating tooltip changes, update the chapter title.
      observer.observe($timeFloatCur[0], {
        childList: true
      });
    }

    /**
     * Renders some lines over the timeline to make it appear like it was split into multiple segments.
     */
    renderChaptersDividers() {

      let chapters = this.model.get("_chapters");
      if (!chapters) return;

      let $rail = this.$(".mejs-time-rail");
      let duration = this.mediaElement.duration;

      _.each(chapters._items, (item, i) => {
        if (i == 0) return;

        let $divider = $("<div>", {class: "mejs-time-rail-divider"});

        $divider.css("left", ((item._time / duration) * 100) + "%");
        $rail.append($divider);
      });
    }

    renderChaptersTimeFloat() {
      this.$(".mejs-time-float").prepend($("<span>", {class: "mejs-time-float-title"}));
    }

    renderChaptersMenu() {
      if (!this.model.get("_chapters")?._isEnabled) return;

      var data = this.model.toJSON();
      var template = Handlebars.templates['mediaPlusChapters'];
      this.$(".component__widget").append(template(data));
    }

    onChapterItemClick(event) {
      let time = $(event.target).data("time");

      this.mediaElement.play();
      this.mediaElement.setCurrentTime(time);
    }
  };

  return Adapt.register('mediaplus', {
    model: MediaPlusModel,
    view: MediaPlusView
  });

});
