define([
    'core/js/adapt',
    "./componentRowModel"
], function(Adapt, ComponentRowModel) {

    class ComponentRowCollection extends Backbone.Collection{

        initialize(models, options) {
            this.listenTo(Adapt, 'remove', this.reset);
            if (!options || !options.blockModel) return;
            this._blockModel = options.blockModel;
            this.repopulate();
        }

        repopulate() {
            this.reset();
            if (!this._blockModel) return;

            let blockModel = this._blockModel;
            let componentModels = blockModel.getChildren().models;
        
            // Automatically determine which components to pair up.
            var curPair = [];
            
            // For each componentModel...
            _.each(componentModels, (componentModel) => {
                
                var _layout = componentModel.get("_layout");
            
                // Wrap isolated "full" and "_isSingle" layouts (they're the only components on their "row").
                if (this.isComponentSingleton(componentModel))
                {
                    // Fallback:
                    // If the previous component wasn't detected as singleton, make it singleton too.
                    if (curPair.length) {
                        this.pairComponents([curPair[0]], blockModel);
                    }

                    // Wrap the singleton component.
                    this.pairComponents([componentModel], blockModel);
                    
                    // Clear the pair for the next row.
                    curPair = [];

                    return true;
                }

                // First child of the pair.
                if (curPair.length == 0)
                {
                    curPair.push(componentModel);
                    return true;
                }

                // Second child of the pair. Wrap both elements if the _layouts are different, and neither is a singleton.
                if (_layout != curPair[0].get("_layout"))
                {
                    curPair.push(componentModel);

                    this.pairComponents(curPair, blockModel);

                    // Clear the pair for the next row.
                    curPair = [];
                    return true;
                }

                // If the next component has the exact same layout as the previous one, then treat the first one as a singleton
                // (mainly done for stickyBlock and other niche customs compatibility).
                // Example:
                /*
                    full
                    left <--
                    left <--
                    left - right
                    left <--
                    left <--
                    left - right

                */
                this.pairComponents([curPair[0]], blockModel); // Pair the previous one.

                // Clear the pair for the next row.
                curPair = [];
                curPair.push(componentModel);
            });

            // Fallback: If there's any stragglers at the end, pair them too.
            if (curPair.length) {
                this.pairComponents(curPair, blockModel);
            }
        }

        // Returns true if the component is "full", or has been marked as a "singleton".
        isComponentSingleton(componentModel){

            // All "full" components are singletons.
            if (componentModel.get("_layout") == "full") return true;

            // Non-"full" components can be manually set to be isolated. 
            var cr = componentModel.get("_componentRow");
            if (!cr) return false;
            
            return Boolean(cr._isSingle);
        }

        pairComponents(componentArray, blockModel) {
            
            let crModel = new ComponentRowModel({
                _index: this.length,
                _children: new Backbone.Collection(componentArray),
                _parent: blockModel,
                _prevSibling: this.last() || undefined,
                _isEnabled: blockModel.get("_componentRow").get("_isEnabled") !== false
            });

            this.add(crModel);

            return crModel;
        }
    }

    return ComponentRowCollection;
});
