define([
  "core/js/adapt",
  "./themePageView",
  "./themeArticleView",
  "./themeBlockView",
  "./themeComponentView",
  "./themeView"
], function(Adapt, ThemePageView, ThemeArticleView, ThemeBlockView, ThemeComponentView, ThemeView) {

  function onDataReady() {
    $("html").addClass(Adapt.course.get("_courseStyle"));

    // Log which overrides were applied (by the grunt "prop-override" task).
    let propOverrides = Adapt.course.get("_propOverride")?._items || [];
    Adapt.log.debug("Build overrides: ", propOverrides);
  }

  function onPostRender(view) {
    var model = view.model;
    var theme = model.get("_didasko");

    if (!theme) return;

    switch (model.get("_type")) {
      case "page":
        new ThemePageView({ model: new Backbone.Model(theme), el: view.$el });
        break;
      case "article":
        new ThemeArticleView({ model: new Backbone.Model(theme), el: view.$el });
        break;
      case "block":
        new ThemeBlockView({ model: new Backbone.Model(theme), el: view.$el });
        break;
      case "component":
        new ThemeComponentView({ model: new Backbone.Model(theme), el: view.$el });
        break;
      default:
        new ThemeView({ model: new Backbone.Model(theme), el: view.$el });
    }
  }

  Adapt.on({
    "app:dataReady": onDataReady,
    "pageView:postRender articleView:postRender blockView:postRender componentView:postRender": onPostRender//,
    //"blockView:preRender": onBlockViewPreRender
  });
});
