define([
    'core/js/adapt',
    'extensions/adapt-contrib-pageLevelProgress/js/completionCalculations'
], function(Adapt, CompletionCalculations) {

    /**
     * FIXME: Doesn't support blocks with _isAvailable: false.
     */

    class SideNavProgressView extends Backbone.View {

        className() {
            return "sidenav__progress";
        }

        events() {
            return {
                "click" : "onClick"
            }
        }

        initialize(){
           
            this.setupEventListeners();
            this.render();
        }

        setupEventListeners() {
            this.listenTo(Adapt.blocks, 'change:_isComplete', this.onBlockCompletionChange);
        }

        render(){
            var template = Handlebars.templates['sideNavProgress'];
            this.$el.html(template(/*this.model.toJSON()*/));
        }

        onBlockCompletionChange(){

            /*if (!Adapt.location._currentId) return;

            var currentModel = Adapt.findById(Adapt.location._currentId);
            var completionState = {
                currentLocation: CompletionCalculations.calculatePercentageComplete(currentModel),
                course: CompletionCalculations.calculatePercentageComplete(Adapt.course)
            };

            console.log(completionState);*/

            let completionPercent = 0;
            Adapt.blocks.each((model) => {
                completionPercent += model.get("_completionPercent");
            });
            completionPercent /= Adapt.blocks.length;

            this.$(".sidenav__progress-indicator-bar").css({
                height: completionPercent + "%"
            });
        }

        // Whenever the progress is clicked, scroll the user to the first incomplete component.
        onClick() {

            this.scrollToFirstIncompleteComponent();
        }

        scrollToFirstIncompleteComponent() {

            let incompleteComponent = Adapt.components.find((model) => {
                return !model.get("_isComplete") && model.getParent().get("_isAvailable"); // Its block must be available (rendered).
            });
            if (!incompleteComponent) return;

            Adapt.navigateToElement("." + incompleteComponent.get("_id"), {duration: 400});
        }

        scrollToFirstIncompleteBlock() {

            let incompleteBlock = Adapt.blocks.find((model) => {
                return !model.get("_isComplete") && model.get("_isAvailable"); // Must be available (rendered).
            });
            if (!incompleteBlock) return;

            Adapt.navigateToElement("." + incompleteBlock.get("_id"), {duration: 400});
        }
        
    };

    return SideNavProgressView;
});
