define([
    'core/js/adapt',
    './playbackControlsView',
    './playbackControlsModel'
], function(Adapt, PlaybackControlsView, PlaybackControlsModel) {

    /**
     * This extension is a partial. Components which require it will need to load and append it themselves.
     * If no component uses it, then it won't do anything (though, it'll still be loaded in by Adapt, which is a shame.).
     * Note:
     * 
     * =====
     * This isn't implemented like the standard Adapt extension.
     * However, there didn't seem to be any other way to implement a partial element that can be used by multiple components.
     */

    return function(modelSettings) {
        
        var model = new PlaybackControlsModel(modelSettings);
        var view = new PlaybackControlsView({
            model: model
        });

        //model.$el = view.$el; // FIXME: Bad. But I need some way of getting the element the view creates.
        return view;
    }
});
