define([
  'core/js/adapt',
  'core/js/views/componentView',
  'libraries/codemirror/lib/codemirror',
  'libraries/codemirror/mode/htmlmixed/htmlmixed'
], function(Adapt, ComponentView, CodeMirror) {

  class TryHtmlView extends ComponentView {

    events() {
      return {
        "click .js-tryhtml-ui-update" :     "onUpdateClick",
        "click .js-tryhtml-ui-toggleanswer" : "onToggleAnswerClick",
        "mouseup .js-tryhtml-content-user": "onCodeMirrorUp"
      }
    }

    initialize() {
      this.setUpModelData();
      ComponentView.prototype.initialize.call(this);
    }

    setUpModelData() {
      this.model.set("_tryHtml", Adapt.course.get("_tryHtml"));
      this.model.set("_isShowingAnswer", false);
    }

    setUpEventListeners() {

      this.listenTo(this.model, "change:_isShowingAnswer", this.onShowingAnswerChange);

      this.$(".js-tryhtml-iframe-user").on("load", this.onIframeUserLoad.bind(this));
      this.$(".js-tryhtml-iframe-correct").on("load", this.onIframeCorrectLoad.bind(this));
    }

    preRender() {
      
    }

    postRender() {

      let lang = this.model.get("_lang"); // Allow for specific languages to be specified in the JSONSynt.
      let mode = "htmlmixed";
      
      switch (lang) {

        case "html":
          mode = "htmlmixed";
          break;

        case "css":
        case "xml":
        case "php":
          mode = lang;
          break;
        
        case "js":
          mode = "javascript";
          break;

        default:
          Adapt.log.error("tryHtmlView.js: \"_lang\" cannot be \"" + lang + "\" (this language is not implemented).");
      }

      this.codeMirror = CodeMirror.fromTextArea(this.$(".js-tryhtml-content-textarea")[0], {
        theme:      "prettify", // Custom theme override to match CodeBox syntax highlighting.
        tabSize: 2,
        tabUnit: 2,
        lineWrapping: true,
        resetSelectionOnContextMenu: false,
        //viewportMargin: Infinity,
        mode: mode
      });

      /**
       * BUGFIX:
       * ---------------
       * In codemirror.js, on line 9616, there is a window.scrollTo() call. Adapt has overridden this function, and codemirror calling
       * it unintentionally causes the entire page to scroll to the top (due to window.scrollY being equal to 0) when the user 
       * opens the context menu.
       * 
       * Unfortunately, this call to windows.scrollTo() can't be prevented, but it can be tricked to instead scroll nowhere.
       * To resolve this, window.scrollY is explicitly set (giving codemirror.js the correct value to scroll to).
       */
      this.codeMirror.on("contextmenu", (codeMirror, event) => {
        let scrollY = Adapt.scrolling.$app.scrollTop() || Adapt.scrolling.$html.scrollTop();
        window.scrollY = scrollY;
      });

      this.setUpEventListeners();
    }

    /**
     * Performs any "_onSubmit" modifiers on the provided HTML and returns it.
     * @param {*} html 
     */
    setUpIframeHTML(_html) {
      
      let html = _html;
      let onSubmit = this.model.get("_onSubmit");

      html = (onSubmit?.prepend || "") + html + (onSubmit?.append || "");

      // EPUB functionality. Prepend a comment that will tell the html2xhtml.js script to ignore this html when validating it for XHTML.
      if (Adapt.isEpubMode()) { 
        html = "<!--!IGNORE-XHTML!-->" + html;
      }

      return html;
    }

    /**
     * Whenever the user clicks the update button, update the iframe with their current code.
     */
    onUpdateClick(event) {

      // Don't allow updating the user code while the update button is disabled.
      if (this.model.get("_isShowingAnswer")) return;

      // Scroll the output box into view.
      Adapt.navigateToElement(this.$(".js-tryhtml-iframe-container"), {
        duration: 400,
        align: "inview"
      });

      let $iframe = this.$(".js-tryhtml-iframe-user");
      $iframe.fadeOut(() => {

        // Prepend and append the "_after" values.
        let html = this.setUpIframeHTML(this.codeMirror.getValue());
        $iframe.contents().find("body").html(html);
      });
      $iframe.fadeIn();
    }

    onShowingAnswerChange(model, isShowingAnswer) {

      // Scroll the answer/input element into view.
      Adapt.navigateToElement(this.$(".js-tryhtml-content-correct"), {
        duration: 400,
        align: "inview"
      });

      this.$(".js-tryhtml-widget").toggleClass("show-correct-answer");

      let tryHtml = this.model.get("_tryHtml");

      this.$(".js-tryhtml-ui-title")
        .text(!isShowingAnswer ? tryHtml._user.title : tryHtml._correct.title);
      
      this.$(".js-tryhtml-ui-toggleanswer")
        .text(isShowingAnswer ? tryHtml._user.buttonText : tryHtml._correct.buttonText);

      this.$(".js-tryhtml-ui-update")
        .text(isShowingAnswer ? tryHtml._update.disabledText : tryHtml._update.buttonText)
        .toggleClass("is-disabled", isShowingAnswer);
    }

    /**
     * Fade in/out the correct code when the user clicks the button.
     */
    onToggleAnswerClick() {
      this.model.set("_isShowingAnswer", !this.model.get("_isShowingAnswer"));
    }

    /**
     * Set up initial view for the user-input iframe
     * (the one which will be updated repeatedly with the user's input).
     */
    onIframeUserLoad() {
      // The content can start once the iframe has loaded.
      this.setReadyStatus();

      let $iframe = this.$(".js-tryhtml-iframe-user");
      $iframe.contents().find("body").html(this.setUpIframeHTML(this.model.get("_user").output));
    }

    onIframeCorrectLoad() {
      let $iframe = this.$(".js-tryhtml-iframe-correct");
      let height = $iframe.contents()
                    .find("body")
                    .html(this.setUpIframeHTML(this.model.get("_correct").output))
                    .parent().height();

      // Set the height of both iframe elements based on the height of the correct output.
      this.$(".js-tryhtml-iframe-container").height(height + 40);
    }

    //#region Bugfix: CodeMirror often loses focus when initially clicked if this isn't added.
    onCodeMirrorUp(event) {

      // FIXME: This needs to be done.
      // Bugfix: right clicking on the TextArea (for some reason) scrolls the page to the very top (in Chrome). This prevents that.
      
      _.delay(() => {
        this.codeMirror.focus();
      }, 1);
    }
    //#endregion
  }
  
  TryHtmlView.template = "tryHtml";
  return TryHtmlView;
});
