define([
  'core/js/adapt',
  './activatorTargetModel'
], function(Adapt, ActivatorTargetModel) {

  class ActivatorElementTargetModel extends ActivatorTargetModel {

    defaults() {
      return _.extend(super.defaults(), {
        "_name": ""     // name attribute(s) of one or more elements in the parent block that this should target.
      });
    }

    initialize() {
      
      // This targetModel needs to wait for its targetted element(s) to be rendered prior to declaring itself "ready".
      // FIXME: At this time, the script assumes the element will be ready by the time the activator is clicked.
      // (Which in all current content, is the case).
      this.setReadyStatus();
    }

    onToggle(isActive) {
      Adapt.annotation.toggleEl(this.$getTargets(), isActive);
    }

    // Find the element(s) in this block with the provided name.
    $getTargets() {
      let $targets = $();
      let names = this.get("_name").split(",").map(e => {return e.trim();});

      let parent = this.get("_activatorModel");
      let blockView = Adapt.findViewByModelId(parent.getParent().get("_id"));

      _.each(names, (name) => {
        $targets = $targets.add(blockView.$(`[name="${name}"]`));
      });

      return $targets;
    }
  }

  return ActivatorElementTargetModel;

});
