define([
  'core/js/adapt'
], function(Adapt) {

  class ReorderItemModel extends Backbone.Model {

    defaults() {
      return {
        _index: -1,              // The correct + JSON index (this item will be correct if submitted when at this index).
        _userIndex: -1,          // The current index of the item as it appears onscreen (the index the user has dragged it to).
        _isCorrect: false,
        _isLocked: false,        // If true, the draggable will be immoveable.
        _isVisited: false,       // == _.includes(_visitedIndexes, _userIndex).
        _visitedIndexes: [],     // Array of all _userIndexes that have been submitted up until this point.

        _isGrabbed: false,
        _positionTop: 0,         // Value used while an item is being dragged.
      }
    }

    initialize() {
      this.setUpEventListeners();
    }

    setUpEventListeners(){
      this.listenTo(this, "change:_userIndex", this.onUserIndexChange);
    }

    /**
     * Whenever the userIndex changes, check if the item has already been visited, and toggle _isVisited.
     * @param {*} model 
     * @param {*} userIndex 
     */
    onUserIndexChange(model, userIndex) {
      let isVisited = _.includes(this.get("_visitedIndexes"), userIndex);
      this.set("_isVisited", isVisited);

      // DD specific: Update the value of isCorrect accordingly.
      if (!isVisited) {
        // Not yet visited; value of _isCorrect is unknown.
        this.set("_isCorrect", undefined);
        return;
      }
      this.set("_isCorrect", this.get("_index") == this.get("_userIndex"));
    }

    /**
     * Sets the current _userIndex to visited.
     */
    setVisited() {

      let visitedIndexes = this.get("_visitedIndexes");
      let userIndex = this.get("_userIndex");
      if (_.includes(visitedIndexes, userIndex)) return;
      visitedIndexes.push(userIndex);
    }

  }

  return ReorderItemModel;

});
