define([
  'core/js/adapt',
  'core/js/models/itemsQuestionModel',
  './imgFinderItemModel'
], function(Adapt, ItemsQuestionModel, ImgFinderItemModel) {

  /**
   * ImgFinder
   * ----------------------------------------------------
   * 
   * Important:
   * - Avoid overlapping _items if it can be helped.
   * 
   * Notes:
   * - Uses the ItemsQuestionModel as a base.
   *   Each item only has two validation states (the user has found and outlined it, or the user hasn't). Because of this,
   *   the user's interactions can be recorded as if they were for an MCQ (with checkboxes) as far as SCORM is concerned.
   *   (In the back-end) Outlining an item is identical to checking the checkbox on for an MCQ item.
   *
   * - If multiple outlines are placed over the same item, only the first will be counted.
   *   All others will be auto-removed (unless they're also touching another item, in which case the process will repeat). 
   * 
   *
   * TODO:
   * - If an item with "_shouldBeSelected: false" gets outlined, it should show some kind of unique state/feedback.
   * - Show the FEEDBACK_HINT.
   * 
   * 
   * FIXME:
   * - Not compatible with screen readers + colour blindness at the moment.
   * - Dragging the outlines is a bit limited due to the containment.
   * - Partly correct feedback not working.
   */

  // This feedback hint is shown if the user doesn't make use of the click-and-drag and/or removal functionality of the widget.
  const FEEDBACK_HINT = "<p><em>(You can click an existing outline to remove it, or you can drag it to reposition it).</em></p>";
  const MAX_GUESSES = 20; // Only store this many guess objects before removing the older ones.

  class ImgFinderModel extends ItemsQuestionModel {

    initialize(...args) {
      super.initialize(...args);

      this.setupModelData();
      this.setupEventListeners();
    }

    /**
     * Override the function from ItemsComponentModel to allow us to use our own model for the items.
     */
    setUpItems() {
      const items = this.get('_items') || [];
      items.forEach((item, index) => {
        item._index = index;
        item._parent = this;
      });
      this.setChildren(new Backbone.Collection(items, { model: ImgFinderItemModel }));
    }

    setupModelData() {
      
      // Add a collection for storing the ImgFinderOutlines.
      let outlines = new Backbone.Collection();
      this.set("_outlines", outlines);

      // The _guesses array will store all locations where an outline has been incorrectly placed (and submitted).
      this.set("_guesses", []);

      this.set("_hasShownFeedbackHint", false);
    }

    getGuesses() {
      return this.get("_guesses");
    }

    addGuess(guess) {
      let g = this.get("_guesses");
      g.push(guess);

      // Remove excess guesses.
      if (g.length > MAX_GUESSES) g.shift();

      this.set("_guesses", g);
    }

    setupEventListeners(...args) {
      if (super.setupEventListeners) super.setupEventListeners(...args);

      this.listenTo(this.get("_outlines"), {
        "add": this.onOutlinesModified,
        "remove": this.onOutlinesModified
      });
    }

    addOutline(outlineModel) {
      this.get("_outlines").push(outlineModel);
    }

    /** 
     * Whenever an outline is added/removed, check whether the user can submit again.
     */
    onOutlinesModified() {
      this.checkCanSubmit();
    }

    canSubmit() {

      // Can submit as long as there's at least one outline present.
      return this.get("_outlines").length >= 1;
    }

    /**
     * How outlines are assigned to items:
     * -----------------------------------
     * - All outlines are looped through, and each outline stores an array of references to all items that it successfully covers, 
     *   sorted by proximity.
     * 
     * - Once that's done, all outlines are looped through. If an outline was detected by multiple items, it is given to the
     *   one that it's closest to (proximity wise). All other items that have it in their arrays remove it.
     * 
     * - Each item then pairs itself with the nearest outline thats still remaining in its array.
     * 
     * @returns 
     */
    validateOutlines() {

      let outlines = this.get("_outlines");
      
      // First, have each outline find and store an array of all _items it's currently covering.
      outlines.each((outline) => {
        outline.calcOverlappedItems();
      });

      // Then, for each item, check which outline determined itself to be closest to it.
      this.getChildren().each(itemModel => {

        if (itemModel.get("_isLocked")) return; // Don't check locked items (they're already been completed).

        let myOutline = null;
        let myProximity = Infinity;

        outlines.each((outline) => {

          if (outline.get("_isLocked")) return; // This outline has already been locked by another item.

          let data = outline.getOverlappingItemData(itemModel);
          
          // This outline is not overlapping this itemModel.
          if (!data) return; 

          // This outline is (one of the outlines) overlapping this itemModel.
          // Store it if it's closer than the previous overlapping outlines checked.
          if (data.proximity < myProximity) {
            myProximity = data.proximity;
            myOutline = outline;
          }
        });

        // Store a reference to the outline that was closest to this itemModel.
        if (!myOutline) {
          itemModel.set("_isActive", false);
          return;
        }

        myOutline.pairWithItem(itemModel);
        itemModel.set("_isActive", true);
      });

      // Remove all outlines which are still un-locked, and are also covering items.
      // (Aka, the user put more than one outline over an item).
      outlines.each(outline => {
        if (!outline) return; // Skip if already removed.

        if (outline.get("_isLocked")) return;
        if (!outline.get("_myItems").length) return;

        outline.remove();
      });

      // For all remaining items, add their bounding-box to the "_guesses" collection.
      outlines.each(outline => {
        this.addGuess(outline.getGuessObj());
        outline.set("_isIncorrect", true);
      });
    }

    
    /**
     * Correct:         ALL and ONLY the _shouldBeSelected items have been outlined.
     * PartlyCorrect:   ONLY _shouldBeSelected items have been outlined, but not all of them.
     * Incorrect:       NO _shouldBeSelected items have been outlined.
     */
    isCorrect() {

      this.validateOutlines();
      let outlines = this.get("_outlines");

      let correctOutlines = [];
      let incorrectOutlines = [];

      outlines.each(outline => {

        if (outline.get("_isCorrect")) {
          correctOutlines.push(outline);
        }
        else {
          incorrectOutlines.push(outline);
        }
      });
      
      const props = {
        
        _numberOfIncorrectAnswers: incorrectOutlines.length, 
        _numberOfCorrectAnswers: correctOutlines.length,
        _numberOfRequiredAnswers: this.getChildren().length,

        _isAtLeastOneIncorrectSelection: Boolean(incorrectOutlines.length),
      };

      this.set(props);

      const hasRightNumberOfCorrectAnswers = (props._numberOfCorrectAnswers === props._numberOfRequiredAnswers);
      const hasNoIncorrectAnswers = !props._isAtLeastOneIncorrectSelection;

      return hasRightNumberOfCorrectAnswers && hasNoIncorrectAnswers;
    }
    
    isPartlyCorrect() {
      return this.get('_numberOfCorrectAnswers') > 0;
    }

    setupFeedback() {
      
      if (!this.has('_feedback')) return;

      if (this.get('_isCorrect')) {
        this.setupCorrectFeedback();
        return;
      }

      // FIXME: Hardcoded.
      if (!this.get("_hasShownFeedbackHint")) {
        this.set("_hasShownFeedbackHint", true);
        
        console.log("FIXME");
        console.log("FIXME");
        console.log("FIXME");
        console.log("FIXME");
        console.log("FIXME");
        console.log("FIXME");
        console.log("FIXME");

        // Add the feedback hint after the incorrect feedback.
        let feedback = this.get("_feedback");
        if (feedback._partlyCorrect.final) this.get("_feedback")._partlyCorrect.body += FEEDBACK_HINT;
        feedback._incorrect.final += FEEDBACK_HINT;

        this.set("_feedback", feedback);
      }

      if (this.isPartlyCorrect()) {
        this.setupPartlyCorrectFeedback();
        return;
      }

      this.setupIncorrectFeedback();
    }
  }


  return ImgFinderModel;

});
