define([
    'core/js/adapt',
    "./tooltipPopupView"
], function(Adapt, TooltipPopupView) {

    // FIXME: Some of the functionality in this view is somewhat redundant due to the new Adapt.createTooltip function I added.

    class TooltipView extends Backbone.View {

        tagName() {
            return "span";
        }

        className() {
            return "tooltip";
        }

        events() {
            return {
                "click .js-tooltip" : "onClick"
            }
        }

        initialize() {
            
            this.setUpEventListeners();
            this.render();
        }

        setUpEventListeners() {
            this.listenTo(this.model, "change:_isActive", this.onActiveChange);
        }

        render() {
            this.$el.html(new Handlebars.SafeString(Handlebars.templates['tooltip'](this.model.toJSON())).string);
        }

        onClick(event) {
            // Prevent clicking the tooltip also toggling the navbar in Online Bookshelf.
            if (Adapt.config.get("_isEpub")) {event.stopPropagation();}

            Adapt.createTooltip(this.$(".js-tooltip-target"), this.model.attributes);
        }

        onActiveChange(model, isActive) {

            this.$(".tooltip__content").toggleClass("is-active", isActive);

            if (isActive) {

                Adapt.createTooltip(this.$el, this.model.attributes);
                return;
            }

            /*if (this.tooltipPopupView) {
                this.tooltipPopupView.remove();
            }*/
        }

    };

    return TooltipView;
});
