define([
  'core/js/adapt',
  'core/js/models/componentModel',
  './codeAnimInspectorVarModel'
], function(Adapt, ComponentModel, CodeAnimInspectorVarModel) {

  class CodeAnimModel extends ComponentModel {

    // Used to set model defaults
    defaults() {

      // Extend from the ComponentModel defaults
      return ComponentModel.resultExtend('defaults', {
        "_timelineFrames": undefined		// Collection of frame objects which will be used to make the codeAnimation. 
      });
    }

    initialize() {

		ComponentModel.prototype.initialize.call(this);
		this.setUpEventListeners();
		this.setUpAnimation();
    }

    // Set up the animation of the code "compiling".
    setUpAnimation() {
		var _animation = this.get("_animation");
		if (!_animation || !_animation._isEnabled) return;

		// Enable inspector by default.
		this.get("_animation")._isInspectorEnabled = this.get("_animation")._isInspectorEnabled !== false; 

		// Get the keyframes from the .json.
		// They cannot be used in this state, and must be processed for the animation.
		var keyframes = this.get("_animation")._keyframes;
		if (!keyframes) throw "codeAnim property _animation._keyframes array must be defined";

		var frames = [];

		// Insert a blank frame at the start (has no highlights and prints nothing).
		frames.push({
			line: 0,
			keyframe: null
		});

		// Process keyframes.
		_.each(keyframes, (k) => {

			let allLines = this._stringToNumbers(k.lines);
			
			// Create a frame for each line in the codebox referenced by this keyframe.
			_.each(allLines, (line) => {
				let f = _.extend({}, k, {
					line: line,
				});
				frames.push(f);
			});
		});
	  
		_animation._frames = frames;

		let _maxNameLength = 5; // Used to set the width of the leftmost column.
								// Needs to be done this way due to the way the CSS is set up.
								// (The CSS will try to minimize space used; but as a consequence, it needs this width set manually).

		// Set up inspector vars.
		// ----------------------------------------------
		let inspectorVars = {};
		_.each(_animation._frames, (frame, frameIdx) => {
			
			if (!frame.vars) return; // This frame has no vars being changed in it. Ignore it.
			_.each(frame.vars, (val, key) => {

				// Create a new CodeAnimInspectorVarModel for each unique variable name provided in the keyframes.
				if (!inspectorVars[key]) {
					inspectorVars[key] = new CodeAnimInspectorVarModel({
						name: key
					});
					if (key.length > _maxNameLength) _maxNameLength = key.length;
				}
				inspectorVars[key].addVal(val, frameIdx);
			});
		});

		this.get("_animation")._maxNameLength = _maxNameLength / 1.2;
		_animation._inspectorVars = inspectorVars;
	}

    /**
     * Get an array of all the line numbers that this keyframe will repeat itself for.
     * _keyframes.lines supports strings similar to a word processor; example:
     * "1,3-10,1-2,12"
     */
    _stringToNumbers(str){

		var nums = str.replace(/\s/g, "");
		nums = nums.split(",");

		var allNums = [];
		_.each(nums, function(n){
			
			// "x-y"
			var match = n.match(/(\d+)-(\d+)/);
			if (match) {
				for (let i = parseInt(match[1]); i <= parseInt(match[2]); i++){
					allNums.push(i);
				}
				return;
			}

			// "x"
			match = n.match(/(\d)+/g);
			if (match !== null) {
				allNums.push(parseInt(match));
				return;
			}

			// Neither (error)
			throw "Invalid _stringToNumbers value provided: \"" + str + "\"";
		});

		return allNums;
    }

    setUpEventListeners() {
      //this.listenTo(Adapt, "app:dataReady", this.onDataReady.bind(this));
    }
  }

  return CodeAnimModel;

});
