const { html } = require("cheerio/lib/api/manipulation");

define([
  'core/js/adapt',
  'core/js/views/componentView',
  'extensions/adapt-playbackControls/js/adapt-playbackControls',
  './codeAnimInspectorVarView'
], function(Adapt, ComponentView, PlaybackControls, CodeAnimInspectorVarView) {

  class CodeAnimView extends ComponentView {

    events() {
      return {
        
      }
    }

    initialize() {
      ComponentView.prototype.initialize.call(this);
      this.setUpViewData();
      this.setUpEventListeners();
    }

    setUpViewData() {
      let animation = this.model.get("_animation");
      if (animation && animation._isEnabled) {
        this.$el.addClass("is-animated");
      }
    }

    setUpEventListeners() {

      // If this component is the target of an adapt-activator, then it will be given the _activatorTarget property.
      // Listen for changes to it.
      this.listenTo(this.model.get("_activatorTarget"), "change:_isActive", this.onActivatorActivate);

      //this.listenTo(Adapt, 'device:changed', this.reRender);

      /*this.listenTo(this.model.get('_children'), {
        'change:_isActive': this.onItemsActiveChange,
        'change:_isVisited': this.onItemsVisitedChange,
        'change:_isVisible': this.onItemsVisibleChange
      });*/
    }

    /**
     * Compatibility with adapt-activator component.
     * @param {*} _isActive 
     */
    onActivatorActivate(activatorTarget, _isActive) {
      
      let navigateSettings = {
        duration: 400,
        align: "inview",
        offset: {
          top: -100,
          left: 0
        }
      };

      if (!this.playbackControlsView) {

        if (_isActive) {

          // Scroll the console (which was just activated) into view.
          Adapt.navigateToElement(this.$(".js-codeanim-console"), navigateSettings);
        }

        return; // No playback controls view is being used; no further action needed.
      }

      // Reset the playbackControls.
      if (!_isActive) {
        this.playbackControlsView.model.pause();
        this.playbackControlsView.model.reset();
      }
      else {
        this.playbackControlsView.model.reset();
        this.playbackControlsView.model.play();
      }

      // Slide down / up the console and inspector box.
      this.model.get("_animation")._isActive = _isActive;
      if (_isActive) {

        let $anim = this.$(".js-codeanim-anim");
        $anim.show(0);
        let animHeight = this.$(".js-codeanim-anim").height();
        $anim.hide(0);

        $anim.slideDown();

        // Scroll the playback controls (and the console) into view.
        navigateSettings.offset.height = animHeight + 100; // offset.height = increase the height used for calculating inview.
        Adapt.navigateToElement(this.$(".playbackcontrols"), navigateSettings);
      }
      else {
        this.$(".js-codeanim-anim").slideUp();
      }
    }

    checkIfResetOnRevisit() {
      var isResetOnRevisit = this.model.get('_isResetOnRevisit');

      // If reset is enabled set defaults
      if (isResetOnRevisit) {
        this.model.reset(isResetOnRevisit);
      }
    }

    /**
     * Due to a portion of this component being hidden prior to activation,
     * adapt-scrollfix cannot automatically calculate its size to scroll it into view.
     * 
     * This function is therefore defined to be used by adapt-scrollfix to get the custom value. 
     */
    getBoundingClientRect() {

      let animation = this.model.get("_animation");
      
      // No animation. Give normal rect.
      if (!animation || !animation._isEnabled) {
        return this.$el[0].getBoundingClientRect();
      }

      // Yes animation. Include the hidden element in the rect.
      let $codeAnimContainer = this.$(".js-codeanim-anim");
      $codeAnimContainer.show(0).css("height", "");
      let bb = this.$el[0].getBoundingClientRect();
      bb.height += 10; // Just a bit of extra buffer.

      // Hide the container again (if applicable).
      if (!animation._isActive) {
        $codeAnimContainer.hide(0);
      }

      return bb;
    }

    preRender() {

      // FIXME: Need to add support for if no _lang defined, get the global lang.
      // The global lang should be gettable by using Adapt.codeBox.defaultLang or something like that.

      // If the console's language is "html", don't escape it.
      if (this.model.get("_console")._lang == "html") {

        // Remove any extra HTML that was generated.
        let htmlConsole = Adapt.codeBox.toString(this.model.get("_console").body);

        // Un-escape all "<"" and ">" characters.
        htmlConsole = htmlConsole.replace(/&lt;/g, "<");
        htmlConsole = htmlConsole.replace(/&gt;/g, ">");
        
        this.model.get("_console").body = htmlConsole;
      }
    }

    postRender() {

      this.renderPlaybackControls();
      this.setReadyStatus();
    }

    renderPlaybackControls() {

      let animation = this.model.get("_animation");
      if (!animation || !animation._isEnabled) return;

      let playbackControlsView = PlaybackControls({
        frameDuration:  animation._frameDuration || 1000,
        duration:       animation._duration,
        frameCount:     animation._frames.length,
        mode:           "steps"
      });
      this.playbackControlsView = playbackControlsView;
      this.$(".codeanim__playbackcontrols-container").html(playbackControlsView.$el);

      // Set up view data
      this.frames = animation._frames;

      // Listen for playbackControls timeline changes.
      this.listenTo(playbackControlsView.model, "change:frameIdx", this.onPlaybackControlsFrameChange);

      // Render inspector vars.
      if (this.model.get("_isInspectorEnabled") !== false) {
        this.renderInspectorVars();
      }
    }

    renderInspectorVars() {
      
      let $container = this.$(".js-codeanim-inspector-vars");
      let varModels = this.model.get("_animation")._inspectorVars;

      _.each(varModels, (varModel) => {
        varModel.set("_playbackControlsModel", this.playbackControlsView.model); // Have each inspector var have access to the playbackControls (they'll need to listen for timeline changes in order to update their values).
        varModel.set("_parentView", this);
        $container.append(new CodeAnimInspectorVarView({model: varModel}).$el);
      });
    }

    _getAnimationFrame(idx){
      let targetFrame;
      try {
        targetFrame = this.frames[idx];
      }
      catch (err) {
        throw err;
      }
      return targetFrame;
    }

    // Whenever the playbackControls timeline moves, update this views state to display the specified frame.
    onPlaybackControlsFrameChange(playbackControlsModel, frameIdx) {
      
      let frame = this._getAnimationFrame(frameIdx);

      // Highlight the codebox line this frame corresponds to.
      this.$(".codebox__line")
      .removeClass("is-codeanim-mark")
      .removeAttr("data-codeanim-anim")
      .filter("[data-line-num=" + frame.line + "]")
      .attr("data-codeanim-anim", frame.anim)
      .addClass("is-codeanim-mark");

      // Show all the console-print elements up to and including this frame's.
      //let print = frame.print;
      
      // FIXME: If the user gives a value of '"print": false', then print won't render that line (it needs to be given as "print": "false" instead).
      let $consolePrints = this.$(".codeanim__console-print");
      $consolePrints.addClass("u-visibility-hidden").filter((i, e) => {
        return $(e).attr("data-frame-idx") <= frameIdx;
      }).removeClass("u-visibility-hidden");
    }
  }
  
  CodeAnimView.template = "codeAnim";
  return CodeAnimView;
});
