define([
  'core/js/adapt'
], function(Adapt) {

  const NAVIGATE_DURATION = 400;

  class GLabelPopupView extends Backbone.View {

    events() {
      return {
        //'focus .js-glabel-item-input'  : 'onItemFocus',
        //'blur .js-glabel-item-input'   : 'onItemBlur',
        'change .js-glabel-item-input' : 'onOptionSelect'//,
        //'keyup .js-glabel-item-input'  : 'onKeyPress'
      }
    }

    className() {
      return 'glabel-popup';
    }

    initialize() {

      this.listenToOnce(Adapt, "notify:opened", this.onOpened);
      this.listenTo(Adapt, "notify:closed", this.onClosed);
      this.listenTo(Adapt, "device:resize", this.onDeviceResize);

      // Update the tooltip in the animation.
      this.listenTo(this.model, {
        "change:tooltip":     this.onTooltipChange,
        "change:_isSelected": this.onIsSelectedChange,
        "change:_isIncorrect":this.onIncorrectChange
      });
      this.listenTo(this.model.get("_options"), {
        "change:_isSelected" : this.onOptionSelectedChange
      });

      this.render();
    }

    // Update the tooltip in the animation.
    // ---------------------------------------------------------
    onTooltipChange(model, tooltip) {
      this.$(".js-glabel-item-tooltip").html(tooltip);
    }
    onIsSelectedChange(model, isSelected) {
      this.$(".js-glabel-popup-tooltip").toggleClass("is-selected", isSelected);
    }
    // ---------------------------------------------------------

    onIncorrectChange(model, isIncorrect) {
      this.$(".js-glabel-item").toggleClass("is-incorrect", isIncorrect);
      this.$(".js-glabel-popup-content-anim").toggleClass("is-incorrect", isIncorrect);
    }

    onOptionSelect(event) {
      if (!this.model.get("_parent").isInteractive()) return;

      let index = $(event.currentTarget).data('index'); // The real index of the option (the same regardless of randomization).
      let isSelected = $(event.currentTarget).prop('checked');

      this.model.get("_options").find((option) => {return option.get("_index") === index;}).toggle(isSelected);

      // Close the popup after a slight delay.
      _.delay(() => {
        this.closePopup();
      }, 100);
    }

    onOptionSelectedChange(optionModel, isSelected) {
      let $label = this.$(".js-glabel-item-label").filter("[data-index=" + optionModel.get("_index") + "]");
      $label.toggleClass("is-selected", isSelected);
    }

    onDeviceResize() {
      this.alignEverything();
    }

    onOpened() {

      this.$el.addClass("u-visibility-hidden"); // Hide this notify until the image inside has loaded; prevents a frame of flicker.

      // FIXME: \/. Could delay the reveal by a significant amount if the connection is slow!
      // Adapt core notify functionality waits for the imageready event before aligning it vertically.
      // We want to align the image after that, so a debounce is added to ensure our event fires just after the core event.
      this.$el.imageready(_.debounce(() => {
        this.alignEverything();
        this.playContentAnim();
        
        // Setting the itemModel to focused will cause the item element in the component to hide itself.
        this.$el.removeClass("u-visibility-hidden");
        this.$el.addClass("is-active");
        this.model.set("_isFocused", true);
      }, 0));
    }

    /**
     * Use this function to get the offsets needed to align elements in this view with the elements in the component.
     * @param {*} $el 
     * @param {*} $target 
     */
    getAlignOffset($el, $target) {
      let myBB      = $el[0].getBoundingClientRect();
      let targetBB  = $target[0].getBoundingClientRect();

      let offset = {
        left: targetBB.left - myBB.left,
        top: targetBB.top - myBB.top
      }

      return offset;
    }

    /**
     * Align the popup's content element horizontally with the popup's item.
     */
    alignContentToItem() {

      let $item = this.$(".js-glabel-item");
      let $container = this.$(".js-glabel-popup-content-helper"); // This takes up the full width of the area the $content can be in.
      let $content = this.$(".js-glabel-popup-content");
      
      // Clear any previously applied offsets.
      $content.css("left", "");
      
      let contentWidth =    $content.outerWidth();
      let containerWidth =  $container.outerWidth();
      let itemWidth =       $item.outerWidth();
      let itemLeftCenter =  parseInt($item.css("left"),10) + (itemWidth/2); // The left center of the item relative to its parent.
      
      // If the item too far to the left or right, align the content to that side (as centering it would cause it to clip).
      if (itemLeftCenter < contentWidth/2) {
        $container.css("justify-content", "flex-start");
      }
      else if (itemLeftCenter > containerWidth - contentWidth/2) {
        $container.css("justify-content", "flex-end");
      }
      // Otherwise, the content can be centered above the item.
      else {
        $content.css("left", itemLeftCenter - contentWidth/2);
      }
    }

    /**
     * Align the popup's cropped image with the one in the component.
     */
    alignImg() {

      let $graphic = this.$(".js-glabel-popup-img");
      let $parGraphic = this.getParentView().$(".js-glabel-img");
      $graphic.css({
        "left": "",
        "top": ""
      });

      let offset = this.getAlignOffset($graphic, $parGraphic);;

      $graphic.width($parGraphic.width());
      $graphic.height($parGraphic.height());
      $graphic.css(offset);
    }

    /**
     * Align this view vertically with the popup's item
     * ------------------------------------------------
     * If the vertical alignment would result in this view going off the top/bottom of the viewport, scroll the main page instead.
     */
    alignViewport() {

      let $item = this.$(".js-glabel-item");
      let $parItem = this.getParentItemEl();

      this.$el.css("top", "");

      let yDiff = this.getAlignOffset($item, $parItem).top;
      let yDiffTarget = undefined;
      
      let yDiffMin = 0;
      let yDiffMax = this.$el.closest(".notify__popup-inner").height() - this.$el.outerHeight();
      let yOffset = 0;

      // If the popup content would be too tall for the viewport, then don't scroll at all.
      if (yDiffMax < 1) {
        yDiff = undefined;
      }
      // If the popup content would move too high up, scroll the page up first to compensate.
      else if (yDiff < yDiffMin) {
        yOffset = yDiff - yDiffMin;
        yDiffTarget = yDiffMin;
      }
      // If the popup content would move too low, scroll the page down first to compensate.
      else if (yDiff > yDiffMax) {
        yOffset = yDiff - yDiffMax;
        yDiffTarget = yDiffMax;
      }

      if (yOffset) {
        this.navigateToRelOffset(yOffset);
      }

      this.$el.css("top", yDiff);
      this.$el.animate({"top": yDiffTarget}, NAVIGATE_DURATION);
    }

    /**
     * Sets up an element to create an "expanding" animation for the revealed content. 
     */
    playContentAnim() {
      let $anim = this.$(".js-glabel-popup-content-anim");
      let $tooltip = this.$(".js-glabel-popup-tooltip");

      $anim.offset($tooltip.offset());
      $anim.outerWidth($tooltip.outerWidth());
      $anim.outerHeight($tooltip.outerHeight());
    
      $anim.hide(0).show(0);

      _.delay(() => {
        $anim.addClass("is-active");
        $tooltip.addClass("is-active");
      });
    }

    /**
     * Aligns the .js-glabel-item in this popup view with the .js-glabel-item belonging to the parent component.
     */
    alignEverything() {

      // Align the popup's item with the component's item horizontally
      // -------------------------------------------------------------
      let $item = this.$(".js-glabel-item");
      let $parItem = this.getParentItemEl();

      $item.css("left", ""); // Clear first.
      $item.css("left", this.getAlignOffset($item, $parItem).left);
      $item.width($parItem.width());
      $item.height($parItem.height());
      

      // Align the content horizontally to the item.
      // -------------------------------------------------------------
      this.alignContentToItem();


      // Align this view vertically with the popup's item.
      // -------------------------------------------------------------
      // If the vertical alignment would result in this view going off the top/bottom of the viewport, scroll the main page instead.
      this.alignViewport();


      // Align the graphic
      // -------------------------------------------------------------
      this.alignImg();
    }

    navigateToRelOffset(relativeTop) {
      // The following needs to be done to accomplish this without causing the notify popup to close.
      let notifyView = Adapt.notify.stack[Adapt.notify.stack.length-1];
      notifyView.hasOpened = false;
      notifyView.isOpen = false;

      Adapt.navigateToRelOffset({
        top: relativeTop
      },
      {
        duration: NAVIGATE_DURATION
      });

      // Allow the notify to be closeable again after navigateToElement has finished.
      _.delay(() => {
        if (Adapt.notify.stack[Adapt.notify.stack.length-1] !== notifyView) {
          Adapt.notify.stack.push(notifyView);
        }
        notifyView.hasOpened = true;
        notifyView.isOpen = true;
      }, 0);
    }

    // Get the view belonging to the parent component.
    getParentView() {
      let parent = this.model.get("_parent");
      return Adapt.findViewByModelId(parent.get("_id"));
    }

    // Find the item element that was just clicked to create this popup.
    getParentItemEl() {
      
      let parentView = this.getParentView();
      return parentView.$(".js-glabel-item").filter("[data-index=" + this.model.get("_index") + "]");
    }

    onClosed(notifyView) {
      
      this.model.trigger("notify:close");

      let $anim = this.$(".js-glabel-popup-content-anim");
      $anim.removeClass("is-active");
      this.$el.removeClass("is-active");
      this.$(".js-glabel-popup-tooltip").removeClass("is-active");

      // Check if the view being closed is this one (in case something has caused there to be multiple NotifyViews being nested).
      if (!notifyView.$el.hasClass("glabel-popup")) return;
    }

    remove() {
      this.model.trigger("remove");
      this.model.set("_isFocused", false);
    }

    render() {
      var data = this.model.toJSON();
      data.view = this;
      data._parent = this.model.get("_parent").toJSON();
      data._options = this.model.get("_options").toJSON();
      /*data._options = this.model.get("_parent").get("_options");
      data._graphic = this.model.get("_parent").get("_graphic");*/

      var template = Handlebars.templates['glabelPopup'];
      this.$el.html(template(data));
    }

    closePopup(event) {
      Adapt.trigger('notify:close');
    }
  }

  return GLabelPopupView;

});
