define([
    'handlebars',
    'core/js/adapt',
    './imgPopupView'
], function(Handlebars, Adapt, ImgPopupView) {

    /**
     * Renders an <img> with optional additional events attached.
     */
    class ImgView extends Backbone.View {

        className() {
            let classes = [
                "ddimg",
                this.model.get("content").trim().length ? "has-content" : "",
                this.model.get("lightbox") ? "has-lightbox" : "",
                this.model.get("_widthAuto") ? "is-auto-width" : "",
                this.model.get("class") || "",
                this.model.get("sizing") || ""
            ];
            return classes.join(" ");
        }

        attributes() {
            return {
                id: this.model.get("_id"),                  // Completely unique ID (used by respImgView mostly).
                name: this.model.get("name") || undefined,
                style: this.model.get("style") || undefined // Allow for inline styles.
            }
        }

        events() {
            
            let _events = {};

            // Add a click event if using a lightbox.
            if (this.model.get("lightbox")) {
                _events["click .js-ddimg-click"] = "onImgClick";
            }

            return _events;
        }

        initialize(){

            this.setUpModelData();
            this.render();
            this.setUpEventListeners();
        }

        setUpModelData() {
            this.setUpLightboxData();
        }

        setUpEventListeners() {
            
            // Detect image loading errors, and add a class if it happens.
            let $img = this.$(".js-ddimg-img");
            $img.on("error", () => {
                this.$el.addClass("has-error");
                $img.addClass("has-error");
            });
        }

        setUpLightboxData() {
            // Possible lightbox values and what they map to:
            // true/false -----------> {_isEnabled: *}
            // Number ---------------> {_isEnabled: true, _width: *}
            // {_width:x, bg:true} --> {_isEnabled: true, _width: *, backgroundEnabled: *}
            // {src: "..."} --> Specifies a different src to use inside the lightbox (instead of using the img's src by default).

            let lightbox = this.model.get("lightbox");
            if (!lightbox) return;

            // Use custom lightbox src; or use same src as the img if none specified.
            const src = (!isNaN(lightbox) || !lightbox?.src) ? this.model.get("src") : lightbox.src; 

            this.model.set("lightbox", {
                _isEnabled: true,
                _width: !isNaN(lightbox) ? lightbox : (lightbox._width || "auto"),
                src: src,
                _backgroundEnabled: Boolean(lightbox.bg)
            });
        }

        render(){
            let template = Handlebars.templates['ddImg'];
            this.$el.html(new Handlebars.SafeString(template(this.model.toJSON())).string);

            // Legacy support
            // --------------------------------------------------------------------------------------------
            // FIXME: Remove once the topic which uses mark.has-shadow has been fixed.
            // If the {{#img}} contains a .mark with .has-shadow, set its overflow to hidden.
            if (this.$(".mark.has-shadow").length) {
                this.$(".ddimg__inner").css("overflow", "hidden");
            }
        }

        onImgClick(event) {

            // If two images are overlapping, only trigger the topmost event.
            event.stopPropagation();

            this.openLightbox();
        }

        openLightbox() {

            if (!this.model.get("lightbox")) return;

            if (this._isPopupOpen) return;
            this._isPopupOpen = true;
            this.popupView = new ImgPopupView({
                model: this.model
            });
    
            Adapt.trigger("notify:popup", {
                _view: this.popupView,
                _isCancellable: true,
                _showCloseButton: true,
                _classes: 'ddimg-lightbox dragscroll'// + (this.model.get('_classes') || "")
            });

            this.listenToOnce(Adapt, {
                'popup:closed': this.onLightboxClosed
            });
        }

        onLightboxClosed() {
            this._isPopupOpen = false;
        }
    };

    return ImgView;
});
