define([
    'core/js/adapt'
], function(Adapt) {

    // FIXME: If the type is "number", the input field may perform unpredictably if the user types "e".
    
    class InputFieldView extends Backbone.View {

        className() {
            let classes = [
                "inputfield",
                this.model.get("_isHintEnabled") ? "hint-enabled" : ""
            ];

            return classes.join(" ");
        }

        attributes() {
            return {
                name: this.model.get("_name"),
                "data-inputfield-id": this.model.get("_id")
            }
        }

        events() {
            return {
                'focus .js-inputfield-input':   'onFocus',
                'blur .js-inputfield-input':    'onBlur',

                'change .js-inputfield-input':  'onInputChange',
                'keyup .js-inputfield-input':   'onInputChange',
                'click .js-inputfield-incorrect-icon':   'onIncorrectIconClick',
                'click .js-inputfield-hint-icon':   'onHintIconClick'
            }
        }

        initialize() {
            this.setUpInputAttributes();
            this.render();
            this.setUpEventListeners();
        }

        /**
         * Set the attributes that the <input> element will used based on the model's _type.
         */
        setUpInputAttributes() {

            let type = this.model.get("_type");
            let attr = {};

            switch (type) {

                case "number":
                    attr.type = "number";
                    attr.step = "any";
                    attr.pattern = "[-+]?[0-9]*[.,]?[0-9]+";
                    break;

                case "text":
                    attr.type = "text";
                    break;

                default:
                    attr.type = "text";
                    Adapt.log.error(`{{#inputField}}: "${type}" is not a valid value for the "type" attribute of an <input> element! Defaulting to input type: "text".`);
            }

            this.model.set("_inputAttributes", attr);
        }

        setUpEventListeners() {
            this.listenTo(this.model, "change:_isSubmitted", this.onIsSubmittedChange);
            this.listenTo(this.model, "change:_isCorrect", this.onIsCorrectChange);
            this.listenTo(this.model, "change:_isLocked", this.onIsLockedChange);
            this.listenTo(this.model, "change:_isHintEnabled", this.onIsHintEnabledChange);

            this.listenTo(this.model, "change:_isFocused", this.onIsFocusedChange);
            this.listenTo(this.model, "change:_val", this.onValChange);
        }

        onIsSubmittedChange(model, isSubmitted) {
            if (!isSubmitted) {
                this.$el.removeClass("is-correct is-incorrect");
            }
            else {
                let isCorrect = this.model.get("_isCorrect");
                this.$el
                    .toggleClass("is-correct", isCorrect)
                    .toggleClass("is-incorrect", !isCorrect);
            }
        }

        onIsLockedChange(model, isLocked) {
            this.$(".js-inputfield-input").prop("disabled", isLocked);
        }

        onFocus() {
            this.model.set("_isFocused", true);
        }

        onBlur() {
            this.model.set("_isFocused", false);
        }

        onIsFocusedChange(model, isFocused) {
            this.$el.toggleClass("is-focused", isFocused);
        }

        onValChange(model, val) {
            this.$el.toggleClass("has-val", Boolean(val.length));
            this.$(".js-inputfield-input").attr("title", val);
        }

        onIsCorrectChange(model, isCorrect) {
            if (!this.model.get("_isSubmitted")) return;
            
            this.$el
                .toggleClass("is-correct", isCorrect)
                .toggleClass("is-incorrect", !isCorrect);
        }

        onIncorrectIconClick(event) {
            this.reset();
        }

        onIsHintEnabledChange(model, isHintEnabled) {
            this.$el.toggleClass("hint-enabled", isHintEnabled);
        }

        render() {
            var data = this.model.toJSON();
            
            var template = Handlebars.templates['inputField'];
            this.$el.html(template(data));

            this.$(".js-inputfield-placeholder").css({
                "min-width": this.model.get("_width") + "em" // Using min-width allows the textinput to be wider if its placeholder is.
            });
        }

        reset(){
            let $input = this.$(".js-inputfield-input");
            $input.val("");
            $input.trigger("change"); // Important to trigger an event change; in case other scripts are listening for it.
            this.model.reset();
        }

        // Update model values whenever the input changes.
        onInputChange(event) {
            let $input = this.$("input");
            this.model.set("_val", $input.val()); // If the input the user types is too long, it will be cut off (and ... added).
                                                  // Set the "title" attribute to the unabridged input (allowing them a way to see the full thing).
        }

        onHintIconClick() {
            this.showAnswer();
        }

        /**
         * Set the value of the input element to that of the first element in the _answers array. 
         */
        showAnswer() {
            let $input = this.$("input");
            $input.val(this.model.getCorrectAnswer());
            $input.trigger("change"); // Important to trigger an event change; in case other scripts are listening for it.
        }
    }

    return InputFieldView;
});
