define([
    'core/js/adapt'
], function(Adapt) {

    /**
     * TODO:
     * - Needs to detect (and show an error message) if the content fails to start (seems to be an issue with EPUB Bookshelf).
     * - Needs to allow for adding a message above the loading GIF.
     * 
     * - Once loaded, needs to change to a normal animation.
     */

    const OPACITY = 0.1;

    class DDLoadingModel extends Backbone.Model {
        
        defaults() {
            return {
                "_percentLoaded": 0,
                "_hasLoaded": false,
                "_hasError": false,
                "text": "" // Text which appears above the loading icon.
            }
        }

        initialize() {

            this.initHelpers();

            this.set("_componentsReady", 0);

            // The "_isReady" property is listened to instead of componentView:postRender, as the latter triggers a lot earlier than
            // the former. 
            this.listenTo(Adapt.components, "change:_isReady", this.onComponentReadyChange.bind(this));
            this.listenTo(Adapt, "contentObjectView:postReady", this.onLoaded.bind(this));
        }

        initHelpers() {

            /**
             * Register Handlebars helper.
             */
            var helpers = {

                loadingIcon: function() {
                    let context = arguments[arguments.length-1];
                    var template = Handlebars.templates['ddLoadingIcon'];

                    return template(context.hash);
                }
            }
        
            for (var name in helpers) {
                if (!helpers.hasOwnProperty(name)) continue;
                Handlebars.registerHelper(name, helpers[name]);
            }
        }

        /**
         * Once loaded, the loading graphic will change its appearance slightly.
         * @param {*} contentObjectView 
         */
        onLoaded(contentObjectView) {
            this.set("_hasLoaded", true);
            this.clearText(); // Remove any custom text that was being shown during the loading phase.
            /*if (this._debugCommentEl) {
                this._debugCommentEl.remove();
                this._debugCommentEl = null;
            }*/
        }

        /**
         * Whenever a component finishes rendering, update the percentage complete.
         */
        onComponentReadyChange(componentModel, _isReady) {
            
            let componentsReady = [];
            Adapt.components.each((componentModel) => {
                if (componentModel.get("_isReady")) {
                    componentsReady.push(componentModel);
                }
            });

            let percentage = componentsReady.length / Adapt.components.length * 100;
            this.set("_percentLoaded", percentage);
            
            // FIXME: If there's a better way of communicating this in the debug (without a large amount of console.logs),
            // please change to that.
            // 
            // DEBUG: Render a hidden list of ready components inside of a comment (useful to determine if and which components
            // aren't loading).
            if (this._debugCommentEl) {
                this._debugCommentEl.remove();
                this._debugCommentEl = null;
            }

            let commentStr = ``;
            Adapt.blocks.each(blockModel => {

                commentStr += blockModel.get("_id") + " --- ";

                let readyComponentCount = 0;
                let waiting = [];
                blockModel.getChildren().each((componentModel) => {
                    let isReady = componentModel.get("_isReady");
                    if (isReady) readyComponentCount++;
                    else waiting.push(componentModel.get("_id"));
                });
                
                commentStr += `${readyComponentCount}/${blockModel.getChildren().length}${waiting.length ? (" [" + waiting.join(", ") + "]") : ''}
`;
            });

            
            this._debugCommentEl = document.createComment(`
Components loaded
-----------------
${commentStr}
`);
            document.body.appendChild(this._debugCommentEl);
        }

        setText(text) {
            this.set("text", text || "");
        }

        /**
         * Shows the loading element with an error message over it.
         * Used to communicate errors to the user that will require their own actions to fix (usually requiring them to refresh).
         * @param {*} errorText 
         */
        showError(errorText) {
            $(".js-loading").show();

            var template = Handlebars.templates['ddLoadingErrorText'];
            this.set("text", template({
                text: errorText
            }));
            this.set("_hasError", true);
        }

        clearText() {
            this.set("text", "");
        }

        /**
         * Core Adapt doesn't have any inbuilt function for showing/hiding the loading element, instead it does this.
         */
        show() {
            $(".js-loading").show();
        }
        hide() {
            $(".js-loading").hide();
        }
    }

    class DDLoadingView extends Backbone.View {

        initialize() {

            // As soon as possible, render the ddLoadingGraphic into the .js-loading element.
            this.render();
            this.setUpEventListeners();
        }

        setUpEventListeners() {
            this.listenTo(this.model, "change:_percentLoaded", this.onPercentLoadedChange.bind(this));
            this.listenTo(this.model, "change:_hasLoaded", this.onHasLoadedChange.bind(this));
            this.listenTo(this.model, "change:_hasError", this.onHasErrorChange.bind(this));
            this.listenTo(this.model, "change:text", this.onTextChange.bind(this));
        }

        onHasErrorChange(model, hasError) {
            this.$el.toggleClass("has-error", hasError);
        }
        
        /**
         * Allow for custom messages to be shown during loading.
         * @param {*} model 
         * @param {*} text 
         */
        onTextChange(model, text) {
            this.$(".js-ddloading-text").html(text);
        }

        /**
         * Adapt sometimes briefly re-shows the loading element when it needs to load something on the fly.
         * 
         * Change the appearance of the loading graphic from a progress animation with a %, to an infinitely looping spinning
         * animation with no % (as that will be harder to calculate when rthings are loading on the fly).
         */
        onHasLoadedChange(model, hasLoaded) {
            this.$(".js-ddloading-graphic").toggleClass("is-idle", hasLoaded);
            this.$(".js-ddloading-img").css("opacity", "");
        }

        onPercentLoadedChange(model, percentLoaded) {

            const o = 1 - OPACITY;
            const graphicCount = 16; // How many images are a part of the graphic.
            const segment = 100/graphicCount;

            let idx = Math.floor(percentLoaded/segment);
            let opacity = (1 - o) + o*((percentLoaded % segment)/segment);

            // Set the opacity for the img index selected.
            let $img = this.$(".js-ddloading-img").filter("[data-logo-index=" + idx + "]");
            $img.css("opacity", opacity);

            // Set the opacity to 1 for all img indexes prior to this one.
            for (let i = 0; i < idx-1; i++) {
                let $i = this.$(".js-ddloading-img").filter("[data-logo-index=" + i + "]").css("opacity", 1);
            }
            
            // Update the percentage text.
            this.$(".js-ddloading-percent").html(Math.floor(percentLoaded) + "%");
        }
        
        render() {

            // EDIT: Don't render anything.
            // The "dd-loading" grunt task will insert the assets/dd-loading-html.html into the index.html/index.xhtml automatically. 

            /*var data = this.model.toJSON();
            data.view = this;
            var template = Handlebars.templates['ddLoading'];
            this.$el.html(template(data));*/
        }
    }

    Adapt.on("router:page", () => {

        let $loading = $(".js-loading").first();
        $loading.addClass("ddloading");

        let model = new DDLoadingModel();
        new DDLoadingView({
            model: model,
            el: $loading[0]
        });

        Adapt.ddLoading = model;
    });
});
