define([
    'core/js/adapt',
    './SideMenuCategoryModel'
  ], function(Adapt, SideMenuCategoryModel) {
  
    /**
     * Stores the collections for each category in the SideMenu.
     */
    class SideMenuCategoryCollection extends Backbone.Collection{
  
      initialize(models, options) {
        this.listenTo(Adapt, 'remove', this.reset);
        if (!options || !options.pageModel) return;
        this._pageModel = options.pageModel;
        this.repopulate();
      }
  
      repopulate() {
        this.reset();
        if (!this._pageModel) return;
  
        // Get the raw menu data from the current pageModel (data set in contentObjects.json).
        let menuData = this._pageModel.get("_menuData");

        // Create a model with a collection for navigation.
        // This one receives special scripting (clicking the items needs to scroll the page + affect articleNav).
        // (Inherited from adapt-dd-sideNav).
        let m = new SideMenuCategoryModel({
          title: "Content",
          _type: "nav",
          _items: Adapt.blocks//,
          //_isActive: true  // Open by default.
        });
        this.add(m);

        // Create a model with a collection for each menuData category.
        _.each(menuData, (cat, name) => {

            let models = [];
            _.each(cat._items, (item) => {
                models.push(new Backbone.Model(item));
            });

            let m = new SideMenuCategoryModel({
              title: cat.title,
              _type: cat.title.toLowerCase(),
              _items: new Backbone.Collection(models)
            });
            
            this.add(m);
        });
      }
    }

    return SideMenuCategoryCollection;
  
  });
  