define([
  'core/js/adapt'
], function(Adapt) {

  const CLICK_ANYWHERE_EVENTS = "click touch mouseup touchend";

  class MatchingPlusDropdownView extends Backbone.View {

    events() {
      return {
        "click .js-matchingplus-dropdown-trigger" : "onTriggerClick",
        "click .js-matchingplus-dropdown-option" : "onOptionClick"
      }
    }

    initialize() {
      this.setUpEventListeners();
    }

    setUpEventListeners() {
      this.listenTo(this.model, "change:_isActive", this.onActiveChange); // The item is "active" while its dropdown is opened.
      this.listenTo(this.model, "change:_optionsSelected", this.onOptionsSelectedChange);
      this.listenTo(this.model, "change:_isDisabled", this.onDisabledChange);
    }

    toggleDropdown(isActive) {

      if (isActive === undefined) isActive = !this.model.get("_isActive");
      this.model.set("_isActive", isActive); // Fallback.

      let $item = this.$el.closest(".js-matchingplus-item");

      // While the dropdown is active, add a class to the root item too (used by custom theme styles).
      $item.toggleClass("is-active", isActive);
      this.$el.toggleClass("is-active", isActive);

      if (isActive) {
        // Listen for a click anywhere except the dropdown to close it.
        this.onClickAnywhereEvent = this.onClickAnywhere.bind(this);
        $(document).on(CLICK_ANYWHERE_EVENTS, this.onClickAnywhereEvent);
      }
      else {
        // Remove the global event this made.
        $(document).off(CLICK_ANYWHERE_EVENTS, this.onClickAnywhereEvent);
      }
    }

    onDisabledChange(model, isDisabled) {
      this.$el.toggleClass("is-disabled", isDisabled);
    }

    onActiveChange(model, isActive) {
      this.toggleDropdown(isActive);
    }

    onTriggerClick(event) {

      // Don't allow interaction after the model is disabled.
      if (this.model.get("_isDisabled")) return;

      this.model.set("_isActive", !this.model.get("_isActive"));
    }

    onOptionClick(event) {

      // Don't allow interaction after the model is disabled.
      if (this.model.get("_isDisabled")) return;

      let $option = $(event.currentTarget);

      let val = $option.data("value"); // This is the dropdown's version of the <select value="X"> attribute.
    
      // TODO:? Support more than one option being selectable? Probably not necessary.
      // Array used regardless in case this functionality is needed at a later date. 
      this.model.set("_optionsSelected", [parseInt(val, 10)]);

      // Close the dropdown after the user makes a selection.
      this.model.set("_isActive", false); 
    }

    /**
     * Mark the selected options as visited and correct/incorrect based on the itemModel values. 
     */
    updateMarking() {

      let optionsSelected = this.model.get("_optionsSelected");
      let anyIncorrect = false;

      _.each(optionsSelected, (optionIdx) => {

        let $option = this.$(".js-matchingplus-dropdown-option").filter("[data-value='" + optionIdx + "']");
        let option = this.model.get("_options").find((o) => {return o._index == optionIdx});

        $option
          .addClass("is-visited")
          .toggleClass("is-correct", option._isCorrect)
          .toggleClass("is-incorrect", option._isIncorrect);

        if (option._isIncorrect) anyIncorrect = true;
      });

      // Add a class to any eliminated options ("_hasSharedUniqueOptions" mode).
      _.each(this.model.get("_options"), (option) => {
        if (!option._isEliminated) return;

        let $option = this.$(".js-matchingplus-dropdown-option").filter("[data-value='" + option._index + "']");
        $option
          .addClass("is-eliminated");
      });


      // Add a validation state to the $trigger too.
      /*let $trigger = this.$(".js-matchingplus-dropdown-trigger");
      $trigger
        .toggleClass("is-incorrect", anyIncorrect)
        .toggleClass("is-correct", !anyIncorrect);*/
      this.$el
        .toggleClass("is-incorrect", anyIncorrect)
        .toggleClass("is-correct", !anyIncorrect);

      this.$el.toggleClass("is-disabled", !anyIncorrect);
    }

    onOptionsSelectedChange(model, optionsSelected) {

      let $options = this.$(".js-matchingplus-dropdown-option");
      $options.removeClass("is-selected");
      
      let triggerText = [];
      let triggerIncorrect = false;

      // Highlight the selected option using a CSS class.
      _.each(optionsSelected, (optionIdx) => {
        let $option = this.$(".js-matchingplus-dropdown-option").filter("[data-value='" + optionIdx + "']");
        $option
          .addClass("is-selected");

        // Get the option that was selected and change the trigger element's text to match it.
        let option = this.model.get("_options").find((o) => {return o._index == optionIdx});
        triggerText.push(option.text);

        // If the user selects the same incorrect option a second (or more) time, mark the trigger element as incorrect too.
        if (option._isIncorrect || option._isEliminated) {
          triggerIncorrect = true;
        }
      });

      // Update the trigger text.
      let $trigger = this.$(".js-matchingplus-dropdown-trigger");
      $trigger
        .addClass("is-visited");

      let $triggerText = this.$(".js-matchingplus-dropdown-trigger-text");
        $triggerText.html(triggerText.join(', '));

      this.$el
        .toggleClass("is-incorrect", triggerIncorrect);
        //.toggleClass("is-correct", !triggerIncorrect);

      // Close the dropdown after the user makes a selection.
      this.model.set("_isActive", false); 
    }

    onClickAnywhere(event) {
      if (event.target == this.el || $.contains(this.el, event.target)) return; // Clicks on or inside this view won't close it.
      this.model.set("_isActive", false);
    }

  }

  return MatchingPlusDropdownView;

});
