define([
  'core/js/adapt',
  'core/js/models/componentModel'
], function(Adapt, ComponentModel) {

  class TryHtmlModel extends ComponentModel {

    // Used to set model defaults
    defaults() {

      // Extend from the ComponentModel defaults
      return ComponentModel.resultExtend('defaults', {
        
      });
    }

    initialize() {

      // Allow for custom languages to be used; but set to default "html" if none provided.
      if (this.get("_lang") === undefined) {
        this.set("_lang", "html");
      }

      // If in EPUB mode, need to throw an error (the CodeMirror plugin uses uppercase classnames, which Bookshelf may reject).
      if (Adapt.config.get("_isEpub")){
        Adapt.log.error("tryHtml: CodeMirror plugin uses capitals in its classnames. Check that they still work with the EPUB reader.");
      }

      this.setUpModelData();

      ComponentModel.prototype.initialize.call(this);
      this.setUpEventListeners();
    }

    setUpModelData() {

      // Add the tryThis logo to this component (using the adapt-dd-componentLogo extension).
      this.set("_componentLogo", {
          src: "assets/trythis-logo.svg"
      });

      // Process the provided HTML code.
      let s = Adapt.codeBox.toString(this.get("_user").code);
      this.get("_user").code = s;

      s = s.replace(/&lt;/g, "<");
      s = s.replace(/&gt;/g, ">");
      this.get("_user").output = s;

      s = Adapt.codeBox.toString(this.get("_correct").code);
      s = s.replace(/&lt;/g, "<");
      s = s.replace(/&gt;/g, ">");
      this.get("_correct").output = s;
    }

    setUpEventListeners() {

    }
  }

  return TryHtmlModel;

});
