define([
    'core/js/adapt'
], function(Adapt) {
  
    /**
     * Collection hardcoded to store only Blocks.
     */

    class SideNavCollection extends Backbone.Collection {
  
      initialize(models, options) {
        this.listenTo(Adapt, 'remove', this.reset);
        if (!options || !options.pageModel) return;
        this._pageModel = options.pageModel;
        this.repopulate();
      }
  
      repopulate() {
        this.reset();
        if (!this._pageModel) return;
        
        var allDescendants = this._pageModel.getAllDescendantModels(true);

        // For our DD menu, we want to add all enabled Blocks
        // (we don't use isActive because articleNav may have temporarily disabled it).
        var allProgressItems = allDescendants.filter((item) => {
            if (item.get("_type") != "block") return false;
            return Boolean(item.get("_isEnabled"));
        });

        this.add(allProgressItems);
      }
    }
  
    return SideNavCollection;
});
  