define([
    'core/js/adapt',
    './blockBackgroundView',
    './blockBackgroundModel'
], function(Adapt, BlockBackgroundView, BlockBackgroundModel) {

    class BlockBackground extends Backbone.Controller {

        initialize() {

            // Store a collection of ALL blockBackgrounds in the page.
            this._blockBackgrounds = new Backbone.Collection({model: BlockBackgroundModel});

            Adapt.on("app:dataReady", this.onDataReady.bind(this));
            Adapt.on("blockView:postRender", this.onBlockViewPostRender.bind(this));
            Adapt.on("componentRowView:postRender", this.onComponentRowViewPostRender.bind(this));
        }

        /**
         * LEGACY SUPPORT:
         * ---------------
         * (DD specific).
         * 
         * In the past, we would define block backgrounds by doing:
         * 
         * "_didasko": {
         *    "_backgroundImage": {
         *      "_large": 	"course/en/images/b-3/bg.jpg",
         *      "_medium": 	"course/en/images/b-3/bg.jpg",
         *      "_small": 	"course/en/images/b-3/bg.jpg"
         *    }
         * },
         * 
         * This script will automatically convert and remove this property from all blocks, and change it so a property that will
         * invoke the adapt-blockBackground extension.
         */
        _legacyBlockBGFix(blockModel) {

            let legacyBG = blockModel.get("_didasko")?._backgroundImage;
            
            // This block doesn't have a legacy background. Ignore it.
            if (!legacyBG) return;
        
            // Remove the legacy background settings from the block.
            blockModel.get("_didasko")._backgroundImage = undefined;
        
            // Check if this block already has the adapt-blockBackground property.
            if (blockModel.get("_blockBackground")) {
                throw "adapt-blockBackground: block " + blockModel.get("_id") + " has both \"_didasko\".\"_backgroundImage\" and \"_blockBackground\" properties.\n\nPlease use only \"_blockBackground\".";
            }
        
            // Add in the equivalent settings to add an adapt-blockBackground to this block.
            blockModel.set("_blockBackground", {
                _isEnabled: true,
                _items: [
                    {
                        _graphic: {
                            _large: legacyBG._large,
                            _small: legacyBG._small
                        }
                    }
                ]
            });
        }

        /**
         * Initialize a blockBackgroundModel for all blocks which use it.
         */
        onDataReady() {
            
            let prevBlockBB = undefined;

            Adapt.blocks.each(block => {

                this._legacyBlockBGFix(block);

                let data = block.get("_blockBackground");
                if (!data || !data._isEnabled) {
                    prevBlockBB = undefined;
                    return;
                }

                // If the _blockBackground is just a solid colour (doesn't have any items), set up a generic model for it instead.
                if (!data._items || !data._items.length) {
                    block.set("_blockBackground", new Backbone.Model(data));
                    prevBlockBB = undefined;
                    return;
                };
                
                data._parent = block;
                
                /**
                 * If the blockBackgroundModel before this one (in the collection) belongs to the blockModel before this one,
                 * then this blockBackgroundModel will need to use a slightly different script to determine if it's inview.
                 * (This allows for sequential blocks to each have blockBackgrounds without there being a jarring transition).
                 */
                if (prevBlockBB) data._prevBlockBackground = prevBlockBB;

                let model = new BlockBackgroundModel(data);
                block.set("_blockBackground", model);

                this._blockBackgrounds.push(model);
                prevBlockBB = model;
            });
        }

        onBlockViewPostRender(blockView) {

            let blockBackground = blockView.model.get("_blockBackground");
            if (!blockBackground || !blockBackground.get("_isEnabled")) return;

            // If the _blockBackground is just a solid colour (doesn't have any items), don't set up models and views
            // -------------------------------------------------------------------------------------------------------
            let items = blockBackground.get("_items");
            if (!items || !items.length) {
                let color = blockBackground.get("_color");
                if (color) {
                    blockView.$el.css("background-color", color);
                }
                return;
            }

            blockBackground.onParentPostRender(); // MUST be called here to ensure it's invoked before the view is initialized.
            let view = new BlockBackgroundView({model: blockBackground});
        }

        // Once the componentRowViews render, append them into their blockBackground groups (if they belong to them).
        onComponentRowViewPostRender(componentRowView) {
            
            let bbgItemId = componentRowView.model.get("_blockBackgroundItemId");
            if (bbgItemId === undefined) return;
            
            // Append the componentRowView to the group element with the matching ID.
            componentRowView.$el.appendTo($(".js-blockbg-group[data-adapt-id=" + bbgItemId + "]"));
        }
    }

    new BlockBackground();
});
