define([
  'core/js/adapt',
  'core/js/views/componentView'
], function(Adapt, ComponentView) {

  // TODO: Have default button labels be defined on a source level, and not a course level? (makes updating everything a lot easier).

  class FreeThinkingView extends ComponentView {

    events() {
      return {
        "click .js-freethinking-btn" : "onShowAnswersBtnClick"
      }
    }

    initialize() {
      ComponentView.prototype.initialize.call(this);
      this.setUpEventListeners();
    }

    setUpEventListeners() {
      this.listenTo(this.model, "change:_isShowingAnswer", this.onShowingAnswerChange);
    }

    onShowingAnswerChange(model, isShowingAnswer) {
      if (isShowingAnswer) {
        this.$(".js-freethinking-header").slideUp();
        this.$(".js-freethinking-correct").slideDown();
      }
      else {
        this.$(".js-freethinking-header").slideDown();
        this.$(".js-freethinking-correct").slideUp();
      }
    }

    onShowAnswersBtnClick() {
      let isShowingAnswer = !this.model.get("_isShowingAnswer");
      this.model.set("_isShowingAnswer", isShowingAnswer);

      let buttons = this.model.get("_freeThinking")._buttons;

      this.$(".js-freethinking-btn")
        .toggleClass("is-disabled", isShowingAnswer)
        .text(isShowingAnswer ? buttons._hideCorrectAnswer.buttonText : buttons._showCorrectAnswer.buttonText);
      
      // Fallback: scroll the freethinking widget into view just to be sure
      if (isShowingAnswer) {
        Adapt.navigateToElement(this.$(".freethinking__content-inner"), {
          duration: 400,
          align: "inview"
        });
      }

      // Mark the component as completed.
      this.setCompletionStatus();
    }

    checkIfResetOnRevisit() {
      var isResetOnRevisit = this.model.get('_isResetOnRevisit');

      // If reset is enabled set defaults
      if (isResetOnRevisit) {
        this.model.reset(isResetOnRevisit);
      }
    }

    preRender() {
      
    }

    postRender() {
      this.setReadyStatus();
    }
  }
  
  FreeThinkingView.template = "freeThinking";
  return FreeThinkingView;
});
