define([
    'core/js/adapt'
], function(Adapt) {

    /**
     * FIXME: Doesn't support blocks with _isAvailable: false.
     */

    class BlocknumView extends Backbone.View {

        className() {
            return "sidenav__blocknum";
        }

        events() {
            return {
                "click" : "onClick",
                "focus .js-sidenav-blocknum-input" : "onInputFocus",
                "change .js-sidenav-blocknum-input" : "onInputChange",
                "oninput .js-sidenav-blocknum-input" : "onInputChange",
                "keydown .js-sidenav-blocknum-input" : "onInputChange",
                "keyup .js-sidenav-blocknum-input" : "onInputKeyup"
                //"click .btn__next" : "onNextClick"
            }
        }

        initialize(){
            
            this.maxInputLength = 2;

            this.setupEventListeners();
            this.render();
        }

        render(){
            var template = Handlebars.templates['sideNavBlocknum'];
            this.$el.html(template(/*this.model.toJSON()*/));
        }

        onInputFocus(event) {
            let input = event.currentTarget;

            // Store the value that was in the input upon focusing (in case the user cancels).
            this.inputVal = input.value;
        }

        onInputChange(event) {
            this.updateInput();
        }

        /**
         * Limit the character length inside the input.
         */
        updateInput() {
            let input = this.$(".js-sidenav-blocknum-input")[0];
            if (input.value.length > this.maxInputLength) {
                input.value = input.value.slice(0, this.maxInputLength);
            }
        }

        onInputKeyup(event) {

            this.updateInput();

            // Special behaviour based on the character pushed.
            let input = this.$(".js-sidenav-blocknum-input")[0];
            if (event.which === 27) { // Esc

                // Revert the value to whatever it was before the input was focused on.
                input.value = this.inputVal;

                // Unfocus the input.
                $(input).blur();
                
                return false;
            }
            if (event.which === 13) { // Return

                // Unfocus the input.
                $(input).blur();

                // If the value was deleted, restore the previous one and do nothing else.
                if (!input.value)
                {
                    input.value = this.inputVal;
                    return false;
                }

                // Only whole numbers allowed.
                // FIXME: "pattern" HTML attribute only seems to do things on submit, which this doesn't.
                let blockNum = parseInt(input.value); 

                // Find the block with the corresponding blockNum
                let targetBlock = Adapt.blocks.find((block) => {
                    return block.get("_blockNum") == blockNum;
                });
                
                // Block wasn't found, go to either the first or last block.
                if (!targetBlock) {
                    let firstBlock = _.first(Adapt.blocks.models);

                    if (blockNum < firstBlock.get("_blockNum")) targetBlock = firstBlock;
                    else targetBlock = _.last(Adapt.blocks.models);
                }

                Adapt.scrollTo("." + targetBlock.get("_id"), {duration: 400});
                this.setInputVal(targetBlock.get("_blockNum"));

                return;

                var b = _this._findByBlockNum(val);
                
                // Block number provided doesn't correspond to any block. Take the user to the nearest alternative.
                if (!b) {
                    var firstBlock = _.first(Adapt["blocks"].models);

                    if (val < firstBlock.get("blockNum")) b = firstBlock;  // Value is too low.
                    else b = _.last(Adapt["blocks"].models);               // Value is too high.
                    
                    val = b.get("blockNum");
                }

                Adapt.pageLevelProgress.scrollToPageElement(b.get("_id"));
                _this._updateBlockNum(b); // Update the blockNum bar as soon as the user submits the value.

                // Update the text value as soon as it is submitted.
                $(this).val(val);
            }
        }

        setInputVal(val) {
            let input = this.$(".js-sidenav-blocknum-input")[0];
            input.value = val;
            this.inputVal = val;
        }

        // If the user clicks the blocknum, highlight the input field for editing.
        onClick() {
            let $input = this.$(".js-sidenav-blocknum-input");
            $input.select();
        }

        setupEventListeners(){

            this.listenTo(this.model, "change:_inviewBlock", this.onInviewBlockChange);
        }

        onInviewBlockChange(model, blockModel) {
            
            // Set the blockNum input value to be that of the currently onscreen blockNum.
            this.setInputVal(blockModel.get("_blockNum"));

            // Stop listening for changes to _completionPercent on the previous blockModel.
            if (this.model.get("_curBlock")) {
                this.stopListening(this.model.get("_curBlock"), "change:_completionPercent");
            }

            // Set the progress value, and start listening for changes to it.
            this.setProgressPercent(blockModel.get("_completionPercent"));
            this.model.set("_curBlock", blockModel);
            this.listenTo(this.model.get("_curBlock"), "change:_completionPercent", this.onCurBlockCompletionPercentChange);
        }

        setProgressPercent(percentage) {
            var $r = this.$(".sidenav__blocknum-half-rot");
            var r = ((1 - percentage/100) * 180);

            $r.eq(0).css({
                "transform": "rotate(" + r + "deg)"
            });
            $r.eq(1).css({
                "transform": "rotate(" + (-r) + "deg)"
            });
        }
        
        onCurBlockCompletionPercentChange(blockModel, completionPercent){
            this.setProgressPercent(completionPercent);
        }
        
    };

    return BlocknumView;
});
