define([
  'core/js/adapt'
], function(Adapt) {

	class CAInspectorVarView extends Backbone.View {

		className() {
			return "codeanim__inspector-row";
		}

		initialize(){
			
			this.setupEventListeners();
			this.render();
		}

		setupEventListeners(){

			this.listenTo(this.model.get("_playbackControlsModel"), "change:frameIdx", this.onFrameIdxChange);
			this.listenTo(this.model, "change:val", this.onValChange);
			this.listenTo(this.model, "change:_isVisible", this.onVisibleChange);

			// FIXME: Set up initial state by triggering the initial change.
			_.debounce(() => {this.onFrameIdxChange(this.model.get("_playbackControlsModel"), this.model.get("_playbackControlsModel").get("frameIdx"));})();
		}
		
		render(){

			var data = this.model.toJSON();
            data.view = this;
            var template = Handlebars.templates['codeAnimInspectorVar'];
            this.$el.html(template(data));
		}

		onFrameIdxChange(model, frameIdx){

			// Determine what state to show.
			var val = undefined;
			var vals = this.model.get("_vals");
			var idx = -1;

			_.each(vals, function(v){
				if (v.frameIdx <= frameIdx) {
					val = v._isEnabled ? v.val : undefined;
					idx = val === undefined ? -1 : v.frameIdx;
				}
			});

			this.model.set("val", val);
		}

		onVisibleChange(model, isVisible){

			// Whenever this view needs to be hidden, make it invisible instead, and move it to a different element.
			// (This will prevent the inspector from resizing its height as the animation plays).
			this.model.get("_parentView")
			.$(isVisible ? ".js-codeanim-inspector-vars-inactive" : ".js-codeanim-inspector-vars-invisible")
			.append(this.$el);

			this.$el.toggleClass("u-visibility-hidden", !isVisible).hide(0).show(0); // hide + show will trigger a CSS animation if there is one.
		}

		onValChange(model, val){

			this.$(".js-codeanim-inspector-var-val").html(val !== undefined ? val : "");
			this.$(".codeanim__inspector-cell-value-inner").hide(0).show(0); // hide + show will trigger a CSS animation if there is one.
			this.model.set("_isVisible", val !== undefined);
		}
	}
  
  	return CAInspectorVarView;
});
