const { history } = require("backbone");

define([
  'core/js/adapt'
], function(Adapt) {

  class ImgFinderOutlineModel extends Backbone.Model {
    
    defaults() {
      return {
        "_size": "avg",                     // The way the size of this outline is determined (set in JSON).
        "_accuracy": 0,                     // How accurately this outline needs to be placed over the outline (0 - 1).

        "_parent": null,                    // Reference to the imgFinderModel which created this Model.
        "_position": {_left: 0, _top: 0},   // Values range from 0-100 (percentages). Location of the CENTER of the outline.

        "_myItem":  null,                   // A reference to the _item that this outline is currently _isCorrect over.
        "_myItems": [],                     // A list of all !_isLocked items that this outline is currently covering.

        // CSS properties to apply to the View.
        // Will get updated as the user interacts with the widget.
        _width: 0,
        _height: 0,
        _top: 0,
        _left: 0
      }
    }

    initialize() {

      // Determine what kind of sizing this outline should use.
      this.calcSize();

      this.onPositionChange(this, this.get("_position")); // Initial setup.

      this.setupEventListeners();
    }

    calcSize() {
      let sizeType = this.get("_size");
      switch (sizeType) {

        /** The outline will take on the size of the _item that's closest to it. */
        case "near":
          throw `ImgFinder: _outline._size == "near" is not implemented yet.`;

        /** All outlines will have the exact same size; that size being the average of all _item sizes */
        case "avg":
        default:
          this.set(this.getAvgOutlineSize());
      }
    }

    setupEventListeners() {
      this.listenTo(this, "change:_position", this.onPositionChange);
    }

    /**
     * Whenever the "_position" changes, update the style properties accordingly.
     * @param {*} model 
     * @param {*} position 
     */
    onPositionChange(model, position) {
      this.set("_left", position._left - this.get("_width")/2);
      this.set("_top", position._top - this.get("_height")/2);

      // Check if this outline was just placed over a previous incorrect guess.
      // Mark this outline as incorrect immediately if that's the case.
      this.set("_isIncorrect", this.isOverGuess());
    }

    isOverGuess() {

      // If the outline is correct, it doesn't matter if it's over an incorrect guess.
      if (this.get("_isLocked") || this.get("_isCorrect")) return false;

      // If the outline is currently over an item, don't mark it as incorrect.
      if (this.calcOverlappedItems().length) return false;

      // Finally, check through all guesses to see if it's over one of them.
      let position = this.get("_position");      
      let guess = this.get("_parent").getGuesses().find(guess => {
        
        if (position._left > guess._left + guess._width) return false;
        if (position._left < guess._left)                return false;
        
        if (position._top > guess._top + guess._height) return false;
        if (position._top < guess._top)                 return false;

        return true;
      });

      return Boolean(guess);
    }

    /**
     * Loops through all _items and stores a list of the ones considered "overlapped" by this outline.
     */
    calcOverlappedItems() {

      // If this outline is locked, it doesn't need to be checked.
      if (this.get("_isLocked")) {
        this.set("_myItems", []);
        return;
      }
      
      let parent = this.get("_parent");
      const accuracy = this.get("_accuracy");

      let items = [];

      parent.getChildren().each((itemModel) => {

        let v = this.getVectorToItem(itemModel);

        // If accuracy == 0, then as long as the center of the outline is within the item's bounds, it's correct.
        // If accuracy == 1, then the outline's center has to be EXACTLY in the center of the item's bounds.
        // (Never set accuracy to 1, as it requires the user to be pixel-perfect).
        let w = itemModel.get("_width")/2 * (1-accuracy);
        let h = itemModel.get("_height")/2 * (1-accuracy);

        // If the center of this outline isn't within the item's box, then the outline isn't overlapping it.
        if (Math.abs(v.x) > w) return false;
        if (Math.abs(v.y) > h) return false;

        items.push({
          proximity: this.getVectorLengthSquared(v), // We don't need accurate proximities, so the more efficient
                                                     // squared length calculation can be used.
          item: itemModel
        });
      });

      items = _.sortBy(items, "proximity");

      this.set("_myItems", items);

      return items;
    }

    /**
     * Returns a reference to a "_myItems" element if the itemModel provided within it.
     * @param {*} itemModel 
     */
    getOverlappingItemData(itemModel) {
      return _.find(this.get("_myItems"), (itemObj) => {return itemObj.item == itemModel;});
    }

    pairWithItem(itemModel) {
      this.set("_item", itemModel);
      this.set("_isCorrect", true);
      this.set("_isIncorrect", false);
      this.set("_isLocked", true);

      // Move this outline to be perfectly centered over the item.
      this.set("_position", itemModel.getPosition());

      itemModel.set({
        _outline: this,
        _isCorrect: true,
        _isLocked: true,
        _isActive: true
      });
    }
    
    /**
     * Returns a vector from this outline's position to the center of the item.
     * Vector values in percentages (0-100).
     * @param {Model} item ImgFinder Item object
     */
    getVectorToItem(item) {

      let position = this.get("_position");

      let x = item.get("_left") + item.get("_width")/2 - position._left;
      let y = item.get("_top") + item.get("_height")/2 - position._top;

      return {
        x: x,
        y: y
      }
    }

    getVectorLength(vector) {
      return Math.sqrt(Math.pow(vector.x,2) + Math.pow(vector.y,2));
    }

    // More efficient.
    getVectorLengthSquared(vector) {
      return Math.pow(vector.x,2) + Math.pow(vector.y,2);
    }

    /**
     * Gets the dimensions of all item elements, then averages them.
     */
    getAvgOutlineSize() {

      let sizeMod = this.get("_sizeMod") || 1;

      let width = 0;
      let height = 0;

      let items = this.get("_parent").getChildren();
      items.each((item) => {
        
        let w = item.get("_width");
        let h = item.get("_height");
        
        // Error check (in case non-number values were entered for _width / _heights);
        if (isNaN(w)) throw `imgFinderView.js: invalid value provided for item "_width": ${w}. Value must be a number!`;
        if (isNaN(h)) throw `imgFinderView.js: invalid value provided for item "_height": ${h}. Value must be a number!`;
        
        width += w;
        height += h;
      });

      let avg = {
        _width: (width / items.length) * sizeMod,
        _height: (height / items.length) * sizeMod
      }

      return avg;
    }

    /**
     * Returns a model with information about where this outline was placed.
     * (Guesses are used to mark a location as already being checked by the user).
     */
    getGuessObj() {
      return {
        _left: this.get("_left"),
        _top: this.get("_top"),
        _width: this.get("_width"),
        _height: this.get("_height")
      };
    }

    remove() {
      // This will send an event to the view to tell it to play a quick removal animation.
      this.set("_isRemoved", true);

      // Immediately remove this outline from the parent model.
      this.collection?.remove(this);
    }
  }

  return ImgFinderOutlineModel;
});
