define([
    'core/js/adapt'
], function(Adapt) {

    /**
     * Model for the entire sliding menu (behaves similar to the Adapt drawer).
     */
    
    class SideMenuModel extends Backbone.Model {

        defaults() {
            return {
                title: "",                   // PageModel (current contentObject) title.
                _pageModel: undefined,       // Current pageModel.
                _categories: undefined,      // categoryCollection (set by adapt-dd-sideMenu). 
                _isActive: false
            }
        }

        initialize(){
            this.set("title", this.get("_pageModel").get("title"));
        }

        toggle(){
            this.set("_isActive", !this.get("_isActive"));
        }
    };

    return SideMenuModel;
});
