define([
  'core/js/adapt',
  'core/js/views/questionView'
], function(Adapt, QuestionView) {

  /**
   * A Question component which shows many items, of which all of the "correct" ones (and none of the incorrect ones) must be clicked on by the user to proceed.
   * Script(s) based on adapt-contrib-mcq component implementation.
   * 
   * Based on the adapt-contrib-mcq scripts.
   * 
   * Notes:
   * 
   * TODO:
   * - Add support for images instead of text for the items.
   * - Add tick and cross icons to the left of the text to indicate correctness (for accessibility). 
   */

  const DESELECT_INCORRECT_DELAY = 1000; // Delay increased to fully wait for the notify__popup to fade out
                                         // (fading it out causes older devices to stutter).

  class FinderView extends QuestionView {
    
    events() {
      return {
        "click .finder__item" : "onItemClick"
      };
    }

    initialize() {
      super.initialize();
      this.update = _.debounce(this.update.bind(this), 1);

      this.setUpEventListeners();
    }

    setupQuestion() {
      this.model.setupRandomisation();

      // Modified:
      // This view's model inherits itemsComponentModel, and as such will consider itself complete if all items have _isVisited: true.
      // adapt-dd-questionPlus doesn't want this, so stop listening for _isVisited events on the items.
      this.model.stopListening(this.model.getChildren(), "change:_isVisited", this.model.checkCompletionStatus);
    }

    // This is important and should give the user feedback on how they answered the question
    // Normally done through ticks and crosses by adding classes
    showMarking() {
      //this.update();
      
      // adapt-dd-questionPlus:
      this.updateMarking();
    }

    update() {
      this.updateMarking();
    }

    postRender() {
      super.postRender();
      this.setUpGridSize();
    }

    /**
     * When the user closes the feedback notify, wait X seconds, then de-activate incorrectly activated items.
     * This was done to improve the UI experience, as some users didn't realize they needed to manually de-select
     * the incorrectly selected items.
     * 
     * DD: DD specific implementation.
     */
    onSubmitted() {
      super.onSubmitted();
      this.listenToOnce(Adapt, "notify:closed", this.onFeedbackClosed);
    }

    onFeedbackClosed() {
      this.deselectIncorrectAnswers();
    }

    deselectIncorrectAnswers() {
      
      _.debounce(() => {
        this.model.getChildren().each((itemModel) => {

          // Only deselect visited answers.
          let isVisited = Boolean(itemModel.get("_isVisited"));
          if (!isVisited) return;

          let shouldBeSelected = Boolean(itemModel.get("_shouldBeSelected"));
          let isActive = Boolean(itemModel.get("_isActive"));
          let isCorrect = shouldBeSelected && isActive;

          if (!isCorrect && isActive) itemModel.set("_isActive", false);
        })
      }, DESELECT_INCORRECT_DELAY)();
    }
    
    showCorrectAnswer() {
      this.update();
    }

    hideCorrectAnswer() {
      this.update();
    }

    setUpEventListeners(){
      
      this.listenTo(this.model.getChildren(), {
        'change:_isActive': this.onItemsActiveChange,
        'change:_isVisited': this.onItemsVisitedChange
      });
    }

    onItemClick(event) {
      if (event) event.preventDefault();

      var item = this.model.getItem($(event.currentTarget).data('adapt-index'));
      item.toggleActive();
    }

    getItemElement(model) {
      var index = model.get('_index');
      return this.$('.js-finder-item').filter('[data-adapt-index="' + index + '"]');
    }

    onItemsActiveChange(model, _isActive) {

      this.getItemElement(model)
        .toggleClass('is-active', _isActive)
        .find(".js-item-label").toggleClass('is-selected', _isActive);
      this.updateContainer();
    }

    onItemsVisitedChange(model, _isVisited) {
      if (!_isVisited) return;
      var $item = this.getItemElement(model);

      // Append the word 'visited.' to the aria-label
      /*var visitedLabel = this.model.get('_globals')._accessibility._ariaLabels.visited + ".";
      $item.find('.aria-label').each(function(index, el) {
        el.innerHTML += " " + visitedLabel;
      });*/

      $item.addClass('is-visited');
    }

    /**
     * adapt-dd-questionPlus will called this whenever the user clicks Submit.
     */
    updateMarking() {

      let isInteractive = this.model.isInteractive();
      let canShowMarking = this.model.get('_canShowMarking');
      let ariaLabels = Adapt.course.get('_globals')._accessibility._ariaLabels;

      // FIXME: Checkbox state changes are a bit messily implemented.

      this.model.getChildren().each((itemModel) => {

        let index = itemModel.get('_index');
        let $item = this.getItemElement(itemModel);
        let $itemLabel = $item.find(".js-item-label");

        let shouldBeSelected = Boolean(itemModel.get("_shouldBeSelected"));
        let isActive = Boolean(itemModel.get("_isActive"));
        // An item is only considered "visited" once it has been submitted at least once.
        let isVisited = Boolean(itemModel.get("_isVisited") || isActive); itemModel.set("_isVisited", isVisited);
        let isCorrect = shouldBeSelected && isActive; // Only mark active items as correct.

        // Disable items only when the item has been completed, or when the entire component has been completed.
        let isDisabled = !isInteractive || isCorrect;
        $item
          .prop('disabled', isDisabled)
          .toggleClass('is-disabled', isDisabled)
          .find(".finder__item-answer-icon")
          .toggleClass('is-disabled', isDisabled);

        // Remove marking from non-visited items.
        if (/*isInteractive || */ !isVisited || !canShowMarking) {
          $item.removeClass('is-correct is-incorrect');
          $itemLabel.removeClass('is-correct is-incorrect');
          return;
        }

        // Mark visited items (that are selected).
        // (DD questionPlus design choice).
        if (!isActive) return;

        $item
          .toggleClass('is-correct', isCorrect)
          .toggleClass('is-incorrect', !isCorrect)
          .toggleClass('is-disabled', isDisabled)
          .prop('disabled', isDisabled);

        $itemLabel
        .toggleClass('is-correct', isCorrect)
        .toggleClass('is-incorrect', !isCorrect)
        .toggleClass('is-disabled', isDisabled);
      });

      this.updateContainer();
    }

    /**
     * The container for the items will gain the .is-incorrect class while it contains any active incorrect items.
     * The container for the items will gain the .is-correct class only when the component is completed.
     */
    updateContainer() {

      let isAtLeastOneIncorrect = Boolean(
        this.model.getChildren().find((itemModel) => {
          return itemModel.get("_isVisited") && itemModel.get("_isActive") && !itemModel.get("_shouldBeSelected");
        })
      );

      let $container = this.$(".finder__items-container");
      $container
        .toggleClass("is-incorrect", isAtLeastOneIncorrect)
        .toggleClass("is-correct", Boolean(this.model.get("_isCorrect")));
    }

    /*onDeviceResize() {
      this.recalcItemSize();
    }*/

    /**
     * FIXME: Temp (niche; only applies if each item is one word).
     * Set up the wrapping CSS grid based on the widest item.
     */
    setUpGridSize() {

      /*
      // Find the widest item.
      let maxSize = 0;
      this.model.getChildren().each((itemModel) => {
        
        let text = itemModel.get("text");
        if (text.length > maxSize) maxSize = text.length;
      });

      // Set the grid column width.
      this.$(".finder__items-container").css({
        "grid-template-columns": "repeat(auto-fit, minmax(" + maxSize + "em, 1fr))" 
      });
      */
    }

    onQuestionRendered() {
      this.setReadyStatus();
      this.update();
    }
  }

  FinderView.template = "finder";
  return FinderView;
});
