define([
    'core/js/adapt'
], function(Adapt) {

    class TimelineModel extends Backbone.Model {

        /**
         * TODO:
         * - Allow for two modes: linear, and steps (one mode has the timeline clearly go through steps, the other smoothly plays it like a video).
         */

        defaults() {
			
			return {
				frameCount: 0,          // Total number of frames.
				frameIdx: -1,           // Index of the current frame in the animation.
				frameDuration: 500,		// How long each frame should be active for during the animation (steps mode only).
				mode: "steps",          // steps | linear
				
				// Calculated //
				duration: undefined,	// Total duration of the animation.
				position: 0,			// Current time in the animation (from 0 to duration).
				
				isPlaying: false,
				isComplete: false,      // Only true if !isPlaying and position == duration.

				_prevTimestamp: undefined		// Used for calculating deltatime.
			}
        }

        initialize(){

            this.setupFrames();
			this.setupEventListeners();
			//this.initFrames();
        }
        
        /**
         * Depending on the settings provided, initialise the range slider with different min/max values.
         */
        setupFrames(){

            if (this.get("mode") == "steps"){

                var frameCount = this.get("frameCount");
                var frameDuration = this.get("frameDuration");
                var duration = this.get("duration");
                
                // Determine the 3rd value based on the value of the other two.
                if (frameCount && frameDuration) {
                    duration = frameCount * frameDuration;
                }
                else if (frameDuration && duration) {
                    frameCount = frameDuration / duration;
                }
                else {
                    frameDuration = frameCount / duration;
                }

                this.set("frameCount", frameCount);
                this.set("frameDuration", frameDuration);
                this.set("duration", duration);

                if (frameCount <= 0)
                    throw "Timeline calculated frameCount cannot be <= 0 (ensure the \"frameDuration\" and \"duration\" properties are manually set)."

                return;
            }

            throw "Non-\"steps\" mode support has not yet been implemented.";
            return;
        }

		setupEventListeners() {
            this.listenTo(this, "change:isPlaying", this.onIsPlayingChange);
            this.listenTo(this, "change:position", this.onPositionChange);
            this.listenTo(this, "change:frameIdx", this.onFrameIdxChange);
		}
		
		onIsPlayingChange(model, isPlaying){

			// Start the requestAnimationFrame loop.
			if (isPlaying) {

				// If the animation was at the end of the timeline, reset it.
				if (this.get("isComplete")) {
					this.set("position", 0);
				}

				this.set("_prevTimestamp", undefined);
				window.requestAnimationFrame(this.step.bind(this));
			}
		}

		onFrameIdxChange(model, frameIdx){

            this.set("position", frameIdx * this.get("frameDuration"));
            this.set("isComplete", frameIdx == this.get("frameCount")-1);
		}

		onPositionChange(model, position){

			// TODO: Disable this when not using the steps mode.

			// Set the frameIdx = to the position / frameDuration.
			var frameIdx = Math.floor(position / this.get("frameDuration"));
			if (frameIdx >= this.get("frameCount")) frameIdx = this.get("frameCount")-1;
			this.set("frameIdx", frameIdx);
		}

		step(timestamp){

			if (!this.get("isPlaying")) return; // Stop if paused.
			if (this.get("_prevTimestamp") === undefined) {this.set("_prevTimestamp", timestamp);}

			var delta = timestamp - this.get("_prevTimestamp"); // FIXME: == 0 on frame 0.
			var position = this.get("position") + delta; // Increment elapsed time.
			this.set("_prevTimestamp", timestamp);

			// If the elapsed time is greater than the duration, stop.
			var duration = this.get("duration");
			if (position > duration) {
				this.set("position", duration);
				this.pause();
				return;
			}

			// Continue to next tick.
			this.set("position", position);
			window.requestAnimationFrame(this.step.bind(this));
		}
		
		togglePlayPause(){
			this.set("isPlaying", !this.get("isPlaying"));
		}

		reset(){
			this.set("isPlaying", false);
			this.set("isComplete", false);
			this.set("position", 0);
			this.set("_prevTimestamp", undefined);
		}

		pause(){
			this.set("isPlaying", false);
		}

		play(){
			this.set("isPlaying", true);
		}
    };

    return TimelineModel;
});
