define([
    'core/js/adapt',
    './stickyElementItemView',
    './stickyElementItemModel'
], function(Adapt, StickyElementItemView, StickyElementItemModel) {

    /**
     * Each stickyElementModel shall be used by multiple stickyElementViews (that have the same id).
     */

    // StickyElement will only work when inside the following components.
    // TBA: Expand this dictionary as more components are tested with this extension.
    // FIXME: Components should register themselves as valid in their own scripts.
    const CompatibleComponents = {
        "tabs": {
            "_hasTransitions": false
        },
        "narrativePlus": {
            "_hasTransitions": true
        }
    }

    class StickyElementModel extends Backbone.Model {

        defaults() {
            return {
                _parent: undefined,                     // Reference to the ComponentModel that this model is in.
                _id: "",                                // Sticky element id.
                body: "",                               // Body HTML for each view.
                
                _items: new Backbone.Collection(),      // Collection of all stickyElementItemModels with this _id.
                _itemScrollTop: 0,                      // All _items views need to synchronize their scrolling elements (if applicable),
                _itemScrollLeft: 0,                     
                                                        // to make them all seem as one.

                _itemActive: undefined,                 // Reference to the currently active StickyElementItemModel with the same _id (if any).
                _itemPrev: undefined,                   // Reference to the StickyElementItemModel that was active before the current one.
            }
        }

        initialize(){

            this.listenTo(this.get("_parent").getChildren(), "change:_isActive", this.onComponentItemActiveChange);
            this.listenTo(this, "change:_itemActive", this.onItemActiveChange);
        }

        checkValidParent() {

            let parentType = this.get("_parent").get("_component");

            let cc = CompatibleComponents[parentType];
            if (cc === undefined) {
                throw "adapt-stickyElement: this extension is not compatible with the \"" + parentType + "\" component.";
            }

            // Record whether the items the parent has animate as they reveal, or if they're instantaneous.
            this.set("_parentHasTransitions", cc._hasTransitions);
        }

        // Called by StickyElementItemModel whenever its parent ComponentItem becomes _isActive.
        setItemActive(itemActiveModel) {
            this.set("_itemActive", itemActiveModel);
        }

        onComponentItemActiveChange(componentItem, isActive) {
            
            // If one of this model's _items is inside of this ComponentItem, do nothing.
            if (this.get("_items").find(item => {return item.get("_componentItem") === componentItem})) return;

            // This ComponentItem has none of this StickyElementModel's items in it. Clear _itemActive.
            this.setItemActive(undefined);
        }

        // Update the _itemPrev property whenever the next _itemActive is set.
        onItemActiveChange(model) {
            this.set("_itemPrev", model._previousAttributes._itemActive);
        }

        /**
         * 
         * @param {*} componentItemModel 
         * @param {*} options 
         * @param {Boolean} options._isStickyBlock If true, the StickyElement will use "StickyBlock" mode. 
         * @returns 
         */
        registerComponentItemModel(componentItemModel, options) {

            let model = new StickyElementItemModel({
                _parent: this,
                _id: this.get("_id"),
                body: this.get("body"),
                _componentItem: componentItemModel,
                _isStickyBlock: Boolean(options?._isStickyBlock)
            });

            let view = new StickyElementItemView({
                model: model
            });

            model.set("_view", view);
            this.get("_items").push(model);

            return view;
        }

        /**
         * Trigger's a stickyElement:itemShow on the currently active item model.
         * Used by the stickyElementItemView to set its initial state.
         */
        refresh(){
            this.get("_itemModels").find((model) => {
                if (model.get("_isActive")){

                    this.trigger("stickyElement:itemShow", model.get("_index"));
                    this.set("_itemCur", model);
                    return true;
                }
            });
        }
    };

    return StickyElementModel;
});
