define([
  'core/js/adapt',
  'core/js/views/questionView',
  './matchingPlusDropdownView'
], function(Adapt, QuestionView, MatchingPlusDropdownView) {

  // https://www.24a11y.com/2019/select-your-poison/
  // https://css-tricks.com/striking-a-balance-between-native-and-custom-select-elements/

  const MIN_OPTION_LENGTH = 12;

  class MatchingPlusView extends QuestionView {

    events() {
      return {
        "change .js-matchingplus-select" : "onSelectChange"
      }
    }

    postRender() {
      super.postRender();
      this.$el.closest(".block").addClass("has-matchingplus");

      // Have a seperate view control each dropdown element.
      this.dropdownViews = [];
      this.model.getChildren().each((itemModel) => {
        this.dropdownViews.push(new MatchingPlusDropdownView({
          model: itemModel,
          el: this.getItemEl(itemModel).find(".js-matchingplus-dropdown")[0]
        }));
      });

      this.setUpEventListeners();

      // Set a consistent width for all the 'select' elements.
      let optionLength = 0;
      _.each(this.model.get("_options"), (option) => {
        optionLength = Math.max(option.length, optionLength);
      });
      optionLength = Math.max(Math.round(optionLength * 0.7), MIN_OPTION_LENGTH);
      this.$(".matchingplus__item-select").css("width", optionLength + "em");
    }

    setUpEventListeners() {
      this.listenTo(this.model.getChildren(), {
        "change:_optionsSelected": this.onItemOptionsSelectedChange,
        "change:_isDisabled": this.onItemDisabledChange
      });
    }

    onQuestionRendered() {
      this.setReadyStatus();
      this.update();
    }

    getItemByIndex(index) {
      return this.model.getChildren().find((item) => {
        return item.get("_index") === index;
      });
    }

    getItemEl(itemModel) {
      return this.$(".js-matchingplus-item").filter("[data-adapt-index=" + itemModel.get("_index") + "]");
    }

    update() {
      this.updateMarking();
    }

    showMarking() {
      this.updateMarking();
    }

    /**
     * Mark the visited options in the dropdown as incorrect/correct.
     */
    updateMarking() {

      _.each(this.dropdownViews, (dropdownView) => {
        dropdownView.updateMarking();
      });

      this.model.getChildren().each(itemModel => {
        this.updateItemState(itemModel);
      });
    }

    updateItemState(itemModel) {
      let $item = this.getItemEl(itemModel);
      let options = itemModel.get("_options");
      let optionsSelected = itemModel.get("_optionsSelected");

      let anyIncorrect = false;
      let anyCorrect = false;

      _.each(optionsSelected, (optionIdx) => {
        let option = _.find(options, (o) => {return o._index == optionIdx;});
        if (option._isIncorrect) anyIncorrect = true;
        if (option._isCorrect) anyCorrect = true;

        if (option._isVisited) {
          let $option = $item.find(".js-matchingplus-option[data-value='" + optionIdx + "']");
          $option
            .toggleClass("is-visited", option._isVisited)
            .toggleClass("is-correct", option._isCorrect)
            .toggleClass("is-incorrect", option._isIncorrect);
        }
      });

      // Mark eliminated options
      _.each(options, (option) => {

        if (!option._isEliminated) return;

        let $option = $item.find(".js-matchingplus-option[data-value='" + option._index + "']");
        $option.toggleClass("is-eliminated", option._isEliminated);
      });

      $item
        .toggleClass("is-incorrect", anyIncorrect)
        .toggleClass("is-correct", !anyIncorrect && anyCorrect);
    }

    setupQuestion() {
			this.model.setupRandomisation();
		}

    resetQuestion() {

			// Not implemented (DD doesn't use this at this time).
			return;
		}

    //#region Synchronise the states of the <select>s, the dropdowns, and the itemModels.

    /**
     * Whenever an itemModel's _optionsSelected values change, set the value for the <select> element, and update the item highlighting.
     */
    onItemOptionsSelectedChange(itemModel, optionsSelected) {
      
      // FIXME: Won't work if there's more than one element in optionsSelected.
      if (!optionsSelected.length) return;
      if (optionsSelected.length > 1) throw "matchingPlusView: multiple _optionsSelected values not supported at this time.";
      
      let optionIdx = optionsSelected[0];

      let $select = this.getItemEl(itemModel).find(".js-matchingplus-select");
      $select.addClass("is-visited");
      $select[0].value = optionIdx;

      this.updateItemState(itemModel);
    }

    /**
     * When the item model is disabled, ensure that there can be no more inputs to the <select> element.
     * @param {*} itemModel 
     * @param {*} isDisabled 
     */
    onItemDisabledChange(itemModel, isDisabled) {
      let $select = this.getItemEl(itemModel).find(".js-matchingplus-select");
      $select.toggleClass("is-disabled", isDisabled);

      if (isDisabled)
        $select.attr("disabled", "");
      else
        $select.removeAttr("disabled");
    }

    /**
     * Whenever the value in the <select> elements changes update item values.
     */
    onSelectChange(event) {
      let itemIdx = $(event.currentTarget).data("adapt-index");
      let itemModel = this.getItemByIndex(itemIdx);

      let val = $(event.currentTarget).val();
      
      // Niche bugfix:
      // On some devices, the user can select the placeholder option. Ignore those cases, and auto-set the select to the last valid value.
      if (!val && val !== 0) {
        let lastVal = itemModel.get("_optionsSelected");
        if (lastVal) {
          $(event.currentTarget).val(lastVal);
        }
        return; 
      }

      itemModel.set("_optionsSelected", [parseInt($(event.currentTarget).val(),10)]);
    }

    //#endregion
  }

  MatchingPlusView.template = "matchingPlus";
  return MatchingPlusView;

});
