define([
    'core/js/adapt',
    "./SideMenuCategoryView"
], function(Adapt, SideMenuCategoryView) {

    // View specifically for the "nav" category.
    // Clicking its items will cause the page to scroll to them.

    class SideMenuCategoryNavView extends SideMenuCategoryView {

        events() {
            return {
                'click .js-nav-item-btn-click': 'scrollToPageElement'
            }
        }

        init(){
            this.setUpModelData();
            this.setUpEventListeners();
        }

        setUpEventListeners() {
            this.listenTo(Adapt.blocks, "change:_isComplete", this.onBlockCompleteChange);
            this.listenTo(Adapt.blocks, "change:_isInview", this.onBlockInviewChange);
        }

        setUpModelData(){
            
            // Determine the title used to refer to each block by finding the first title in its child components.
            _.each(this.model.get("_items").models, (block) => {

                if (!block.get("_sideMenu")) block.set("_sideMenu", {});

                let title = "";
                _.find(block.getChildren().models, (component) => {
                    title = component.get("title");
                    if (title) return true;
                });

                if (!title) throw "Block " + block.get("_id") + " doesn't have any child components with a defined title.";
                block.get("_sideMenu").title = title;
                //block.get("_sideMenu")._isQuestionType = block.get("_isQuestionType");
            });
        }

        onBlockCompleteChange(blockModel, isComplete) {

            this.$(".js-nav-item-btn-click[data-id='" +blockModel.get("_id") + "']").toggleClass("is-complete", isComplete);
        }

        onBlockInviewChange(blockModel, isInview) {
            this.$(".js-nav-item-btn-click[data-id='" +blockModel.get("_id") + "']").toggleClass("is-inview", isInview);
        }

        /**
         * Used implementation from PageLevelProgressView.js
         * @param {*} event 
         */
        async scrollToPageElement(event) {
            if (event && event.cancelable && event.preventDefault) event.preventDefault();
      
            var $target = $(event.currentTarget);
            if ($target.is('.is-disabled')) return;
      
            var id = $target.attr('data-id');
            var model = Adapt.findById(id);
      
            // Trigger an event on the model being scrolled to.
            // The articleNav extension will catch it and change articles as necessary.
            model.trigger("blockView:navigate", model);
            
            if (!model.get('_isRendered')) {
              try {
                await Adapt.parentView.renderTo(id);
              } catch(err) {
                return;
              }
            }
      
            var currentComponentSelector = '.' + id;
            Adapt.navigateToElement(currentComponentSelector, {duration: 400});

            // Hide the menu if on a smaller device.
            // FIXME: Currently determined by the CSS opacity of the shadow.
            let $shadow = $(".sidemenu__shadow.js-sidemenu-shadow-click");
            if (parseFloat($shadow.css("opacity"))) {
                Adapt.sideMenu.toggle();
            }
        }
        
    };

    SideMenuCategoryNavView.template = 'sideMenuCategoryNav';
    return SideMenuCategoryNavView;
});
