define([
    'handlebars',
    'core/js/adapt',
    './figureView',
    './figureModel'
], function(Handlebars, Adapt, FigureView, FigureModel) {

    /**
     * adapt-dd-figure
     * --------------------------------------
     * Register a Handlebars helper for the very commonly used <figure> container element.
     */

    let CAPTION_COUNTS = {}; // Global value which is used to set the "Figure X" text on the figures which use <figcaption>.

    class Figure extends Backbone.Controller {

        initialize() {
            Adapt.on('router:page', this.onRouterPage);
        }

        onRouterPage() {

            // Helpers
            // ---------------------
            Handlebars.registerHelperEl("figure", function() {
                let context = arguments[arguments.length-1];
                let modelJSON = _.extend({}, context.hash, {
                    content: context.fn(this)
                });

                return new FigureView({
                    model: new FigureModel(modelJSON)
                }).$el;
            });
        }

        _getCaptionCount(type, increment) {
            if (CAPTION_COUNTS[type] === undefined) {
                CAPTION_COUNTS[type] = 0;
            }
            if (increment) {
                CAPTION_COUNTS[type]++;
            }

            return CAPTION_COUNTS[type];
        }

        /**
         * Legacy:
         * -------
         * (Used for BU11; do not use elsewhere).
         * Creates a prefix for a caption with a unique, incremented number.
         */
        createCaptionData(captionStr, content) {
            
            let type = "Figure";
            // If there's a table inside, change the prefix.
            if (content && (content.string || content).includes("<table")) type = "Table";

            if (captionStr === true) captionStr = " ";

            /**
             * Invisible captions: if a caption string is prefixed with a "!", then it will be invisible.
             * (this is used by BU11 StickyBlocks to keep figureItem height consistent).
             */
            let isInvis = captionStr[0] === "!";
            let c = this._getCaptionCount(type, !isInvis);
            if (isInvis) captionStr = captionStr.substr(1); // Remove the "!".

            let captionPrefix = `${type} ${c}${captionStr.trim().length == 0 ? "" : "."} `;
            // If there's a "!!" at the start of the string, don't show a prefix at all.
            if (captionStr[0] === "!") {
                captionStr = captionStr.substr(1); // Remove the second "!".
                captionPrefix = "";
            }

            let data = {
                captionPrefix: captionPrefix,
                caption: captionStr,
                isCaptionInvis: isInvis
            }
            
            return data;
        }
    }

    let figure = new Figure();
    Adapt.figure = figure;
});
