define([
    'core/js/adapt',
    './sideNavProgressView',
    './sideNavBlocknumView'
], function(Adapt, SideNavProgressView, SideNavBlocknumView) {

    /**
     * This will be placed at the bottom / right of the sideNav bar and will contain the SideNavBlockView and the SideNavProgressView.
     */

    class SideNavWidgetView extends Backbone.View {

        className() {
            return "sidenav__widget";
        }

        events() {
            return {
                //"click .btn__prev" : "onPrevClick",
                //"click .btn__next" : "onNextClick"
            }
        }

        initialize(){
            
            this.setupEventListeners();
            this.render();
        }

        render(){
            this.$el.append(new SideNavProgressView({model: this.model}).$el);
            this.$el.append(new SideNavBlocknumView({model: this.model}).$el);
        }

        setUpModelData(){
            this.model.set("_inviewBlock", Adapt.blocks.models[0]);
        }

        // Listen for whenever a block moves into view.
        setupEventListeners(){

            this.listenTo(Adapt, "blockView:postRender", this.setUpBlockInview);
        }

        setUpBlockInview(view) {

            view.$el.off("onscreen.sideNavBlockView");
            view.$el.on("onscreen.sideNavBlockView", (event, m) => {

                const threshold = 50;
                
                if (m.percentFromTop < threshold && m.percentFromBottom < threshold) {

                    // Only one child will be active at once; the one that's closest to the vertical center of the screen.
                    if (this.model.get("_inviewBlock") && this.model.get("_inviewBlock") !== view.model) {
                        this.model.get("_inviewBlock").set("_isInview", false); // FIXME: Used by SideMenu.
                    }

                    this.model.set("_inviewBlock", view.model);
                    this.model.get("_inviewBlock").set("_isInview", true);
                }
            });
        }

        remove() {
            Adapt.blocks.each((blockModel) => {
                Adapt.findViewByModelId(blockModel.get("_id")).$el.off("onscreen.sideNavBlockView");
            });

            Backbone.View.remove.apply(this, arguments);
        }
        
    };

    return SideNavWidgetView;
});
