define([
    'core/js/adapt',
    './extraLearningView'
], function(Adapt, ExtraLearningView) {

    /**
     * adapt-extraLearning
     * --------------------------------------
     * This extension renders an inline extraLearning button which shows a popup for optional learning material when clicked.
     * 
     * LIMITATIONS:
     * - Can only be used within componentViews (and any notify popups they create) at this time.
     * 
     * FIXME:
     * - If the button is double-clicked, will cause the animation to bug out (does not break the content though).
     */

    Adapt.on('router:page', () => {

        Handlebars.registerHelperEl("extraLearning", function() {

            let context = arguments[arguments.length-1];
            let hash = context.hash;

            // Find the componentModel that this Helper was placed in.
            let componentModel = Handlebars.getComponentModelFromContext(context);

            // Find the corresponding popup data that was added in the "partial" to the component's data.
            let id = hash.id;
            let popupData = _.find(componentModel.get("_extraLearning")._items, (extraLearningItem) => {
                return extraLearningItem._id == id;
            });
            if (!popupData) throw "{{#extraLearning}}: Could not find _extraLearning._items element with _id: \"" + id + "\"";

            // Set the text on the button to what was defined in course.json
            let _default = Adapt.course.get("_extraLearning");
            if (!_default) {
                throw "No \"_extraLearning\" property defined in course.json.\n\nSee src/extensions/adapt-extraLearning/example.json for instructions.";
            }

            let type = popupData._type;
            let title = "MISSING_TITLE_ERROR";
            if (!_default[type]) {
                throw "Could not find course._extraLearning data with key(\"_type\"): \"" + type + "\".";
            }
            title = _default[type].title;
            let content = context.fn ? context.fn(this).trim() : undefined;

            let modelJSON = {
                content: content,
                title: content || popupData.title || title,
                _type: type,
                _componentParent: componentModel,
                _popupData: popupData
            };

            return new ExtraLearningView({
                model: new Backbone.Model(_.extend({}, hash, modelJSON))
            }).$el;
        });
    });
});
