define([
    'handlebars',
    'core/js/adapt',
    './figureItemView'
], function(Handlebars, Adapt, FigureItemView) {

    // Item helper
    // ------------------

    /**
     * @param {?string} hash.class                    Additional CSS classes to add to the root item element.
     * @param {?object | ?string} hash.sizing         adapt-sizing object (see adapt-sizing for more info).
     * 
     * @param {?boolean | ?number} hash.lightbox      If not falsy, will allow the user to click the item and have it appear in a popup.
     *                                                "auto": will make the item in the popup use auto width.
     *                                                1 - 16: will make the item set its width to this sizing value.
     * @param {?boolean} hash.scaledown               If true, will 'transform: scale()' down the inner item element to fit.
     * 
     * @param {?boolean} hash.stack                   If true, will stack all child elements in the item on top of each other.
     *                                                (For customs).
     * 
     * @param {?string} hash.caption                  (LEGACY): adds a figurecaption.
     * 
     * @returns An empty figureItem (which will be assigned a view + contents once the parent {{#figure}} is rendered).
     */
    function renderFigureItem() {
        let data = Handlebars.getContextHash.apply(this, arguments);
        
        if (data.sizing === undefined) data.sizing = "auto"; // Default to auto width if no sizing specified.
        if (data.sizing) data.sizing = Adapt.sizing.getSizingClass(data.sizing);
        
        // New feature: allow horizontal scrollbars to be fully disabled on figure items (if specified). Important: keep the default as true (legacy support).
        if (data.scrolling === undefined) data.scrolling = true;

        // If data.scaledown == true, will 'transform: scale()' down the inner item element to fit.
        if (data.scaledown || data.scaleDown) {
            data.scrolling = false;
            data.scaleDown = true;
        }

        // If "lightbox" is enabled, will 'transform: scale()' down the inner item element to fit.
        // If scaled down, will show a lightbox icon and trigger a popup to open when clicked.
        if (data.lightbox) {
            data.scrolling = false;
            data.scaleDown = true;
            data.lightbox = data.lightbox;
        }

        // Legacy: support for captions.
        if (data.caption) {
            data = _.extend({}, data, Adapt.figure.createCaptionData(data.caption, data.content));
        }

        // FIXME: Temporary implementation to pass the model data to the figureItemView.
        let d = {
            scaleDown: data.scaleDown,
            lightbox: data.lightbox,
            scrolling: data.scrolling,
            caption: data.caption,
            captionPrefix: data.captionPrefix,
            stack: Boolean(data.stack)
        }
        data.data = JSON.stringify(d);

        let html = Handlebars.templates["ddFigureItem"](data);
        return new Handlebars.SafeString(html);
    }
    
    Handlebars.registerHelper("item", renderFigureItem);
    // Legacy.
    Handlebars.registerHelper("figureItem", renderFigureItem);
    Handlebars.registerHelper("figureitem", renderFigureItem);

    // figuretext helpers
    // ------------------
    // FIXME: Should be figureText (for consistency).
    function renderFigureText() {
        let data = Handlebars.getContextHash.apply(this, arguments);
        if (data.sizing) data.sizing = Adapt.sizing.getSizingClass(data.sizing);

        let html = Handlebars.templates["ddFigureText"](data);
        return new Handlebars.SafeString(html);
    }
    
    Handlebars.registerHelper("figureText", renderFigureText);
    // Legacy.
    Handlebars.registerHelper("figuretext", renderFigureText);

    /**
     * Renders a figure container element.
     * These containers are frequently created with {{#figure}} helpers to create custom layouts in our content.
     */
    class FigureView extends Backbone.View {

        className() {
            let classes = [
                "figure",
                this.model.get("class") || "",
                this.model.get("reverse") ? "is-reverse" : "",
                this.model.get("align") ? ("align-" + this.model.get("align")) : "",
                this.model.get("justify") ? ("justify-" + this.model.get("justify")) : "",
                this.model.get("wrap") === false ? "no-wrap" : "",
                this.model.get("mode") ? ("is-mode-" + this.model.get("mode")) : "",
                this.model.get("theme") ? ("theme-" + this.model.get("theme")) : "",
            ];

            // Flex direction (row | column | etc.).
            // -----------------------------------------------------------
            let direction = this.model.get("direction");
            
            // Legacy (old naming) support ("column" property deprecated).
            if (direction === undefined) {
                direction = this.model.get("column") ? "column" : "row";
            }

            // is-row | is-column | is-wrap
            classes.push(`is-${direction}`);

            return classes.join(" ");
        }

        tagName() {
            return "figure";
        }

        attributes() {
            let attr = {};
            if (this.model.get("style")) attr.style = this.model.get("style");
            if (this.model.get("name")) attr.name = this.model.get("name");
            return attr;
        }

        initialize(){

            this.render();
            this.setupEventListeners();
        }

        setupEventListeners(){

            
        }

        

        render(){
            let template = Handlebars.templates['ddFigure'];
            this.$el.html(new Handlebars.SafeString(template(this.model.toJSON())).string);

            // Ensure that the children of the figure all have the .figure__item class.
            // Each child that doesn't needs to be wrapped in one.
            // EDIT: Not necessary.
            //this.$(".figure__inner").children().filter(":not(.figure__item)").wrap("<div class='figure__item'></div>");

            // Apply a view for each item.
            this.$(".js-figure-item").each((i, e) => {

                // FIXME: Very bad way of passing the data, but should work for now.
                let modelData = {};
                try {
                    modelData = JSON.parse($(e).attr("data-model"));
                    $(e).removeAttr("data-model");
                }
                // JSON.parse shouldn't fail, but if it does, use an empty object as a fallback.
                catch (err) {
                    modelData = {};
                }

                modelData = _.extend({}, modelData, {
                    _parent: this.model
                });
                
                new FigureItemView({
                    model: new Backbone.Model(modelData),
                    el: e
                })
            });

            // Set up debug interact for positioning mark elements.
            _.delay(() => {
                if (Adapt.debug?.setUpInteract) {

                    let $marks = this.$(".mark");
                    if (!$marks.length) return;

                    Adapt.debug.setUpInteract(
                        this.$(".mark"),
                        this.$(".ddimg__inner"),
                        false
                    );
                }
            }, 0);
        }
    };

    return FigureView;
});
