define([
    'core/js/adapt'
], function(Adapt) {

    /**
     * adapt-sizing
     * ============
     * 
     * Description:
     * ------------
     * Give the designers more control over the dimensions of components and other elements.
     * 
     * TODO:
     * -----
     * - This extension may be renamed in the future.
     * 
     * FIXME:
     * ------
     * 
     * 
     * Notes:
     * ------
     * 
     */

    class Sizing extends Backbone.Controller{

        initialize() {
            Adapt.on("componentView:postRender", this.componentViewPostRender.bind(this));
        }

        /**
         * Automatically apply classes to components based on their _sizing properties.
         * @param {*} componentView 
         */
        componentViewPostRender(componentView) {

            let sizing = componentView.model.get("_sizing");
            if (!sizing || sizing._isEnabled === false) return;

            componentView.$el.addClass(this.getSizingClasses(sizing));
        }

        /**
         * Check that only valid values have been entered for the specified property.
         * @param {*} sizingObj 
         * @param {*} propName 
         * @param {*} isBoolValid 
         */
        _validateProperty(sizingObj, propName, isBoolValid) {

            let prop = sizingObj[propName];
            if (prop === undefined || prop === "" || (isBoolValid && prop === false)) return false;

            if (prop == "auto") return true;

            if ((isNaN(prop) && (isBoolValid ? typeof prop !== "boolean" : true)) || 
                prop <= 0 || 
                prop > 12) {
                Adapt.log.error("Invalid value provided for _sizing." + propName + ": " + prop + "\n(value must be: [1-12|\"auto\"]" + (isBoolValid ? " | true | false" : "") +").");
                return false;
            }

            return true;
        }

        /**
         * Process various different formats that the sizing data can come in.
         * MOST of the time, the "sizing" parameter will be a properly formatted object.
         * 
         * These alternatives exist mainly for the sake ofm making helpers ({{#item sizing="*"}} for example),
         * easier to type out (as most of the time, the only value needed in a "sizing" object is "_width").
         * 
         * Examples:
         * {_width: 8, _maxWidth: true} -----> {_width: 8, _maxWidth: true}
         * 8 --------------------------------> {_width: 8}
         * "8" ------------------------------> {_width: 8}
         * "!8"  ----------------------------> {_width: 8, _maxWidth: false}
         * "auto" ---------------------------> {_width: "auto"}
         * [8,true] -------------------------> {_width: 8, _maxWidth: true}
         * @param {*} sizing Value taking on any of the above formats. 
         */
        _getSizingObj(sizing) {

            if (!sizing) return {};

            // Array of values provided. Apply to _width, then _maxWidth.
            if (Array.isArray(sizing)) return {_width: sizing[0], _maxWidth: sizing[1]};

            // Correctly formatted object provided.
            if (typeof sizing === "object") return sizing;

            // Single number provided. Set _width to it.
            if (typeof sizing === "number") return {_width: sizing};

            // String provided. Set _width to it (parse if integer).
            if (typeof sizing === "string") {

                // Check if it's formatted as "!12":
                if (sizing.match(/^![0-9]+$/g)) {
                    sizing = sizing.substring(1);

                    if (!isNaN(sizing)) return {_width: parseInt(sizing), _maxWidth: false};
                    else {
                        Adapt.log.error("adapt-sizing: 'sizing' parameter provided does not match any of the accepted formats.\n\nSizing value provided: " + JSON.stringify(sizing));
                    }
                }
                
                return {_width: isNaN(sizing) ? sizing : parseInt(sizing)};
            }

            Adapt.log.error("adapt-sizing: 'sizing' parameter provided does not match any of the accepted formats.\n\nSizing value provided: " + JSON.stringify(sizing));
        }

        /**
         * Returns a string of sizing classes based on the options object provided.
         * @param {*} options._width
         * @param {*} options._maxWidth
         */
        getSizingClasses(sizing) {

            sizing = this._getSizingObj(sizing);

            let classNames = [];
            classNames.push("col");

            // Width
            // ----------------------------------------------
            classNames.push(this._validateProperty(sizing, "_width", false) ? "col-" + sizing._width : undefined);
            
            // MaxWidthInner
            // ----------------------------------------------
            // EXPERIMENTAL! Should only be used on components for now.
            // 
            // If _maxWidthInner === true:
            // - Apply the current MaxWidth value to the inner element of the child at all times.
            //
            // If !isNaN(_maxWidthInner):
            // - Override the sizing._maxWidth value with it.
            if (sizing._maxWidthInner !== undefined) {
                classNames.push("col-maxwidth-inner");
                if (
                    sizing._maxWidth === undefined ||
                    !isNaN(sizing._maxWidthInner)
                ) {
                    sizing._maxWidth = sizing._maxWidthInner;
                }
            }

            // MaxWidth
            // ----------------------------------------------
            // New rule:
            // - if _maxWidth is undefined:
            //      - Set it to true if _width is defined and not "auto"
            //      - Set it to false in all other instances.
            // - if _maxWidth is true and _width is undefined, set _width to 6 (this used to be done with CSS).
            if (sizing._maxWidth === undefined) {
                sizing._maxWidth = sizing._width == "auto" ? false : sizing._width !== undefined; 
            }
            else if (sizing._maxWidth === true && sizing._width === undefined) {
                sizing._width = 6;
            }
            classNames.push(this._validateProperty(sizing, "_maxWidth", true) ? ("col-maxwidth" + (sizing._maxWidth === true ? "" : ("-" + sizing._maxWidth))) : undefined);

            // MinWidth
            // ----------------------------------------------
            if (sizing._minWidth === undefined) sizing._minWidth = false; 
            classNames.push(this._validateProperty(sizing, "_minWidth", true) ? ("col-minwidth" + (sizing._minWidth === true ? "" : ("-" + sizing._minWidth))) : undefined);

            // Float
            // ----------------------------------------------
            if (sizing._float) {
                classNames.push("col-float");
                switch (sizing._float) {
                    case "left":
                        classNames.push("col-float-left");
                        break;
                    default:
                        classNames.push("col-float-right");
                }
            }

            // MinHeight
            // ----------------------------------------------
            // EXPERIMANTAL! Only used in HotgraphicFlip.
            // Sets a minHeight value based on a % of @device-width-medium.
            /*if (sizing._minHeight) {
                classNames.push(this._validateProperty(sizing, "_minHeight", false) ? ("col-minheight-" + sizing._minHeight) : undefined);
            }*/

            return classNames.join(" ").trim();
        }

        /**
         * Note: _sizing._maxWidth will have no effect on the sizing grid classes.
         * @param {*} sizing 
         */
        getSizingGridClasses(sizing) {
            let classNames = [];
            
            // MaxCols (has a similar effect as _maxWidth, except this is applied at all device sizes).
            classNames.push(this._validateProperty(sizing, "_maxCols", false) ? "sg-maxcols-" + sizing._maxCols : undefined);

            // Item widths
            classNames.push(this._validateProperty(sizing, "_width", false) ? "sg-cols-" + sizing._width : undefined);

            // MinHeight
            // ----------------------------------------------
            // EXPERIMANTAL! Only used in HotgraphicFlip.
            // Sets a minHeight value based on a % of @device-width-medium.
            //classNames.push(this._validateProperty(sizing, "_minHeight", false) ? ("sg-minheight-" + sizing._minHeight) : undefined);

            return classNames.join(" ").trim();
        }

        /**
         * Whenever the device is resized, this function will check the items and apply a horizontal-centering fix for the last row.
         * Needs to be called in POSTRENDER.
         * @param {*} $grid 
         */
        setUpGridListeners($grid) {

            // If the grid isn't found, show an error and continue.
            // (The grid will be slightly misaligned, but it doesn't break anything; it only looks a bit off).
            if (!$grid.length) {
                Adapt.log.error("adapt-sizing: Could not initialize $grid listeners ($grid element not found).");
                return;
            }

            Adapt.on("device:resize", () => {this.updateGridEl($grid);});

            // Initial resize.
            _.delay(() => {
                this.updateGridEl($grid);
            });
        }

        /**
         * Troubleshooting:
         * ----------------
         * If the items are misaligning, ensure that their grid item element DOES NOT have max-width set.
         * The script below needs the $grid.children() to all use the full width available to them.
         * It is however, perfectly fine to use max-width on inner elements of the items.
         * 
         * @param {*} $grid 
         * @returns 
         */
        updateGridEl($grid) {

            let $items = $grid.children();
            
            // First, remove all orphan classes.
            $items.removeClass(function(idx, className) {
                return (className.match(/(^|\s)sg-orphan-\S+/g) || []).join(' ');
            });

            let columnGap = parseInt($grid.css("column-gap"), 10);
            const THRESHOLD = columnGap/2;
            
            let gridWidth = $grid.outerWidth();
            let itemWidth = $items.first().outerWidth(); // All items should have the same width.
            let itemCount = $items.length;

            let w = -columnGap;
            let columnCount = 0;
            while (Math.round(w) < gridWidth - THRESHOLD) {
                w += itemWidth + columnGap;
                columnCount++;
            }
            //console.log(columnCount);
            //if (w > gridWidth) columnCount--; // Adjust math.

            //console.log(w + " > " + (gridWidth + THRESHOLD));

            let orphanCount = itemCount % columnCount;
            if (orphanCount == 0) return;

            // Add an orphan class to the first child of the last row.
            $items.eq(itemCount - orphanCount).addClass("sg-orphan-" + (columnCount - orphanCount));
        }

        // Classes which should be applied to the parent element (the one which has "display: flex;").
        getParentClasses(sizing) {

            sizing = this._getSizingObj(sizing);
            let classNames = [];
            
            // Float
            if (Boolean(sizing._float)) {
                classNames.push("par-contains-float");
                classNames.push("u-clearfix"); // Also needed to ensure floats don't overflow.
            }
            else {
                classNames.push("par-contains-flex");
            }

            return classNames.join(" ").trim();
        }

        // Synonym
        getSizingClass() {
            return this.getSizingClasses.apply(this, arguments);
        }

        hasFloatingChildren(sizing){
            return Boolean(sizing._float);
        }

        // Synonym
        hasFloat(){
            return this.hasFloatingChildren.apply(this, arguments);
        }
    }

    let sizing = new Sizing();

    Adapt.sizing = sizing;
});
