define([
  'core/js/adapt',
  'core/js/models/itemModel'
], function(Adapt, ItemModel) {


  class ImgFinderItemModel extends ItemModel {

    initialize(...args) {
      super.initialize(...args);
      this.setupModelData();
    }

    setupModelData() {

    }

    getPosition() {
      return {
        _left: this.get("_left") + this.get("_width")/2,
        _top: this.get("_top") + this.get("_height")/2
      }
    }
  }

  return ImgFinderItemModel;
});
