define([
  'core/js/adapt'
], function(Adapt) {

	const UNDEFINED = "UNDEFINED";

	class CAInspectorVarModel extends Backbone.Model {

		defaults() {
			return {
				_playbackControlsModel: undefined, // Reference to the codeAnim's playbackControls model. Set when the View is created.
				
				name: "",		// Var name.

				// The current value from _vals being shown to the user.
				// Set to this "UNDEFINED" to ensure the first value it's set to is guaranteed to trigger a change.
				val: UNDEFINED, 

				_vals: [		// Array of all values this var will have and at what frames.
							// If the val === "UNDEFINED", then the view will hide itself for that frame(s).
							// ASSUMES VALS ARE SORTED IN ASCENDING FRAMEIDX.
					{
						val: "",
						frameIdx: 0,
						_isEnabled: true
					}
				],
				
				// Last frameIdx that a change occured on.
				lastFrameIdx: -2,	// Updates as the timeline is moved.
			};
		}

		initialize(){
			this.set("_vals", []);

			// Have the var start off invisible on frame 0.
			// This can be overridden by setting a value for frame 0 in components.json (probably; untested).
			this.addVal(UNDEFINED, 0);
		}

		convertVal(val) {

			switch (typeof val) {

				case "string":

					// Hide the View if manually set to UNDEFINED.
					if (val === UNDEFINED) {
						return undefined;
					}

					// Show "undefined" as a string if set to it.
					if (val === "undefined") {
						return "<em>undefined</em>";
					}

					// If the string starts with "~", don't wrap it in "".
					if (val.length && val[0] == "~") {
						return val.substring(1); // Remove the "~".
					}

					// Ensure all valid strings are wrapped in "" in the View.
					return "\"" + val + "\"";

				case "object":

					let v = val;
					// Recur through all children.
					_.each(v, (val, key) => {
						v[key] = this.convertVal(val);
					});

					v = JSON.stringify(v, null, " ");
					// Remove newline characters.
					v = v.replace(/\n/g, "");

					// Modify escaped and remove non-escaped doublequotes.

					// Find any escaped " characters (these were generated by the above "string" case).
					v = v.replace(/("\\")(.*?)(\\"")/g, (match, $1, $2, $3) => {
						return "!!!'!!!" + $2 + "!!!'!!!";
					});
					// Remove any remaining non-escaped " characters.
					v = v.replace(/([^\\])(")/g, (match, $1, $2) => {
						return $1;
					});
					// Replace the !!!'!!! placeholders with ".
					v = v.replace(/!!!'!!!/g, '"');

					// JSON.stringify will add spaces inside the outer parts of [ ... ] and { ... }, remove them (looks better).
					v = v.replace(/(^[[{])(\s+)([\S\s]*)(\s+)([}\]]$)/g, "$1$3$5");

					return v.replace(/(\[)(\s+)/g, "$1");
			}

			return val;
		}

		addVal(val, frameIdx) {

			let _val = this.convertVal(val);

			let v = {
				_isEnabled: _val === false ? false : true,
				val: _val,
				frameIdx: frameIdx
			};

			let _vals = this.get("_vals");
			_vals.push(v);
			this.set("_vals", _vals);
		}

		/*setUpVals() {
			
			_.each(this.get("vals"), function(v){

				v._isEnabled = true;

				if (typeof v.val == "string") {

					// Hide the View if manually set to UNDEFINED.
					if (v.val === "UNDEFINED") {
						v._isEnabled == false;
						return;
					}

					// Ensure all valid strings are wrapped in "".
					v.val = "\"" + v.val + "\"";
				}
			});
		}*/
	}
  
  	return CAInspectorVarModel;
});
