define([
    'core/js/adapt'
], function(Adapt) {

    // View for each individual category's content.
    // Gets passed a collection by SideMenuView.

    class SideMenuCategoryContentView extends Backbone.View {

        className() {
            return "smcategory smcategory-type-" + this.model.get("_type");
        }

        init(){}

        initialize(){
            
            this.init();
            this.render();

            //this.setupTopicData();
        }

        render(){
            var template = Handlebars.templates[this.constructor.template];

            var data = {
                title: this.model.get("title"),
                _type: this.model.get("_type"),
                _items: this.model.get("_items").toJSON()
            }
            this.$el.html(template(data));
        }
        
    };

    SideMenuCategoryContentView.template = 'sideMenuCategory';
    return SideMenuCategoryContentView;
});
