define([
    'core/js/adapt',
    'core/js/a11y/scroll',
    'libraries/jquery-ui.scrollParent.js' // Standalone jquery-ui library which only includes the functionality this needs. 
], function(Adapt, Scroll) {

    // TODO: Convert to Backbone Controller for consistency.

    const SCROLL_MARGIN = 48;           // Margin used for align: inview scrolling. FIXME: Hardcoded.
    const IPHONE_FIX = true;     // Hardcoded fix for iPhone Safari (remove if unnecessary).
    const SCROLL_DURATION = 400;

    let isScrolling = false; // True while the scrolling animation is playing.
    let scrollPriority = -Infinity; // NavigateToEl calls won't be called if a higher scrollPriority is scrolling.

    Adapt.scrollfix = {};

    /**
     * Set up _scrollingContainer._isEnabled
     * -------------------------------------
     * - When not on a mobile device, we (DD) want this to be enabled, due to the way we've set up the notify popup element.
     * - On mobile devices, it should be disabled, as having it enabled prevents overscroll behaviour hiding the navbar.
     * - 'offlineStorage:prepare' event used to ensure this is triggered BEFORE core/js/scrolling.js has a chance to initialize.
     */
    Adapt.on("offlineStorage:prepare", () => {
        
        // Adapt.device doesn't seem to have an inbuilt way to check if we're using a mobile device or not, so use this method instead.
        if( /Mobi|Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) ) {
            Adapt.log.info("adapt-scrollfix.js: Mobile device detected.");
            
            // FIXME: This was breaking StickyBlocks (if the mobile device was wide enough to be considered not-small).
            // FIXME: This does however, mean that overscroll behaviour will stop working.
            /*if (Adapt.config.get('_scrollingContainer')) {
                Adapt.config.get('_scrollingContainer')._isEnabled = false;
            }*/

            // FIXME: Adding this class; should it be in a different extension?
            $("html").addClass('device-mobile');

            return;
        }
    });

    Adapt.on("app:dataReady", () => {
        overrideNavigateToElement();
        overridePreventScroll();
        Adapt.navigateToRelOffset = navigateToRelOffset;
    });

    /**
     * EXPERIMENTAL OVERRIDE:
     * 
     * BUG: Adapt's code scroll.js introduces the following error in some instances of notify:
     * "Ignored attempt to cancel a touchmove event with cancelable=false, for example because scrolling is in progress
     * and cannot be interrupted."
     * 
     * This error can be disabled manually using the following.
     * This feature is currently needed for the ddImg lightbox feature to scroll properly on phones.
     * 
     * FIXME: Names could use improvement.
     */
    let disableA11yPreventScroll = false;
    function overridePreventScroll() {
        let _preventScroll = Adapt.a11y._scroll._preventScroll;
        Adapt.a11y._scroll._preventScroll = function() {
            if (disableA11yPreventScroll) return;
            return _preventScroll.apply(Adapt.a11y._scroll, arguments);
        };

        Adapt.scrollfix.disableA11yScroll = function(){disableA11yPreventScroll = true;}
        Adapt.scrollfix.enableA11yScroll = function(){disableA11yPreventScroll = false;}
    }

    
    /**
     * New function. When called, will scroll the page to the offset provided (no need to provide an element).
     * 
     * @param {Object} [offset.top]
     * @param {Object} [offset.left]
     * @param {Object} settings settings can be provided here in the same way as for Adapt.navigateToElement. 
     */
    function navigateToRelOffset(offset, settings) {
        
        // Adapt requires all scrolling to use navigateToElement, so to achieve the scroll we want,
        // create a temporary element for Adapt to scroll to.
        let $target = $("<div class='scrollfix__target'>");
        $("#app").append($target);
        $target.offset({
            top: offset.top ? ($(window).scrollTop() + offset.top) : undefined,
            left: offset.left ? ($(window).scrollLeft() + offset.left) : undefined
        });

        // Nagivate to it.
        let s = _.extend({
            offset: {
                top: 0,
                left: 0
            },
            ignoreNav: true
        }, settings);
        Adapt.navigateToElement($target, s);

        // And remove the temporary target.
        $target.remove();
    }

    function overrideNavigateToElement() {
        let _navigateToElement = Adapt.navigateToElement;
        
        // FIXME: Adapt's code navigateToElement logs a warning due to this not being a collection element.
        Adapt.navigateToElement = function(selector, settings) {

            // PDF mode: if in PDF mode, don't scroll to anything.
            if (Adapt.isPDFMode()) return;

            // New: scroll priorities.
            // Don't scroll if there's currently an animation with a higher priority animating.
            // FIXME: Crudely implemented. Scrollfix should be a Backbone Model/Controller.
            // FIXME: A high priority call will not interrupt an already animating low priority call.
            // FIXME: Low priority scrolls can still sometimes get through if the high priority scroll target is already inview.
            settings.priority = settings.priority || 0;
            if (!isScrolling) {
                scrollPriority = settings.priority;
            }
            else {
                if (settings.priority < scrollPriority) return;
                scrollPriority = settings.priority;
            }

            if (!settings) settings = {};
            if (settings.duration === undefined) settings.duration = SCROLL_DURATION;
            if (settings.offset === undefined) settings.offset = {top: -SCROLL_MARGIN, left: 0};
            if (settings.fail === undefined) settings.fail = onScrollFail;
            if (settings.onAfter === undefined) {
                settings.onAfter = onScrollAfter;
            }
            else {
                // Extend the onAfter call.
                let onAfter = settings.onAfter;
                settings.onAfter = function() {
                    onAfter();
                    onScrollAfter();
                }
            }

            // Adjust the top offset to accomodate for the navbar being at the top.
            // FIXME: This check is specific to the DD implementation only.
            if (!settings.ignoreNav && Adapt.device.screenSize !== 'large') {
                settings.offset.top -= $(".nav").height();
            }

            let $obj = $(); 
            let uniqueId = _.uniqueId("scrollfix");

            // Allow for jQuery objects to be used instead of selectors.
            if (selector instanceof jQuery) {
                $obj = selector.addClass(uniqueId);
                selector = "." + uniqueId;
                arguments[0] = selector;
            }
            // FIXME: I think this will break if a string is passed to "selector" parameter.

            // If scrollNested, check if the target is inside a nested scrolling element, and scroll that too.
            // (Supports horizontal scrolling only (at this time)).
            /*if (settings.scrollNested === true) {
                let $scrollParent = $obj.scrollParent();
                // Ignore if the scroll parent is the app or the nav.
                // FIXME: Hardcoded selection.
                if ($scrollParent.attr("id") != "app" && !$scrollParent.hasClass("notify__popup")) {
                    // Scroll the $obj into view.
                    $scrollParent.scrollTo($obj, {
                        axis: 'x',
                        duration: $.scrollTo.defaults.duration
                    });
                }
            }*/


            /**
             * If scrollNested, check if the target is inside a nested scrolling element, and scroll that too.
             * (Supports vertical scrolling only (at this time)).
             * 
             * TODO: At the moment, the scrolling always brings the target to the top of the $scrollParent;
             * allow for inview type scrolling of the target.
             */
            let scrollNestedCallback = undefined;
            if (settings.scrollNested === true) {
                let $scrollParent = $obj.scrollParent();

                /**
                 * Ignore if the scroll parent is the #app, nav, or #adapt (<html>).
                 * 
                 * FIXME: Hardcoded selection.
                 */
                let scrollParentId = $scrollParent.attr("id");
                if (
                    scrollParentId != "adapt" &&                // <html> 
                    scrollParentId != "app" &&                  // app
                    !$scrollParent.hasClass("notify__popup")    // nav
                ) {
                    
                    let scrollTop = $scrollParent.scrollTop();

                    // Scroll the $obj into view instantly.
                    $scrollParent.scrollTo($obj, {
                        axis: 'y',
                        duration: 0
                    });

                    // Create a callback to invoke after the remainder of this function has used the $scrollParent's
                    // offset for its calculation.
                    scrollNestedCallback = () => {

                        let destScrollTop = $scrollParent.scrollTop();

                        // Set the $scrollParent to the location it was previously in.
                        $scrollParent.scrollTop(scrollTop);

                        // Animate to the location it was just in.
                        $scrollParent.scrollTo(destScrollTop, {
                            axis: 'y',
                            duration: SCROLL_DURATION //$.scrollTo.defaults.duration
                        });
                    };
                }
            }

            // Call at the end of the function.
            let cleanup = () => {
                if (scrollNestedCallback) scrollNestedCallback();
                $obj.removeClass(uniqueId);
            }

            // If the alignment is "center", try to center the elements in the viewport.
            // Or if they're too tall for the viewport, change the alignment to "inview".
            /*if (settings.align == "center") {

            }*/

            // If the alignment is "inview", don't do anything if it already is.
            if (settings.align == "inview") {

                let inview = getElementInview(selector, settings.offset);

                // If both the top and bottom are inview, do nothing.
                if (inview.inview === 0) {
                    cleanup();
                    onScrollAfter();
                    return;
                }

                // Ensure only the top element has the selector.
                // (_navigateToElement() call below has issues if its selector finds multiple elements).
                $obj.removeClass(uniqueId);
                inview.$topObj.addClass(uniqueId);

                // FIXME:? manually specified offset.top values get overridden here.
                // If the top is inview and the bottom isn't, scroll down just enough to focus on the bottom.
                if (inview.inview === 1) {
                    //let originalOffsetTop = settings.offset.top || 0;
                    settings.offset.top = -Adapt.device.screenHeight + inview.height + SCROLL_MARGIN; //+ originalOffsetTop;

                    // If on an iPhone, scroll down a bit further (FIXME: Temporary fix).
                    if (_useIphoneFix()) {
                        settings.offset.top += SCROLL_MARGIN*2;
                    }
                }
            }

            // If the element being scrolled to is within a notify popup, scroll within that popup instead if set to do so.
            // TODO: Improve the offset calculation for this feature.
            if ($obj.closest(".notify").length && settings.notifyEnabled === true) {
                $(".notify__popup").scrollTo($obj, _.extend(
                    settings,
                    {
                        axis: 'y',
                        duration: SCROLL_DURATION
                    })
                );
            }
            // Otherwise, navigate to the element normally.
            else {
                _navigateToElement.apply(this, arguments);
            }

            isScrolling = true;
            cleanup();
        }
    }

    // Called if the scrolling animation is interrupted.
    function onScrollFail() {
        isScrolling = false;
        scrollPriority = -Infinity;
    }

    // Called when the scrolling animation completes.
    function onScrollAfter() {
        isScrolling = false;
        scrollPriority = -Infinity;
    }

    function getScreenTop() {

        // Adjust the top offset to accomodate for the navbar.
        if (Adapt.device.screenSize === 'small') {
            return SCROLL_MARGIN + $(".nav").height();
        }
        return SCROLL_MARGIN;
    }

    function _useIphoneFix() {
        return IPHONE_FIX && $("html").hasClass("iphone");
    }

    function getScreenBottom(){

        // If on an iPhone, give slightly more space on the bottom.
        if (_useIphoneFix()) {
            return Adapt.device.screenHeight - SCROLL_MARGIN*3;
        }

        return Adapt.device.screenHeight - SCROLL_MARGIN;
    }
    
    /**
     * Returns an object based on if the top & bottom of the selected element(s) are inview.
     * @param {*} selector 
     */
    function getElementInview(selector, offset) {
        let $obj = $(selector);
        if (!$obj.length) {
            Adapt.log.error("adapt-scrollfix.getElementInview() could not find any elements with the provided selector: " + selector);
            return;
        }

        let bbs = [];
        $obj.each((i, e) => {

            // If a component is selected, allow it to specify their own custom boundingClientRect.
            if ($(e).hasClass("component") /*|| $(e).hasClass("block")*/) {
                let componentView = Adapt.findViewByModelId($(e).data("adapt-id"));

                if (componentView.getBoundingClientRect) {
                    let bb = componentView.getBoundingClientRect();
                    bb.$obj = $(e);
                    bbs.push(bb);
                }
                else {
                    let bb = $(e)[0].getBoundingClientRect();
                    bb.$obj = $(e);
                    bbs.push(bb);
                }
            }
            // If a non-component element is selected, get the boundingClientRect normally.
            else {
                let bb = $(e)[0].getBoundingClientRect();
                bb.$obj = $(e);
                bbs.push(bb);
            }
        });

        let minTop = Infinity;
        let maxBottom = -Infinity;

        // Reference to the topmost element of all selected elements.
        //
        // Explanation:
        // --------------------------------------------------------
        // If the core Adapt.navigateToElement function is called, and it finds multiple elements that match the selector it
        // is provided, it can scroll unpredictable. This is done to fix this.
        let $topObj = $obj.first(); 

        _.each(bbs, (bb) => {

            if (bb.top < minTop) {
                minTop = bb.top;
                $topObj = bb.$obj;
            }
            if (bb.bottom > maxBottom) {
                maxBottom = bb.bottom;
            }
        });

        // 1 -/+ added to prevent this triggering after a scroll places it exactly where it needs to be.
        let screenTop = getScreenTop()-1;
        let screenBottom = getScreenBottom()+1;

        let inview = 0;
        let height = maxBottom - minTop;

        // Increase the height if offset.height is specified.
        if (offset.height !== undefined) {
            height = Math.max(height, offset.height);
        }

        // If the selection is too tall for the viewport, always scroll to the top.
        if (maxBottom - minTop > screenBottom - screenTop) {
            inview = -1;
        }
        else {
            // If the bottom of the element is off the bottom of the screen, then the view needs to scroll down.
            if (maxBottom > screenBottom) inview = 1;
            // If the top of the element is off the top of the screen, then it needs to scroll up.
            // (This will override the above statement, as seeing the top of the element is more important than seeing the bottom).
            if (minTop < screenTop) inview = -1;
        }

        return {
            inview: inview,
            /*topInview: minTop > screenTop && minTop < screenBottom,
            bottomInview: maxBottom > screenTop && maxBottom < screenBottom,*/
            height: height,
            $topObj: $topObj
        }
    }
});
