define([
    'core/js/adapt',
    './resourcePopupPopupView'
], function(Adapt, ResourcePopupPopupView) {

    class ResourcePopupBtnView extends Backbone.View {

        className() {
            return "resourcepopup-btn btn-text";
        }

        tagName() {
            return "button"
        }

        events() {
            return {
                "click" : "onClick"
            }
        }

        initialize(){

            this.setUpViewData();
            this.render();
        }

        setUpViewData() {
            this.popupView = null;
            this._isPopupOpen = false;
        }

        render(){
            this.$el.html(new Handlebars.SafeString(this.model.get("buttonText")).string);
        }

        onClick(){
            this.openPopup();
        }

        /**
         * Open a popup using the default Adapt notify functionality.
         */
        openPopup() {

            if (this._isPopupOpen) return;
            this._isPopupOpen = true;
    
            this.popupView = new ResourcePopupPopupView({
                model: this.model
            });
    
            Adapt.trigger("notify:popup", {
                _view: this.popupView,
                _isCancellable: true,
                _showCloseButton: true,
                _classes: 'resourcepopup ' + (this.model.get('_classes') || "")
            });

            this.listenToOnce(Adapt, {
                'popup:closed': this.onPopupClosed
            });
        }

        onPopupClosed() {
            this.$el.addClass("is-visited");
            this._isPopupOpen = false;
        }
    };

    return ResourcePopupBtnView;
});
