define([
    'core/js/adapt'
], function(Adapt) {

    /**
     * QuestionPlusMultiModel
     * ---------------------------
     * This handles instances where a block contains multiple Question components at once, and creates a stylish transition
     * between them,
     * as well as slightly modifying how the feedback is displayed.
     * 
     * This functionality creates a Question block with multiple "steps".
     * In DD's Adobe Edge Animate templates, this was referred to as a "Your Turn: Learning Pathway" type functionality.
     * 
     * WARNING:
     * - Will have errors if there are multiple question component's inside of the same componentRow.
     * 
     * FIXME:
     * - Assumes all componentRows in the block have _isQuestionType: true.
     */

    class QuestionPlusMultiModel extends Backbone.Model {

        defaults() {
            return {
                "_parent": undefined,       // Reference to the blockModel that contains the question components.
                "_children": undefined,     // Collection of ComponentRows that have _isQuestionType: true.
            }
        }

        initialize() {

            this.hideQuestionRows();

            // All (except the first) componentRow's in this block should have dividers.
            this.get("_children").each((model, i) => {
                if (i == 0) return;
                model.set("_hasDivider", true);
            });

            /*this.listenTo(this.get("_parent"), "change:_isComplete", (model, _isComplete) => {
                console.log(_isComplete);
            });*/

            this.listenTo(this.get("_parent").get("_children"), "change:_isComplete", this.onQuestionComponentComplete);
        }

        /**
         * Hides all question componentRows after the first incomplete question componentRow encountered.
         * This won't prevent the other components being rendered, but it will ensure the user will have to step through each
         * Question componentRows in sequential order.
         */
        hideQuestionRows() {

            let crVisible = true;

            _.each(this.get("_children").models, (componentRow, i) => {

                // If this componentRow is not complete, then hide all componentRows which come after it.
                if (crVisible && !componentRow.get("_isComplete")) {
                    crVisible = false;
                    return;
                }

                // Set the componentRows of all other componentRows to hide themselves.
                // (They'll still render, but won't be visible until the previous child is completed).
                componentRow.set("_isVisible", crVisible);
                
                /*setTimeout(() => {
                    Adapt.findViewByModelId(component.get("_id")).$el.slideUp();
                }, 5000);*/
            });
        }

        /**
         * Whenever a Question component in this block is complete, reveal the next one and scroll to it.
         */
        onQuestionComponentComplete(model, _isComplete) {

            if (!_isComplete) return;

            let nextIncompleteComponent = this.showNextQuestionComponent();
            if (!nextIncompleteComponent) return; // If there's no more incomplete Question components in the Block, do nothing.

            // And scroll to it once the user closes the feedback.
            this.listenToOnce(Adapt, "notify:closed", () => {
                
                _.debounce(() => {
                    Adapt.navigateToElement("." + nextIncompleteComponent.get("_id"), {duration: 400});
                })();
            });
        }

        showNextQuestionComponent() {

            let nextIncompleteComponent = _.find(this.get("_parent").getChildren().models, (component) => {
                if (!component.get("_isComplete")) return true;
            });
            
            // If there's no more components to complete, do nothing.
            if (!nextIncompleteComponent) return undefined;
            
            // Have the next incomplete component's componentRow reveal itself.
            // Note: was originally placed inside of notify:closed callback, but if something were to go wrong
            // and prevent the callback, it would leave the componentRow inaccessible.
            // This way looks slightly worse, but removes that risk.
            nextIncompleteComponent.get("_componentRow").set("_isVisible", true);

            return nextIncompleteComponent;
        }
    };

    return QuestionPlusMultiModel;
});
