define([
    'core/js/adapt',
    './SideMenuCategoryView',
    './SideMenuCategoryNavView',
    './SideMenuCategoryResourcesView',
    './SideMenuCategoryCreditsView',
    './SideMenuHelpers'
], function(Adapt, SideMenuCategoryView, SideMenuCategoryNavView, SideMenuCategoryResourcesView, SideMenuCategoryCreditsView) {

    /**
     * View for the main menu. Handles revealing the menu, as well as the accordion functionality within it.
     * A plugin for the accordion is not used, due to the very specific requirements that our menu requires.
     * 
     * FIXME: Menu must be fully hidden (display: none) when not visible.
     */

    class SideMenuView extends Backbone.View {

        className() {
            return "sidemenu";
        }

        events() {
            return {
                "click .sidemenu__btn" :                    "onCloseBtnClick",
                //"touchstart .js-sidemenu-shadow-click" :  "onShadowTouchstart",
                //"touchstart .sidemenu__menu" :  "onShadowTouchstart",
                "click .js-sidemenu-shadow-click" :         "onCloseBtnClick",
                "click .js-toggle-accordion-item" :         "onAccordionItemClick",
                "click .js-sidemenu-drawer-btn" :           "onMenuBtnClick"
            }
        }

        initialize(){
            
            this.setupEventListeners();
            this.render();

            // FIXME: Temp.
            _.debounce(() => {this.model.get("_categories").models[0].set("_isActive", true);})();
        }

        render(){
            var template = Handlebars.templates['sideMenu'];
            this.$el.html(template(_.extend(this.model.get("_categories").toJSON(), {title: this.model.get("title")})));

            this.renderCategories();

            // Append the main toggle button.
            this.$('.sidemenu__btn-container').html(Handlebars.templates['sideMenuBtn']());
        }

        onMenuBtnClick(event) {

            //if (event.currentTarget != event.target) return;
            this.model.toggle();
        }

        setupEventListeners(){
            this.listenTo(this.model, "change:_isActive", this.onIsActiveChange);
            this.listenTo(Adapt, "device:resize", this.onDeviceResize);

            this.listenTo(this.model.get("_categories"), {
                "change:_isActive": this.onAccordionItemActiveChange
            })
        }

        /**
         * iOS fix. Prevents the user being able to scroll the page while it's covered by the shadow.
         * Also fixes a bug where the user couldn't scroll the sideMenuCategory views.
         * @param {*} event 
         */
        onShadowTouchstart(event) {
            event.preventDefault();
        }

        onDeviceResize(){
            this.updateAccordion();

            // FIXME: \/ doesn't work (due to being CSS based at this time).
            this.updateScrollEnabled(); // Check if this device resize has made the shadow appear.
        }

        onIsActiveChange(model, _isActive){
            
            //this.handleFocus();
            //return;

            //Adapt.drawer.triggerCustomView(new SideMenuView({model: this.model}));
            
            this.$el.toggleClass("is-active", _isActive);
            $("#wrapper").toggleClass("is-sidemenu-active", _isActive);

            // Change the state of the menu button.
            $(".js-sidemenu-drawer-btn")
                .toggleClass("is-burger", !_isActive)
                .toggleClass("is-cross", _isActive);
            
            this.updateScrollEnabled();

            if (_isActive) {
                this.focus();
            }
            else {
                this.unfocus();
            }
        }

        focus() {

            // Keep focus from previous action
            this.$previousActiveElement = $(document.activeElement);

            // Focus on the menu
            // FIXME: The following scripts were copied from notifyView.js.
            // It's likely they're not working as intended.
            $.inview();
            Adapt.a11y.popupOpened(this.$el);
            Adapt.a11y.scrollDisable('body');
            $('html').addClass('notify');
            // Set focus to first accessible element
            Adapt.a11y.focusFirst(this.$(".sidemenu__menu"), { defer: false });
        }

        unfocus() {

            // FIXME: The following scripts were copied from notifyView.js.
            // It's likely they're not working as intended.
            Adapt.a11y.scrollEnable('body');
            $('html').removeClass('notify');
            // Return focus to previous active element
            Adapt.a11y.popupClosed(this.$previousActiveElement);
        }

        /**
         * FIXME:
         * BUG: Disabling scroll on the body breaks scrolling of SideMenuCategories on touch devices! 
         * Throws this error:
         * "Ignored attempt to cancel a touchmove event with cancelable=false, for example because scrolling is in progress and cannot be interrupted."
         */
        updateScrollEnabled() {

            let _isActive = this.model.get("_isActive");

            // Disable/enable scroll if the shadow became active.
            // FIXME: BAD way of detecting this.
            if (_isActive) {
                _.debounce(() => {
                    if (this.$(".js-sidemenu-shadow-click").css("opacity") > 0) {
                        Adapt.a11y.scrollDisable('body');
                    }
                }, 50)();
            }
            else {
                Adapt.a11y.scrollEnable('body');
            }
        }

        onAccordionItemClick(event) {

            // Get the category item in the collection.
            let index = $(event.currentTarget).data("index");
            let item = this.model.get("_categories").models[index];

            let _isActive = item.get("_isActive");
            if (_isActive) return; // Don't do anything if the accordion item is already open.

            // Close all other accordion items.
            _.each(this.model.get("_categories").models, (model) => {
                model.set("_isActive", false);
            });
            item.set("_isActive", true);

            /*
            this.$(".sidemenu__accordion-content").slideUp();

            let $content = this.$(".sidemenu__accordion-content").filter("[data-index=" + id + "]");
            $content.slideDown();*/
        }

        // Slide down the clicked item, and slide up all the others.
        onAccordionItemActiveChange(model, isActive){

            if (!isActive) return;

            let $btns = this.$(".sidemenu__accordion-item-btn");
            let $items = this.$(".sidemenu__accordion-item-content");

            $btns.toggleClass("is-active", false);
            $items.toggleClass("is-active", false);

            let index = this.model.get("_categories").indexOf(model);
            let $item = $items.filter("[data-index=" + index + "]").toggleClass("is-active", true); // The only active item.
            $btns.filter("[data-index=" + index + "]").toggleClass("is-active", true);

            // Set the max-height styles for all $items.
            let $accordion = this.$(".sidemenu__accordion");
            let accordionHeight = $accordion.height();
            
            let itemHeight = accordionHeight - $btns.outerHeight() * this.model.get("_categories").models.length;
            $items.each((i, e) => {
                $(e).css("height", "");
                $(e).css("max-height", $(e).height())
            });
            $items.hide(0).show(0).css("max-height", 0); // Finish any previous transition that may still be playing.
            $item.css("max-height", itemHeight);

            $item.one('transitionend', () => {
                // Re-selecting the active item ensures the correct one is targetted (if many transitions are activated at once, the active item may change).
                let $activeItem = $items.filter(".is-active");
                $activeItem.css({
                    "max-height": "none",
                    "height": $activeItem.height()      // iOS fix. The scrollable element needs to have a fixed height.
                });
                $items.filter(":not(.is-active)").hide(0);
            });
        }

        /**
         * Update the active accordion item's height (iOS fix).
         */
        updateAccordion(){
            let $item = this.$(".sidemenu__accordion-item-content").filter(".is-active");
            $item.css("height","").css("height", $item.height());
        }

        renderCategories() {

            let parentModel = this.model;
            
            // Create a new view for each one.
            this.model.get("_categories").each((model, idx) => {

                var view = undefined;
                model.set("_parent", parentModel);

                switch(model.get("_type"))
                {
                    case "nav":

                        view = new SideMenuCategoryNavView({
                            model: model
                        });
                        break;

                    case "resources":
                        view = new SideMenuCategoryResourcesView({
                            model: model
                        });
                        break;

                    // Legacy (defunt): "Authors" has now been fully replaced with "Credits".
                    case "authors":
                        Adapt.log.error(`SideMenuView:\n the contentObjects "_authors" property is no longer being used. Please remove it and replace it with "_credits".`);
                        view = new SideMenuCategoryView({
                            model: model
                        });
                        break;

                    case "credits":
                        view = new SideMenuCategoryCreditsView({
                            model: model
                        });
                        break;

                    case "references":
                        model.get("_items").comparator = (model) => {return model.get("body").toLowerCase();};
                        model.get("_items").sort();
                        view = new SideMenuCategoryView({
                            model: model
                        });
                        break;

                    case "glossaries":
                        model.get("_items").comparator = (model) => {return model.get("title").toLowerCase();}
                        model.get("_items").sort();
                        view = new SideMenuCategoryView({
                            model: model
                        });
                        break;

                    default:
                        view = new SideMenuCategoryView({
                            model: model
                        });
                }

                this.$(".sidemenu__accordion-item-content").filter("[data-index=" + idx + "]").html(view.$el);
            });

            return;
        }

        onCloseBtnClick(event) {
            this.model.toggle();
        }
        
    };

    return SideMenuView;
});
