define([
  'core/js/adapt',
  'core/js/models/questionModel',
  'core/js/models/itemModel'
], function(Adapt, QuestionModel, ItemModel) {

  class GLabelModel extends QuestionModel {

    init() {
      super.init.call(this);

      this.setupQuestionItems();
      this.setupQuestionOptions();
      this.checkCanSubmit();
    }

    // Copied from core/js/models/itemsComponentModel.js
    toJSON() {
      const json = super.toJSON();
      // Make sure _items is updated from child collection
      json._items = this.getChildren().toJSON();
      return json;
    }

    /**
     * Create a collection of item models out of the items.
     */
    setupQuestionItems() {
      
      const items = this.get('_items') || [];
      items.forEach((item, index) => {
        item._index = index;
        item._parent = this;
        item.tooltip = "?"; // This value will change as the user clicks on options. // FIXME: Temp.
      });
      this.setChildren(new Backbone.Collection(items, { model: ItemModel }));
    }

    isCorrect() {

      var numberOfCorrectAnswers = 0;

      this.getChildren().each(function(item, index) {

        /**
         * An item is correct when only the options with _shouldBeSelected are selected.
         */
        var isCorrect = true;
        item.get("_options").find(option => {

          // Stop searching if there's any option in the wrong state.
          if (option.get("_shouldBeSelected") !== option.get("_isSelected")) {
            isCorrect = false;
            return true;
          }
        });

        /**
         * All options which have been selected should be marked as visited.
         */
         item.get("_options").each((option) => {
          if (option.get("_isSelected")) {
            option.set("_isVisited", true);

            let isCorrect = option.get("_shouldBeSelected") === option.get("_isSelected");
            option.set("_isCorrect", isCorrect);
            option.set("_isIncorrect", !isCorrect);

            // Disable interaction once correct.
            option.set("_isEnabled", !isCorrect);
          }
        });
        
        if (!isCorrect) {
          item.set("_isCorrect", false);
          item.set("_isIncorrect", true);
          return;
        }

        numberOfCorrectAnswers++;
        item.set("_isCorrect", true);
        item.set("_isIncorrect", false);

        // Disable all options for this item once correct.
        item.get("_options").each(option => {option.set("_isEnabled", false);});

        this.set({
          '_isAtLeastOneCorrectSelection': true
        });

      }, this);

      this.set('_numberOfCorrectAnswers', numberOfCorrectAnswers);

      if (numberOfCorrectAnswers === this.getChildren().length) {
        return true;
      }

      return false;
    }
    
    setupQuestionOptions() {
      
      const options = this.get('_options') || [];
      let _options = []; // Convert the component's _options property from an array of strings to an array of objects.

      options.forEach((option, index) => {
        _options.push({
          text: option,
          _isEnabled: true, // Becomes disabled once the option is correct.
          _index: index
        });
      });

      this.set("_options", _options);
      this.setupRandomisation();

      // Give each item a collection from this array.
      this.getChildren().each((item) => {

        let options = new Backbone.Collection(this.get("_options"));
        
        // Check if the copy of the option belonging to this item should be selected for this item.
        options.each((option) => {
          option.set("_shouldBeSelected", Boolean(_.includes(item.get("_optionsCorrect"), option.get("_index"))));
          option.set("_parent", item);
          option.toggle = (_isSelected) => {
            this._toggleOption(option, _isSelected);
          }
        });
        item.set("_options", options);

        // Listen for events on the options.
        // FIXME: The item model should be made into its own script instead of being defined on the fly like it is here.
        item.listenTo(options, "change:_isSelected", (optionModel, _isSelected) => {
          
          // Update the tooltip whenever the options change.
          let tooltip = [];
          let isIncorrect = false;
          item.get("_options").each((option) => {
            if (option.get("_isSelected")) tooltip.push(option.get("key"));

            // If the option is incorrectly selected, and has been visited, mark the item as incorrect.
            if (!isIncorrect && 
                option.get("_isSelected") !== option.get("_shouldBeSelected") && 
                option.get("_isVisited")) {
              isIncorrect = true;
            }
          });
          item.set("_isIncorrect", isIncorrect);
          item.set("tooltip", tooltip.length ? tooltip.join(",") : "?");

          // An item is considered "selected" when one or more of its options have been selected.
          // All items must be selected in order to submit the answer (whether it be right or wrong).
          item.set("_isSelected", Boolean(tooltip.length));

          // Check whether the component can be submitted.
          this.checkCanSubmit();
        });
      });
    }

    _toggleOption(optionModel, _isSelected) {

      let isRadio = this.get("_isRadio");

      // Checkbox
      if (!isRadio){
        optionModel.set("_isSelected", _isSelected === undefined ? !Boolean(optionModel.get("_isSelected")) : Boolean(_isSelected));
      }
      // Radio (radio buttons can only be toggled on).
      else {
        optionModel.set("_isSelected", true);
        optionModel.collection.each((option) => {
          if (option != optionModel) option.set("_isSelected", false);
        });
      }
    }

    getItem(index) {
      return this.getChildren().findWhere({ _index: index });
    }

    getActiveItem(index) {
      return this.getChildren().findWhere({ _isActive: true });
    }

    /**
     * This component randomizes itself by shuffling the order that the _options are displayed in.
     */
    setupRandomisation() {
      if (!this.get('_isRandom') || !this.get('_isEnabled')) return;
      
      this.set("_options", _.shuffle(this.get("_options")));
      
      // Assign an alphabet letter as the key the user will use to identify each option.
      this.get("_options").forEach((option, index) => {
        let key = (index + 10).toString(36).toUpperCase();
        option.key = key;
      });
    }

    /**
     * Can submit if every item has at least one option selected.
     * @returns 
     */
    canSubmit() {
      return this.getChildren().every((itemModel) => {return itemModel.get("_isSelected");});
    }

    // Core
    // ---------------------------------------------------------------------------------------

    restoreUserAnswers() {

      if (!this.get('_isSubmitted')) return;

      let userAnswer = this.get('_userAnswer');
      let items = this.getChildren();

      _.each(userAnswer, (optionIndexes, itemIndex) => {
        
        let item = items.find((item) => {return item.get("_index") == itemIndex;});
        item.get("_options").each(option => {
          if (_.includes(optionIndexes, option.get("_index"))) {
            option.set("_isSelected", true);
          }
        });
      });
    }

    storeUserAnswer() {

      var userAnswer = new Array(this.getChildren().length);

      this.getChildren().each(function(item) {

        let itemAnswer = [];
        item.get("_options").each(option => {
          if (option.get("_isSelected")) itemAnswer.push(option.get("_index"));
        });
        userAnswer[item.get("_index")] = itemAnswer;
      }, this);

      this.set({
        '_userAnswer': userAnswer
      });
    }

    resetUserAnswer() {

      this.set('_userAnswer', []);

      this.getChildren().each((item) => {
        item.set("_isCorrect", false);
        item.set("_isIncorrect", false);
        item.set("_isSelected", false);

        item.get("_options").each((option) => {
          option.set("_isSelected", false);
          option.set("_isVisited", false);
        });
      });
    }

    /**
     * used by adapt-contrib-spoor to get the user's answers in the format required by the cmi.interactions.n.student_response data field
     * returns the user's answers as a string in the format 'answer1[,]answer2[,]answer3'
     * the use of [,] as an answer delimiter is from the SCORM 2004 specification for the fill-in interaction type
     * 
     * --------------------------------------------------------------
     * 
     * Each item could have one or MORE of its buttons selected.
     * SCORM (or Adapt?) doesn't have a way to store multiple multiple "choice" interactions that in one go,
     * so I've set this up to use "fill-in" as the submission type for now.
     * 
     * Example: Item0 has the first 3 options selected, Item1 has the second and third, and Item2 has only the
     * last option selected:
     * 
     * 0.1.2[,]1.2[,]4
     * 
     * FIXME: This is most likely going to look a bit messy if the answers are viewed in the LMS UI.
     * FIXME: Has not been tested yet!!!
     */
    getResponse() {

      let resp = [];
      this.getChildren().each((item) => {

        let optionIndexes = [];
        item.get("_options").each((option) => {
          if (option.get("_isSelected")) optionIndexes.push(option.get("_index"));
        });

        resp.push(optionIndexes.join('.'));
      });

      return resp.join('[,]');
    }

    /**
    * Used by adapt-contrib-spoor to get the type of this question in the format required by the cmi.interactions.n.type data field
    * @return {string}
    */
    getResponseType() {
      return "fill-in";
    }

  }

  return GLabelModel;

});
