define([
    'core/js/adapt',
    './SideMenuCategoryCollection',
    './SideMenuModel',
    './SideMenuView'
], function(Adapt, SideMenuCategoryCollection, SideMenuModel, SideMenuView) {

    class SideMenu extends Backbone.Controller{

        initialize() {

            Adapt.on({
                'app:dataReady': this.onDataReady.bind(this)
            });
            
        }

        onDataReady() {
            
            this.setUpEventListeners();
        }

        setUpEventListeners() {
            this.listenTo(Adapt, {
                'router:page': this.renderSideMenuBtn
            });
        }

        renderSideMenuBtn(pageModel) {

            var categoryCollection = new SideMenuCategoryCollection(null, {
                pageModel: pageModel
            });

            /**
             * Going with non-drawer implementation (Adapt's core drawer functionality is too different).
             * TODO: Have it still trigger the same drawer events as the core drawer.
             */

            // FIXME: BEING RENDERED TOO SOON! ArticleNav needs to change values on blocks first.
            // Note: not sure what that FIXME means. It seems to be working now.
            let sideMenuModel = new SideMenuModel({
                "_pageModel": pageModel,
                "_categories": categoryCollection
            });
            let sideMenuView = new SideMenuView({
                model: sideMenuModel
            });

            this.sideMenuModel = sideMenuModel;

            // Insert the sidemenu just after the real drawer.
            $(".drawer").first().after(sideMenuView.$el);
        }

        toggle() {
            this.sideMenuModel.toggle();
        }
    }

    Adapt.sideMenu = new SideMenu();
});
