define([
    'handlebars',
    'core/js/adapt',
    "./nestComponentParentModel"
], function(Handlebars, Adapt, nestComponentParentModel) {

    /**
     * nestComponent helper
     * --------------------
     * The component with an "_id" equal to the one in this element will be rendered inside of it instead of in its block
     * where it's meant to go (the default component render location).
     */
    Adapt.on('router:page', () => {
        Handlebars.registerHelperEl("nestComponent", function() {
            
            let context = arguments[arguments.length-1];
            let hash = context.hash;
            
            let id = hash.id;
            if (!id) throw "{{#nestComponent}} helper must have an \"id\" property.";

            // Find the componentModel with the provided "_id".
            let childModel = Adapt.findById(id);
            if (!childModel) throw "{{#nestComponent}} could not find component with \"_id\": \"" + id + "\"";

            let parentModel = Handlebars.getComponentModelFromContext(context);

            // Set properties on the models (Views which detect these properties will modify their core behaviour).

            // Parent
            // -----------------------------------
            let parentNestComponent = parentModel.get("_nestComponent");
            if (!parentNestComponent) {
                throw '{{#nestComponent}}: Component with "_id": "' + parentModel.get("_id") + '" contains {{#nestComponent}} elements, but does not have the "_nestComponent" partial added.';
            }
            try {
                parentNestComponent.registerChild(childModel);
            }
            catch (err) {
                throw "adapt-nestComponent: parent component \"_nestComponent\" incorrectly applied. Must be: \"_nestComponent\": {\"_isParent\": true}"
            }

            // Child
            // -----------------------------------
            if (childModel.get("_nestComponent"))
                throw "{{#nestComponent}}: Child with \"_id\": \"" + id + "\" cannot also be a nestComponent parent / cannot be nested in multiple parents at once.";
            if (childModel.get("_layout") != "full")
                throw '{{#nestComponent}} only supports components with "_layout": "full" at this time ("' + id + '").';
            childModel.set("_nestComponent", {
                _isChild: true,
                _parent: parentModel
            });

            return $("<div class='nestcomponent' data-nestcomponent-id='" + id + "'>");
        });
    });


    /**
     * adapt-nestComponent
     * ===================
     * 
     * Description:
     * ------------
     * THIS EXTENSION IS EXPERIMENTAL AND SUBJECT TO CHANGE.
     * 
     * FIXME:
     * ------
     * - Check that this works when hosted online (as CHILD components may take a while to render. SET_RW-20 is good for testing).
     * - Check that component completion still operates correctly.
     * - If the PARENT is re-rendered, the CHILD components need to be re-rendered too!
     *   (This may be fixable by using the adaptView addChildren() function to assign the child components).
     * - Will likely cause issues when used with the default pageLevelProgress extension (which the DD isn't using).
     * - Needs to trigger a device:resize on all child components once they are appended.
     * 
     * Notes:
     * ------
     * - Components intended for nesting must be declared AFTER their parent in components.json.
     * - This feature goes against the core structure of Adapt, and should be monitored to ensure it doesn't cause any issues.
     * 
     */

    class NestComponent extends Backbone.Controller{

        initialize() {
            
            Adapt.on({
                'app:dataReady': this.onDataReady.bind(this)
            });
        }

        onDataReady() {
            Adapt.on("componentView:preRender", this.onParentComponentViewPreRender.bind(this));
            Adapt.on("componentView:postRender", this.onChildComponentViewPostRender.bind(this));
            //Adapt.on("questionView:showFeedback", this.onQuestionViewShowFeedback.bind(this));
        }

        /**
         * Parent extension
         * @param {*} componentView 
         */
        onParentComponentViewPreRender(componentView) {

            let componentModel = componentView.model;
            let nestComponent = componentModel.get("_nestComponent");

            // Only extend nested component parents.
            if (!nestComponent || !nestComponent._isParent) return;

            // Have the parent component only setReadyStatus once its child components are ready.
            // ----------------------------------------------------------------------------------
            componentModel.set("_isReady", false);
            componentModel.set("_nestComponent", new nestComponentParentModel({_parentView: componentView}));
        }

        onChildComponentViewPostRender(componentView) {

            let componentModel = componentView.model;
            let nestComponent = componentModel.get("_nestComponent");

            // Only extend nested component children.
            if (!nestComponent || !nestComponent._isChild) return;

            // Have the child component append itself inside the parent component's nestComponent element.
            // ----------------------------------------------------------------------------------
            let parentView = Adapt.findViewByModelId(nestComponent._parent.get("_id"));
            // Compatible with adapt-componentRow.
            parentView.$(".nestcomponent[data-nestcomponent-id=" + componentModel.get("_id") + "]").append(componentModel.get("_componentRow") ? componentView.$el.closest(".componentrow") : componentView.$el);
        }
    }

    new NestComponent();
});
