define([
    'core/js/adapt'
], function(Adapt) {

    // View for each individual category's content.
    // Gets passed a collection by SideMenuView.

    class SideMenuCategoryCreditsView extends Backbone.View {

        className() {
            return "smcategory smcategory-type-" + this.model.get("_type");
        }

        init(){}

        initialize(){
            
            this.init();
            this.render();

            //this.setupTopicData();
        }

        render(){

            var template = Handlebars.templates[this.constructor.template];

            var data = {
                _type: this.model.get("_type"),
                _items: this.model.get("_items").toJSON(),
                copyright: this.getDDCopyrightHtml()
            }

            this.$el.html(template(data));
        }

        /**
         * FIXME: Hardcoded.
         * Generates the standard DD copyright message.
         */
        getDDCopyrightHtml() {

            // Ensure the copyright is using whatever the current year is.
            let year = new Date().getFullYear();

            return `&#169; ${year} Didasko Digital. All Rights Reserved.`;
        }
        
    };

    SideMenuCategoryCreditsView.template = 'sideMenuCategoryCredits';
    return SideMenuCategoryCreditsView;
});
