define([
    'core/js/adapt',
    './codeBoxView',
    'libraries/jquery.actual.min'
], function(Adapt, CodeBoxView) {

    /**
     * Same as the regular CodeBoxView, only the content is rendered inside of an iframe instead of a normal element.
     * This is used mainly for HTML consoleboxes; as it prevents the contents from being affected (and affecting) any of
     * Adapt's CSS.
     */
    class CodeBoxIframeView extends CodeBoxView {

        events() {
            return _.extend({}, super.events, {
                
            });
        }

        setupEventListeners(){
            super.setupEventListeners.apply(this, arguments);

            // To ensure the iframe is always the correct height, set up an event listener for device resizing.
            Adapt.on("device:resize", this.updateIframeSize.bind(this));
        }

        /**
         * Override the original function.
         */
         renderContent(){
            let template = Handlebars.templates['codeBoxIframe'];
            this.$el.html(new Handlebars.SafeString(template(this.model.toJSON())).string);

            this.$(".js-codebox-iframe").on("load", this.onIframeLoad.bind(this));
        }

        waitUpdateIframeContent($iframe) {

            return new Promise(resolve => {

                let doc = $iframe[0].contentWindow.document;
                let content = this.model.get("content");

                // Extract all external script tags (tags with src) from the content.
                // They need to be added in a different way.
                // ------------------------------------------------------------------
                let scripts = [];
                content = content.replace(/<script.*?src=(['"]).*?\1.*?(<\/script>|\/>)/g, function(match) {
                    let src = match.match(/src=(['"])(.*?)\1/)[2];
                    scripts.push(src);
                    return "";
                });
                // ------------------------------------------------------------------

                // Extract all inline script tags.
                // They need to be added in a different way.
                // ------------------------------------------------------------------
                let inlineScripts = [];
                content = content.replace(/<script.*?>*.?>((.|\n)*)<\/\s*?script>/gm, function(match, $1) {
                    inlineScripts.push($1);
                    return "";
                });
                // ------------------------------------------------------------------

                // EPUB functionality. Prepend a comment that will tell the html2xhtml.js script to ignore this html when
                // validating it for XHTML.
                if (Adapt.isEpubMode()) {
                    content = "<!--!IGNORE-XHTML!-->" + content;
                }

                doc.body.innerHTML = content;

                // Give the HTML element in the iframe ALL classes of the main Adapt <html>.
                // This will allow for mode-specific styles to be used.
                doc.documentElement.className = $("html")[0].className;

                _.each(scripts, (src) => {
                    let s = doc.createElement("script");
                    s.src = src;
                    doc.body.appendChild(s);
                });

                _.each(inlineScripts, (code) => {
                    let s = doc.createElement("script");
                    s.type = 'text/javascript';
                    try {
                        s.appendChild(doc.createTextNode(code));
                        doc.body.appendChild(s);
                    }
                    // Fallback for older browsers.
                    catch (e) {
                        s.text = code;
                        doc.body.appendChild(s);
                    }
                });

                // Resolve once the HTML changes have been applied.
                // FIXME: Triggers multiple times in succession for every iframe on initial load.

                $iframe.contents().ready(() => {
                    $iframe.contents().off("ready");

                    // If DOMContentLoaded is already ready, resolve immediately.
                    if (doc.readyState === "complete" || doc.readyState === "loaded") {
                        resolve();
                        return;
                    }
                    // Otherwise wait for it.
                    doc.addEventListener("DOMContentLoaded", () => {
                        resolve();
                    }, {once: true});
                });
            });
        }

        /**
         * Once the iframe is ready for it, populate it with the provided HTML content, and calculate its height.
         * 
         * FIXME: This gets triggered multiple times per iframe.
         */  
        async onIframeLoad() {
            
            let $iframe = this.$(".js-codebox-iframe");
            await this.waitUpdateIframeContent($iframe);

            // CUSTOM: If there's a <form> in the iframe, overload it's "submit" event.
            let codeBox = Adapt.course.get("_codeBox");
            if (codeBox._formSubmitMessageEnabled === true) {
                $iframe.contents().find("form").off("submit").on("submit", (event) => {
                    event.preventDefault();
                    alert("Form submitted!");
                });
            }

            this.updateIframeSize();
        }

        // Waits for all images to have loaded in the provided iframe before resolving.
        waitIframeImgLoad($iframe) {
            return new Promise(resolve => {
                let $imgs = $iframe.contents().find("img");

                let remainingImages = $imgs.length;
                if (remainingImages == 0) {
                    resolve();
                    return;
                }

                let decrementCounter = function() {
                    remainingImages--;
                    if (remainingImages == 0) resolve();
                }

                $imgs.each((i, e) => {
                    if (e.complete) decrementCounter();
                    else $(e).one("load", () => {decrementCounter();});
                });
            });
        }

        /**
         * Updates the height CSS of the iframe to match its contents.
         */
        async updateIframeSize() {

            let $iframe = this.$(".js-codebox-iframe");
            $iframe.css("height", "");

            // If the real $iframe is display:none after the resize, clone it and check the clone's height.
            if ($iframe.is(":hidden")) {

                let $clone = $iframe.clone();
                $clone.off("load"); // Just in case it copied the original iframe's events.

                $clone.css({
                    "height": "0px",
                    "max-height": "0px"
                });
                $clone.on("load", async () => {

                    //$clone.one("load", this.onIframeLoad.bind(this));

                    // Set the clone's width to be identical to the originals (for accurate calculation).
                    let width = $iframe.actual("outerWidth");
                    $clone.width(width);

                    await this.waitUpdateIframeContent($clone);
                    await this.waitIframeImgLoad($clone);

                    let height = $clone[0].contentWindow.document.body.scrollHeight;
                    $iframe.height(height);

                    // Remove clone (we only needed it for calculating its height).
                    $clone.remove();
                });
                $("body").append($clone);
            }
            // Otherwise, just set its height based on its content.
            else {

                await this.waitIframeImgLoad($iframe);

                $iframe.css({"height": "0px"}); // Reset height before re-calculating.
                try {
                    let height = $iframe[0].contentWindow.document.body.scrollHeight;
                    $iframe.height(height);
                }
                catch (err) {
                    Adapt.log.error(err, $iframe);
                }
            }
        }
    };

    return CodeBoxIframeView;
});
