define([
    'core/js/adapt'
], function(Adapt) {

    // FIXME:
    // "_prevBlockBackground" implementation works; but has several visual bugs + is crudely implemented.
    // It should do for now, but could really use a rework if time permits.

    const VIEWPORT_MARGIN = "15%"; // Once a group crosses this point in the viewport, its background will appear.

    class BlockBackgroundView extends Backbone.View {

        className() {
            return "blockbg";
        }

        initialize() {

            this.onDeviceResize = _.debounce(this.onDeviceResize.bind(this), 100);

            this.blockView = Adapt.findViewByModelId(this.model.get("_parent").get("_id"));
            this.blockView.$el.addClass("has-blockbg").toggleClass("has-prevblockbg", Boolean(this.model.get("_prevBlockBackground")));

            this.setUpItemGraphics();
            this.render();
            this.onDeviceChange();
            this.updateClipMask();
            this.setUpEventListeners();
        }

        /**
         * Determing the item graphic to use on the initial load. 
         */
        setUpItemGraphics() {
            _.each(this.model.get("_items"), (item, i) => {
                if (!item._graphic) return;

                item._graphic.src = item._graphic["_" + (Adapt.device.screenSize == "large" ? "large" : "small")];
            });
        }

        onDeviceChange() {
            this.updateItemGraphics();
        }

        onDeviceResize() {
            this.updateClipMask();
        }

        /**
         * Whenever the device is resized, update the CSS clip style on this element.
         * 
         * Explanation:
         * ------------
         * Without the clipping mask, the 'position: fixed' .js-blockbg-item elements within it will 
         * overflow its borders (this happens regardless of whether overflow: hidden is applied or not).
         * 
         * Using a clipping mask seems to fix this.
         */
        updateClipMask() {

            if (!this.model.get("_isInview")) return; // Don't do anything if not inview (the user won't see it anyway).

            let coords = [
                "0",                        // Top
                "100vw",                    // Right
                this.$el.height() + "px",   // Bottom
                "0"                         // Left
            ]

            this.$el.css("clip", `rect(${coords.join(",")})`);
        }

        updateItemGraphics() {

            _.each(this.model.get("_items"), (item, i) => {

                let $item = this.$(".js-blockbg-item").filter("[data-adapt-index=" + i + "]");
                let $group = this.blockView.$(".js-blockbg-group-img").filter("[data-adapt-index=" + i + "]");
                let url = "url(" + item._graphic["_" + (Adapt.device.screenSize == "large" ? "large" : "small")] + ")";

                $item.css("background-image", url);
                $group.css("background-image", url);
            });
        }

        render() {

            let modelJSON = this.model.toJSON();

            // Render background elements.
            let template = Handlebars.templates['blockBackground'];
            this.$el.html(new Handlebars.SafeString(template(modelJSON)).string);
            this.blockView.$el.prepend(this.$el);

            // Render groups.
            template = Handlebars.templates['blockBackgroundGroups'];
            this.blockView.$(".component__container").prepend(new Handlebars.SafeString(template(modelJSON)).string);
        }

        setUpEventListeners() {

            this.listenTo(this.model, "change:_activeItemIdx", this.onActiveItemIdxChange.bind(this));
            this.listenTo(this.model, "change:_isInview", this.onIsInviewChange.bind(this));
            
            let prevBlockBackground = this.model.get("_prevBlockBackground");
            if (prevBlockBackground) {
                this.listenTo(prevBlockBackground, "change:_isInview", this.onPrevBlockBackgroundIsInviewChange.bind(this));
            }
            
            this.listenTo(Adapt, "device:changed", this.onDeviceChange);
            this.listenTo(Adapt, "device:resize", this.onDeviceResize);

            this.setUpObservers();
        }

        /**
         * Whenever this blockBackground is inview in any way, isInview == true.
         */
        onIsInviewChange(model, isInview) {
            
            // Perform an update to the clipmask as soon as this element becomes visible again.
            if (isInview) this.updateClipMask();

            let $items = this.$(".js-blockbg-item");
            
            let prevBlockBackground = this.model.get("_prevBlockBackground");

            // If the previous block has a blockBackground, then show this blockBackground in a different way.
            if (prevBlockBackground) {
                
                // When coming into view, hide all background items.
                // The onGroupInview callback will unhide the appropriate item once the user scrolls down a bit further.
                if (isInview) {
                    if (prevBlockBackground.get("_isInview")) {
                        $items.removeClass("is-active").addClass("is-hidden");
                    }
                    else {
                        $items.removeClass("is-hidden");
                    }
                    $items.toggle(isInview);
                }
                // When going out of view, fade out smoothly if the prevBlockBackground is inview.
                // Otherwise, hide instantly as normal.
                else {
                    if (!prevBlockBackground.get("_isInview")) {
                        // If this blockBackground has a prevBlockBackground, only hide its items once both it and the pbb have !_isInview
                        $items.toggle(false);
                    }
                }

                return;
            }

            $items.toggle(isInview);
        }

        /**
         * If this blockBackground has a prevBlockBackground, only hide its items once both it and the pbb have !_isInview
         */
        onPrevBlockBackgroundIsInviewChange(pbb, isInview) {

            if (!isInview && !this.model.get("_isInview")) {
                let $items = this.$(".js-blockbg-item");
                $items.toggle(false);
            }
        }

        setUpObservers() {

            // Observe whenever this view is out of view (and hide ALL items when it is).
            // DISABLE IF CAUSING TOO MANY PERFORMANCE ISSUES.
            const mainObserver = new IntersectionObserver((entries) => {
                entries.forEach((entry) => {
                    this.model.set("_isInview", entry.isIntersecting);
                });
            }, {
                root: null, // Browser viewport.
                threshold: [0]
            });
            mainObserver.observe(this.$el[0]);

            // Observe whenever a .blockbg__group goes into view.
            const observer = new IntersectionObserver((entries) => {
                entries.forEach(this.onGroupInview.bind(this));
            }, {
                root: null, // Browser viewport.
                rootMargin: "-" + VIEWPORT_MARGIN + " 0%", // Wait for the group to cross the middle of the viewport.
                threshold: [0]
            });

            let $groups = this.blockView.$(".js-blockbg-group");
            $groups.each((i, e) => {
                observer.observe(e);
            });
        }

        onGroupInview(entry) {

            let prevBlockBackground = this.model.get("_prevBlockBackground");
            
            let $group = $(entry.target);
            let index = $group.data("adapt-index");

            // Only react to elements entering the viewport (unless there's a prevBlockBackground).
            if (!entry.isIntersecting) {

                // Hide items on the way out.
                if (prevBlockBackground && prevBlockBackground.get("_isInview")) {
                    this.$(".js-blockbg-item")
                        .filter("[data-adapt-index=" + index + "]")
                        .addClass("is-hidden");
                }

                return;
            }

            this.model.set("_activeItemIdx", index);

            // prevBlockBackground support.
            if (prevBlockBackground) {
                this.$(".js-blockbg-item")
                    .filter("[data-adapt-index=" + index + "]")
                    .removeClass("is-hidden");
            }
        }

        // Fade in the appropriate background for the item.
        onActiveItemIdxChange(model, idx) {

            this.$(".js-blockbg-item")
                .removeClass("is-active")
                .filter("[data-adapt-index=" + idx + "]")
                .addClass("is-active");
        }
    }

    return BlockBackgroundView;
});
