define([
    'core/js/adapt'
], function(Adapt) {

    /**
     * Base model for each category inside the sidemenu__accordion.
     * (Each category will have a different collection of items assigned to its SideMenuCategoryModel).
     */
    
    class SideMenuCategoryModel extends Backbone.Model {

        defaults() {
            return {
                title: "UNDEFINED", // Title on the accordion.
                type: "UNDEFINED",  // Used for classnames.
                _items: undefined,  // Collection of item models.
                
                _isActive: false    // Whether the view for this category has been expanded in the parent accordion.
            }
        }

        initialize(){
            
        }

        toggle(){
            this.set("_isActive", !this.get("_isActive"));
        }
    };

    return SideMenuCategoryModel;
});
