define([
    'core/js/adapt'
], function(Adapt) {

    
    class FigureModel extends Backbone.Model {

        initialize() {
            this.initCaption();
        }

        /**
         * All figures which have captions need to have a "Figure X: " prefix.
         * The value of "X" must be unique for each figure, and increment with each one.
         */
        initCaption() {
            if (!this.get("caption")) return;
            this.set(Adapt.figure.createCaptionData(this.get("caption"), this.get("content")));
        }
    };

    return FigureModel;
});
