define([
    'handlebars',
    'core/js/adapt',
    './tooltipView',
    './tooltipPopupView'
], function(Handlebars, Adapt, TooltipView, TooltipPopupView) {

    /**
     * adapt-tooltip
     * --------------------------------------
     * - Creates inline tooltip popups that are activated by clicking on specific highlighted text.
     * - Can be given an optional icon after the highlighted text.
     */

    // Helpers
    // ---------------------
    // Hardcoded for DD functionality.

    Adapt.on('router:page', () => {

        /**
         * Reference
         * -----------------------------------------------
         */
        Handlebars.registerHelperEl("reference", function() {
            let context = arguments[arguments.length-1];

            // PDF mode override
            // ---------------------------------
            if (Adapt.isPDFMode()) {
                
                // Instead of showing a clickable book icon, show the popup text inline with the text,
                // preceeded by the book icon.
                let html = Handlebars.templates["reference-pdf"]({
                    // Insert data that the template will need to use here.
                    example: "EXAMPLE"
                });

                return new Handlebars.SafeString(html);
            }
            
            let modelJSON = _.extend({}, context.hash, {
                buttonText: "",
                content: context.fn(this),
                _img: "reference", // FIXME: Uses .svg for the icon.
                type: "reference"
            });

            return new TooltipView({
                model: new Backbone.Model(modelJSON)
            }).$el
        });

        /**
         * Glossary
         * -----------------------------------------------
         * @param {string} context.term The glossary term (MUST match the glossary item title in contentObjects.json).
         * @param {string} context.fn() The text the user clicks on to show the glossary. context.term will be set to this if it's not defined.
         */
        Handlebars.registerHelperEl("glossary", function() {
            let context = arguments[arguments.length-1];

            // Determine the glossary term provided.
            let glossaryTerm = context.hash.term || context.fn(this);
            glossaryTerm = glossaryTerm.trim();

            if (!glossaryTerm) throw "{{#glossary}}: Must have a valid \'term\' property.";

            // Replace the href with the external URL.
            let pageModel = Adapt.findById(Adapt.location._currentId);
            let glossaries = pageModel.get("_menuData")._glossaries;
            
            let glossaryData = _.find(glossaries._items, (g) => {
                return g.title.toLowerCase() == glossaryTerm.toLowerCase();
            });
            if (!glossaryData) throw "{{#glossary}}: Could not find glossary definition for \"" + glossaryTerm + "\".";

            let modelJSON = _.extend({}, context.hash, {
                buttonText:     context.fn(this),
                title:          glossaryData.title,
                content:        glossaryData.body,
                _icon:           "",
                type:           "glossary"
            });

            return new TooltipView({
                model: new Backbone.Model(modelJSON)
            }).$el
        });
    });
    
    /**
     * Adapt plugin
     * -------------
     * Allow tooltips to be manually created by other scripts over any element.
     * FIXME: Will likely need some reworking in the future.
     * @param {jQuery object} element   The element to render and center the tooltip over.
     * @param {string} settings.content The innerHTML of the tooltip popup.
     * @returns 
     */
    Adapt.createTooltip = function(element, settings = {}){
        
        // Allow a HTML string to just be passed as the second parameter as a shortcut.
        if (typeof settings === "string") {
            settings = {
                content: settings
            }
        }

        let tooltipPopupView = new TooltipPopupView({model: new Backbone.Model(_.extend(
            settings || {},
            {$target: $(element)}
            )
        )});

        return tooltipPopupView;
    }
});
