define([
    'handlebars',
    'core/js/adapt',
    './annotationView'
], function(Handlebars, Adapt, AnnotationView) {

    /**
     * adapt-annotation
     * --------------------------------------
     * This extension sets up resource buttons in the content to show fullscreen popup resources.
     * 
     * TODO:
     *  - May be changed to being a normal Component; depends on how frequently it's seen inside of other Components.
     * 
     * FIXME:
     * - Won't work if the resource is inside of another fullscreen reveal (like a HotgraphicPlus).
     * - A bit tedious to use (needs a partial to work).
     */

    // Helpers
    // ---------------------
    
    /**
     * Use these to create the inline tags for the resource button.
     * 
     * Note:
     * -----
     * Using Handlebars fixes the issue previous extensions developed for DD had, that being that
     * they couldn't be used inside of dynamically generated HTML (like inside of a popup for example).
     * Now, whenever the Handlebars is compiled, it knows to create the appropriate View for the thing.
     * Plus, it keeps the components.json tidy and consistant to use Handlebars expressions for such things.
     * (And no need for custom HTML tags).
     * 
     * FIXME:
     * ------
     * Had to use a weird workaround to get this one working. Monitor for possible bugs.
     */

    class Annotation extends Backbone.Controller {

        initialize() {
            Adapt.on('router:page', this.onRouterPage);
            this.initHelpers();
        }

        onRouterPage() {

            let annotationNum = 1; // Give each annotation in the unit a unique number (used by PDF mode).

            // Annotation
            // --------------------------------------------------
            Handlebars.registerHelperEl("annotation", function() {

                let context = arguments[arguments.length-1];
                let hash = context.hash;

                // Find the componentModel that this Helper was placed in.
                /*let componentModel = Handlebars.getComponentModelFromContext(context);
                console.log(componentModel);*/

                // The "for" parameter tells the annotation which elements it will activate.
                if (!hash.for) {
                    // If no "for" value set, check if a tooltip being used.
                    if (!hash.tooltip) {
                        throw "Annotation must have the \"for\" and/or \"tooltip\" parameter defined.";
                    }

                    // Annotations shouldn't have icons if they're only for tooltips.
                    if (hash.hasIcon === undefined){
                        hash.hasIcon = false;
                    }
                }
                else {
                    // Annotations should always have icons if they have "for" targets.
                    if (hash.hasIcon === undefined){
                        hash.hasIcon = true;
                    }
                }

                let modelJSON = {
                    content: context?.fn ? context.fn(this) : "", //context?.fn ? context.fn(this) : "",
                    _for: hash.for ? hash.for.split(",") : undefined,
                    _tooltip: hash.tooltip,
                    _hasIcon: hash.hasIcon !== false,
                    _number: annotationNum,
                    _parent: Handlebars.getComponentModelFromContext(context)
                };

                annotationNum++;

                return new AnnotationView({
                    model: new Backbone.Model(modelJSON)
                }).$el;
            });
        }

        initHelpers() {
            /**
             * Normal helpers
             */
            var helpers = {

                // 
                // 
                /**
                 * Mark <mark>
                 * --------------------------------------------------
                 * @param {string} absolute String defining 4 values for absolutely positioning the mark (in percent; "LEFT,TOP,WIDTH,HEIGHT").
                 * @returns HTML string
                 */
                mark: function() {
                    let context = arguments[arguments.length-1];

                    let data = context.hash || {};

                    // Allow for absolute positioning of the marks (used to make them overlay images).
                    // FIXME: May be renamed/reworked in future.
                    // FIXME: If an absolute mark is used inside of an img which is inside of a scrollable container, the width will be inaccurate.
                    if (data.absolute) {
                        let vals = data.absolute.split(",");
                        if (vals.length < 2) {
                            throw "{{#mark}} helper: the \"absolute\" property must have a string value formatted as follows: \"LEFT,TOP,WIDTH,HEIGHT\".\n\nFor example: absolute=\"10,10,20,20\".\n\nYou entered: absolute=\"" + absolute + "\"";
                        }
                        data.absolute = {
                            left: vals[0].trim() + "%",
                            top: vals[1].trim() + "%"
                        }
                        if (vals.length >= 3) data.absolute.width = vals[2].trim() + "%";
                        if (vals.length >= 4) data.absolute.height = vals[3].trim() + "%";
                    }

                    data._tag = "mark";

                    data = _.extend(
                        {},
                        {content: context.fn?.apply(this)},
                        data
                    );
                    let template = Handlebars.templates['mark'];

                    return new Handlebars.SafeString(template(data)).string;
                },

                // MarkBlock <div>
                // --------------------------------------------------
                markBlock: function() {
                    let context = arguments[arguments.length-1];

                    let data = _.extend({content: context.fn(this)}, context.hash || {}, {_tag: "div"});
                    let template = Handlebars.templates['mark'];

                    return new Handlebars.SafeString(template(data)).string;
                },

                // Fade <span>
                // --------------------------------------------------
                fade: function() {
                    let context = arguments[arguments.length-1];

                    let data = _.extend({content: context.fn(this)}, context.hash || {}, {_tag: "span"});
                    let template = Handlebars.templates['fade'];

                    return new Handlebars.SafeString(template(data)).string;
                },
                
                // FadeBlock <div>
                // --------------------------------------------------
                // Block level fading (should NOT be used inline).
                fadeBlock: function() {
                    let context = arguments[arguments.length-1];

                    let data = _.extend({content: context.fn(this)}, context.hash || {}, {_tag: "div"});
                    let template = Handlebars.templates['fade'];

                    return new Handlebars.SafeString(template(data)).string;
                },

                // Annotation tooltip fallback
                // --------------------------------------------------
                // If PDF mode is used, tooltips will be rendered into the nearest fallback element.
                // Should always be placed AFTER the annotation it refers to.
                // Note: likely to be renamed in future.
                // FIXME: Only one per component allowed.
                // FIXME: Will not work inside of popups.
                tooltipFallback: function() {
                    let template = Handlebars.templates['tooltipFallback'];
                    return new Handlebars.SafeString(template({})).string;
                }
            }

            for (var name in helpers) {
                if (helpers.hasOwnProperty(name)) {
                    Handlebars.registerHelper(name, helpers[name]);
                }
            }
        }

        /**
         * Manually "activates"/"deactivates" the provided $el(s) in exactly the same way that clicking an annotation button would. 
         */
        toggleEl($el, isActive) {

            $el.addClass("is-annotation-target").each((i, e) => {

                // Toggle it.
                if (!isActive){
                    $(e)
                        .toggleClass("is-active", false)
                        .trigger("annotation:inactive");
                }
                // Ensure de-activations happen before activations.
                // FIXME: Can be done with less _.debounce calls.
                else {
                    _.debounce(() => {
                        $(e)
                            .toggleClass("is-active", true)
                            .trigger("annotation:active");
                    })();
                }
            });
        
            if (!isActive) return;

            // Scroll the recently toggled element into view.
            Adapt.navigateToElement($el, {
                align: "inview",
                scrollNested: true
            });
        }
    }


    Adapt.annotation = new Annotation();
});
