const G = require("glob");

define([
  'core/js/adapt',
  'core/js/views/questionView',
  './imgFinderOutlineModel',
  './imgFinderOutlineView'
], function(Adapt, QuestionView, ImgFinderOutlineModel, ImgFinderOutlineView) {

  /**
   * A Question component which shows the user an image, and has them outline one or more specific areas on it to complete the task.
   * 
   * The user can click anywhere to create an outline; click on an existing outline to remove it; or click and drag an existing outline
   * to move it around.
   * 
   * How it works:
   * -------------
   * - An "item" for this template is an invisible area (hitbox) of the image that the user must place an outline over in order to
   *   complete the task.
   * - The user places an outline by clicking anywhere on the image.
   *   The dimensions of the outline are determined by the _outline._size property.
   *      - If "auto" || "avg", the outline will have the averaged dimensions of all item hotspots.
   *      - If "near", the outline will take on the dimensions of the hotspot CLOSEST to where the user clicked.
   *        This should be used SPARINGLY, as it could give away the answer.
   * - An item is considered "outlined" if the center of the outline element is within the item hitbox.
   * - If multiple outlines are covering the same item, the one closest to the item will be registered.
   *   The other will be re-checked against the remaining, not-yet-checked item hitboxes.
   * - Incorrectly placed outlines will leave behind a hitbox of their own. If the user tries to outline that area again, the outline will
   *   automatically turn red, before they submit anything. If the outline is also over a valid item hitbox, it will NOT turn red. a.k.a, 
   *   an outline NEVER turns red if submitting it would result in it being labelled as "correct".
   * 
   * TODO:
   * -----
   * - Allow for optional items (hotspots that aren't mandatory to be outlined, but also don't turn red).
   * - Allow for partial completion (example: there are 10 hotspots, and only 6 need to be outlined for a "complete" score).
   * - Prevent cheating using the browser's developer tools.
   */

  class ImgFinderView extends QuestionView {
    
    events() {
      return {
        "mousedown .js-imgfinder-outline-area" : "onOutlineAreaDown",
        "touchstart .js-imgfinder-outline-area" : "onOutlineAreaDown"
      };
    }

    initialize() {
      this.setUpModelData();
      super.initialize();
      this.setupEventListeners();
      
      /*this.update = _.debounce(this.update.bind(this), 1);
      this.setUpEventListeners();*/
    }

    setUpModelData() {

      // Set up adapt-sizing string for the background-graphic.
      let graphic = this.model.get("_graphic");
      if (graphic && graphic._sizing) {
        graphic._sizing._classes = Adapt.sizing.getSizingClass(graphic._sizing);
      }
    }

    setupEventListeners() {
      this.listenTo(this.model, {
        "change:_isSubmitted": this.onIsSubmittedChange,
        "change:_isCorrect": this.onIsCorrectChange
      });
    }

    /** 
     * When the user gets the component correct; disable further interaction.
     */
    onIsSubmittedChange(model, isSubmitted) {

      if (!isSubmitted) return;
            
      this.$el
          .toggleClass("is-submitted", isSubmitted);
    }
    onIsCorrectChange(model, isCorrect) {
            
      this.$el
          .toggleClass("is-correct", isCorrect)
          .toggleClass("is-incorrect", !isCorrect);
    }

    /**
     * Allow for easy positioning of pin items using the adapt-dd-debug plugin.
     */
    setUpDebug() {

      if (!Adapt.debug?._isEnabled) return;

      
      if (Adapt.debug?.setUpInteract) {
        let $items = this.$(".js-imgfinder-item");
        let $container = this.$(".js-imgfinder-item-container");
        Adapt.debug.setUpInteract($items, $container, false);
      }
    }

    /**
     * Whenever the user clicks on the area where outlines are created, add one.
     * @param {*} event 
     */
    onOutlineAreaDown(event) {

      if (this.model.get("_isSubmitted")) return; // Don't allow the creation of more outlines after submitting.

      // Location (relative to the outline area) to place the CENTER of the outline.
      // Needs to be in percentages.
      let $area = this.$(".js-imgfinder-outline-area");

      // Only register clicks on the area (and not on the outlines within it).
      if (!$area.is($(event.target))) return;

      event.preventDefault();
      event.stopPropagation(); // Probably not needed.

      let x = 0;
      let y = 0;
      let bb = $area[0].getBoundingClientRect();

      // Get the offset relative to the $area.
      // A different approach needs to be used depending on if the event was touch or click.
      if (event.touches) {
        let touch = event.touches[0] || event.changedTouches[0];

        x = touch.clientX - bb.left;
        y = touch.clientY - bb.top;
      }
      else {
        x = event.clientX - bb.left;
        y = event.clientY - bb.top;
      }

      let pos = {
        _left: (x / $area.width()) * 100,
        _top: (y / $area.height()) * 100
      }

      this.addOutline(pos);
    }

    /**
     * Adds an outline in the provided position.
     * @param {Object} position Location that the center of the outline should be; RELATIVE to the onOutlineAreaDown.
     */
    addOutline(position) {

      let $area = this.$(".js-imgfinder-outline-area");

      let modelData = _.extend({}, this.model.get("_outline"), {
        _position: position, // In percentage values!
        _parent: this.model
      });

      let outlineModel = new ImgFinderOutlineModel(modelData);
      let outlineView = new ImgFinderOutlineView({model: outlineModel});

      $area.append(outlineView.$el);

      // Store it in the collection.
      this.model.addOutline(outlineModel);
    }

    



















    setupQuestion() {
      this.model.setupRandomisation();

      // Modified:
      // This view's model inherits itemsComponentModel, and as such will consider itself complete if all items have _isVisited: true.
      // adapt-dd-questionPlus doesn't want this, so stop listening for _isVisited events on the items.
      this.model.stopListening(this.model.getChildren(), "change:_isVisited", this.model.checkCompletionStatus);
    }

    // This is important and should give the user feedback on how they answered the question
    // Normally done through ticks and crosses by adding classes
    showMarking() {
      //this.update();
      
      // adapt-dd-questionPlus:
      this.updateMarking();
    }

    update() {
      this.updateMarking();
    }

    postRender() {
      super.postRender();
      this.setUpDebug();
    }

    /**
     * When the user closes the feedback notify, wait X seconds, then de-activate incorrectly activated items.
     * This was done to improve the UI experience, as some users didn't realize they needed to manually de-select
     * the incorrectly selected items.
     * 
     * DD: DD specific implementation.
     */
    onSubmitted() {
      super.onSubmitted();
      this.listenToOnce(Adapt, "notify:closed", this.onFeedbackClosed);
    }

    onFeedbackClosed() {
      //this.deselectIncorrectAnswers();
    }
    
    showCorrectAnswer() {
      this.update();
    }

    hideCorrectAnswer() {
      this.update();
    }

    setUpEventListeners(){
      
      this.listenTo(this.model.getChildren(), {
        'change:_isActive': this.onItemsActiveChange,
        'change:_isVisited': this.onItemsVisitedChange
      });
    }

    onItemClick(event) {
      if (event) event.preventDefault();

      var item = this.model.getItem($(event.currentTarget).data('adapt-index'));
      item.toggleActive();
    }

    getItemElement(model) {
      var index = model.get('_index');
      return this.$('.js-finder-item').filter('[data-adapt-index="' + index + '"]');
    }

    onItemsActiveChange(model, _isActive) {

      this.getItemElement(model)
        .toggleClass('is-active', _isActive)
        .find(".js-item-label").toggleClass('is-selected', _isActive);
      this.updateContainer();
    }

    onItemsVisitedChange(model, _isVisited) {
      if (!_isVisited) return;
      var $item = this.getItemElement(model);

      // Append the word 'visited.' to the aria-label
      /*var visitedLabel = this.model.get('_globals')._accessibility._ariaLabels.visited + ".";
      $item.find('.aria-label').each(function(index, el) {
        el.innerHTML += " " + visitedLabel;
      });*/

      $item.addClass('is-visited');
    }

    /**
     * adapt-dd-questionPlus will called this whenever the user clicks Submit.
     */
    updateMarking() {

      let isInteractive = this.model.isInteractive();
      let canShowMarking = this.model.get('_canShowMarking');
      let ariaLabels = Adapt.course.get('_globals')._accessibility._ariaLabels;

      // FIXME: Checkbox state changes are a bit messily implemented.

      this.model.getChildren().each((itemModel) => {

        let index = itemModel.get('_index');
        let $item = this.getItemElement(itemModel);
        let $itemLabel = $item.find(".js-item-label");

        let shouldBeSelected = Boolean(itemModel.get("_shouldBeSelected"));
        let isActive = Boolean(itemModel.get("_isActive"));
        // An item is only considered "visited" once it has been submitted at least once.
        let isVisited = Boolean(itemModel.get("_isVisited") || isActive); itemModel.set("_isVisited", isVisited);
        let isCorrect = shouldBeSelected && isActive; // Only mark active items as correct.

        // Disable items only when the item has been completed, or when the entire component has been completed.
        let isDisabled = !isInteractive || isCorrect;
        $item
          .prop('disabled', isDisabled)
          .toggleClass('is-disabled', isDisabled)
          .find(".finder__item-answer-icon")
          .toggleClass('is-disabled', isDisabled);

        // Remove marking from non-visited items.
        if (/*isInteractive || */ !isVisited || !canShowMarking) {
          $item.removeClass('is-correct is-incorrect');
          $itemLabel.removeClass('is-correct is-incorrect');
          return;
        }

        // Mark visited items (that are selected).
        // (DD questionPlus design choice).
        if (!isActive) return;

        $item
          .toggleClass('is-correct', isCorrect)
          .toggleClass('is-incorrect', !isCorrect)
          .toggleClass('is-disabled', isDisabled)
          .prop('disabled', isDisabled);

        $itemLabel
        .toggleClass('is-correct', isCorrect)
        .toggleClass('is-incorrect', !isCorrect)
        .toggleClass('is-disabled', isDisabled);
      });

      this.updateContainer();
    }

    /**
     * The container for the items will gain the .is-incorrect class while it contains any active incorrect items.
     * The container for the items will gain the .is-correct class only when the component is completed.
     */
    updateContainer() {

      let isAtLeastOneIncorrect = Boolean(
        this.model.getChildren().find((itemModel) => {
          return itemModel.get("_isVisited") && itemModel.get("_isActive") && !itemModel.get("_shouldBeSelected");
        })
      );

      let $container = this.$(".finder__items-container");
      $container
        .toggleClass("is-incorrect", isAtLeastOneIncorrect)
        .toggleClass("is-correct", Boolean(this.model.get("_isCorrect")));
    }

    /*onDeviceResize() {
      this.recalcItemSize();
    }*/

    onQuestionRendered() {
      this.setReadyStatus();
      this.update();
    }
  }

  ImgFinderView.template = "imgFinder";
  return ImgFinderView;
});
