define([
    'core/js/adapt',
    './codeBoxFadeBlockView'
], function(Adapt, CodeBoxFadeBlockView) {

    /**
     * Add google-code-prettify to the requirejs shim.
     * FIXME:
     * I'm not sure this is the correct way to add shim elements to Adapt,
     * but I couldn't find much other documentation about it online.
     */
    require.config({
        shim: {
            "libraries/google-code-prettify/prettify": {
                exports: "PR"
            }
        }
    });

    const RENDER_LINE_CONTAINERS = true; 
    const DESKTOP_BOOKSHELF_TAB_STRING = "&nbsp;&nbsp;"; // Fallback for adapt-epub compatibility.

    // The following languages need additional scripts loaded from libraries/google-code-prettify/*
    // FIXME: Hardcoded.
    const LANG_SCRIPTS = [
        "css",
        "lua",
        "basic",
        "apollo"
    ];

    class CodeBoxView extends Backbone.View {

        className() {

            let sizingClasses = this.model.get("_sizing");

            let classes = [
                "codebox",
                this.model.get("_classes") || "",
                sizingClasses || "",
                sizingClasses ? "has-sizing" : "",
                "is-" + this.model.get("_type"),
                "lang-" + this.model.get("_lang"),
                this.model.get("_wrappingEnabled") ? "wrapping-enabled" : ""
            ];

            return classes.join(" ");
        }

        attributes() {
            return {
                "data-codebox-lang": this.model.get("_lang") || "auto",
                "style": this.model.get("_style") || ""
            }
        }

        tagName() {
            return "div"; // <pre> not being used as the grunt task already uniquely formats the code.
        }

        events() {

        }

        initialize(){

            // Escape HTML content (if applicable).
            this.escapeHTMLContent();

            this.setupEventListeners();
            //this.initTargets();
            this.render();
        }

        setupEventListeners(){

        }

        renderContent(){
            let template = Handlebars.templates['codeBox'];
            this.$el.html(new Handlebars.SafeString(template(this.model.toJSON())).string);
        }

        render(){

            this.renderContent();

            // adapt-dd-figure compatibility
            // ------------------------------------------
            // Add a class to the closest .figure element (if applicable).
            _.debounce(()=>{
                this.$el.closest(".figure").addClass("is-figure-codebox");
            }, 0)();

            // adapt-epub compatibility
            // ------------------------------------------
            // Desktop Bookshelf renders tab characters too large. If it's detected, have the
            // tab characters be made of &nbsp; instead.
            if (Adapt.config.get('_isDesktopBookshelf')) {
                this.$(".tab").html(DESKTOP_BOOKSHELF_TAB_STRING);
            }

            //this.setUpFigure();
            this.renderPrettify();
        }

        /**
         * If the language is "html" (or "xml" or "xhtml"), the code content needs to have all of its < and > escaped.
         */
        escapeHTMLContent() {

            // Ensure that this the right language is used.
            let lang = this.model.get("_lang");
            if (
                lang != "html" &&
                lang != "xml" &&
                lang != "xhtml"
            ) return;

            let _type = this.model.get("_type");
            let content = this.model.get("content");

            // If this is the console, remove all generated <br> tags and leave the rest as is.
            if (_type == "console") {
                this.model.set("content", content.replace(Adapt.codeBox.BR_REGEX, ""));
                return;
            }

            // If this is the codebox, do nothing (jsonsynt-build.js has already escaped the < and >).
        }

        /**
         * Apply syntax highlighting to codeBox views with type "code".
         */
        renderPrettify() {
            if (this.model.get("_type") !== "code") return;

            let lang = this.model.get("_lang");

            // Don't pretty print if language is set to "none".
            if (lang == "none") return;
            
            // Also don't pretty print if set to pseudo / pseudocode.
            if (lang == "pseudo" || lang == "pseudocode") return;

            // Load the required lang script from the library too (if the language uses one).
            let langRequired = [];
            if (LANG_SCRIPTS.includes(lang)) langRequired.push("libraries/google-code-prettify/lang-" + lang);

            require(["libraries/google-code-prettify/prettify"], (Prettify) => {

                // lang-* script must be loaded after prettify.
                require(langRequired, () => {

                    let $content = this.$(".js-codebox-content");

                    // Wrap the elements in syntax highlighting tags.
                    $content.html(PR.prettyPrintOne($content.html(), lang || undefined)); //.addClass("prettyprint");

                    // After syntax highlighting, wrap each line in its own element.
                    // This is done to allow for optional line-colouration / numbers.
                    if (!RENDER_LINE_CONTAINERS) return;
                    this.renderLineContainers();
                });
            });
        }

        /**
         * Returns and array of HTML strings which will need to be appended to the element.
         * @param {*} blockHTML
         */
        _getLinesHTMLArray(blockHTML) {

            let html = blockHTML;
            let htmlLines;
            
            htmlLines = html.split(Adapt.codeBox.BR_REGEX);
            let arr = [];

            // Trim leading and trailing <br/>s
            // Done to make the following two formatting styles have the same outcome:
            /*
            <div>
            *CODE*
            </div>

            <div>*CODE*</div>
            */
            if (htmlLines.length && !htmlLines[0].trim()) htmlLines.shift();
            if (htmlLines.length >= 1 && !htmlLines[htmlLines.length-1].trim()) htmlLines.pop();

            _.each(htmlLines, (htmlLine, idx) => {

                // If there's nothing in the line, add a newline symbol instead.
                // This will show up in the codebox due to its white-space: pre-line; CSS property.
                let h = htmlLine;
                let isEmpty = false;
                if ($(h).text().trim().length == 0) {
                    // If the htmlLine is completely blank, just add a newline.
                    if (h.trim().length == 0) {
                        h = "\n";
                        isEmpty = true;
                    }
                    // If something has already filled the htmlLine with tags, instead add the newline at the end of the last one.
                    else {
                        h.replace(/(<\/)(?!.*<\/)/g, "\n</"); // Insert a newline at the end of the last element.
                        isEmpty = true;
                    }
                }

                arr.push('<div class="codebox__line' + (isEmpty ? ' is-empty' : '') + '">' + h + '</div>');
            });

            return arr;
        }

        /**
         * FIXME:(?)
         * This sort of processing of how the lines are rendered and the special treatment for <div>s could be deferred to Grunt,
         * but would be quite complex to implement. 
         */
        renderLineContainers() {

            // Each line of code is determined based on the <br> tags that grunt added when compiling.
            let $codeBoxInner = this.$(".js-codebox-content");
            let htmlLines = [];

            // Unique clause: keep any outer <div> wrappers (wrap code lines in them).
            // (Gives compatibility with {{#fadeBlock}} and {{#markBlock}} helpers).
            let $divs = $codeBoxInner.children("div");

            // Contains no nested DIVs (common).
            if (!$divs.length) {
                htmlLines = this._getLinesHTMLArray($codeBoxInner.html());
            }
            // Contains nested DIVs. Insert lines sequentially, but keep outer div wrappers.
            else {
                
                // Insert all elements prior to the first div
                // ------------------------------------------
                // prevUntil gives the children in the reverse order, so reverse it again.
                let $first = $($divs.first().prevUntil("div").get().reverse());
                // The HTML is derrived this way to ensure <br/> tags are included too.
                if ($first.length) {
                    htmlLines = htmlLines.concat(this._getLinesHTMLArray($first.wrapAll("<div>").parent().html()));
                }

                // Insert all div elements, and all elements between divs
                // ------------------------------------------------------
                $divs.each((i, e) => {
                    
                    // Add the DIV header.
                    let openingTagHtml = e.outerHTML.match(/^<div(>|.*?[^?]>)/);
                    if (!openingTagHtml) {
                        Adapt.log.error("codeBoxView.js: could not determine opening <div> tag HTML for the following element:", $(e));
                    }
                    openingTagHtml = openingTagHtml[0];
                    htmlLines.push(openingTagHtml);

                    // Add the lines in the DIV.
                    htmlLines = htmlLines.concat(this._getLinesHTMLArray($(e).html()));

                    // Add a closing tag.
                    htmlLines.push("</div>");

                    // Add the lines for elements that come after this DIV but aren't inside one.
                    let $next = $(e).nextUntil("div");
                    if ($next.length) {
                        htmlLines = htmlLines.concat(this._getLinesHTMLArray($next.wrapAll("<div>").parent().html()));
                    }
                });
            }

            // Finally, modify the HTML to the new processed htmlLines.

            let $codeBoxLines = $('<div class="codebox__lines">');
            $codeBoxLines.html(htmlLines.join(""));
            // Set the data-line-num attributes for the codebox__lines, but not the wrapper <div>s.
            $codeBoxLines.find(".codebox__line").each((i, e) => {
                $(e).attr("data-line-num", i+1);
            });
            this.$(".js-codebox-content").empty().append($codeBoxLines);

            this.renderFadeBlocks();
        }

        /**
         * Custom functionality:
         * If a .fade.is-accordion element is detected inside the code, modify the elements to allow it to collapse it.
         * 
         * FIXME: Fairly hardcoded at this time. Needs to be made more flexible if used often.
         */
        renderFadeBlocks() {

            let $fadeBlocks = this.$(".fade.is-accordion");
            if (!$fadeBlocks.length) return;

            $fadeBlocks.each((i, e) => {

                $(e).addClass("fadeblock");
                let $headerLine = $(e).find(".codebox__line:first-child");
                let $contentLines = $(e).find(".codebox__line:not(:first-child)");
                
                // Wrap the first codebox line in the "header" div.
                $headerLine.wrapAll("<div class='fadeblock__header' title='View omitted code'>")

                // Wrap all but the first codebox line in a div.
                $contentLines.wrapAll("<div class='fadeblock__content'>");

                new CodeBoxFadeBlockView({
                    el: $(e),
                    model: new Backbone.Model({
                        _isActive: false
                    })
                });
            });
        }
    };

    return CodeBoxView;
});
