define([
    'handlebars',
    'core/js/adapt',
    './downloadView'
], function(Handlebars, Adapt, DownloadView) {

    /**
     * adapt-dd-download
     * --------------------------------------
     * Register a Handlebars helper for creating download links.
     */

    // Helpers
    // ---------------------

    Adapt.on('router:page', () =>{

        Handlebars.registerHelperEl("download", function() {

            let data = Handlebars.getContextHash.apply(this, arguments);
            data._download = data.download; // Property cannot have the same name as the helper (confuses Handlebars).
            data._icon = data.icon || "download"; // Allow for custom icons to be used.

            // If in Epub mode, test if the URL is local or absolute.
            if (Adapt.config.get("_isEpub")){            
                var rAbsoluteURL = new RegExp('^(?:[a-z]+:)?//', 'i');
                
                // The URL is local (inside of the .epub zip). Redirect it to an external server.
                if (!rAbsoluteURL.test(data.href)) {

                    let filePath;
                    try {
                        filePath = data.href.match(/^course[/\\](.+)?[/\\]downloads[/\\](.+)$/);
                        filePath = filePath[2];
                    }
                    catch (err) {
                        throw "{{#download}} href property has invalid URL:\n\n" + data.href + "\n\nAll local downloadable files must be located in \"course/**/downloads/**.";
                    }

                    const externalURL = Adapt.config.get("_download")?._externalMirrorURL;
                    if (!externalURL) throw "config.json: _download._externalMirrorURL must be defined!";

                    // Replace the href with the external URL.
                    let pageModel = Adapt.findById(Adapt.location._currentId);

                    let productionCode =    pageModel.get("productionCode");
                    let topicNum =          pageModel.get("topicNum");
                    if (productionCode === undefined)   throw "\"productionCode\" must be defined in contentObjects.jsonsynt!";     
                    if (topicNum === undefined)         throw "\"topicNum\" must be defined in contentObjects.jsonsynt!";     

                    data.href = externalURL + "/" + productionCode + "/" + productionCode + "." + topicNum + "/" + filePath;
                    data._isExternal = true;
                }
            }

            // NEW: Detect if the file being provided will be automatically opened by the browser if the user clicks the download link.
            // Append some instructions if that's the case (FIXME: Hardcoded message).
            let modelJSON = _.extend({}, data, {
                tooltip: data._isExternal ? "Right-click and select ‘Save link as...’ to save this file to your device." : ""
            });

            return new DownloadView({
                model: new Backbone.Model(modelJSON)
            }).$el;
        });

    });
});
