define([
  'core/js/adapt',
  'core/js/views/componentView'
], function(Adapt, ComponentView) {

  class HotgraphicFlipView extends ComponentView{
    
    // FIXME: Better names for the two events.
    events() {
      return {
        'click .js-hotgraphicflip-item-click': 'onItemClick',
        'click .js-hotgraphicflip-item-btn': 'onItemBtnClick'
      }
    }

    initialize(){

      this.setUpModelData();
      
      ComponentView.prototype.initialize.call(this);

      this.setUpEventListeners();
      this.checkIfResetOnRevisit();
    }

    checkIfResetOnRevisit() {
      var isResetOnRevisit = this.model.get('_isResetOnRevisit');

      // If reset is enabled set defaults
      if (isResetOnRevisit) {
        this.model.reset(isResetOnRevisit);
      }
    }

    setUpModelData() {
      // adapt-sizing extension compatibility.
      // FIXME: implements its own version of sizing.
      let itemSizing = this.model.get("_itemSizing");
      if (itemSizing && itemSizing._width) {
        //itemSizing._classes = Adapt.sizing.getSizingClass(itemSizing); 
        itemSizing._classes = "col-sm-" + itemSizing._width;

        // FIXME: Non standard implementation (needs to use adapt-sizing).
        if (itemSizing._maxWidthInner) {
          itemSizing._itemClasses = "--maxwidth-inner-" + itemSizing._maxWidthInner;
        }
      }
    }

    preRender() {
      //this.render();
    }

    postRender() {

      // Only start showing the component once all images are ready.
      this.$('.hotgraphicflip__widget').imageready(() => {
        this.setReadyStatus();
        _.debounce(this.recalcItemHeight.bind(this), 0)();
      });

      // Not being used.
      if (this.model.get('_setCompletionOn') === 'inview') {
        this.setupInviewCompletion('.component__widget');
      }
    }

    setUpEventListeners() {
      
      this.onDeviceResize = _.debounce(this.onDeviceResize, 0);
      this.listenTo(Adapt, 'device:resize', this.onDeviceResize);

      this.listenTo(this.model.getChildren(), {
        'change:_isActive': this.onItemsActiveChange,
        'change:_isVisited': this.onItemsVisitedChange
      });
    }

    getItemElement(model) {
      var index = model.get('_index');
      return this.$('.js-hotgraphicflip-item-click').filter('[data-index="' + index + '"]');
    }

    onItemClick(event) {

      // If inactive, allow clicking anywhere on the card to flip.
      // If active, only flip back if the icon is clicked (different event).
      var item = this.model.getItem($(event.currentTarget).data('index'));
      if (item.get("_isActive")) return;

      // Important; prevents the click bleeding through to the flip-back button.
      // iOS bugfix(?): Performed after the "_isActive" check (something was preventing click events from triggering on buttons
      // inside the item-back element).
      event.preventDefault();
      event.stopPropagation();

      item.toggleActive();
      item.toggleVisited(true);
    }

    // Flip back.
    onItemBtnClick(event) {
      event.preventDefault();
      event.stopPropagation(); // Important; prevents the click bleeding through to the flip-forward button.

      var item = this.model.getItem($(event.currentTarget).data('index'));
      if (!item.get("_isActive")) return;

      item.toggleActive();
    }

    onItemsActiveChange(model, _isActive) {
      this.getItemElement(model).toggleClass('is-active', _isActive);
    }

    onItemsVisitedChange(model, _isVisited) {
      if (!_isVisited) return;
      var $item = this.getItemElement(model);

      // Append the word 'visited.' to the aria-label
      var visitedLabel = this.model.get('_globals')._accessibility._ariaLabels.visited + ".";
      $item.find('.aria-label').each(function(index, el) {
        el.innerHTML += " " + visitedLabel;
      });

      $item.addClass('is-visited');
    }

    /**
     * Ensure all the items have the same height.
     * FIXME: Is there no better way of doing this?
     */
    onDeviceResize() {
      this.recalcItemHeight();
    }

    /**
     * Min-height is used in-case the text size changes after the resize due to external methods
     * (better to have the text visible and the cards misaligned than the opposite).
     */
    recalcItemHeight(){
      let $items = this.$(".hotgraphicflip__item");

      // Remove previous min-height.
      $items.css({
        "min-height": ""
      });

      // Find the tallest item.
      let h = 0;
      $items.each((i, e) => {
        let _h = $(e).height();
        if (_h > h) {
          h = _h;
        }
      });

      // Enforce the _itemSizing._minHeightRatio (if present).
      let minHeightRatio = this.model.get("_itemSizing")?._minHeightRatio;
      if (minHeightRatio && !isNaN(minHeightRatio)) {
        let w = $items.first().width();      // Get the width of the first item (all items have the same width).
        h = Math.max(h, w*minHeightRatio);  // Set the minimum height to ensure the ratio is met.
      }

      $items.css({
        "min-height": h
      });
    }
  }

  HotgraphicFlipView.template = "hotgraphicFlip";
  return HotgraphicFlipView;

});
