define([
  'core/js/adapt',
  'core/js/views/componentView'
], function(Adapt, ComponentView) {

  class HotgraphicMiniView extends ComponentView{
    
    events() {
      return {
        'click .js-hotgraphicmini-item-click': 'onItemClick'
      }
    }

    initialize(){

      this.setUpModelData();
      
      ComponentView.prototype.initialize.call(this);

      this.setUpEventListeners();
      this.checkIfResetOnRevisit();
    }

    checkIfResetOnRevisit() {
      var isResetOnRevisit = this.model.get('_isResetOnRevisit');

      // If reset is enabled set defaults
      if (isResetOnRevisit) {
        this.model.reset(isResetOnRevisit);
      }
    }

    setUpModelData() {
      // adapt-sizing extension compatibility.
      let itemSizing = this.model.get("_itemSizing");
      if (itemSizing && itemSizing._width) {
        itemSizing._classes = Adapt.sizing.getSizingGridClasses(itemSizing);
      }
    }

    setUpSizingListeners() {
      let itemSizing = this.model.get("_itemSizing");
      if (itemSizing) {
        Adapt.sizing.setUpGridListeners(this.$(".sizing-grid"));
      }
    }

    preRender() {
      //this.render();
    }

    postRender() {

      // Only start showing the component once all images are ready.
      this.$('.hotgraphicmini__widget').imageready(() => {
        this.setReadyStatus();
        _.debounce(this.recalcContentMargins.bind(this), 0)();
      });

      // Not being used.
      if (this.model.get('_setCompletionOn') === 'inview') {
        this.setupInviewCompletion('.component__widget');
      }

      this.setUpSizingListeners();
    }

    setUpEventListeners() {
      
      this.onDeviceResize = _.debounce(this.onDeviceResize, 0);
      this.listenTo(Adapt, 'device:resize', this.onDeviceResize);

      this.listenTo(this.model.getChildren(), {
        'change:_isActive': this.onItemsActiveChange,
        'change:_isVisited': this.onItemsVisitedChange
      });
    }

    getItemElement(model) {
      var index = model.get('_index');
      return this.$('.js-hotgraphicmini-item').filter('[data-adapt-index="' + index + '"]');
    }

    onItemClick(event) {
      if (event) event.preventDefault();

      var item = this.model.getItem($(event.currentTarget).data('adapt-index'));
      item.toggleActive();
      item.toggleVisited(true);
    }

    onItemsActiveChange(model, _isActive) {
      this.getItemElement(model).toggleClass('is-active', _isActive);
    }

    onItemsVisitedChange(model, _isVisited) {
      if (!_isVisited) return;
      var $item = this.getItemElement(model);

      // Append the word 'visited.' to the aria-label
      var visitedLabel = this.model.get('_globals')._accessibility._ariaLabels.visited + ".";
      $item.find('.aria-label').each(function(index, el) {
        el.innerHTML += " " + visitedLabel;
      });

      $item.addClass('is-visited');
      $item.find(".js-hotgraphicmini-item-click").addClass("is-visited");
    }

    /**
     * Ensure all the items have the same height.
     * FIXME: Is there no better way of doing this?
     */
    onDeviceResize() {
      this.recalcContentMargins();
    }

    /**
     * Hide the item contents until the item is clicked to reveal them.
     */
    recalcContentMargins(){

      let $contents = this.$(".js-hotgraphicmini-content");

      // Remove previous margin.
      $contents.css({
        "margin-bottom": ""
      });

      // Remove the vertical presence of the content elements by giving them a -height margin.
      $contents.each((i, e) => {
        let h = $(e).height();
        $(e).css("margin-bottom", -h).hide(0).show(0); // Skip the CSS transition.
      });
    }
  }

  HotgraphicMiniView.template = "hotgraphicMini";
  return HotgraphicMiniView;
});
