define([
    'core/js/adapt',
], function(Adapt) {

    class StickyElementItemModel extends Backbone.Model {

        defaults() {
            return {
                _parent: undefined,             // StickyElementModel parent.

                _id: "",                        // Inherited from StickyElementModel parent.
                _componentItemIndex: "",        // The Component item index.

                _componentItem: undefined,      // Component item that contains the StickyElement (or the componentModel itself if the item is in a StickyBlock).
                _view: undefined,               // Reference to the view (done because the StickyElementItemViews need to access each other).

                _isActive: false,               // Whether this model + view is currently visible.

                _isStickyBlock: false           // This is item will behave slightly differently if using StickyBlock mode.
            }
        }

        initialize() {
            this.set("_componentItemIndex", this.get("_componentItem").get("_index"));
            this.set("_isActive", this.get("_componentItem").get(this.get("_isStickyBlock") ? "_isVisible" : "_isActive"));

            //console.log(this.get("_componentItem").toJSON());

            this.setUpEventListeners();
        }

        // Return the amount of items belonging to this group.
        getItemsCount() {

            // adapt-stickyBlock compatibility.
            // Return the amount of siblings this component has if being used in a stickyBlock.
            if (this.get("_isStickyBlock")) {
                return this.get("_componentItem").get("_stickyBlockSiblings")?.length;
            }

            return this.get("_parent").get("_items").length;
        }

        setUpEventListeners() {

            if (!this.get("_isStickyBlock")) {
                this.listenTo(this.get("_componentItem"), `change:_isActive`, this.onComponentItemActiveChange);
            }
            // adapt-stickyBlock compatibility:
            else {
                this.listenTo(this.get("_componentItem"), `stickyBlock:inview`, this.onStickyBlockInview);
            }

            this.listenTo(this, "change:_isActive", this.onActiveChange);
        }

        onComponentItemActiveChange(itemModel, isActive) {
            this.set("_isActive", isActive);
        }

        /**
         * adapt-stickyBlock compatibility.
         * @param {*} activeIndex 
         */
         onStickyBlockInview() {

            let view = this.get("_view");
            if (!view) return;

            // Determine the StickyBlock's active item index (the index of the stickyChild that is inview right now).
            let activeIndex = view.$el.getParentStickyBlockIndex();

            this.set("_isActive", true);
            this.set("_componentItemIndex", activeIndex); // This value normally doesn't change; but in stickyBlocks it can.
        }

        onActiveChange(model, isActive) {
            if (isActive) this.get("_parent").setItemActive(this);
        }

        /**
         * Returns the previously active StickyElementItemModel.
         * Returns undefined if the previously active ComponentItem didn't contain a StickyElementItemModel with the same "_id" as this one.
         */
        getItemPrev() {
            return this.get("_parent").get("_itemPrev");
        }

        /**
         * Trigger's a stickyElement:itemShow on the currently active item model.
         * Used by the stickyElementItemView to set its initial state.
         */
        refresh(){
            console.log("REFRESH WON'T DO ANYTHING YET");
            //this.get("_parent").refresh();
        }

        /**
         * Get the StickyElementItemModel that was active before the current one (or null if there was none).
         */
        getPrevActiveStickyElementItem() {

        }
    };

    return StickyElementItemModel;
});
