define([
    'core/js/adapt'
], function(Adapt) {

    const CLICK_ANYWHERE_EVENTS = "click touch mouseup touchend";

    class TooltipPopupView extends Backbone.View {

        tagName() {
            return "div";
        }

        className() {
            let classes = [
                "tooltip-popup",
                (this.model.get("type") || this.model.get("_type")) ? ("is-" + (this.model.get("type") || this.model.get("_type"))) : ""
            ]
            return classes.join(" ");
        }

        events() {
            return {
                //"click .js-tooltip" : "onClick"
            }
        }

        initialize() {
            
            this.setUpEventListeners();
            this.render();
        }

        setUpEventListeners() {

            this.listenTo(Adapt, {'device:resize': this.onDeviceResize});

            _.debounce(() => {
                // The user clicking anywhere or scrolling is enough to close the tooltip popup.
                this.onClickAnywhereEvent = this.onClickAnywhere.bind(this);
                $(document).on(CLICK_ANYWHERE_EVENTS, this.onClickAnywhereEvent);
            }, 0)();
        }

        onClickAnywhere(event) {

            let innerEl = this.$(".tooltip-popup__inner")[0];

            if (event.target == innerEl || $.contains(innerEl, event.target)) return; // Clicks on or inside the popup won't close it.
            this.close();
        }

        onDeviceResize() {
            this.close();
        }

        close() {
            this.model.set("_isActive", false);
            this.remove();
        }

        // Override default remove function.
        remove() {
            this.model.set("_isActive", false);
            $(document).off(CLICK_ANYWHERE_EVENTS, this.onClickAnywhereEvent); // Remove the global event this made.
            Backbone.View.prototype.remove.apply(this, arguments);
        }

        render() {
            this.$el.html(new Handlebars.SafeString(Handlebars.templates['tooltipPopup'](this.model.toJSON())).string);
            
            // Append self to the page, or if in notify_popup, append to that instead (whichever is closest to the $target).
            // FIXME: Not the best way to do this, but this does fix several issues the tooltipPopupView was having.
            // FIXME: Minor bugs if the tooltip is inside a nested scrollbar (however, this is a very rare occurence).
            // FIXME: Can be resolved if instead, the closest scrolling element was found (using jQuery-ui)?
            this.$parent = this.model.get("$target").closest(".notify__popup, #app, .sidemenu");
            this.$parent.append(this.$el);
            this.alignToTarget();
        }

        alignToTarget() {
            let $target = this.model.get("$target");
            if (!$target || !$target.length) {
                throw "toolipPopupView needs an existing $target element to align to";
            }
            $target = $target.first();

            // EPUB bugfix:
            // ------------
            // Due to how the scrolling of the .notify__popup element is set up, the getBoundingClientRect() function has a bug
            // where it sometimes returns incorrect values if used on elements inside of it.
            // 
            // To resolve this, temporarily disable the scrolling of the closest .notify__popup, then re-enable it.

            /**
             * Try to center the popup over the target horizontally.
             * If this would result in it clipping off the left/right of the viewport, move it slightly off-center
             * to fix.
             * 
             * "getClientRects" is used instead of "getBoundingClientRect" because it accounts for the
             * $target wrapping across multiple lines.
             */

            let targetBB = $target[0].getClientRects()[0]; //$target[0].getBoundingClientRect();
            let elBB = this.el.getBoundingClientRect();

            let parentWidth = this.$parent[0].clientWidth; // Element width - scrollbar.

            let left = targetBB.left - ((elBB.width - targetBB.width) / 2);
            let _left = left;
            left = Math.min(parentWidth - elBB.width, left); // Fix clipping off the right side.
            left = Math.max(0, left); // Fix clipping off the left side.

            // Determine the tail offset.
            let tailOffset = _left - left;
            this.$(".tooltip-popup__tail-container").css({
                "transform": "translateX(" + tailOffset + "px)"
            });

            this.$el.offset({
                left: left,
                top: $target.offset().top
            });

            // Ensure that the tooltip is focused on (scroll it into view).
            try {
                Adapt.navigateToElement(this.$(".tooltip-popup__inner"), {
                    duration: 400,
                    align: "inview",
                    notifyEnabled: true
                });
            }
            catch (err) {
                // Done to prevent errors if a tooltip is used in the sidemenu.
            }
        }

    };

    return TooltipPopupView;
});
