define([
    'core/js/adapt'
], function(Adapt) {

    /**
     * adapt-events
     * ============
     * 
     * Description:
     * ------------
     * Core Adapt doesn't seem to have any way of listening for a preRender / postRender on individual models,
     * instead requiring the events be listened to globally.
     * 
     * Warning:
     * --------
     * There could be a good reason why core Adapt doesn't come with these events.
     * Try to avoid using too much until they've been fully tested.
     */

    class Events extends Backbone.Controller{

        initialize() {
            
            Adapt.on({
                'app:dataReady': this.onDataReady.bind(this)
            });
        }

        onDataReady() {
            // Make global events listenable on an individual level.
            Adapt.on("componentView:preRender", this.componentViewPreRender.bind(this));
            Adapt.on("componentView:postRender", this.componentViewPostRender.bind(this));
            Adapt.on("blockView:preRender", this.blockViewPreRender.bind(this));
            Adapt.on("blockView:postRender", this.blockViewPostRender.bind(this));

            // New helper events:
            // -----------------------------------

            // blockView:childrenReady
            Adapt.blocks.each((blockModel) => {
                
                blockModel.listenTo(blockModel.getChildren(), {
                    "change:_isReady": (model) => {

                        // Check if the block's children have any !isReady members remaining.
                        let notReadyChild = blockModel.getChildren().find((child) => {
                            return !child.get("_isReady");
                        });
                        // If there is, then don't trigger the event yet.
                        if (notReadyChild) {
                            return;
                        }

                        // All children are ready.
                        let blockView = Adapt.findViewByModelId(blockModel.get("_id"));
                        blockModel.trigger("blockView:childrenReady", blockView);
                        Adapt.trigger("blockView:childrenReady view:childrenReady", blockView); // Trigger globally too.
                    }
                });
            });
        }

        componentViewPreRender(view) {view.model.trigger("componentView:preRender", view);}
        componentViewPostRender(view) {view.model.trigger("componentView:postRender", view);}
        blockViewPreRender(view) {view.model.trigger("blockView:preRender", view);}
        blockViewPostRender(view) {view.model.trigger("blockView:postRender", view);}
    }

    new Events();
});
