define([
  'core/js/adapt',
  'components/adapt-contrib-accordion/js/adapt-contrib-accordion'
], function(Adapt, Accordion) {

  // FIXME: View needs improvements (just a crude modification of Accordion View for now).

  let TryThisView = Accordion.view.extend({

    postRender: function() {
      Accordion.view.prototype.postRender.call(this);
    }

  }, {
    template: 'tryThis'
  });

  let TryThisModel = Accordion.model.extend({
    
    initialize: function() {

      // Add the tryThis logo to this component (using the adapt-dd-componentLogo extension).
      this.set("_componentLogo", {
          src: "assets/trythis-logo.svg"
      });

      // Apply the values in "_reflect" as the only "_item" for the Accordion component that TryThis inherited.
      let reflect = this.get("_reflect");

      if (reflect) {
        this.set("_items", [
          {
            title: "Reflect",
            body: reflect?.body || ""
          }
        ]);
      }
      else {
        this.set("_items", []);
      }

      Accordion.model.prototype.initialize.call(this);
    }
  });

  return Adapt.register('trythis', {
    model: TryThisModel,
    view: TryThisView
  });

});
